/* global isNaN */
import { t } from '../locale/locale';
import helper from './helper';
import { ChineseCharacterCodingLength } from '../config';

const rules = {
  phone: /^[1-9]\d{10}$/,
  email: /w+([-+.]w+)*@w+([-.]w+)*.w+([-.]w+)*/,
};

function returnMessage(flag, key, ...arg) {
  let message = '';
  if (!flag) {
    message = t(`validation.${key}`, ...arg);
  }
  return [flag, message];
}

export default class CustomerValidator {
  // operator: b|nb|eq|neq|lt|lte|gt|gte
  // type: date|number|list|phone|email
  initData(cellProps = {}, customerValidate, rindex, cindex) {
    const {
      cellType, required, value, operator,
    } = cellProps;
    this.required = required;
    this.value = value;
    this.type = cellType;
    this.operator = operator;
    this.message = '';
    this.customerValidate = customerValidate;
    this.rindex = rindex;
    this.cindex = cindex;
    this.cellProps = cellProps;
    return this;
  }

  parseValue(v) {
    const { type } = this;
    // if (type === 'date') {
    //   return new Date(v);
    // }
    if (type === 'text') {
      return helper.getRealTextLen(v, ChineseCharacterCodingLength).realLen;
    }
    if (type === 'number') {
      return Number(v);
    }
    return v;
  }

  equals(other) {
    let flag = this.type === other.type
      && this.required === other.required
      && this.operator === other.operator;
    if (flag) {
      if (Array.isArray(this.value)) {
        flag = helper.arrayEquals(this.value, other.value);
      } else {
        flag = this.value === other.value;
      }
    }
    return flag;
  }

  values() {
    return this.value.split(',');
  }

  validate(v) {
    const {
      required, operator, value, type,
    } = this;
    if (typeof this.customerValidate === 'function' && this.cellProps) {
      const [isRight, validationTip] = this.customerValidate(this.cellProps, v, this.rindex, this.cindex);
      if (!isRight) {
        return [isRight, validationTip];
      }
    }
    if (!type) {
      return [true];
    }
    if (required && /^\s*$/.test(v)) {
      return returnMessage(false, 'required');
    }
    if (/^\s*$/.test(v)) return [true];
    if (rules[type] && !rules[type].test(v)) {
      return returnMessage(false, 'notMatch');
    }
    if (type === 'list') {
      return returnMessage(this.values().includes(v), 'notIn');
    }
    let key = 'subjectString';
    if (this.type === 'numeric') {
      key = 'subjectNumber';
      if (isNaN(v)) {
        return returnMessage(
          false,
          'isNaN',
          t(`validation.${key}`),
        );
      }
    }
    if (operator) {
      const v1 = this.parseValue(v);
      if (operator === 'be') {
        const [min, max] = value;
        return returnMessage(
          v1 >= min && v1 <= max,
          'between',
          t(`validation.${key}`),
          min,
          max,
        );
      }
      if (operator === 'nbe') {
        const [min, max] = value;
        return returnMessage(
          v1 < min || v1 > max,
          'notBetween',
          t(`validation.${key}`),
          min,
          max,
        );
      }
      if (operator === 'eq') {
        return returnMessage(
          v1 === value,
          'equal',
          t(`validation.${key}`),
          value,
        );
      }
      if (operator === 'neq') {
        return returnMessage(
          v1 !== value,
          'notEqual',
          t(`validation.${key}`),
          value,
        );
      }
      if (operator === 'lt') {
        return returnMessage(
          v1 < value,
          'lessThan',
          t(`validation.${key}`),
          value,
        );
      }
      if (operator === 'lte') {
        return returnMessage(
          v1 <= value,
          'lessThanEqual',
          t(`validation.${key}`),
          value,
        );
      }
      if (operator === 'gt') {
        return returnMessage(
          v1 > value,
          'greaterThan',
          t(`validation.${key}`),
          value,
        );
      }
      if (operator === 'gte') {
        return returnMessage(
          v1 >= value,
          'greaterThanEqual',
          t(`validation.${key}`),
          value,
        );
      }
    }
    return [true];
  }
}
