/* eslint-disable no-param-reassign */
function cloneDeep(obj) {
  return JSON.parse(JSON.stringify(obj));
}

const mergeDeep = (object = {}, ...sources) => {
  sources.forEach((source) => {
    Object.keys(source).forEach((key) => {
      const v = source[key];
      // console.log('k:', key, ', v:', source[key], typeof v, v instanceof Object);
      if (typeof v === 'string' || typeof v === 'number' || typeof v === 'boolean') {
        object[key] = v;
      } else if (typeof v !== 'function' && !Array.isArray(v) && v instanceof Object && v.nodeType === undefined) {
        object[key] = object[key] || {};
        mergeDeep(object[key], v);
      } else {
        object[key] = v;
      }
    });
  });
  // console.log('::', object);
  return object;
};

function equals(obj1, obj2) {
  const keys = Object.keys(obj1);
  if (keys.length !== Object.keys(obj2).length) return false;
  for (let i = 0; i < keys.length; i += 1) {
    const k = keys[i];
    const v1 = obj1[k];
    const v2 = obj2[k];
    if (v2 === undefined) return false;
    if (typeof v1 === 'string' || typeof v1 === 'number' || typeof v1 === 'boolean') {
      if (v1 !== v2) return false;
    } else if (Array.isArray(v1)) {
      if (v1.length !== v2.length) return false;
      for (let ai = 0; ai < v1.length; ai += 1) {
        if (!equals(v1[ai], v2[ai])) return false;
      }
    } else if (typeof v1 !== 'function' && !Array.isArray(v1) && v1 instanceof Object) {
      if (!equals(v1, v2)) return false;
    }
  }
  return true;
}

/*
  objOrAry: obejct or Array
  cb: (value, index | key) => { return value }
*/
const sum = (objOrAry, cb = value => value) => {
  let total = 0;
  let size = 0;
  Object.keys(objOrAry).forEach((key) => {
    total += cb(objOrAry[key], key);
    size += 1;
  });
  return [total, size];
};

function deleteProperty(obj, property) {
  const oldv = obj[`${property}`];
  delete obj[`${property}`];
  return oldv;
}

function rangeReduceIf(min, max, inits, initv, ifv, getv, exceptRowSet = new Set()) {
  let s = inits;
  let v = initv;
  let i = min;
  for (; i < max; i += 1) {
    if (s > ifv) break;
    if (!exceptRowSet.has(i)) {
      v = getv(i);
      s += v;
    }
  }
  return [i, s - v, v];
}

function rangeSum(min, max, getv) {
  let s = 0; let start = min; let end = max;
  if (min > max) {
    start = max;
    end = min;
  }
  for (let i = start; i < end; i += 1) {
    s += getv(i);
  }
  if (min > max) {
    s = 0 - s;
  }
  return s;
}

function rangeEach(min, max, cb) {
  for (let i = min; i < max; i += 1) {
    cb(i);
  }
}

function rangeEach2(min, max, cb) {
  for (let i = min; i <= max; i += 1) {
    cb(i);
  }
}

function arrayEquals(a1, a2) {
  if (a1.length === a2.length) {
    for (let i = 0; i < a1.length; i += 1) {
      if (a1[i] !== a2[i]) return false;
    }
  } else return false;
  return true;
}

/**
 * A specialized version of `.filter` for arrays without support for callback
 * shorthands and `this` binding.
 *
 * {@link https://github.com/lodash/lodash/blob/master/lodash.js}
 *
 * @param {Array} array The array to iterate over.
 * @param {Function} predicate The function invoked per iteration.
 * @returns {Array} Returns the new filtered array.
 */
function arrayFilter(array, predicate) {
  let index = -1;
  let iterable = array;

  if (!Array.isArray(array)) {
    iterable = Array.from(array);
  }

  const { length } = iterable;
  const result = [];
  let resIndex = -1;

  while (++index < length) {
    const value = iterable[index];

    if (predicate(value, index, iterable)) {
      result[++resIndex] = value;
    }
  }

  return result;
}

/**
 * A specialized version of `.forEach` for arrays without support for callback
 * shorthands and `this` binding.
 *
 * {@link https://github.com/lodash/lodash/blob/master/lodash.js}
 *
 * @param {Array|*} array The array to iterate over or an any element
 * with implemented iterator protocol.
 * @param {Function} iteratee The function invoked per iteration.
 * @returns {Array} Returns `array`.
 */
function arrayEach(array, iteratee) {
  let iterable = array;

  if (!Array.isArray(array)) {
    iterable = Array.from(array);
  }

  const { length } = iterable;

  for (let index = length - 1; index >= 0; index--) {
    if (iteratee(iterable[index], index, iterable) === false) {
      break;
    }
  }


  return array;
}
/**
 * A specialized version of `.map` for arrays without support for callback
 * shorthands and `this` binding.
 *
 * @param {Array} array The array to iterate over.
 * @param {Function} iteratee The function invoked per iteration.
 * @returns {Array} Returns the new filtered array.
 */
function arrayMap(array, iteratee) {
  let index = -1;
  let iterable = array;

  if (!Array.isArray(array)) {
    iterable = Array.from(array);
  }

  const { length } = iterable;
  const result = [];
  let resIndex = -1;

  while (++index < length) {
    const value = iterable[index];

    result[++resIndex] = iteratee(value, index, iterable);
  }

  return result;
}

/**
 * A specialized version of `.forEach` for objects.
 *
 * @param {Object} object The object to iterate over.
 * @param {Function} iteratee The function invoked per iteration.
 * @returns {Object} Returns `object`.
 */
function objectEach(object, iteratee) {
  for (const key in object) {
    if (!object.hasOwnProperty || (object.hasOwnProperty && Object.prototype.hasOwnProperty.call(object, key))) {
      if (iteratee(object[key], key, object) === false) {
        break;
      }
    }
  }
  return object;
}

/**
 * Extend the Base object (usually prototype) of the functionality the `mixins` objects.
 *
 * @param {Object} Base Base object which will be extended.
 * @param {Object} mixins The object of the functionality will be "copied".
 * @returns {Object}
 */
function mixin(Base, ...mixins) {
  if (!Base.MIXINS) {
    Base.MIXINS = [];
  }
  arrayEach(mixins, (mixin) => {
    Base.MIXINS.push(mixin.MIXIN_NAME);

    objectEach(mixin, (value, key) => {
      if (Base.prototype[key] !== void 0) {
        throw new Error(`Mixin conflict. Property '${key}' already exist and cannot be overwritten.`);
      }
      if (typeof value === 'function') {
        Base.prototype[key] = value;

      } else {
        let getter = function _getter(propertyName, initialValue) {
          propertyName = `_${propertyName}`;

          let initValue = (value) => {
            if (Array.isArray(value) || isObject(value)) {
              value = deepClone(value);
            }

            return value;
          };

          return function() {
            if (this[propertyName] === void 0) {
              this[propertyName] = initValue(initialValue);
            }

            return this[propertyName];
          };
        };
        let setter = function _setter(propertyName) {
          propertyName = `_${propertyName}`;

          return function(value) {
            this[propertyName] = value;
          };
        };
        Object.defineProperty(Base.prototype, key, {
          get: getter(key, value),
          set: setter(key),
          configurable: true,
        });
      }
    });
  });

  return Base;
}
function isFormulaExpression(expression) {
  return typeof expression === 'string' && expression.length >= 2 && expression.charAt(0) === '=';
}
function isDropdownRangeExpression(cell) {
  if (cell && cell.typeOptions && cell.typeOptions.sourceRange) {
    return true;
  }
  return false;
}

/**
 * @description: 此方法用于下拉大数据量滚动时，把多余的已经渲染过的行删除，释放内存，删除条件在这里判断，格表和流表第一行不能删除
 * @param {type} 流表、行号
 * @return: boolean
 * @Author: linjian
 * @Date: 2019-09-17 13:53:13
 */
function isRowInStream(streamCharts, row) {
  return streamCharts.some((item) => {
    const { finalFirstRows, finalLastRows } = item;
    if (finalFirstRows < row || finalLastRows >= row) {
      return true;
    }
    return false;
  });
}


function getStreamRangeItem(streamCharts, rowIndex) {
  let streamItem;
  streamCharts.some((item) => {
    const { finalFirstRows, finalLastRows } = item;
    if (finalFirstRows <= rowIndex || finalLastRows >= rowIndex) {
      streamItem = item;
      return true;
    }
    return false;
  });
  return streamItem;
}

function getStreamItem(streamCharts, row) {
  let streamItem;
  streamCharts.some((item) => {
    const { finalFirstRows } = item;
    if (finalFirstRows === row) {
      streamItem = item;
      return true;
    }
    return false;
  });
  return streamItem;
}

function getStreamLastRowTempRow(streamCharts, row) {
  let tempRow;
  streamCharts.some((item) => {
    const { finalFirstRows, finalLastRows } = item;
    if (finalLastRows === row) {
      tempRow = finalFirstRows;
      return true;
    }
    return false;
  });
  return tempRow;
}

function getReferenceString(instance, { row, column }) {
  const cell = instance.getCell(row, column);
  const str = cell.text;
  if (str && str[0] === '=') {
    return str;
  }
  if (cell && cell.typeOptions && cell.typeOptions.sourceRange) {
    return cell.typeOptions.sourceRange;
  }
  return false;
}

// 移出指定格子的公式依赖
function removeDependents(dataProxy, ri, ci) {
  if (dataProxy.isCalCulatorFormula) {
    dataProxy.dependences.all.forEach((dependence) => {
      // 这里是删除行的处理
      dependence.dependents.map((dependent) => {
        const { row: dependentRow, column: dependentCol } = dependent;
        if (dependentRow === ri && dependentCol === ci) {
          dependence.removeDependent(dependent);
        }
        return true;
      });
    });
  }
}

// 更新streamCharts
function updateStreamCharts({
  streamCharts = [], actionType = 'insert-row', start = 0, n = 0,
}) {
  streamCharts.forEach((item) => {
    const { finalFirstRows, finalLastRows } = item;
    if (actionType === 'insert-row') { // 插行
      if (start < finalFirstRows) {
        item.finalFirstRows += n;
        item.finalLastRows += n;
      }
      if (start >= finalFirstRows && start <= finalLastRows) {
        item.finalLastRows += n;
      }
    } else { // 删行
      if (start < finalFirstRows) {
        item.finalFirstRows -= n;
        item.finalLastRows -= n;
      }
      if (start >= finalFirstRows && start <= finalLastRows) {
        item.finalLastRows -= n;
      }
    }
    return item;
  });
  return streamCharts;
}

function getRealTextLen(char, ChineseCharacterCodingLength) {
  let realLen = 0;
  for (let index = 0; index < char.length; index++) {
    const text = char.charCodeAt(index);
    if (text > 127 || text === 94) {
      realLen += ChineseCharacterCodingLength;
    } else {
      realLen += 1;
    }
  }
  return { realLen, ChineseCharacterCodingLength };
}

/**
 * @description: 用来获取多选数组结果
 * @param {type}
 * @return:
 * @Author: linjian
 * @Date: 2019-06-13 19:50:15
 */
function getTreeData(tree, target, result) {
  tree.forEach((item) => {
    const { id, text } = item;
    const indexTarget = target.indexOf(id);
    if (indexTarget !== -1) {
      result.push(text);
      if (Array.isArray(target)) {
        target.splice(indexTarget, 1);
      }
    }
    if (item.children && item.children.length > 0) {
      getTreeData(item.children, target, result);
    }
    return true;
  });
}


/**
 * @description: 用来获取多选数组结果,专门给外部粘贴使用，外部粘贴要正反比较下拉项id和text
 * @param {type}
 * @return:
 * @Author: linjian
 * @Date: 2019-06-13 19:50:15
 */
function getTreeDataForOutPaste(tree, target, reaultKeys = []) {
  tree.forEach((item) => {
    const { id, text } = item;
    if (target.includes(id) || target.includes(text)) {
      reaultKeys.push(id);
    }
    if (item.children && item.children.length > 0) {
      getTreeData(item.children, target, reaultKeys);
    }
    return true;
  });
}

function isCellInRange(targetRange, ri, ci) {
  if (!targetRange) {
    return false;
  }
  const {
    sri, eri, sci, eci,
  } = targetRange;
  return ri >= sri && ri <= eri && ci >= sci && ci <= eci;
}


function isCellInMerge(merges, ri, ci) {
  let isInMerge = false;
  if (!merges || !merges._ || merges._.length === 0) {
    return false;
  }
  merges._.forEach((item) => {
    const {
      sri, sci, eri, eci,
    } = item;
    if (ri >= sri && ri <= eri && ci >= sci && ci <= eci) {
      if (!(ri === sri && ci === sci)) {
        isInMerge = true;
        return false;
      }
    }
    return true;
  });
  return isInMerge;
}

function getLastCellMerge(merges, ri, ci) {
  let isInMerge = false;
  let lastCi = 0;
  let firstCi = 0;
  let firstRi = 0;
  let lastRi = 0;
  if (!merges || !merges._ || merges._.length === 0) {
    return { isInMerge };
  }

  merges._.some((item) => {
    const {
      sri, sci, eri, eci,
    } = item;
    if (ri >= sri && ri <= eri && ci >= sci && ci <= eci) {
      isInMerge = true;
      lastCi = eci;
      firstCi = sci;
      firstRi = sri;
      lastRi = eri;
      return true;
    }
    return false;
  });
  return {
    isInMerge, lastCi, firstCi, firstRi, lastRi,
  };
}

export default {
  cloneDeep,
  merge: (...sources) => mergeDeep({}, ...sources),
  equals,
  arrayEquals,
  sum,
  rangeEach,
  rangeEach2,
  rangeSum,
  rangeReduceIf,
  deleteProperty,
  arrayFilter,
  arrayEach,
  arrayMap,
  objectEach,
  mixin,
  isFormulaExpression,
  getReferenceString,
  removeDependents,
  getStreamItem,
  isDropdownRangeExpression,
  updateStreamCharts,
  getRealTextLen,
  getTreeData,
  getStreamLastRowTempRow,
  isCellInMerge,
  getLastCellMerge,
  isCellInRange,
  isRowInStream,
  getStreamRangeItem,
  getTreeDataForOutPaste,
};
