import helper from '../../core/helper';
import BaseCell from './_base';
import ExpressionModifier from '../expressionModifier';

const STATE_OUT_OFF_DATE = 1;
const STATE_COMPUTING = 2;
const STATE_UP_TO_DATE = 3;
const states = [STATE_OUT_OFF_DATE, STATE_COMPUTING, STATE_UP_TO_DATE];
const { arrayFilter } = helper;
/**
 * Class responsible for wrapping formula expression. It contains calculated value of
 * the formula, an error if it has happened
 * and cell references which indicates a relationship with regular
 * cells. This object uses physical cell coordinates.
 *
 * @class CellValue
 * @util
 */
class CellDependent extends BaseCell {
  /**
   * Out of date state indicates cells ready for recomputing.
   *
   * @returns {Number}
   */
  static get STATE_OUT_OFF_DATE() {
    return STATE_OUT_OFF_DATE;
  }

  /**
   * Computing state indicates cells under processing.
   *
   * @returns {Number}
   */
  static get STATE_COMPUTING() {
    return STATE_COMPUTING;
  }

  /**
   * Up to date state indicates cells with fresh computed value.
   *
   * @returns {Number}
   */
  static get STATE_UP_TO_DATE() {
    return STATE_UP_TO_DATE;
  }

  constructor(row, column) {
    super(row, column);

    /**
     * List of precedents cells.
     *
     * @type {Array}
     */
    this.references = [];
    /**
     * Computed value.
     *
     * @type {*}
     */
    this.value = null;
    /**
     * Indicates cell state.
     *
     * @type {String}
     */
    this.state = STATE_OUT_OFF_DATE;
    // 相关reference的字符串，在 插入/删除 行/列后，转成expressionModifier对象
    this.referenceRange = null;
    // 外部依赖
    this.externalReferences = {
      // SHEET2 : []
    };
  }

  /**
   * Set computed value.
   *
   * @param {*} value
   */
  setValue(value) {
    this.value = value;
  }

  /**
   * Get computed value.
   *
   * @returns {*}
   */
  getValue() {
    return this.value;
  }

  /**
   * Set error message for this cell.
   *
   * @param {String} error Error name.
   */
  setError(error) {
    this.error = error;
  }

  /**
   * Get error name for this cell.
   *
   * @returns {String|null}
   */
  getError() {
    return this.error;
  }

  /**
   * Check if cell value is marked as error.
   *
   * @returns {Boolean}
   */
  hasError() {
    return this.error !== null;
  }

  /**
   * Set cell state.
   *
   * @param {Number} state Cell state.
   */
  setState(state) {
    if (states.indexOf(state) === -1) {
      throw Error(`Unrecognized state: ${state}`);
    }
    this.state = state;
  }

  /**
   * Check cell state.
   *
   * @returns {Boolean}
   */
  isState(state) {
    return this.state === state;
  }

  /**
   * Add precedent cell to the collection.
   *
   * @param {CellReference} cellReference Cell reference object.
   */
  add(cellReference) {
    // 自身依赖自身
    if (this.isEqual(cellReference)) {
      throw new Error('#SELF_REF!');
    }
    const { sheetName } = cellReference;
    let targetReferences;
    if (sheetName) {
      if (!this.externalReferences[sheetName]) {
        this.externalReferences[sheetName] = [];
      }
      targetReferences = this.externalReferences[sheetName];
    } else {
      targetReferences = this.references;
    }
    if (!this.hasReference(cellReference)) {
      targetReferences.push(cellReference);
    }
  }

  /**
   * Remove precedent cell from the collection.
   *
   * @param {CellReference} cellReference Cell reference object.
   */
  remove(cellReference) {
    if (this.isEqual(cellReference)) {
      throw new Error('#REF!');
    }
    let arr;
    if (cellReference.sheetName) {
      arr = this.externalReferences[cellReference.sheetName] || [];
    } else {
      arr = this.references;
    }
    const index = arr.indexOf(cellReference);
    if (index > -1) {
      arr.splice(index, 1);
    }
  }

  /**
   * Clear all precedent cells.
   */
  clearReferences() {
    this.references.length = 0;
    this.externalReferences = {};
  }

  /**
   * Check if cell value has precedents cells.
   *
   * @returns {Boolean}
   */
  hasReferences() {
    return this.references.length > 0;
  }

  /**
   * Check if cell reference is precedents this cell.
   *
   * @param {CellReference} cellReference Cell reference object.
   * @returns {Boolean}
   */
  hasReference(cellReference) {
    const { sheetName } = cellReference;
    let targetReferences;
    if (sheetName) {
      if (!this.externalReferences[sheetName]) {
        return false;
      }
      targetReferences = this.externalReferences[sheetName];
    } else {
      targetReferences = this.references;
    }
    return arrayFilter(targetReferences, (cell) => {
      cell.isEqual(cellReference, true);
    }).length > 0;
  }

  setReferenceRange(referenceRange) {
    this.referenceRange = referenceRange;
  }

  // 如果 referenceRange 是字符串，则强转成 ExpressionModifier 对象
  transferReferenceRange() {
    let { referenceRange } = this;
    if (typeof referenceRange === 'string') {
      referenceRange = new ExpressionModifier(referenceRange);
      this.referenceRange = referenceRange;
    }
    return referenceRange;
  }

  translateTo(rowOffset, columnOffset) {
    this.row = this.row + rowOffset;
    this.column = this.column + columnOffset;
  }

  // 返回 referenceRange 是否已经改变
  operate(operation, start, amount) {
    const { row: origRow, column: origColumn } = this;
    const referenceRange = this.transferReferenceRange();
    switch (operation) {
      case 'insertRow':
        if (origRow >= start) {
          this.translateTo(amount, 0);
        }
        referenceRange.startTranslate(
          operation,
          { row: amount },
          { row: start, column: origColumn },
        );
        break;
      case 'removeRow':
        if (origRow >= start) {
          this.translateTo(-amount, 0);
        }
        referenceRange.startTranslate(
          operation,
          { row: -amount },
          { row: start, column: origColumn },
        );
        break;
      case 'insertCol':
        if (origRow >= start) {
          this.translateTo(0, amount);
        }
        referenceRange.startTranslate(
          operation,
          { column: amount },
          { row: origRow, column: start },
        );
        break;
      case 'removeCol':
        if (origRow >= start) {
          this.translateTo(0, -amount);
        }
        referenceRange.startTranslate(
          operation,
          { column: -amount },
          { row: origRow, column: start },
        );
        break;
      default: throw Error(`${operation}方法不存在`);
    }
    // refreshAfterRowColOperation 参数，用于自定义公式生成自定义依赖
    // 在行列变化时，强制重新生成依赖，重新计算
    if (referenceRange.changed || this.refreshAfterRowColOperation) {
      return referenceRange.toString();
    }
    return false;
  }
}

export default CellDependent;
