import { CellRange } from './cell_range';

export default class Selector {
  constructor() {
    this.range = new CellRange(0, 0, 0, 0);
    this.ri = 0;
    this.ci = 0;
  }

  multiple() {
    return this.range.multiple();
  }

  setIndexes(ri, ci) {
    this.ri = ri;
    this.ci = ci;
  }

  // linjian 添加设置cellrange方法，全选全部 可以使用
  setRange(range) {
    const {
      sri, sci, eri, eci,
    } = range;
    this.range.sri = sri;
    this.range.sci = sci;
    this.range.eri = eri;
    this.range.eci = eci;
  }

  size() {
    return this.range.size();
  }
}
