// 定位算法
const getPosition = (relativeElem, elem, { elemOffsetWidth = 0, elemOffsetHeight = 0, cellRect }) => {
  let rect = null;
  if (relativeElem) {
    rect = relativeElem.getBoundingClientRect(); // 绑定元素的坐标;
  } else {
    rect = cellRect;
  }
  const elemWidth = elem.offsetWidth; // 控件的宽度;
  const elemHeight = elem.offsetHeight; // 控件的高度

  // 滚动条高度
  // eslint-disable-next-line no-undef
  const scrollArea = (type = 'scrollTop') => document.body[type] || document.documentElement[type];
  // eslint-disable-next-line no-undef
  const winArea = (type = 'clientHeight') => document.documentElement[type];
  const margin = 5;
  let { left } = rect;
  let top = rect.bottom;
  if (cellRect) {
    ({ top } = rect);
  }

  // 如果右侧超出边界
  const totalLeft = left + elemWidth + margin + elemOffsetWidth;
  const clientWidth = winArea('clientWidth');
  const clientHeight = winArea();
  if (totalLeft > clientWidth) {
    left -= totalLeft - clientWidth - elemOffsetWidth;
  } else {
    left += elemOffsetWidth;
  }

  // 如果底部超出边界
  if (top + elemHeight + margin + elemOffsetHeight > clientHeight) {
    top = rect.top + elemOffsetHeight > elemHeight // 顶部是否有足够区域显示完全
      ? rect.top + elemOffsetHeight - elemHeight
      : clientHeight - elemHeight - elemOffsetHeight;
    // top -= margin * 2;
  } else {
    top += elemOffsetHeight;
  }


  const leftP = `${left + scrollArea('scrollLeft')}px`;
  const topP = `${top + scrollArea('scrollLeft')}px`;
  return { left: leftP, top: topP };
};

export default {
  getPosition,
};
