/* global document laydate */
import { h } from './element';
import { cssPrefix, baseFormat } from '../config';
import { CellRange } from '../core/cell_range';
import { bind } from './event';
import Tree from '../utils/tree';
import helper from './helper';

const selectorHeightBorderWidth = 2 * 2 - 1;
let startZIndex = 10;

class SelectorElement {
  constructor() {
    this.cornerEl = h('div', `${cssPrefix}-selector-corner`);
    // linjian 添加下拉三角
    this.dropdownBtn = h('div', `${cssPrefix}-dropdown-btn`);
    // linjian 添加placeholder容器
    this.placeholderDom = h('div', `${cssPrefix}-placeholder to__tip_items`).on('mousemove', (e) => {
      e.preventDefault();
      e.stopPropagation();
    }).on('mousedown', (e) => {
      e.preventDefault();
      e.stopPropagation();
    // eslint-disable-next-line newline-per-chained-call
    }).on('mousewheel', (e) => {
      e.stopPropagation();
    });
    this.areaEl = h('div', `${cssPrefix}-selector-area`)
      .children(
        this.cornerEl,
        this.dropdownBtn,
        this.placeholderDom,
      ).hide();
    // this.areaEl = h('div', `${cssPrefix}-selector-area`)
    //   .child(this.cornerEl).hide();
    this.clipboardEl = h('div', `${cssPrefix}-selector-clipboard`).hide();
    this.autofillEl = h('div', `${cssPrefix}-selector-autofill`).hide();
    this.el = h('div', `${cssPrefix}-selector`)
      .css('z-index', `${startZIndex}`)
      .children(this.areaEl, this.clipboardEl, this.autofillEl)
      .hide();
    startZIndex += 1;
  }

  setOffset(v) {
    this.el.offset(v).show();
    return this;
  }

  hide() {
    this.el.hide();
    return this;
  }

  setAreaOffset(v) {
    const {
      left, top, width, height,
    } = v;
    this.areaEl.offset({
      width: width - selectorHeightBorderWidth + 0.8,
      height: height - selectorHeightBorderWidth + 0.8,
      left: left - 0.8,
      top: top - 0.8,
    }).show();
  }

  setClipboardOffset(v) {
    const {
      left, top, width, height,
    } = v;
    this.clipboardEl.offset({
      left,
      top,
      width: width - 5,
      height: height - 5,
    });
  }

  showAutofill(v) {
    const {
      left, top, width, height,
    } = v;
    this.autofillEl.offset({
      width: width - selectorHeightBorderWidth,
      height: height - selectorHeightBorderWidth,
      left,
      top,
    }).show();
  }

  hideAutofill() {
    this.autofillEl.hide();
  }

  showClipboard() {
    this.clipboardEl.show();
  }

  hideClipboard() {
    this.clipboardEl.hide();
  }
}

function calBRAreaOffset(offset) {
  const { data } = this;
  const {
    left, top, width, height, scroll, l, // t,
  } = offset;
  const ftwidth = data.freezeTotalWidth();
  const ftheight = data.freezeTotalHeight();
  let left0 = left - ftwidth;
  if (ftwidth > l) left0 -= scroll.x;
  const top0 = top - ftheight;
  // linjian 注释掉  影响定位
  // if (ftheight > t) top0 -= scroll.y;
  return {
    left: left0,
    top: top0,
    width,
    height,
  };
}

function calTAreaOffset(offset) {
  const { data } = this;
  const {
    left, width, height, l, t, scroll,
  } = offset;
  const ftwidth = data.freezeTotalWidth();
  let left0 = left - ftwidth;
  if (ftwidth > l) left0 -= scroll.x;
  return {
    left: left0, top: t, width, height,
  };
}

function calLAreaOffset(offset) {
  const { data } = this;
  const {
    top, width, height, l, t, scroll,
  } = offset;
  const ftheight = data.freezeTotalHeight();
  let top0 = top - ftheight;
  // console.log('ftheight:', ftheight, ', t:', t);
  if (ftheight > t) top0 -= scroll.y;
  return {
    left: l, top: top0, width, height,
  };
}

function setBRAreaOffset(offset) {
  const { br } = this;
  br.setAreaOffset(calBRAreaOffset.call(this, offset));
}

function setTLAreaOffset(offset) {
  const { tl } = this;
  tl.setAreaOffset(offset);
}

function setTAreaOffset(offset) {
  const { t } = this;
  t.setAreaOffset(calTAreaOffset.call(this, offset));
}

function setLAreaOffset(offset) {
  const { l } = this;
  l.setAreaOffset(calLAreaOffset.call(this, offset));
}

function setLClipboardOffset(offset) {
  const { l } = this;
  l.setClipboardOffset(calLAreaOffset.call(this, offset));
}

function setBRClipboardOffset(offset) {
  const { br } = this;
  br.setClipboardOffset(calBRAreaOffset.call(this, offset));
}

function setTLClipboardOffset(offset) {
  const { tl } = this;
  tl.setClipboardOffset(offset);
}

function setTClipboardOffset(offset) {
  const { t } = this;
  t.setClipboardOffset(calTAreaOffset.call(this, offset));
}

function setAllAreaOffset(offset) {
  setBRAreaOffset.call(this, offset);
  setTLAreaOffset.call(this, offset);
  setTAreaOffset.call(this, offset);
  setLAreaOffset.call(this, offset);
}

function setAllClipboardOffset(offset) {
  setBRClipboardOffset.call(this, offset);
  setTLClipboardOffset.call(this, offset);
  setTClipboardOffset.call(this, offset);
  setLClipboardOffset.call(this, offset);
}

// linjian下拉树节点单击事件
function domClick(e, target, currentTarget) {
  const sri = parseInt(currentTarget.getAttribute('sri'), 10);
  const sci = parseInt(currentTarget.getAttribute('sci'), 10);
  const cell = this.data.getCell(sri, sci);
  const { typeOptions, customerText, cellProps } = cell;
  const {
    allowParentSelection, allowMultiSelect, link, level,
  } = typeOptions;
  const isLeaf = e.getAttribute('is-leaf');
  // 不能选中叶子节点，只能在非联动的时候才可以
  if (!allowParentSelection && isLeaf === 'false' && !link) {
    const { settings } = this.data;
    const { hooks } = settings;
    if (hooks && hooks.tipMessage) {
      hooks.tipMessage('toast', '不支持选中非叶子节点');
    }
    return false;
  }
  let id = '';
  let text = '';
  // 单选和联动都没有CheckBox
  if (!allowMultiSelect || link) {
    id += `${e.getAttribute('value')}`;
    text += `${e.getAttribute('text')}`;
    h(currentTarget).hide();
    // 如果是联动，要把当前节点的下级全部清空
    if (link) {
      link.forEach((linkItem, linkIndex) => {
        if (linkIndex > level) {
          const { row, col } = linkItem;
          const linkCell = this.data.getCell(row, col);
          linkCell.id = '';
          linkCell.text = '';
          linkCell.dropdownText = '';
        }
        return false;
      });
    }
  } else {
    // 选中子元素所有input框
    const subItem = e.parentNode.nextElementSibling;
    const inputList = subItem.querySelectorAll('.to__item');
    let checked = !e.querySelector('input').checked;
    if (target.type === 'checkbox') {
      checked = !checked;
    } else {
      // 选中当前input框
      e.querySelector('input').checked = checked;
    }
    for (let i = 0; i < inputList.length; i++) {
      const item = inputList[i];
      item.querySelector('input').checked = checked;
    }
    const cList = document.getElementsByName('cName');
    for (let i = 0; i < cList.length; i++) {
      const ele = cList[i];
      if (ele.checked) {
        id += `${ele.getAttribute('value')};`;
        text += `${ele.getAttribute('text')};`;
      }
    }
    if (id) {
      id = id.substring(0, id.length - 1);
    }
    if (text) {
      text = text.substring(0, text.length - 1);
    }
  }
  if (customerText !== '' && customerText !== undefined) {
    if (!/;$/.test(customerText) && text) {
      cell.dropdownText = `${customerText};${text}`;
      id = `${customerText};${id}`;
    } else {
      cell.dropdownText = (customerText || '') + text;
      id = `${customerText}${id}`;
    }
  } else {
    cell.dropdownText = id;
  }
  // h(e.target.parentElement.parentElement).hide();
  cellProps.initText = this.data.getCellTextOrDefault(sri, sci);
  this.data.setCellText(sri, sci, id, 'finished');
  this.sheet.refresh();
  return true;
}
// linjian下拉checkbox框点击事件
function dropClick(dom) {
  // 切换样式状态
  if (dom.className.indexOf('to__roate') > -1) {
    dom.className = '';
  } else {
    dom.className = 'to__roate';
  }
  // 显示隐藏内容
  const domShow = dom.parentNode.parentNode.nextElementSibling;
  if (domShow.className.indexOf('to__show') > -1) {
    domShow.className = 'to__subItem';
  } else {
    domShow.className = 'to__subItem to__show';
  }
}
function getTargetType(dom, result) {
  if (!dom) {
    return;
  }
  const clickType = dom.getAttribute('click-type');
  if (!clickType) {
    getTargetType(dom.parentElement, result);
  } else {
    result.domTarget = dom;
    result.clickType = clickType;
  }
}

export default class Selector {
  constructor(data, sheet) {
    this.data = data;
    this.sheet = sheet;
    this.br = new SelectorElement();
    this.t = new SelectorElement();
    this.l = new SelectorElement();
    this.tl = new SelectorElement();
    this.br.el.show();
    this.offset = null;
    this.areaOffset = null;
    this.indexes = null;
    this.range = null;
    this.arange = null;
    // linjian 添加下拉项容器
    this.dropdownItems = h('div', `${cssPrefix}-dropdown-items to__content`).on('click', (e) => {
      e.stopPropagation();
      const { target } = e;
      const result = {};
      getTargetType(target, result);
      const { clickType, domTarget } = result;
      switch (clickType) {
        case 'domClick':
          domClick.call(this, domTarget, target, e.currentTarget);
          break;
        case 'dropClick':
          dropClick.call(null, domTarget);
          break;
        default:
          // domClick.call(this, domTarget, target, e.currentTarget);
          break;
      }
    }).on('mousemove', (e) => {
      e.preventDefault();
      e.stopPropagation();
    }).on('mousedown', (e) => {
      e.preventDefault();
      e.stopPropagation();
    // eslint-disable-next-line newline-per-chained-call
    }).on('mousewheel', (e) => {
      e.stopPropagation();
    });
    this.sheet.el.el.appendChild(this.dropdownItems.el);
    // linjian添加图片容器
    this.imageItems = h('div', `${cssPrefix}-image-items`);
    this.data.imageItems = this.imageItems;
    // 用于处理一个格子有多个图片的时候  可以切换图片显示
    this.imageSwitchBtn = h('ul', `${cssPrefix}-image-switch-btn`).children(
      h('li', `${cssPrefix}-image-left-switch`),
      h('li', `${cssPrefix}-image-right-switch`),
    ).hide();
    this.imageSwitchBtn.on('click', (e) => {
      e.preventDefault();
      e.stopPropagation();
      const { target, currentTarget } = e;
      if (target.nodeName.toUpperCase() === 'LI') {
        const { className } = target;
        // const text = target.innerText;
        const sri = currentTarget.getAttribute('sri');
        const sci = currentTarget.getAttribute('sci');
        const cell = this.data.getCell(sri, sci);
        const { typeOptions, imageId } = cell;
        const { images } = typeOptions;
        let selectedIndex = 0;
        let selectedItem;
        images.some((item, index2) => {
          if (item.selected) {
            selectedIndex = index2;
            item.selected = false;
            return true;
          }
          return false;
        });
        switch (className) {
          case 'x-spreadsheet-image-right-switch':// 展示下一张图片
            if (selectedIndex + 1 >= images.length) {
              [selectedItem] = images;
            } else {
              selectedItem = images[selectedIndex + 1];
            }
            break;
          case 'x-spreadsheet-image-left-switch':// 展示上一张图片
            if (selectedIndex === 0) {
              selectedItem = images[images.length - 1];
            } else {
              selectedItem = images[selectedIndex - 1];
            }
            break;
          default:
            break;
        }
        // eslint-disable-next-line no-undef
        const imageTemp = document.getElementById(imageId);
        const {
          path, name, width, height, left, top,
        } = selectedItem;
        selectedItem.selected = true;
        imageTemp.src = path;
        imageTemp.name = name;
        imageTemp.style.width = `${width}px`;
        imageTemp.style.height = `${height}px`;
        imageTemp.style.top = top;
        imageTemp.style.left = left;
      }
    }).on('mousemove', (e) => {
      e.preventDefault();
      e.stopPropagation();
    }).on('mousedown', (e) => {
      e.preventDefault();
      e.stopPropagation();
    });
    this.data.imageSwitchBtn = this.imageSwitchBtn;
    this.el = h('div', `${cssPrefix}-selectors`)
      .children(
        this.tl.el,
        this.t.el,
        this.l.el,
        this.br.el,
        this.imageItems,
        this.imageSwitchBtn,
      ).hide();
    // for performance
    this.lastri = -1;
    this.lastci = -1;

    startZIndex += 1;
    // linjian  添加sheet实例
    this.sheet = sheet;

    const dropdownBtnBR = this.br.el.el.querySelector(`.${cssPrefix}-selector-area`).querySelector(`.${cssPrefix}-dropdown-btn`);
    const dropdownBtnT = this.t.el.el.querySelector(`.${cssPrefix}-selector-area`).querySelector(`.${cssPrefix}-dropdown-btn`);
    const dropdownBtnL = this.l.el.el.querySelector(`.${cssPrefix}-selector-area`).querySelector(`.${cssPrefix}-dropdown-btn`);
    const dropdownBtnTL = this.tl.el.el.querySelector(`.${cssPrefix}-selector-area`).querySelector(`.${cssPrefix}-dropdown-btn`);
    bind(dropdownBtnBR, 'click', this.dropdownFn.bind(this, dropdownBtnBR));
    bind(dropdownBtnT, 'click', this.dropdownFn.bind(this, dropdownBtnT));
    bind(dropdownBtnL, 'click', this.dropdownFn.bind(this, dropdownBtnL));
    bind(dropdownBtnTL, 'click', this.dropdownFn.bind(this, dropdownBtnTL));
  }

  hide() {
    this.el.hide();
  }

  resetOffset() {
    const {
      data, tl, t, l, br,
    } = this;
    const freezeHeight = data.freezeTotalHeight();
    const freezeWidth = data.freezeTotalWidth();
    if (freezeHeight > 0 || freezeWidth > 0) {
      tl.setOffset({ width: freezeWidth, height: freezeHeight });
      t.setOffset({ left: freezeWidth, height: freezeHeight });
      l.setOffset({ top: freezeHeight, width: freezeWidth });
      br.setOffset({ left: freezeWidth, top: freezeHeight });
    } else {
      tl.hide();
      t.hide();
      l.hide();
      br.setOffset({ left: 0, top: 0 });
    }
  }

  resetAreaOffset() {
    // console.log('offset:', offset);
    const offset = this.data.getSelectedRect();
    const coffset = this.data.getClipboardRect();
    setAllAreaOffset.call(this, offset);
    setAllClipboardOffset.call(this, coffset);
    this.resetOffset();
  }

  resetBRTAreaOffset() {
    const offset = this.data.getSelectedRect();
    const coffset = this.data.getClipboardRect();
    setBRAreaOffset.call(this, offset);
    setTAreaOffset.call(this, offset);
    setBRClipboardOffset.call(this, coffset);
    setTClipboardOffset.call(this, coffset);
    this.resetOffset();
  }

  resetBRLAreaOffset() {
    const offset = this.data.getSelectedRect();
    const coffset = this.data.getClipboardRect();
    setBRAreaOffset.call(this, offset);
    setLAreaOffset.call(this, offset);
    setBRClipboardOffset.call(this, coffset);
    setLClipboardOffset.call(this, coffset);
    this.resetOffset();
  }

  dropdownFn(dropdownBtn, e) {
    e.stopPropagation();
    // 获取列宽
    const { data } = this;
    const cellRange = data.selector.range;
    const { sri, sci } = cellRange;
    const colWidth = { width: this.data.cols.getWidth(sci) };
    const cellProps = this.data.getCellProps(sri, sci);
    const $this = this;
    if (cellProps.cellType === 'dropdown') {
      const dropDownItems = this.sheet.el.el.querySelector('.x-spreadsheet-dropdown-items');
      if (dropDownItems.style.display === 'block') {
        return;
      }
      const cell = this.data.getCell(sri, sci);
      const { typeOptions } = cell;
      const source = cell.source || [];
      dropDownItems.setAttribute('sri', sri);
      dropDownItems.setAttribute('sci', sci);
      const tree = new Tree(dropDownItems, typeOptions, this.data);
      tree.getData(source, dropDownItems);
      // linjian 先显示出来 才能计算宽度
      h(dropDownItems).show();
      const rowWidth = this.data.cols._[sci].width || this.data.cols.width;
      const { top, left } = helper.getPosition(dropdownBtn, dropDownItems,
        { elemOffsetWidth: -rowWidth - 1, elemOffsetHeight: 2 });
      dropDownItems.style.top = top;
      dropDownItems.style.left = left;
      dropDownItems.style.display = 'block';
    } else {
      dropdownBtn.id = `J_${(`${Math.random()}`).substring(2)}`;
      laydate.render({
        elem: `#${dropdownBtn.id}`,
        type: cellProps.cellType,
        format: baseFormat[`${cellProps.cellType}BaseFormat`],
        show: true, // 直接显示
        colWidth,
        change: (/* value  ,date */) => { // 监听日期被切换
          // console.log(value);
        },
        // eslint-disable-next-line object-shorthand
        done: function (value /* ,date */) { // 监听日期完成选择
          this.elem[0].textContent = '';
          cellProps.initText = $this.data.getCellTextOrDefault(sri, sci);
          $this.data.setCellText(sri, sci, value, 'finished');
          $this.sheet.refresh();
        },
      });
    }
  }

  /**
   * @description: 用来处理日期、时间、日期时间 三角展示和隐藏
   * @param {type}
   * @return:
   * @Author: linjian
   * @Date: 2019-05-10 10:48:42
   */
  showOrHideDropdownBtn(range) {
    // 如果是只读，则不显示三角。
    const cell = this.data.getCell(range.sri, range.sci);
    if (this.data.isReadOnlySheet || (cell && cell.readOnly)) {
      this.hideDropDownBtn(this.el, 'none');
      return false;
    }
    this.sheet.el.el.querySelector('.x-spreadsheet-dropdown-items').style.display = 'none';
    // linjian 给下拉 时间 日期时间 日期加上右侧小三角图形
    // 只有选中单个格子的时候才显示小三角
    const cellProps = this.data.getCellProps(range.sri, range.sci);
    let { merge } = cell || {};
    if (!merge) {
      merge = [0, 0];
    }
    if (range.eci === range.sci + merge[1] && range.eri === range.sri + merge[0] && cellProps) {
      const { cellType } = cellProps;
      if (cellType === 'date' || cellType === 'datetime' || cellType === 'time' || cellType === 'dropdown') {
        this.hideDropDownBtn(this.el, 'block');
      } else {
        this.hideDropDownBtn(this.el, 'none');
      }
    } else { // 删除下三角
      this.hideDropDownBtn(this.el, 'none');
    }
    return true;
  }

  hideDropDownBtn(el, display) {
    el.children().forEach((item) => {
      const selectorArea = item.querySelector('.x-spreadsheet-selector-area');
      if (item.style.display === 'block' && selectorArea) {
        selectorArea.querySelector('.x-spreadsheet-dropdown-btn').style.display = display;
      }
    });
  }

  showOrHidePlaceHolderBtn(range) {
    // 如果是只读，则不显示三角。
    // linjian 给下拉 时间 日期时间 日期加上右侧小三角图形
    // 只有选中单个格子的时候才显示小三角
    this.sheet.el.el.querySelector('.x-spreadsheet-placeholder').style.display = 'none';
    const cellProps = this.data.getCellProps(range.sri, range.sci);
    const cell = this.data.getCell(range.sri, range.sci);
    if (range.eci === range.sci && range.eri === range.sri && cellProps && (cellProps.placeholder || cell.isError)) {
      const { isError, errorMsg } = cell;
      const rowHeight = this.data.rows._[range.sri].height || this.data.rows.height;

      this.hidePlaceHolderBtn(this.el, 'block', cellProps.placeholder || {}, isError, errorMsg, rowHeight);
    } else { // 删除下三角
      this.hidePlaceHolderBtn(this.el, 'none', {});
    }
    return true;
  }

  hidePlaceHolderBtn(el, display, { title = '有效性', content = '' }, isError, errorMsg, rowHeight) {
    const templatePlaceholder = `
      <div class="items">
        ${isError ? '<div class="left-items"></div>' : ''}
        <div class="right-items">
          <div class="right-items-title">
            ${title}
          </div>
          <div class="right-items-content">
            ${isError && errorMsg ? errorMsg : content}
          </div>
        </div>
      </div>
    `;
    el.children().forEach((item) => {
      const selectorArea = item.querySelector('.x-spreadsheet-selector-area');
      if (item.style.display === 'block' && selectorArea) {
        const placeholderDom = selectorArea.querySelector('.x-spreadsheet-placeholder');
        placeholderDom.innerHTML = templatePlaceholder;
        placeholderDom.style.top = `${rowHeight}px`;
        // 计算 top left 默认是左上角
        placeholderDom.style.display = display;
      }
    });
  }

  set(ri, ci, indexesUpdated = true) {
    const { data } = this;
    const cellRange = data.calSelectedRangeByStart(ri, ci);
    const {
      sri, sci, eri, eci,
    } = cellRange;
    if (indexesUpdated) {
      let [cri, cci] = [ri, ci];
      if (ri < 0) cri = 0;
      if (ci < 0) cci = 0;
      data.selector.setIndexes(cri, cci);
      this.indexes = [cri, cci];
    }

    this.moveIndexes = [sri, sci];
    // this.sIndexes = sIndexes;
    // this.eIndexes = eIndexes;
    this.range = cellRange;
    this.resetAreaOffset();
    this.el.show();
    this.showOrHideDropdownBtn.call(this, cellRange);
    this.showOrHidePlaceHolderBtn.call(this, cellRange);
    // 添加选中后钩子
    const { afterSelection } = data.hook;
    if (afterSelection) {
      afterSelection.call(data, sri, sci, eri, eci);
    }
    // linjian fx值更新
    const { fx } = data;
    if (fx) {
      fx.setData(data.getCellTextOrDefault(sri, sci));
    }
  }

  setEnd(ri, ci, moving = true) {
    const { data, lastri, lastci } = this;
    if (moving) {
      if (ri === lastri && ci === lastci) return;
      this.lastri = ri;
      this.lastci = ci;
    }
    this.range = data.calSelectedRangeByEnd(ri, ci);
    setAllAreaOffset.call(this, this.data.getSelectedRect());

    this.showOrHideDropdownBtn.call(this, this.range);
    // 添加选中后钩子
    const {
      sri, sci, eri, eci,
    } = this.range;
    const { afterMultiSelection } = data.hook;
    if (afterMultiSelection) {
      afterMultiSelection.call(data, sri, sci, eri, eci);
    }
  }

  reset() {
    // console.log('::::', this.data);
    let { sri, eri } = this.data.selector.range;
    const { sci, eci } = this.data.selector.range;
    const { len } = this.data.rows;
    if (eri >= len) {
      eri = len - 1;
    }
    if (sri >= len) {
      sri = len - 1;
    }
    this.data.selector.setRange({
      sri, sci, eri, eci,
    });
    this.setEnd(eri, eci);
  }

  showAutofill(ri, ci) {
    if (ri === -1 && ci === -1) return;
    // console.log('ri:', ri, ', ci:', ci);
    // const [sri, sci] = this.sIndexes;
    // const [eri, eci] = this.eIndexes;
    const {
      sri, sci, eri, eci,
    } = this.range;
    const [nri, nci] = [ri, ci];
    // const rn = eri - sri;
    // const cn = eci - sci;
    const srn = sri - ri;
    const scn = sci - ci;
    const ern = eri - ri;
    const ecn = eci - ci;
    if (scn > 0) {
      // left
      // console.log('left');
      this.arange = new CellRange(sri, nci, eri, sci - 1);
      // this.saIndexes = [sri, nci];
      // this.eaIndexes = [eri, sci - 1];
      // data.calRangeIndexes2(
    } else if (srn > 0) {
      // top
      // console.log('top');
      // nri = sri;
      this.arange = new CellRange(nri, sci, sri - 1, eci);
      // this.saIndexes = [nri, sci];
      // this.eaIndexes = [sri - 1, eci];
    } else if (ecn < 0) {
      // right
      // console.log('right');
      // nci = eci;
      this.arange = new CellRange(sri, eci + 1, eri, nci);
      // this.saIndexes = [sri, eci + 1];
      // this.eaIndexes = [eri, nci];
    } else if (ern < 0) {
      // bottom
      // console.log('bottom');
      // nri = eri;
      this.arange = new CellRange(eri + 1, sci, nri, eci);
      // this.saIndexes = [eri + 1, sci];
      // this.eaIndexes = [nri, eci];
    } else {
      // console.log('else:');
      this.arange = null;
      // this.saIndexes = null;
      // this.eaIndexes = null;
      return;
    }
    if (this.arange !== null) {
      // console.log(this.saIndexes, ':', this.eaIndexes);
      const offset = this.data.getRect(this.arange);
      offset.width += 2;
      offset.height += 2;
      const {
        br, l, t, tl,
      } = this;
      br.showAutofill(calBRAreaOffset.call(this, offset));
      l.showAutofill(calLAreaOffset.call(this, offset));
      t.showAutofill(calTAreaOffset.call(this, offset));
      tl.showAutofill(offset);
    }
  }

  hideAutofill() {
    ['br', 'l', 't', 'tl'].forEach((property) => {
      this[property].hideAutofill();
    });
  }

  showClipboard() {
    const coffset = this.data.getClipboardRect();
    setAllClipboardOffset.call(this, coffset);
    ['br', 'l', 't', 'tl'].forEach((property) => {
      this[property].showClipboard();
    });
  }

  hideClipboard() {
    ['br', 'l', 't', 'tl'].forEach((property) => {
      this[property].hideClipboard();
    });
  }
}
