export default class Editor {
  constructor(formulas, viewFn, rowHeight) {
    this.viewFn = viewFn;
    this.rowHeight = rowHeight;
    this.formulas = formulas;
    this.suggest = new Suggest(formulas, (it) => {
      suggestItemClick.call(this, it);
    });
    // this.datepicker = new Datepicker();
    // this.datepicker.change((d) => {
    //   // console.log('d:', d);
    //   this.setText(dateFormat(d));
    //   this.clear();
    // });
    this.textElID = `J${utils.uuID()}`;
    this.areaEl = h('div', `${cssPrefix}-editor-area`)
      .children(
        this.textEl = h('textarea', '')
          .on('input', evt => inputEventHandler.call(this, evt))
          .on('keydown', (evt) => {
            const keyCode = evt.keyCode || evt.which;
            const {
              key, ctrlKey, metaKey,
            } = evt;
            if (this.el.el.style.opacity === '0') {
              if (ctrlKey || metaKey || keyCode === 113 || keyCode === 8 || key === 'Delete') {
                this.windowKeyDownEvt(evt);
              }
              if (keyCode >= 37 && keyCode <= 40) {
                this.windowKeyDownEvt(evt);
              }
            }
          })
          .on('blur', (evt) => {
            if (this.el.el.style.opacity === '0') {
              return;
            }
            const editorValue = this.textEl.val().trim();
            editorValue.startsWith('=') || (this.formulaHanging = false);
            this.formulaHanging || this.clear.call(this, evt);
          }),
        this.textlineEl = h('div', 'textline'),
        this.suggest.el,
        // this.datepicker.el,
      )
      .on('mousemove.stop', () => {})
      .on('mousedown.stop', () => {});
    this.textEl.attr('id', this.textElID);
    // this.el = h('div', `${cssPrefix}-editor`)
    //   .child(this.areaEl).hide();
    this.el = h('div', `${cssPrefix}-editor`)
      .child(this.areaEl);
    this.suggest.bindInputEvents(this.textEl);

    this.areaOffset = null;
    this.freeze = { w: 0, h: 0 };
    this.cell = null;
    this.inputText = '';
    this.change = () => {};
  }

  setFreezeLengths(width, height) {
    this.freeze.w = width;
    this.freeze.h = height;
  }

  // 新增isNeedTriggerChange事件，如果为true，则要触发change
  clear(isNeedTriggerChange = false) {
    // const { cell } = this;
    // const cellText = (cell && cell.text) || '';
    if (this.inputText !== '' || isNeedTriggerChange) {
      this.change('finished', this.inputText);
    } else if (this.textEl.el.value !== '' && this.el.css('opacity') !== '0') {
      // linjian 修复英文状态下 未双击单元格输入 不触发写入单元格问题出现问题
      this.change('finished', this.textEl.el.value);
    }

    this.cell = null;
    this.areaOffset = null;
    this.inputText = '';
    // this.el.hide();
    this.el.css({ opacity: 0, width: '0' });
    this.textEl.val('');
    this.textlineEl.html('');
    resetSuggestItems.call(this);
    // this.datepicker.hide();
  }

  setOffset(offset, suggestPosition = 'top') {
    const {
      textEl, areaEl, suggest, freeze, el,
    } = this;
    if (offset) {
      this.areaOffset = offset;
      const {
        left, top, width, height, l, t,
      } = offset;
      // console.log('left:', left, ',top:', top, ', freeze:', freeze);
      const elOffset = { left: 0, top: 0 };
      // top left
      if (freeze.w > l && freeze.h > t) {
        //
      } else if (freeze.w < l && freeze.h < t) {
        elOffset.left = freeze.w;
        elOffset.top = freeze.h;
      } else if (freeze.w > l) {
        elOffset.top = freeze.h;
      } else if (freeze.h > t) {
        elOffset.left = freeze.w;
      }
      el.offset(elOffset);
      areaEl.offset({ left: left - elOffset.left - 0.8, top: top - elOffset.top - 0.8 });
      textEl.offset({ width: width - 9 + 0.8, height: height - 3 + 0.8 });
      const sOffset = { left: 0 };
      sOffset[suggestPosition] = height;
      suggest.setOffset(sOffset);
      suggest.hide();
    }
  }

  setCell(cell, validator, isNeedWriteToCell = true) {
    // console.log('::', validator);
    const { el, datepicker, suggest } = this;
    // linjian
    // el.show();
    // this.areaEl.el.focus();

    this.cell = cell;
    // linjian
    if (isNeedWriteToCell) {
      const text = (cell && cell.text) || '';
      this.setText(text);
    }

    el.css({ opacity: 1, width: '100%' });

    this.validator = validator;
    if (validator) {
      const { type } = validator;
      // if (type === 'date') {
      //   datepicker.show();
      //   if (!/^\s*$/.test(text)) {
      //     datepicker.setValue(text);
      //   }
      // }
      if (type === 'list') {
        suggest.setItems(validator.values());
        suggest.search('');
      }
    }
  }

  setText(text, position) {
    this.inputText = text;
    setText.call(this, text, position || `${text}`.length);
    // console.log('setText: ', position);
    resetTextareaSize.call(this);
  }
}
