/**
 *  self.name： Worker 的名字。该属性只读，由构造函数指定。
    self.onmessage：指定message事件的监听函数。
    self.onmessageerror：指定 messageerror 事件的监听函数。发送的数据无法序列化成字符串时，会触发这个事件。
    self.close()：关闭 Worker 线程。
    self.postMessage()：向产生这个 Worker 线程发送消息。
    self.importScripts()：加载 JS 脚本。
 */
/* global Worker */
const webWorkObj = {
/**
     * @param url: 代表需要加载的webworker地址
     * @param options:代表初始化传递的入参，一般情况下需要传递 name参数  用来区分主线程不同的webworker名称
     * @param callback:用来接收webwork处理结果
     */
  createWebWorker: (url, options, callback) => {
    let instance;
    if (typeof Worker !== 'undefined') {
      instance = new Worker(url, options);
    } else {
      console.log('Sorry, your browser does not support Web Workers...');
    }
    if (instance) {
      instance.onmessage = (event) => {
        callback(null, event);
        instance.terminate();
      };
      instance.onerror = (event) => {
        console.error([
          'ERROR: Line ', event.lineno,
          ' in ', event.filename, ': ',
          event.message,
        ].join(''));
        callback(event, null);
        instance.terminate();
      };
    }
    return instance;
  },
  stopWorker(instance) {
    instance.terminate();
  },
};

