/* global isNaN window */
/* eslint-disable no-underscore-dangle */
/* eslint-disable no-param-reassign */
const LABEL_EXTRACT_REGEXP = /^([$])?([A-Za-z]+)([$])?([0-9]+)$/;
const COLUMN_LABEL_BASE = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
const COLUMN_LABEL_BASE_LENGTH = COLUMN_LABEL_BASE.length;

// 获取表达式中的所有范围 如 A1:B1
export function getRanges(expression) {
  // 应对纯范围的情况
  let deleteFirst = false;
  if (expression[0] !== '=') {
    deleteFirst = true;
    // eslint-disable-next-line no-param-reassign
    expression = `=${expression}`;
  }
  // ([+\-*/%(,=<>]\s*) 操作符号
  // ([\u4e00-\u9fa5_\w-]*\s*!\s*)? 可能存在的sheetName
  // (\$?[A-Z]+\$?\d+(\s*:\s*\$?[A-Z]+\$?\d+)?|\$?[A-Z]\s*:\s*\$?[A-Z]) 表格范围表达式
  // 如 A2、 B2 : E4、 B:E、 $A2、 $A$2
  // (?!\s*\() 后面不跟 (
  const patt = /([+\-*/%(,=<>]\s*)(([\u4e00-\u9fa5_\w-]*|'[^']*')\s*!\s*)?(\$?[A-Za-z]+\$?\d+(\s*:\s*\$?[A-Za-z]+\$?\d+)?|\$?[A-Za-z]\s*:\s*\$?[A-Za-z])(?!\s*\()/g;
  const result = [];
  let matchObj;
  do {
    matchObj = patt.exec(expression);
    if (matchObj) {
      let start = matchObj.index;
      if (matchObj[1]) {
        start += matchObj[1].length;
      }
      if (matchObj[2]) {
        start += matchObj[2].length;
      }
      const content = matchObj[4];
      let end = start + content.length;
      if (deleteFirst) {
        start -= 1;
        end -= 1;
      }
      result.push({
        start,
        content,
        end,
        sheetName: matchObj[3],
      });
    }
  } while (matchObj);
  return result;
}

// 获取范围分解后的数据
export function getDataAfterSplit(text) {
  let startIndex = 0;
  let lastIndex;
  const dataAfterSplit = [];
  const ranges = getRanges(text);
  ranges.forEach(({ content, start }) => {
    lastIndex = start;
    content.replace(/([A-Z]+)(\d+)/g, '$1,$2,').split(',').forEach((v) => {
      if (v.length) {
        if (isNaN(v)) {
          lastIndex += v.length;
        } else {
          dataAfterSplit.push(text.substring(startIndex, lastIndex),
            parseInt(v, 10));
          startIndex = lastIndex + v.length;
          lastIndex = startIndex;
        }
      }
    });
  });
  if (startIndex !== text.length) {
    dataAfterSplit.push(text.substring(startIndex));
  }
  return dataAfterSplit;
}

/**
 * 单元格表达式转大写
 *
 * @param {String} expression Formula expression.
 * @returns {String}
 */
export function transferRangeToUpperCase(expression) {
  getRanges(expression).forEach(({ content, start, end }) => {
    // eslint-disable-next-line no-param-reassign
    expression = expression.substr(0, start) + content.toUpperCase() + expression.substr(end);
  });
  return expression;
}


/**
 * Convert row index to label.
 *
 * @param {Number} row Row index.
 * @returns {String} Returns row label (eq. '1', '7').
 */
export function rowIndexToLabel(row) {
  let result = '';

  if (row >= 0) {
    result = `${row + 1}`;
  }

  return result;
}

/**
 * Convert column index to label.
 *
 * @param {Number} column Column index.
 * @returns {String} Returns column label (eq. 'ABB', 'CNQ').
 */
export function columnIndexToLabel(column) {
  let result = '';

  while (column >= 0) {
    result = String.fromCharCode((column % COLUMN_LABEL_BASE_LENGTH) + 97) + result;
    column = Math.floor(column / COLUMN_LABEL_BASE_LENGTH) - 1;
  }

  return result.toUpperCase();
}

/**
 * Convert row and column indexes into cell label.
 *
 * @param {Object} row Object with `index` and `isAbsolute` properties.
 * @param {Object} column Object with `index` and `isAbsolute` properties.
 * @returns {String} Returns cell label.
 */
export function toLabel(row, column) {
  const rowLabel = (row.isAbsolute ? '$' : '') + rowIndexToLabel(row.index);
  const columnLabel = (column.isAbsolute ? '$' : '') + columnIndexToLabel(column.index);

  return columnLabel + rowLabel;
}

/**
 * Convert row label to index.
 *
 * @param {String} label Row label (eq. '1', '5')
 * @returns {Number} Returns -1 if label is not recognized otherwise proper row index.
 */
export function rowLabelToIndex(label) {
  let result = parseInt(label, 10);

  if (isNaN(result)) {
    result = -1;
  } else {
    result = Math.max(result - 1, -1);
  }

  return result;
}

/**
 * Convert column label to index.
 *
 * @param {String} label Column label (eq. 'ABB', 'CNQ')
 * @returns {Number} Returns -1 if label is not recognized otherwise proper column index.
 */
export function columnLabelToIndex(label) {
  let result = 0;

  if (typeof label === 'string') {
    label = label.toUpperCase();

    for (let i = 0, j = label.length - 1; i < label.length; i += 1, j -= 1) {
      result += Math.pow(COLUMN_LABEL_BASE_LENGTH, j) * (COLUMN_LABEL_BASE.indexOf(label[i]) + 1);
    }
  }
  --result;

  return result;
}

/**
 * Extract cell coordinates.
 * 'A3' => [{index: 2, label: '3'}, {index: 0, label: 'A'}]
 * @param {String} label Cell coordinates (eq. 'A1', '$B6', '$N$98').
 * @returns {Array} Returns an array of objects.
 */
export function extractLabel(label) {
  if (typeof label !== 'string' || !LABEL_EXTRACT_REGEXP.test(label)) {
    return [];
  }
  const [, columnAbs, column, rowAbs, row] = label.toUpperCase().match(LABEL_EXTRACT_REGEXP);

  return [
    {
      index: rowLabelToIndex(row),
      label: row,
      isAbsolute: rowAbs === '$',
    },
    {
      index: columnLabelToIndex(column),
      label: column,
      isAbsolute: columnAbs === '$',
    },
  ];
}

export function getRangeStringFromString(str) {
  if (str[0] !== '=') {
    str = `=${str}`;
  }
  const result = [];
  // (?:[+\-*/%(,=<>]\s*) 操作符号
  // (?:[\u4e00-\u9fa5_\w-]*\s*!\s*)? 可能存在的sheetName
  // (\$?[A-Z]+\$?\d+(\s*:\s*\$?[A-Z]+\$?\d+)?|\$?[A-Z]\s*:\s*\$?[A-Z]) 表格范围表达式，
  // 如 A2、 B2 : E4、 B:E、 $A2、 $A$2
  // (?!\s*\() 后面不跟 (
  const patt = /(?:[+\-*/%(,=<>]\s*)((([\u4e00-\u9fa5_\w-]*|'[^']*')\s*!\s*)?(\$?[A-Z]+\$?\d+(\s*:\s*\$?[A-Z]+\$?\d+)?|\$?[A-Z]\s*:\s*\$?[A-Z]))(?!\s*\()/g;
  let matchObj;
  do {
    // 找到 形如 sheet1 ! E1, sheet1 ! E1 : E3, E1, E1 : E3
    matchObj = patt.exec(str);
    if (matchObj) {
      result.push(matchObj[1]);
    }
  } while (matchObj);
  return result;
}

// "A1:B2" => [{row:0, column:0},{row:1, column:1}]
// hot 用于获取 B:E 格式的行数
export function parseRange(rangStr, hot) {
  return new Promise((resolve, reject) => {
    // (([\u4e00-\u9fa5_\w-]*|'[^']*')\s*!\s*)? 对应 sheetName ! 或 'sheetName' !
    // (\$?[A-Z]+\$?\d+(\s*:\s*\$?[A-Z]+\$?\d+)?|\$?[A-Z]\s*:\s*\$?[A-Z])
    // 表格范围表达式，如 A2、 B2 : E4、 B:E、$A$2、$B:$E
    const matchObj = rangStr.match(/^(([\u4e00-\u9fa5_\w-]*|'[^']*')\s*!\s*)?(\$?[A-Z]+\$?\d+(\s*:\s*\$?[A-Z]+\$?\d+)?|\$?[A-Z]\s*:\s*\$?[A-Z])$/);
    if (matchObj) {
      const startParam = {};
      // sheetName
      let sheetName = matchObj[2];
      const start = (instance, isDelay) => {
        const range = matchObj[3].split(/\s*:\s*/);
        // 判断是否 形如 A:B、$A:B 这种整列选中的格式
        if (/^\$?[A-Z]+$/.test(range[0])) {
          startParam.row = 0;
          startParam.column = columnLabelToIndex(range[0]);
          resolve([
            startParam,
            { row: instance.rows.len - 1, column: columnLabelToIndex(range[1]) },
            isDelay,
          ]);
          return;
        }
        const [startRow, startCol] = extractLabel(range[0]);
        startParam.row = startRow.index;
        startParam.column = startCol.index;
        // 结束范围
        if (range[1]) { // B2 : E4、$B$2 : $E$4 这种格式
          const [endRow, endCol] = extractLabel(range[1]);
          resolve([startParam, { row: endRow.index, column: endCol.index }, isDelay]);
          return;
        }
        // A2、 $A$2 这种格式
        resolve([startParam, {}, isDelay]);
      };
      if (sheetName) {
        // 去除可能存在的单引号
        sheetName = sheetName.replace(/(^')|('$)/g, '');
        startParam.sheetName = sheetName;
        const externalHot = window.xsObj.getInstanceByName(sheetName);
        if (externalHot) {
          start(externalHot.data);
        } else if (hot.initing) {
          window.xsObj.addExternalInstanceFinishInitialization(sheetName, (instance, isDelay) => {
            start(instance, isDelay);
            hot.initing = false;
          });
        } else {
          reject('#REF!');
        }
        return;
      }
      start(hot);
      return;
    }
    resolve(false);
  });
}
