// gobal var
const cellPaddingWidth = 5;
const tableFixedHeaderCleanStyle = { fillStyle: '#f4f5f8' };
const tableGridStyle = {
  fillStyle: '#fff',
  lineWidth: thinLineWidth(),
  strokeStyle: '#e6e6e6',
};
const especialCellType = ['image', 'file', 'checkbox'];

function tableFixedHeaderStyle() {
  return {
    textAlign: 'center',
    textBaseline: 'middle',
    font: `500 ${npx(12)}px Source Sans Pro`,
    fillStyle: '#585757',
    lineWidth: thinLineWidth(),
    strokeStyle: '#e6e6e6',
  };
}

// linjian这里增加传入startPostion起始位置，用于数据量大的时候 不需要从头开始算当前格子的left top
function getDrawBox(rindex, cindex, startPostion, viewRange, renderPosition) {
  const { data } = this;
  const {
    left, top, width, height, mergeRealHeight,
  } = data.cellRect(rindex, cindex, startPostion, viewRange, renderPosition);
  return new DrawBox(left, top, width, height, cellPaddingWidth, mergeRealHeight);
}
