/**
 * @description: 用来格式化数据
 * @param {cell} 单元格
 * @param {cellprops} 单元格属性
 * @param {cellText} 原始文本
 * @return: cellText 格式化后的文本
 * @Author: linjian
 * @Date: 2019-05-13 15:00:51
 */
function formatText(cell, cellProps, cellText, data, rindex, cindex) {
  let format = 'normal';
  let extendFormat;// 扩展参数主要是存储 数值精度，日期的格式等
  if (cellProps) {
    extendFormat = parseInt(cellProps.scale || 0, 10);
    if (cellProps.format) {
      if (cellProps.format.indexOf('%') !== -1) {
        if (cellProps.type === 'permillage') {
          format = 'permillage';
        } else {
          format = 'percent';
        }
      } else if (cellProps.format.indexOf('￥') !== -1) {
        format = 'rmb';
      } else if (cellProps.format.indexOf('$') !== -1) {
        format = 'usd';
      } else {
        switch (cellProps.cellType) {
          case 'numeric':
            if (cellProps.format.indexOf(',') !== -1) {
              format = 'number';
            } else {
              format = 'normalNumber';
            }
            extendFormat = parseInt(cellProps.scale, 10);
            break;
          case 'date':
            format = 'date';
            extendFormat = (cellProps.format || 'YYYY-MM-DD').toUpperCase();
            break;
          case 'datetime':
            format = 'date';
            extendFormat = (cellProps.format || 'YYYY-MM-DD hh:mm:ss').split(' ');
            extendFormat = extendFormat[0].toUpperCase() + ' ' + extendFormat[1];
            break;
          case 'time':
            format = 'time';
            extendFormat = (cellProps.format || 'HH:mm:ss').replace(/[h]/g, 'H');
            break;
          default:
            break;
        }
      }
    }
    if (cellProps.cellType === 'dropdown') {
      const { dropdownText } = cell;
      if (dropdownText) {
        // eslint-disable-next-line no-param-reassign
        cellText = dropdownText;
      } else if (cellText) {
        const { source } = cell;
        if (source) {
          const result = [];
          const sourceCellText = cellText.split(';');
          helper.getTreeData(source, sourceCellText, result);
          const { typeOptions } = cell;
          const { allowCustomValue } = typeOptions;
          // 如果是可以自定义输入下拉项，需要把自定义的项一起加进去 上面的getTreeData会把匹配到从sourceCellText的删除,那么剩下就是customer
          if (allowCustomValue && sourceCellText.length > 0) {
            cell.customerText = sourceCellText.join(';');
            if (!/;$/.test(cell.customerText)) {
              cell.customerText = `${cell.customerText};`;
            }
          }
          if (result.length === 0 && cell.customerText) {
            cell.customerText = cell.customerText.substring(0, cell.customerText.length - 1);
          }
          // eslint-disable-next-line no-param-reassign
          cellText = (cell.customerText || '') + result.join(';');
        } else {
          // eslint-disable-next-line no-console
          console.error('sheetName:' + data.name + ',行：' + (rindex + 1) + ',列：' + (cindex + 1) + ',缺少下拉数据源。');
        }
        // format = 'string';
      }
    }
  }
  // eslint-disable-next-line no-param-reassign
  cellText = formatm[format].render(cellText, extendFormat);
  return cellText;
}

// 这里增加传入startPostion起始位置，用于数据量大的时候 不需要从头开始算当前格子的left top
// eslint-disable-next-line object-curly-newline
function renderCell(rindex, cindex, startPostion, {
// eslint-disable-next-line no-unused-vars
  fw, fh, tx, ty,
} = {}, viewRange, renderPosition) {
  const { draw, data } = this;
  const { sortedRowMap } = data;
  let nrindex = rindex;
  if (sortedRowMap.has(rindex)) {
    nrindex = sortedRowMap.get(rindex);
  }
  const cell = data.getCell(nrindex, cindex);
  if (cell === null) return;

  const style = data.getCellStyleOrDefault(nrindex, cindex);
  // 自适应行高
  if (data.isAjustRowHeight) {
    style.textwrap = true;
  }
  // linjian 获取单元格属性
  const { cellProps, noCalcFormula, formularValue } = cell;
  // console.log('style:', style);
  const dbox = getDrawBox.call(this, rindex, cindex, startPostion, viewRange, renderPosition);
  // console.log('row:',rindex,'col:',cindex,'top:'+dbox.y);
  dbox.bgcolor = style.bgcolor;
  draw.rect(dbox, () => {
    if (!cellProps || !especialCellType.includes(cellProps.cellType)) {
      // render text
      // linjian 对0特殊处理
      let rawText = cell.text;
      if (rawText === 0) {
        rawText = '0';
      }
      let cellText = '';
      if (helper.isFormulaExpression(rawText)) {
        const rootFormular = rawText.replace(/[(|)|=]/g, '');
        if (noCalcFormula || formularValue === 0 || formularValue === false) {
          cellText = formularValue;
        } else if (!formularValue && data.customerFormular && typeof data.customerFormular[rootFormular] === 'function') {
          cellText = data.customerFormular[rootFormular](rindex, cindex);
          // rows不能缓存 如果是增删行会有改变的
          if (rootFormular !== 'ROWS') {
            // 值写进去缓存起来
            data.setCellFormulaValue(rindex, cindex, cellText);
          }
        } else {
          cellText = formularValue || '';
        }
      } else {
        cellText = rawText || '';
      }
      // linjian 增加默认值处理
      if (cellProps && cellProps.defaultValue && cellText === '') {
        // eslint-disable-next-line no-param-reassign
        cellText = cellProps.defaultValue;
      }
      // linjian 这里换成直接取formularValue值，公式则在dataDaxy里面计算
      // let cellText = _cell.render(rawText || '', formulam,
      // (y, x) => (data.getCellTextOrDefault(x, y)));
      // linjian 添加自定义校验内容处理
      const { hook, customerValidator } = data; // hook.customerValidate是外部传入的校验
      const [flag, message] = customerValidator.initData(cellProps, hook.customerValidate, rindex, cindex).validate(cellText);
      if (flag) {
        // linjian  增加自定义格式化方式
        cellText = formatText(cell, cellProps, cellText, data, rindex, cindex);
        if (cell.isError) {
          delete cell.isError;
        }
        // if (style.format) {
        //   // console.log(data.formatm, '>>', cell.format);
        //   cellText = formatm[style.format].render(cellText);
        // }
      } else {
        cell.isError = true;
        draw.error(dbox);
        cell.errorMsg = message;
      }
      const font = Object.assign({}, style.font);
      font.size = getFontSizePxByPt(font.size);
      draw.text(cellText, dbox, {
        align: style.align || 'left',
        valign: style.valign,
        font,
        color: style.color,
        strike: style.strike,
        underline: style.underline,
      }, style.textwrap);
    } else {
      // 图片和附件单独处理
      const { typeOptions } = cell;
      if (especialCellType.includes(cellProps.cellType)) {
        const { files } = typeOptions || {};
        let { images } = typeOptions || {};
        let fileCount = -1; // linjian 用来记录附件的个数
        const font = Object.assign({}, style.font);
        font.size = getFontSizePxByPt(font.size);
        const textStyle = {
          align: style.align,
          valign: style.valign,
          font,
          color: style.color,
          strike: style.strike,
          underline: style.underline,
        };
        if (Array.isArray(images) && images.length > 0) {
          const boxWidth = dbox.width;
          const boxHeight = dbox.height;
          let selectedItem = images[0];
          images = images.map((item) => {
            if (item.selected) {
              selectedItem = item;
            }
            let {
              width, height,
            } = item;
            // 下面先处理大于情况
            if (height > boxHeight - 8) {
              const scale = (boxHeight - 5) / height;
              // eslint-disable-next-line operator-assignment
              width = scale * width;
              height = boxHeight - 5;
            }
            if (width > boxWidth) {
              const scale = boxWidth / (width * 2);
              // eslint-disable-next-line operator-assignment
              height = scale * height;
              width = boxWidth / 2;
            }
            // 在处理图片太小情况
            if (boxHeight - height > 5 && boxWidth / 2 > width) {
              // linjian 下面先按照高度处理 处理完成后如果宽度超出在按照宽度处理
              let scale = (boxHeight - 5) / height;
              // eslint-disable-next-line operator-assignment
              width = scale * width;
              height = boxHeight - 5;
              if (boxWidth / 2 < width) {
                scale = boxWidth / (width * 2);
                // eslint-disable-next-line operator-assignment
                height = scale * height;
                width = boxWidth / 2;
              }
            }
            // if (boxWidth / 2 > width) {
            //   width = boxWidth / 2;
            // }
            const paddingY = (boxHeight - height) / 2;
            const paddingX = (boxWidth - width) / 2;
            const top = (paddingY + dbox.y + ty) + 'px';
            const left = (paddingX + dbox.x + tx) + 'px';
            item.top = top;
            item.left = left;
            item.width = width;
            item.height = height;
            return item;
          });
          typeOptions.images = images;
          const {
            path, name, width, height, left, top,
          } = selectedItem;
          let isExistImageObj = false;
          if (cell.imageId) {
            // eslint-disable-next-line no-undef
            const imageTemp = document.getElementById(cell.imageId);
            if (imageTemp) {
              isExistImageObj = true;
              imageTemp.src = path;
              imageTemp.style.left = left;
              imageTemp.style.top = top;
              imageTemp.style.width = `${width}px`;
              imageTemp.style.height = `${height}px`;
              imageTemp.style.display = 'block';
            }
          }
          if (!cell.imageId || !isExistImageObj) {
            // eslint-disable-next-line no-undef
            const image = new Image();
            image.src = path;
            image.name = name;
            image.style.width = `${width}px`;
            image.style.height = `${height}px`;
            const uuid = utils.uuID();
            image.id = uuid;
            image.style.left = left;
            image.style.top = top;
            cell.imageId = uuid;
            data.imageItems.el.appendChild(image);
          }
          data.imageItems.el.style.display = 'block';
        }
        if (!images || images.length === 0 || cellProps.cellType === 'file' || cellProps.cellType === 'checkbox') {
          const {
            imageImg, imageFile, imageCheckedBox, imageUncheckedBox,
          } = data;
          let imgObj = imageImg;
          if (files) {
            imgObj = imageFile;
            fileCount = files.length || 0;
          } else {
            if (cellProps.cellType === 'checkbox') {
              if (cell.text) {
                imgObj = imageCheckedBox;
              } else {
                imgObj = imageUncheckedBox;
              }
            }
            let { height, width } = imgObj;
            const boxHeight = dbox.height;
            const boxWidth = dbox.width;
            if (height > boxHeight) {
              const scale = (boxHeight - 8) / height;
              // eslint-disable-next-line operator-assignment
              width = scale * width;
              height = boxHeight - 5;
            }
            if (width > boxWidth) {
              const scale = boxWidth / width * 2;
              // eslint-disable-next-line operator-assignment
              height = scale * height;
              width = boxWidth / 2;
            }
          }
          // eslint-disable-next-line no-undef
          draw.drawImage(imgObj, dbox, fileCount, textStyle);
        }
      }
    }
    // linjian 为批注和注解添加三角形标识
    if (cell.annotation || cell.comment) {
      const rightX = dbox.x + dbox.width;
      const rightY = dbox.y;
      let [x1, y1, x2, y2, x3, y3] = [rightX - 10, rightY, rightX, rightY, rightX, rightY + 10];
      let fillColor = '#ff0000';
      if (cell.comment) {
        [x1, y1, x2, y2, x3, y3] = [dbox.x, dbox.y + 10, dbox.x, dbox.y, dbox.x + 10, dbox.y];
        fillColor = '#1ecece';
      }
      draw.triangle(x1, y1, x2, y2, x3, y3, fillColor);
    }
    // error
    const error = data.validations.getError(rindex, cindex);
    if (error) {
      // console.log('error:', rindex, cindex, error);
      draw.error(dbox);
    }
    return true;
  });
  if (style.border !== undefined) {
    dbox.setBorders(style.border);
    // this.bboxes.push(dbox);
    draw.strokeBorders(dbox);
  }
}

function renderAutofilter(viewRange, startPostion) {
  const { data, draw } = this;
  if (viewRange) {
    const { autoFilter } = data;
    if (!autoFilter.active()) return;
    const afRange = autoFilter.hrange() || [];
    afRange.forEach((item) => {
      if (viewRange.intersects(item)) {
        item.each((ri, ci) => {
          const { isInMerge, lastCi, firstCi } = helper.getLastCellMerge(data.merges, ri, ci);
          if (!isInMerge || ci === lastCi) {
            const dbox = getDrawBox.call(this, ri, ci, startPostion);
            let isHasFilter = false;
            let filter = null;
            if (isInMerge) {
              filter = autoFilter.getFilter(firstCi, ci);
            } else {
              filter = autoFilter.getFilter(ri, ci);
            }
            if (filter) {
              if ((filter.value && filter.value.length > 0) || filter.order) {
                isHasFilter = true;
              }
            }
            draw.dropdown(dbox, isHasFilter);
          }
        });
      }
    });
  }
}

function renderContent(viewRange, fw, fh, tx, ty, startPostion, renderPosition) {
  const { draw, data } = this;
  // linjian render之前，先把image全部隐藏
  const imgObjTemp = data.imageItems.el.querySelectorAll('img');
  if (imgObjTemp) {
    imgObjTemp.forEach((item) => { item.style.display = 'none'; });
  }
  draw.save();
  draw.translate(fw, fh)
    .translate(tx, ty);

  const { exceptRowSet } = data;
  // const exceptRows = Array.from(exceptRowSet);
  const filteredTranslateFunc = (ri) => {
    const ret = exceptRowSet.has(ri);
    if (ret) {
      const height = data.rows.getHeight(ri);
      draw.translate(0, -height);
    }
    return !ret;
  };

  const exceptRowTotalHeight = data.exceptRowTotalHeight(viewRange.sri, viewRange.eri);
  // 1 render cell
  draw.save();
  draw.translate(0, -exceptRowTotalHeight);

  // 1 render cell
  this.bboxes = [];
  // console.time('renderText');
  viewRange.each((ri, ci) => {
    // linjian 添加合并单元格处理 如果不是合并单元格的第一个格子，则不渲染
    const isInMerge = this.data.isCellInMerge(ri, ci);
    // huanglei 如果是隐藏的列 也不渲染
    const isHideCol = this.data.cols.isHideCol(ci);
    if (!isInMerge && !isHideCol) {
      renderCell.call(this, ri, ci, startPostion, {
        fw, fh, tx, ty,
      }, viewRange);
    }
  }, ri => filteredTranslateFunc(ri));
  draw.restore();
  // startPostion ={};
  // viewRange.each((ri, ci) => {
  //   renderCell.call(this, ri, ci);
  // });
  // console.timeEnd('renderText');
  // 2 render cell border
  // startPostion ={};
  // renderCellBorders.call(this);
  // this.bboxes = [];
  // 2 render mergeCell
  const rset = new Set();
  draw.save();
  draw.translate(0, -exceptRowTotalHeight);
  const tempPostion = Object.assign({}, startPostion);
  // if (renderPosition === 'left-down') {
  //   tempPostion.sri = data.freeze[0] - 1;
  // }
  data.eachMergesInView(viewRange, ({ sri, sci, eri }) => {
    if (!exceptRowSet.has(sri)) {
      renderCell.call(this, sri, sci, tempPostion, {
        fw, fh, tx, ty,
      }, viewRange, renderPosition);
    } else if (!rset.has(sri)) {
      rset.add(sri);
      const height = data.rows.sumHeight(sri, eri + 1);
      draw.translate(0, -height);
    }
  });
  draw.restore();
  // 3 render autofilter
  renderAutofilter.call(this, viewRange, startPostion);
  draw.restore();
}

// eslint-disable-next-line no-unused-vars
function customerRenderContent(viewRange, fw, fh, tx, ty, startPostion) {
  const { draw, data } = this;
  draw.save();
  draw.translate(fw, fh)
    .translate(tx, ty);

  const { exceptRowSet } = data;
  // const exceptRows = Array.from(exceptRowSet);
  const filteredTranslateFunc = (ri) => {
    const ret = exceptRowSet.has(ri);
    if (ret) {
      const height = data.rows.getHeight(ri);
      draw.translate(0, -height);
    }
    return !ret;
  };

  const exceptRowTotalHeight = data.exceptRowTotalHeight(viewRange.sri, viewRange.eri);
  // 1 render cell
  draw.save();
  draw.translate(0, -exceptRowTotalHeight);
  viewRange.each((ri, ci) => {
    renderCell.call(this, ri, ci, startPostion);
  }, ri => filteredTranslateFunc(ri));
  draw.restore();
  // viewRange.each((ri, ci) => {
  //   renderCell.call(this, ri, ci);
  // });
  // console.timeEnd('renderText');
  // 2 render mergeCell
  const rset = new Set();
  draw.save();
  draw.translate(0, -exceptRowTotalHeight);

  // 3 render mergeCell
  data.eachMergesInView(viewRange, ({ sri, sci, eri }) => {
    if (!exceptRowSet.has(sri)) {
      renderCell.call(this, sri, sci);
    } else if (!rset.has(sri)) {
      rset.add(sri);
      const height = data.rows.sumHeight(sri, eri + 1);
      draw.translate(0, -height);
    }
  });
  draw.restore();

  // 3 render autofilter
  renderAutofilter.call(this, viewRange, startPostion);

  draw.restore();
}

function renderSelectedHeaderCell(x, y, w, h) {
  const { draw } = this;
  draw.save();
  draw.attr({ fillStyle: 'rgba(75, 137, 255, 0.08)' })
    .fillRect(x, y, w, h);
  draw.restore();
}

// linjian 添加流表、格表判断识别
function getStreamFlag(data, ri) {
  let result = '';
  const { streamCharts } = data.settings;
  if (!streamCharts) {
    return '';
  }
  streamCharts.some((item) => {
    const { finalFirstRows, finalLastRows } = item;
    if (ri >= finalFirstRows && ri <= finalLastRows) {
      if (finalLastRows === finalFirstRows) {
        result = '+';
      } else if (finalFirstRows === ri) {
        result = '↓';
      } else if (finalLastRows === ri) {
        result = '↑';
      }
      return true;
    }
    return false;
  });
  return result;
}

// viewRange
// type: all | left | top
// w: the fixed width of header
// h: the fixed height of header
// tx: moving distance on x-axis
// ty: moving distance on y-axis
function renderFixedHeaders(type, viewRange, w, h, tx, ty) {
  const { draw, data } = this;
  const sumHeight = viewRange.h; // rows.sumHeight(viewRange.sri, viewRange.eri + 1);
  const sumWidth = viewRange.w; // cols.sumWidth(viewRange.sci, viewRange.eci + 1);
  const nty = ty + h;
  const ntx = tx + w;

  draw.save();
  // draw rect background
  draw.attr(tableFixedHeaderCleanStyle);
  if (type === 'all' || type === 'left') draw.fillRect(0, nty, w, sumHeight);
  if (type === 'all' || type === 'top') draw.fillRect(ntx, 0, sumWidth, h);

  const {
    sri, sci, eri, eci,
  } = data.selector.range;
  // console.log(data.selectIndexes);
  // draw text
  // text font, align...
  draw.attr(tableFixedHeaderStyle());
  // y-header-text
  if (type === 'all' || type === 'left') {
    data.rowEach(viewRange.sri, viewRange.eri, (i, y1, rowHeight) => {
      const y = nty + y1;
      const ii = i;
      draw.line([0, y], [w, y]);
      if (sri <= ii && ii < eri + 1) {
        renderSelectedHeaderCell.call(this, 0, y, w, rowHeight);
      }
      // linjian 添加流表标识
      const streamFlag = getStreamFlag(data, ii);
      draw.fillText(streamFlag + (ii + 1), w / 2, y + (rowHeight / 2));
    });
    draw.line([0, sumHeight + nty], [w, sumHeight + nty]);
    draw.line([w, nty], [w, sumHeight + nty]);
  }
  // x-header-text
  if (type === 'all' || type === 'top') {
    data.colEach(viewRange.sci, viewRange.eci, (i, x1, colWidth) => {
      const x = ntx + x1;
      const ii = i;
      draw.line([x, 0], [x, h]);
      if (sci <= ii && ii < eci + 1) {
        renderSelectedHeaderCell.call(this, x, 0, colWidth, h);
      }
      draw.fillText(stringAt(ii), x + (colWidth / 2), h / 2);
    });
    draw.line([sumWidth + ntx, 0], [sumWidth + ntx, h]);
    draw.line([0, h], [sumWidth + ntx, h]);
  }
  draw.restore();
}

function renderFixedLeftTopCell(fw, fh) {
  const { draw } = this;
  draw.save();
  // left-top-cell
  draw.attr({ fillStyle: '#f4f5f8' })
    .fillRect(0, 0, fw, fh);
  // linjian 添加左上角全选按钮
  draw.triangle(fw * 1 / 4, fh * 7 / 8, fw * 7 / 8, fh * 7 / 8, fw * 7 / 8, fh * 1 / 4, '#d8d8d8');
  draw.restore();
}

function renderContentGrid({
  sri, sci, eri, eci, w, h,
}, fw, fh, tx, ty) {
  const { draw, data } = this;
  const { settings } = data;

  draw.save();
  draw.attr(tableGridStyle)
    .translate(fw + tx, fh + ty);
  // const sumWidth = cols.sumWidth(sci, eci + 1);
  // const sumHeight = rows.sumHeight(sri, eri + 1);
  // console.log('sumWidth:', sumWidth);
  draw.clearRect(0, 0, w, h);
  if (!settings.showGrid) {
    draw.restore();
    return;
  }
  // console.log('rowStart:', rowStart, ', rowLen:', rowLen);
  data.rowEach(sri, eri, (i, y, ch) => {
    if (i !== sri) {
      // console.log('row:',i,'y:', y);
      draw.line([0, y], [w, y]);
    }
    if (i === eri) {
      // console.log('row:',i,'y:', y + ch);
      draw.line([0, y + ch], [w, y + ch]);
    }
  });
  data.colEach(sci, eci, (i, x, cw) => {
    if (i !== sci) draw.line([x, 0], [x, h]);
    if (i === eci) draw.line([x + cw, 0], [x + cw, h]);
  });
  draw.restore();
}

function renderFreezeHighlightLine(fw, fh, ftw, fth) {
  const { draw, data } = this;
  const twidth = data.viewWidth() - fw;
  const theight = data.viewHeight() - fh;
  draw.save()
    .translate(fw, fh)
    .attr({ strokeStyle: 'rgba(75, 137, 255, .6)' });
  draw.line([0, fth], [twidth, fth]);
  draw.line([ftw, 0], [ftw, theight]);
  draw.restore();
}

/** end */
class Table {
  constructor(el, data) {
    this.el = el;
    this.draw = new Draw(el, data.viewWidth(), data.viewHeight());
    this.data = data;
  }

  customerRender() {
    // resize canvas
    const { data } = this;
    const { rows, cols } = data;
    // fixed width of header
    const fw = cols.indexWidth;
    // fixed height of header
    const fh = rows.height;

    this.draw.resize(data.viewWidth(), data.viewHeight());
    this.clear();

    const viewRange = data.viewRange();
    // renderAll.call(this, viewRange, data.scroll);
    const tx = data.freezeTotalWidth();
    const ty = data.freezeTotalHeight();
    const { x } = data.scroll;
    // 1
    renderContentGrid.call(this, viewRange, fw, fh, tx, ty);
    // linjian 这里将下面两句代码做了位置互换，由于横向滚动 有合并单元格存在时，
    // 合并单元格内容会把左侧列表头覆盖，所以先渲染内容，后渲染表头，就不会存在这种问题，
    // 这里原来代码是 renderContent.call(this, viewRange, fw, fh, -x, -y);，把-y改成0，
    // 是为了如果行数较多时，每次没有必要从对应行位置长度开始，从0开始即可
    const startPostion = { sri: viewRange.sri, top: 0 };
    if (ty !== 0) {
      startPostion.top = ty;
    }
    data.startPostion = startPostion;
    // console.log('startRange:',viewRange.sri);
    renderContent.call(this, viewRange, fw, fh, -x, startPostion.top, startPostion);
    renderFixedHeaders.call(this, 'all', viewRange, fw, fh, tx, ty);
    renderFixedLeftTopCell.call(this, fw, fh);
    const [fri, fci] = data.freeze;
    if (fri > 0 || fci > 0) {
      // 2
      if (fri > 0) {
        const vr = viewRange.clone();
        vr.sri = 0;
        vr.eri = fri - 1;
        vr.h = ty;
        renderContentGrid.call(this, vr, fw, fh, tx, 0);
        renderFixedHeaders.call(this, 'top', vr, fw, fh, tx, 0);
        renderContent.call(this, vr, fw, fh, -x, 0);
      }
      // 3
      if (fci > 0) {
        const vr = viewRange.clone();
        vr.sci = 0;
        vr.eci = fci - 1;
        vr.w = tx;
        renderContentGrid.call(this, vr, fw, fh, 0, ty);
        renderFixedHeaders.call(this, 'left', vr, fw, fh, 0, ty);
        // linjian 从起始点开始计算 renderContent.call(this, vr, fw, fh, 0, -y);
        renderContent.call(this, vr, fw, fh, 0, startPostion.top, startPostion);
      }
      // 4
      const freezeViewRange = data.freezeViewRange();
      renderContentGrid.call(this, freezeViewRange, fw, fh, 0, 0);
      renderFixedHeaders.call(this, 'all', freezeViewRange, fw, fh, 0, 0);
      renderContent.call(this, freezeViewRange, fw, fh, 0, 0);
      // 5
      renderFreezeHighlightLine.call(this, fw, fh, tx, ty);
    }
    // 添加水印
    if (data.isShowWatermark) {
      const colIndexWidth = data.cols.indexWidth;
      const rowIndexWidth = data.rows.height;
      this.draw.drawWaterMark('repeat', data.waterMarkContent, colIndexWidth, rowIndexWidth);
    }
  }

  render() {
    // resize canvas
    const { data } = this;
    const { rows, cols } = data;
    // fixed width of header
    const fw = cols.indexWidth;
    // fixed height of header
    const fh = rows.height;
    // fixed row and col
    const [fri, fci] = data.freeze;

    this.draw.resize(data.viewWidth(), data.viewHeight());
    this.clear();

    const viewRange = data.viewRange();

    // renderAll.call(this, viewRange, data.scroll);
    const tx = data.freezeTotalWidth();
    const ty = data.freezeTotalHeight();
    const { x, y } = data.scroll;
    // 1
    renderContentGrid.call(this, viewRange, fw, fh, tx, ty);
    // linjian 这里将下面两句代码做了位置互换，由于横向滚动 有合并单元格存在时，合并单元格内容会把左侧列表头覆盖，所以先渲染内容，后渲染表头，就不会存在这种问题 ，
    // 这里传入起始位置，用于数据量大的时候 不需要从头开始算当前格子的left top
    const startPostion = { sri: viewRange.sri, top: 0 };
    if (ty !== 0) {
      startPostion.top = ty;
    }
    data.startPostion = startPostion;
    // console.log('startRange:',viewRange.sri);
    renderContent.call(this, viewRange, fw, fh, -x, startPostion.top, startPostion);
    renderFixedHeaders.call(this, 'all', viewRange, fw, fh, tx, ty);
    renderFixedLeftTopCell.call(this, fw, fh);
    if (fri > 0 || fci > 0) {
      // 3 render freeze left-down area
      if (fci > 0) {
        const vr = viewRange.clone();
        vr.sci = 0;
        vr.eci = fci - 1;
        vr.w = tx;
        renderContentGrid.call(this, vr, fw, fh, 0, ty);
        renderFixedHeaders.call(this, 'left', vr, fw, fh, 0, ty);
        // linjian 从起始点开始计算 renderContent.call(this, vr, fw, fh, 0, -y);
        renderContent.call(this, vr, fw, fh, 0, -y, { sri: 0, top: 0 }, 'left-down');
      }
      // linjian没有重新渲染，left-down 的左侧表头没有了
      if (fci === 0) {
        const vr = viewRange.clone();
        renderFixedHeaders.call(this, 'left', vr, fw, fh, 0, ty);
      }
      // 2 render freeze top-right area
      if (fri > 0) {
        const vr = viewRange.clone();
        vr.sri = 0;
        vr.eri = fri - 1;
        vr.h = ty;
        renderContentGrid.call(this, vr, fw, fh, tx, 0);
        renderFixedHeaders.call(this, 'top', vr, fw, fh, tx, 0);
        renderContent.call(this, vr, fw, fh, -x, 0, { sri: 0, top: 0 });
      }
      // 4 left-top
      const freezeViewRange = data.freezeViewRange();
      renderContentGrid.call(this, freezeViewRange, fw, fh, 0, 0);
      renderFixedHeaders.call(this, 'all', freezeViewRange, fw, fh, 0, 0);
      renderContent.call(this, freezeViewRange, fw, fh, 0, 0, { sri: 0, top: 0 });
      // 5
      renderFreezeHighlightLine.call(this, fw, fh, tx, ty);
    }
    // 添加水印
    if (data.isShowWatermark) {
      const colIndexWidth = data.cols.indexWidth;
      const rowIndexWidth = data.rows.height;
      this.draw.drawWaterMark('repeat', data.waterMarkContent, colIndexWidth, rowIndexWidth);
    }
  }

  clear() {
    this.draw.clear();
    this.data.imageItems.el.style.display = 'none';
  }
}

export default Table;
