/* eslint-disable no-use-before-define */
/* eslint-disable no-param-reassign */
import Dependent from './cell/dependent';
import Reference from './cell/reference';
import helper from '../core/helper';
import {
  parseRange, getRangeStringFromString, columnIndexToLabel, rowIndexToLabel,
} from './utils';
import '@babel/polyfill';

// import localHooks from "../mixins/localHooks";
const {
  arrayEach, arrayFilter, arrayMap, rangeEach2, mixin, objectEach,
} = helper;
const staticHooks = new Map();

class Dependence {
  constructor(instance, name) {
    this.name = name;
    this.instance = instance;
    // 数据初始化
    this.reset();
  }

  add(dependent, references, needRefresh = true, sheet) {
    if (!(references instanceof Array)) {
      references = [references];
    }
    dependent = this.getDependent(dependent, true);
    try {
      arrayEach(references, (reference) => {
        reference = this.getReference(reference, true);
        dependent.add(reference);
        reference.add(dependent);
      });
    } catch (e) {
      // 依赖生成错误
      const { tipMessage } = this.instance.settings.hooks;
      if (tipMessage) {
        tipMessage('alert', `${columnIndexToLabel(dependent.column) + rowIndexToLabel(dependent.row)}不可依赖自身`);
      }
      dependent.setError(e);
      dependent.setValue(e.message);
    }
    if (needRefresh) {
      const { row, column } = dependent;
      const text = this.instance.getCellTextOrDefault(row, column);
      if (helper.isFormulaExpression(text)) {
        this.instance.calculateFormularsValue(dependent, sheet);
      } else {
        this.instance.calculateDropdownRangeValue(dependent, sheet);
      }
    }
  }

  removeDependent(dependent) {
    // 如果入参不是Dependent实例，则从this.dependents中获取对应的
    // 否则，则dependent肯定在this.dependents中，直接开始删就行
    if (!(dependent instanceof Dependent)) {
      dependent = this.hasCell(dependent, this.dependents);
    }
    if (dependent) {
      // 从 this.dependents 中删除
      this.dependents.splice(this.dependents.indexOf(dependent), 1);
      // 删除 dependent 中的所有 reference
      this.removeAllReferenceInDenpendent(dependent);
    }
  }

  removeReference(reference) {
    if (!(reference instanceof Reference)) {
      reference = this.getReference(reference, false);
    }
    if (reference) {
      arrayEach(reference.dependents, (dependent) => {
        dependent.remove(reference);
      });
      let arr;
      if (reference.sheetName) {
        arr = this.externalReferences[reference.sheetName];
      } else {
        arr = this.references;
      }
      arr.splice(arr.indexOf(reference), 1);
    }
  }

  // 删除某个 reference 中的 dependent
  removeDependentInReference(reference, dependent) {
    const dependentsInReference = reference.dependents;
    let index = dependentsInReference.indexOf(dependent);
    if (index > -1) {
      dependentsInReference.splice(index, 1);
    }
    // 当前 reference 没有 dependent 了，就从 this.references 中删除
    if (dependentsInReference.length === 0) {
      const { sheetName } = reference;
      let allReferences;
      if (sheetName) {
        allReferences = this.externalReferences[sheetName] || [];
      } else {
        allReferences = this.references;
      }
      index = allReferences.indexOf(reference);
      if (index > -1) {
        allReferences.splice(index, 1);
      }
    }
  }

  // 删除某个 dependent 中的所有 reference
  removeAllReferenceInDenpendent(dependent) {
    arrayEach(dependent.references.slice(0), (reference) => {
      this.removeDependentInReference(reference, dependent);
    });
    // 外部依赖
    objectEach(dependent.externalReferences, (references) => {
      arrayEach(references.slice(0), (reference) => {
        this.removeDependentInReference(reference, dependent);
      });
    });
    dependent.clearReferences();
  }

  getDependent(dependent, addToArrayWhileNone) {
    return this.getCell(dependent, this.dependents, Dependent, addToArrayWhileNone);
  }

  getReference(reference, addToArrayWhileNone) {
    const { sheetName } = reference;
    let targetArray;
    if (sheetName) {
      if (!this.externalReferences[sheetName]) {
        this.externalReferences[sheetName] = [];
      }
      targetArray = this.externalReferences[sheetName];
    } else {
      targetArray = this.references;
    }
    return this.getCell(reference, targetArray, Reference, addToArrayWhileNone);
  }

  getReferenceCrossTable(reference, sheetName) {
    if (!this.externalReferences[sheetName]) {
      return false;
    }
    const targetArray = this.externalReferences[sheetName];
    return this.getCellCrossTable(reference, targetArray, Reference);
  }

  // 依赖某个 reference 的所有 dependent
  getAllDependentsByReferenceCrossTable(reference, arr = [], sheetName) {
    reference = this.getReferenceCrossTable(reference, sheetName);
    arrayEach(reference.dependents, (dependent) => {
      if (arr.indexOf(dependent) === -1) {
        arr.push(dependent);
        this.getAllDependentsByReferenceCrossTable(dependent, arr, sheetName);
      }
    });
    return arr;
  }

  // 依赖某个 reference 的所有 dependent
  getAllDependentsByReference(reference, arr = [], isGlobal) {
    reference = this.getReference(reference, false);
    arrayEach(reference.dependents, (dependent) => {
      if (arr.indexOf(dependent) === -1) {
        arr.push(dependent);
        this.getAllDependentsByReference(dependent, arr);
      }
    });
    return arr;
  }

  // 从dependents中获取dependent或从reference中获取
  // 无则创建
  getCell(cell, array, Constructor, addToArrayWhileNone = false) {
    if (cell instanceof Constructor) {
      return cell;
    }
    let tmp = this.hasCell(cell, array);
    if (!tmp) {
      tmp = new Constructor(cell.row, cell.column, cell.sheetName);
      if (addToArrayWhileNone) {
        array.push(tmp);
      }
    }
    return tmp;
  }

  // 从dependents中获取dependent或从reference中获取
  // 无则创建
  getCellCrossTable(cell, array, Constructor, addToArrayWhileNone = false) {
    if (cell instanceof Constructor) {
      return cell;
    }
    let tmp;
    array.some((value) => {
      if (value.row === cell.row && value.column === cell.column) {
        tmp = value;
        return true;
      }
      return false;
    });
    if (!tmp) {
      tmp = new Constructor(cell.row, cell.column, cell.sheetName);
      if (addToArrayWhileNone) {
        array.push(tmp);
      }
    }
    return tmp;
  }

  hasCell(cell, array) {
    let tmp = false;
    arrayEach(array, (cellFind) => {
      if (cellFind.isEqual(cell)) {
        tmp = cellFind;
        return false;
      }
      return true;
    });
    return tmp;
  }

  // reference 对应单元格的值改变后
  afterReferenceChange(dependents, sheet, rowOffset = 0) {
    if (dependents.length === 0) {
      return;
    }
    const status = Dependent.STATE_OUT_OFF_DATE;
    arrayEach(dependents, (dependent) => {
      dependent.setState(status);
    });
    // 遍历每个受影响的 dependent，然后根据 references 组成 source
    arrayEach(dependents, (dependent) => {
      dependent.setError(null);
      const { row, column } = dependent;
      const text = this.instance.getCellTextOrDefault(row, column);
      if (helper.isFormulaExpression(text)) {
        this.instance.calculateFormularsValue(dependent, sheet, rowOffset);
      } else {
        this.instance.calculateDropdownRangeValue(dependent, sheet, rowOffset);
      }
      dependent.setState(dependent.constructor.STATE_UP_TO_DATE);
    });
  }

  // reference 对应单元格的值改变后
  afterReferenceChangeCrossTable(dependents, instance) {
    if (dependents.length === 0) {
      return;
    }
    const status = Dependent.STATE_OUT_OFF_DATE;
    arrayEach(dependents, (dependent) => {
      dependent.setState(status);
    });
    // 遍历每个受影响的 dependent，然后根据 references 组成 source
    arrayEach(dependents, (dependent) => {
      instance.calculateFormularsValue(dependent);
      dependent.setState(dependent.constructor.STATE_UP_TO_DATE);
    });
  }

  // dependent 的 referenceRange 改变时调用
  async afterReferenceRangeChange(dependent, newReference, sheet) {
    const dependentObj = this.getDependent(dependent, true);
    // 清空原来的错误
    dependentObj.setError(null);
    dependentObj.refreshAfterRowColOperation = false;
    // 清空原来的依赖关系
    this.removeAllReferenceInDenpendent(dependentObj);
    // 增加异步是为了应对在初始化时，其他实例未初始化而解析失败
    let isDelay = false;
    if (typeof newReference === 'string') {
      try {
        [newReference, isDelay] = await getCellsByStr(newReference, this.instance);
      } catch (error) {
        newReference = [];
        dependentObj.setValue(error);
        dependentObj.setError(error);
      }
    }
    // 增加依赖
    this.add(dependentObj, newReference, true, sheet);
    return [dependentObj, isDelay];
  }

  getOutOfDateDependents() {
    return arrayFilter(this.dependents, cell => cell.isState(Dependent.STATE_OUT_OFF_DATE));
  }

  reset() {
    // 存放 Dependent 创建的实例
    this.dependents = [];
    // 存放 Reference 创建的实例
    this.references = [];
    // 外部的 Reference
    this.externalReferences = {
      // SHEET2: []
    };
  }
}
// mixin(Dependence, localHooks);
/*
* 用于存放依赖关系
* */
class Dependences {
  constructor(instance) {
    this.all = new Map();
    this.instance = instance;
    this.onAfterReferenceValueChange = this.onAfterReferenceValueChange;
    // instance.addHook('afterSetDataAtCell', (...args) => this.onAfterSetDataAtCell(...args));
    // instance.addHook('afterSetDataAtRowProp', (...args) => this.onAfterSetDataAtCell(...args));
    // instance.addHook('afterCreateRow', (...args) => this.onAfterCreateRow(...args));
    // instance.addHook('afterCreateCol', (...args) => this.onAfterCreateCol(...args));
    // instance.addHook('afterRemoveRow', (...args) => this.onAfterRemoveRow(...args));
    // instance.addHook('afterRemoveCol', (...args) => this.onAfterRemoveCol(...args));
    // instance.addHook('afterLoadData', () => this.onAfterLoadData());
    // instance.addHook('afterReferenceValueChanging', (...args) => this.onAfterReferenceValueChange(...args), 1);
  }

  // 从all中获取key对应的dependence，如果没有，则创建
  getDependence(key) {
    let dependence = this.all.get(key);
    if (!dependence) {
      dependence = new Dependence(this.instance, key);
      this.all.set(key, dependence);
    }
    return dependence;
  }

  async update(dependenceName, dependent, sheet, rowOffset) {
    const dependence = this.getDependence(dependenceName);
    let isDelay = false;
    if (typeof dependent === 'string') {
      [dependent] = await parseRange(dependent);
    }

    // 获取公式字符串，如果是公式返回公式值，如果不是 返回 false
    const referencesStr = helper.getReferenceString(this.instance, dependent);
    if (!referencesStr) {
      return null;
    }
    [dependent, isDelay] = await dependence.afterReferenceRangeChange(dependent, referencesStr, sheet);
    dependent.referenceRange = referencesStr;
    if (!dependent.getError() && isDelay) {
      // 如果是延迟加载 这里添加对所有表依赖这个单元格的公式重新计算
      this.onAfterReferenceValueChange([dependent], sheet, rowOffset);
    }
    return dependence;
  }

  changeSheetName(oldName, newName) {
    this.all.forEach((dependence) => {
      const { externalReferences } = dependence;
      const tmp = externalReferences[oldName];
      if (tmp) {
        externalReferences[newName] = tmp;
        delete externalReferences[oldName];
        const changedDependent = [];
        arrayEach(tmp, (reference) => {
          reference.setSheetName(newName);
          reference.pushDependentsTo(changedDependent);
        });
        arrayEach(changedDependent, (dependent) => {
          let referenceString = dependence.runLocalHooks('getReferenceString', dependent);
          const reg = new RegExp(/([-+*/%<>=(,]\s*)([^-+*/%<>=(,!\s]*)(\s*!\s*[a-zA-Z]+(\d+)?)/g);
          let matchObj;
          do {
            matchObj = reg.exec(referenceString);
            if (matchObj) {
              if (matchObj[2].replace(/(^')|('$)/g, '') === oldName) {
                const tmpStr = `${matchObj[1]}'${newName}'${matchObj[3]}`;
                referenceString = referenceString.substr(0, matchObj.index) + tmpStr +
                referenceString.substr(matchObj.index + matchObj[0].length);
                reg.lastIndex = matchObj.index + tmpStr.length;
              }
            }
          } while (matchObj);
          dependence.runLocalHooks('setReferenceString', dependent, referenceString);
        });
      }
    });
  }

  changeExternalRowCol(externalSheetName, { row, col }, amount) {
    this.all.forEach((dependence, dependenceName) => {
      const externalReferences = dependence.externalReferences[externalSheetName];
      if (!externalReferences) {
        return;
      }
      if (row !== undefined) {
        const dependents = [];
        if (amount > 0) { // 增加行
          arrayEach(externalReferences, (reference) => {
            if (reference.row >= row) {
              reference.translateTo(amount, 0);
              reference.pushDependentsTo(dependents);
            }
          });
          arrayEach(dependents, (dependent) => {
            const referenceString = dependence.runLocalHooks('getReferenceString', dependent);
            const reg = new RegExp(`'?${externalSheetName}'?\\s*!\\s*[A-Z]+\\s*:\\s*[A-Z]+`);
            // 暂时重新生成依赖吧，后面可以改成直接增加reference
            if (referenceString.match(reg)) {
              dependence.afterReferenceRangeChange(dependent, referenceString);
            }
          });
        } else if (amount < 0) { // 删除行
          arrayEach(externalReferences.slice(0), (reference) => {
            if (reference.row < row) {
              return;
            }
            if (reference.row === row) {
              dependence.removeReference(reference);
            } else {
              reference.translateTo(amount, 0);
            }
            reference.pushDependentsTo(dependents);
          });
        }
      }
    });
  }

  onAfterReferenceValueChange(changedCells, sheet, rowOffset = 0) {
    // 遍历各个dependence的 afterReferenceValueChange 回调函数
    this.all.forEach((dependence) => {
      const dependentNeedChanging = [];
      arrayEach(changedCells, (changedCell) => {
        const reference = dependence.getReference(changedCell);
        if (reference) {
          // 获取 A2 改变后的会影响到的所有单元格，添加的 dependentNeedChanging
          dependence.getAllDependentsByReference(reference, dependentNeedChanging);
        }
      });
      dependence.afterReferenceChange(dependentNeedChanging, sheet, rowOffset);
    });
    // linjian 更新跨表依赖
    window.xsObj.instanceArray.map((instance) => {
      if (instance.data.name !== this.instance.name) {
        instance.data.dependences.all.forEach((dependence) => {
          const dependentNeedChanging = [];
          arrayEach(changedCells, (changedCell) => {
            const copyCell = Object.assign({ sheetName: this.instance.name }, changedCell);
            const reference = dependence.getReference(copyCell);
            if (reference) {
              // 获取 A2 改变后的会影响到的所有单元格，添加的 dependentNeedChanging
              dependence.getAllDependentsByReference(reference, dependentNeedChanging, true);
            }
          });
          dependence.afterReferenceChange(dependentNeedChanging, instance.sheet, 0);
        });
      }
      return true;
    });
  }

  onAfterSetDataAtCell(changes, source) {
    if (['loadData', 'requestNextPageData', 'SetFormulasReferenceString'].indexOf(source) > -1) {
      return;
    }
    const changedCells = [];
    arrayEach(changes, ([row, column, oldValue, newValue]) => {
      changedCells.push({ row, column });
    });
    // 如果是公式，需要update一下
    this.all.forEach((dependence, dependenceName) => {
      arrayEach(changedCells, (changedCell) => {
        // const { row, column, sheetName } = changedCell;
        const referenceString = dependence.runLocalHooks('getReferenceString', changedCell);
        if (referenceString) {
          this.update(dependenceName, changedCell);
        } else {
          dependence.removeDependent(changedCell);
        }
      });
    });
    this.instance.runHooks('afterReferenceValueChanging', changedCells);
  }

  onAfterCreateRow(index, amount) {
    this.all.forEach((dependence, name) => {
      arrayEach(dependence.dependents, (dependent) => {
        const changedResult = dependent.operate('insertRow', index, amount);
        if (changedResult !== false) {
          dependence.runLocalHooks('setReferenceString', dependent, changedResult);
          dependence.afterReferenceRangeChange(dependent, changedResult);
        }
      });
    });
  }

  onAfterRemoveRow(index, amount) {
    this.all.forEach((dependence, name) => {
      const toRun = [];
      arrayEach(dependence.dependents.slice(0), (dependent) => {
        const { row: originRow } = dependent;
        if (index <= originRow && originRow < index + amount) {
          dependence.removeDependent(dependent);
          return;
        }
        // 单元格的referenceString上移一行
        const changedResult = dependent.operate('removeRow', index, amount);
        if (changedResult !== false) {
          toRun.push(() => {
            dependence.runLocalHooks('setReferenceString', dependent, changedResult);
            dependence.afterReferenceRangeChange(dependent, changedResult);
          });
        }
      });
      // 需等所有单元格上移后，才能setReferenceString
      arrayEach(toRun, func => func());
    });
  }

  onAfterCreateCol(index, amount) {
    this.all.forEach((dependence, name) => {
      arrayEach(dependence.dependents, (dependent) => {
        const changedResult = dependent.operate('insertCol', index, amount);
        if (changedResult !== false) {
          dependence.runLocalHooks('setReferenceString', dependent, changedResult);
          dependence.afterReferenceRangeChange(dependent, changedResult);
        }
      });
    });
  }

  onAfterRemoveCol(index, amount) {
    this.all.forEach((dependence, name) => {
      arrayEach(dependence.dependents, (dependent) => {
        const { column: originCol } = dependent;
        if (index <= originCol && originCol < index + amount) {
          dependence.removeDependent(dependent);
          return;
        }
        const changedResult = dependent.operate('removeCol', index, amount);
        if (changedResult !== false) {
          dependence.runLocalHooks('setReferenceString', dependent, changedResult);
          dependence.afterReferenceRangeChange(dependent, changedResult);
        }
      });
    });
  }

  onAfterLoadData() {
    this.all.forEach((dependence) => {
      dependence.runLocalHooks('afterLoadData');
    });
  }

  removeDependence(name) {
    this.all.set(name, null);
  }

  static addStaticLocalHook(dependenceName, hookName, callback) {
    let hook = staticHooks.get(dependenceName);
    if (!hook) {
      hook = {};
      staticHooks.set(dependenceName, hook);
    }
    let arr = hook[hookName];
    if (!arr) {
      arr = [];
      hook[hookName] = arr;
    }
    arr.push(callback);
  }
}

// 根据字符串生成 单元格格式
async function getCellsByStr(str, hot) {
  const rangeStrings = getRangeStringFromString(str);
  const ranges = await Promise.all(arrayMap(rangeStrings, rangeString => parseRange(rangeString, hot)));
  const cache = new Map();
  const result = [];
  const push = (row, column, sheetName) => {
    const name = `${sheetName}-${row}-${column}`;
    if (!cache.get(name)) {
      cache.set(name, true);
    }
    result.push({ row, column, sheetName });
  };
  let isDelay = false;
  arrayEach(ranges, (range) => {
    if (!Object.prototype.hasOwnProperty.call(range[1], 'row')) {
      const [{ row, column, sheetName }] = range;
      push(row, column, sheetName);
    } else {
      // eslint-disable-next-line prefer-destructuring
      isDelay = range[2];
      const [
        { row: startRow, column: startCol, sheetName },
        { row: endRow, column: endCol },
      ] = range;
      rangeEach2(startRow, endRow, (row) => {
        rangeEach2(startCol, endCol, (col) => {
          push(row, col, sheetName);
        });
      });
    }
  });
  return [result, isDelay];
}
export default Dependences;
