export function to2dArray(arr) {
  let i = 0;
  const ilen = arr.length;

  while (i < ilen) {
    arr[i] = [arr[i]];
    i += 1;
  }
}

export function extendArray(arr, extension) {
  let i = 0;
  const ilen = extension.length;

  while (i < ilen) {
    arr.push(extension[i]);
    i += 1;
  }
}

export function pivot(arr) {
  const pivotedArr = [];

  if (!arr || arr.length === 0 || !arr[0] || arr[0].length === 0) {
    return pivotedArr;
  }

  const rowCount = arr.length;
  const colCount = arr[0].length;

  for (let i = 0; i < rowCount; i += 1) {
    for (let j = 0; j < colCount; j += 1) {
      if (!pivotedArr[j]) {
        pivotedArr[j] = [];
      }

      pivotedArr[j][i] = arr[i][j];
    }
  }

  return pivotedArr;
}

/**
 * A specialized version of `.reduce` for arrays without support for callback
 * shorthands and `this` binding.
 *
 * {@link https://github.com/lodash/lodash/blob/master/lodash.js}
 *
 * @param {Array} array The array to iterate over.
 * @param {Function} iteratee The function invoked per iteration.
 * @param {*} [accumulator] The initial value.
 * @param {Boolean} [initFromArray] Specify using the first element of `array` as the initial value.
 * @returns {*} Returns the accumulated value.
 */
export function arrayReduce(array, iteratee, accumulator, initFromArray) {
  let index = -1;
  let iterable = array;

  if (!Array.isArray(array)) {
    iterable = Array.from(array);
  }
  const { length } = iterable;

  if (initFromArray && length) {
    index += 1;
    accumulator = iterable[index];
  }
  index += 1;
  while (index < length) {
    accumulator = iteratee(accumulator, iterable[index], index, iterable);
  }

  return accumulator;
}

/**
 * A specialized version of `.filter` for arrays without support for callback
 * shorthands and `this` binding.
 *
 * {@link https://github.com/lodash/lodash/blob/master/lodash.js}
 *
 * @param {Array} array The array to iterate over.
 * @param {Function} predicate The function invoked per iteration.
 * @returns {Array} Returns the new filtered array.
 */
export function arrayFilter(array, predicate) {
  let index = -1;
  let iterable = array;

  if (!Array.isArray(array)) {
    iterable = Array.from(array);
  }

  const { length } = iterable;
  const result = [];
  let resIndex = -1;
  index += 1;
  while (index < length) {
    const value = iterable[index];
    resIndex += 1;
    if (predicate(value, index, iterable)) {
      result[resIndex] = value;
    }
  }

  return result;
}

/**
 * A specialized version of `.map` for arrays without support for callback
 * shorthands and `this` binding.
 *
 * @param {Array} array The array to iterate over.
 * @param {Function} iteratee The function invoked per iteration.
 * @returns {Array} Returns the new filtered array.
 */
export function arrayMap(array, iteratee) {
  let index = -1;
  let iterable = array;

  if (!Array.isArray(array)) {
    iterable = Array.from(array);
  }

  const { length } = iterable;
  const result = [];
  let resIndex = -1;
  index += 1;
  while (index < length) {
    const value = iterable[index];
    resIndex += 1;
    result[resIndex] = iteratee(value, index, iterable);
  }

  return result;
}

/**
 * A specialized version of `.forEach` for arrays without support for callback
 * shorthands and `this` binding.
 *
 * {@link https://github.com/lodash/lodash/blob/master/lodash.js}
 *
 * @param {Array|*} array The array to iterate over or an
 * any element with implemented iterator protocol.
 * @param {Function} iteratee The function invoked per iteration.
 * @returns {Array} Returns `array`.
 */
export function arrayEach(array, iteratee) {
  let index = -1;
  let iterable = array;

  if (!Array.isArray(array)) {
    iterable = Array.from(array);
  }

  const { length } = iterable;
  index += 1;
  while (index < length) {
    if (iteratee(iterable[index], index, iterable) === false) {
      break;
    }
  }

  return array;
}

/**
 * Calculate sum value for each item of the array.
 *
 * @param {Array} array The array to process.
 * @returns {Number} Returns calculated sum value.
 */
export function arraySum(array) {
  return arrayReduce(array, (a, b) => a + b, 0);
}

/**
 * Returns the highest value from an array. Can be array of numbers or array of strings.
 * NOTICE: Mixed values is not supported.
 *
 * @param {Array} array The array to process.
 * @returns {Number} Returns the highest value from an array.
 */
export function arrayMax(array) {
  return arrayReduce(array, (a, b) => (a > b ? a : b), Array.isArray(array) ? array[0] : undefined);
}

/**
 * Returns the lowest value from an array. Can be array of numbers or array of strings.
 * NOTICE: Mixed values is not supported.
 *
 * @param {Array} array The array to process.
 * @returns {Number} Returns the lowest value from an array.
 */
export function arrayMin(array) {
  return arrayReduce(array, (a, b) => (a < b ? a : b), Array.isArray(array) ? array[0] : undefined);
}

/**
 * Calculate average value for each item of the array.
 *
 * @param {Array} array The array to process.
 * @returns {Number} Returns calculated average value.
 */
export function arrayAvg(array) {
  if (!array.length) {
    return 0;
  }

  return arraySum(array) / array.length;
}

/**
 * Flatten multidimensional array.
 *
 * @param {Array} array Array of Arrays
 * @returns {Array}
 */
export function arrayFlatten(array) {
  return arrayReduce(
    array,
    (initial, value) => initial.concat(Array.isArray(value) ? arrayFlatten(value) : value),
    [],
  );
}

/**
 * Unique values in the array.
 *
 * @param {Array} array The array to process.
 * @returns {Array}
 */
export function arrayUnique(array) {
  const unique = [];

  arrayEach(array, value => {
    if (unique.indexOf(value) === -1) {
      unique.push(value);
    }
  });

  return unique;
}
/**
 * 遍历range中每个cell - xyx
 * 懒得再建一个helper了，就放array里，range也是array
 * */
export function rangeArrayEach(range, operation) {
  arrayEach(range, ({ from, to }) => {
    // 相对当前区域的行，列
      let relativeRow = 0;
      let relativeCol = 0;
      // 区域行列总数
      let rowTotalSize = 1;
      let colTotalSize = 1;
    if (from.row === to.row && from.col === to.col) {
      // 只有一个格子
      // 回调
      operation(from.row, from.col, relativeRow, relativeCol, rowTotalSize, colTotalSize);
    } else {
      const startRow = Math.min(from.row, to.row);
      const endRow = Math.max(from.row, to.row);
      const startCol = Math.min(from.col, to.col);
      const endCol = Math.max(from.col, to.col);
      rowTotalSize = endRow - startRow + 1;
      colTotalSize = endCol - startCol + 1;
      for (let row = startRow; row <= endRow; row += 1) {
        for (let col = startCol; col <= endCol; col += 1) {
          // 这个格子对应当前区域的哪行哪列
          relativeRow = row - startRow;
          relativeCol = col - startCol;
          // 回调
          operation(row, col, relativeRow, relativeCol, rowTotalSize, colTotalSize);
        }
      }
    }
  });
}
/**
 * 获取数组中非undefined元素
 * */
export function getArrayContent(arr, callback) {
  if (arr instanceof Array && arr.length) {
    arrayEach(arr, (item, index) => {
      if (typeof item !== 'undefined') {
        callback(item, index);
      }
    });
  }
}
/**
 * 遍历二维数组
 * */
export function array2DEach(arr, callback) {
  arrayEach(arr, (rowArray, row) => {
    arrayEach(rowArray, (value, col) => {
      callback(value, row, col, arr);
    });
  });
}
