/* global window */
import en from './en';
import de from './de';
import nl from './nl';
import zhCn from './zh-cn';

let $lang = 'en';
const $messages = {
  en,
  de,
  nl,
  zhCn,
};

function translate(key, messages) {
  if (messages && messages[$lang]) {
    let message = messages[$lang];
    const keys = key.split('.');
    for (let i = 0; i < keys.length; i += 1) {
      const property = keys[i];
      const value = message[property];
      if (i === keys.length - 1) return value;
      if (!value) return undefined;
      message = value;
    }
  }
  return undefined;
}

function t(key, ...arg) {
  let v = translate(key, $messages);
  if (!v && window && window.xsObj && window.xsObj.spreadsheet
    && window.xsObj.spreadsheet.$messages) {
    v = translate(key, window.xsObj.spreadsheet.$messages);
  }
  if (arg.length > 0) {
    arg.forEach((item) => {
      v = v.replace(/\{\}/, item);
    });
  }
  return v || '';
}

function tf(key) {
  return () => t(key);
}

function locale(lang, message) {
  $lang = lang;
  if (message) {
    $messages[lang] = message;
  }
}

export default {
  t,
};
// window.t = t;
export {
  locale,
  t,
  tf,
};
