/* eslint-disable no-case-declarations */
/* global document isNaN */
import helper from './helper';
// import { expr2expr } from './alphabet';
import { getRanges, getDataAfterSplit } from '../dependences/utils';

class Rows {
  constructor({ len, height, minHeight = 0 }, tipMessage, streamCharts = []) {
    this._ = {};
    this.len = len;
    // default row height
    this.height = height;
    // add by linjian support minHeight property to implement mores smaller height resizer
    this.minHeight = minHeight;
    this.tipMessage = tipMessage;
    this.streamItem = null;
    this.isNewStreamItem = false;
    this.streamCharts = streamCharts;
  }

  getHeight(ri) {
    let row = this.get(ri);
    if (row && row.height) {
      return row.height;
    }
    // linjian 下面处理如果行不存在，又是流表中的某一行，则取流表首行的高度
    if (!this.streamItem) {
      this.streamItem = helper.getStreamRangeItem(this.streamCharts, ri);
      if (!this.streamItem) {
        return this.height;
      }
      this.isNewStreamItem = true;
    }
    let { finalFirstRows, finalLastRows } = this.streamItem;
    if (!this.streamItem && (ri < finalFirstRows || ri > finalLastRows)) {
      this.streamItem = helper.getStreamRangeItem(this.streamCharts, ri);
    }
    ({ finalFirstRows, finalLastRows } = this.streamItem);
    row = this.get(finalFirstRows);
    if (row && row.height) {
      return row.height;
    }
    this.isNewStreamItem = false;
    return this.height;
  }

  setHeight(ri, v) {
    const row = this.getOrNew(ri);
    row.height = v;
  }

  setStyle(ri, style) {
    const row = this.getOrNew(ri);
    row.style = style;
  }

  sumHeight(min, max, exceptSet) {
    return helper.rangeSum(min, max, (i) => {
      if (exceptSet && exceptSet.has(i)) return 0;
      return this.getHeight(i);
    });
  }

  totalHeight() {
    return this.sumHeight(0, this.len);
  }

  get(ri) {
    return this._[ri];
  }

  getOrNew(ri) {
    this._[ri] = this._[ri] || { cells: {} };
    return this._[ri];
  }

  getCell(ri, ci) {
    const row = this.get(ri);
    if (row !== undefined && row.cells !== undefined && row.cells[ci] !== undefined) {
      return row.cells[ci];
    }
    return null;
  }

  getCellMerge(ri, ci) {
    const cell = this.getCell(ri, ci);
    if (cell && cell.merge) return cell.merge;
    return [0, 0];
  }

  getCellOrNew(ri, ci) {
    const row = this.getOrNew(ri);
    row.cells[ci] = row.cells[ci] || {};
    return row.cells[ci];
  }

  // what: all | text | format
  setCell(ri, ci, cell, what = 'all') {
    const row = this.getOrNew(ri);
    if (!row.cells) {
      row.cells = {};
    }
    if (what === 'all') {
      row.cells[ci] = cell;
    } else if (what === 'text') {
      row.cells[ci] = row.cells[ci] || {};
      const distCell = row.cells[ci];
      const { cellProps } = distCell;
      if (!cellProps) {
        distCell.cellProps = {
          cellType: 'text',
        };
      }
      const { cellType } = distCell.cellProps;
      // row.cells[ci].text = cell.text;
      switch (cellType) {
        case 'text':
          row.cells[ci].text = cell.text;
          break;
        case 'numeric':
          if (cell.text !== '' && isNaN(cell.text)) {
            distCell.isEllipsisSave = true;
            return `第${ri + 1}行，第${ci + 1}列，只能输入数字\n`;
          }
          row.cells[ci].text = parseFloat(cell.text);
          break;
        case 'dropdown':
          const { source } = distCell;
          const result = [];
          // 如果没有数据源 直接跳过
          if (!source) {
            return true;
          }
          helper.getTreeData(source || [], cell.text, result);
          if (result.length === 0) {
            distCell.isEllipsisSave = true;
            return `第${ri + 1}行，第${ci + 1}列，无找到匹配的下拉项\n`;
          }
          row.cells[ci].text = cell.text;
          break;
        case 'image':
          const srcCellProps = cell.cellProps;
          if (!srcCellProps || srcCellProps.cellType !== 'image') {
            distCell.isEllipsisSave = true;
            return `第${ri + 1}行，第${ci + 1}列，单元格类型不匹配\n`;
          }
          const { typeOptions } = distCell;
          typeOptions.images = JSON.parse(JSON.stringify(cell.typeOptions.images));
          break;
        case 'file':
          const srcFileCellProps = cell.cellProps;
          if (!srcFileCellProps || srcFileCellProps.cellType !== 'file') {
            distCell.isEllipsisSave = true;
            return `第${ri + 1}行，第${ci + 1}列，单元格类型不匹配\n`;
          }
          const typeOptionsFile = distCell.typeOptions;
          typeOptionsFile.files = JSON.parse(JSON.stringify(cell.typeOptions.files));
          break;
        default:
          row.cells[ci].text = cell.text;
          break;
      }
    } else if (what === 'format') {
      row.cells[ci] = row.cells[ci] || {};
      row.cells[ci].style = cell.style;
      if (cell.merge) row.cells[ci].merge = cell.merge;
    }
    return true;
  }

  // linjian 添加外部复制自定义的setCell
  setCellForOutPaste(ri, ci, cell) {
    const row = this.getOrNew(ri);
    if (!row.cells) {
      row.cells = {};
    }
    row.cells[ci] = row.cells[ci] || {};
    const distCell = row.cells[ci];
    const { cellProps } = distCell;
    if (!cellProps) {
      distCell.cellProps = {
        cellType: 'text',
      };
    }
    const { cellType } = distCell.cellProps;
    // row.cells[ci].text = cell.text;
    switch (cellType) {
      case 'text':
        row.cells[ci].text = cell.text;
        break;
      case 'numeric':
        if (cell.text.includes('%')) {
          cell.text = parseFloat(cell.text.replace(/%|,/g, '')) / 100.0;
        } else if (cell.text.includes('‰')) {
          cell.text = parseFloat(cell.text.replace(/,|‰/g, '')) / 1000.0;
        } else if (cell.text !== '' && isNaN(cell.text)) {
          distCell.isEllipsisSave = true;
          return `第${ri + 1}行，第${ci + 1}列，只能输入数字\n`;
        }
        row.cells[ci].text = parseFloat(cell.text);
        break;
      case 'dropdown':
        const { source } = distCell;
        const result = [];
        // 如果没有数据源 直接跳过
        if (!source) {
          return true;
        }
        helper.getTreeDataForOutPaste(source || [], cell.text, result);
        if (result.length === 0) {
          distCell.isEllipsisSave = true;
          return `第${ri + 1}行，第${ci + 1}列，无找到匹配的下拉项\n`;
        }
        row.cells[ci].text = result.join(',');
        break;
      case 'image':
        // 不处理
        break;
      case 'file':
        // 不处理
        break;
      default:
        row.cells[ci].text = cell.text;
        break;
    }

    return true;
  }

  setCellText(ri, ci, text) {
    const cell = this.getCellOrNew(ri, ci);
    cell.text = text;
  }

  setCellFormulaValue(ri, ci, text) {
    const cell = this.getCellOrNew(ri, ci);
    cell.formularValue = text;
  }

  // 专门给外部复制粘贴使用
  outCopyPaste(srcCellRange, dstCellRange, copeClipData) {
    const {
      sri, sci, eri, eci,
    } = srcCellRange;
    const dsri = dstCellRange.sri;
    const dsci = dstCellRange.sci;
    const deri = dstCellRange.eri;
    const deci = dstCellRange.eci;
    const [rn, cn] = srcCellRange.size();
    let resultMsg = '';
    // console.log('drn:', drn, ', dcn:', dcn);
    for (let i = sri; i <= eri; i += 1) {
      if (copeClipData[i]) {
        for (let j = sci; j <= eci; j += 1) {
          const srcData = copeClipData[i][j];
          if (srcData && srcData.text !== undefined && srcData.text !== '') {
            for (let ii = dsri; ii <= deri; ii += rn) {
              for (let jj = dsci; jj <= deci; jj += cn) {
                const nri = ii + (i - sri);
                const nci = jj + (j - sci);
                // linjian 增加只读单元格判断
                const cell = this.getCell(nri, nci) || {};
                if (cell.readOnly) {
                  resultMsg += `第${nri + 1}行，第${nci + 1}列，单元格是只读类型，不能填写\n`;
                  cell.isEllipsisSave = true;
                  continue;
                }
                const setTextResult = this.setCellForOutPaste(nri, nci, srcData);
                if (!(typeof setTextResult === 'boolean')) {
                  resultMsg += setTextResult;
                }
              }
            }
          }
        }
      }
    }
    if (resultMsg) {
      this.tipMessage('alert', resultMsg);
    }
  }


  // what: all | format | text
  copyPaste(srcCellRange, dstCellRange, what, autofill = false, cb = () => {}) {
    const {
      sri, sci, eri, eci,
    } = srcCellRange;
    const dsri = dstCellRange.sri;
    const dsci = dstCellRange.sci;
    const deri = dstCellRange.eri;
    const deci = dstCellRange.eci;
    const [rn, cn] = srcCellRange.size();
    const [drn, dcn] = dstCellRange.size();
    let resultMsg = '';
    // console.log(srcIndexes, dstIndexes);
    let isAdd = true;
    let dn = 0;
    if (deri < sri || deci < sci) {
      isAdd = false;
      if (deri < sri) dn = drn;
      else dn = dcn;
    }
    // console.log('drn:', drn, ', dcn:', dcn);
    for (let i = sri; i <= eri; i += 1) {
      if (this._[i]) {
        for (let j = sci; j <= eci; j += 1) {
          if (this._[i].cells && this._[i].cells[j]) {
            for (let ii = dsri; ii <= deri; ii += rn) {
              for (let jj = dsci; jj <= deci; jj += cn) {
                const nri = ii + (i - sri);
                const nci = jj + (j - sci);
                const ncell = helper.cloneDeep(this._[i].cells[j]);
                // linjian 增加只读单元格判断
                const cell = this.getCell(nri, nci) || {};
                let isFormula = false;
                if (cell.readOnly) {
                  resultMsg += `第${nri + 1}行，第${nci + 1}列，单元格是只读类型，不能填写\n`;
                  cell.isEllipsisSave = true;
                  continue;
                }
                // ncell.text
                if (autofill && ncell && ncell.text && ncell.text.length > 0) {
                  const { text } = ncell;
                  let n = (jj - dsci) + (ii - dsri) + 1;
                  // console.log('n:', n);
                  if (!isAdd) {
                    n -= dn + 1;
                  }
                  if (text[0] === '=') {
                    // ncell.text = text.replace(/\w{1,3}\d/g, (word) => {
                    //   let [xn, yn] = [0, 0];
                    //   if (sri === dsri) {
                    //     xn = n;
                    //   } else {
                    //     yn = n;
                    //   }
                    //   console.log('n', n);
                    //   // console.log('xn:', xn, ', yn:', yn, expr2expr(word, xn, yn));
                    //   return expr2expr(word, xn, yn);
                    // });
                    // linjian 增加公式递增处理 上面原来代码有问题
                    let startIndex = 0;
                    let lastIndex;
                    const ranges = getRanges(text);
                    let result = '';
                    // eslint-disable-next-line no-loop-func
                    ranges.forEach(({ content, start }) => {
                      lastIndex = start;
                      content.replace(/([A-Z]+)(\d+)/g, '$1,$2,').split(',').forEach((v) => {
                        if (v.length) {
                          if (isNaN(v)) {
                            lastIndex += v.length;
                          } else {
                            result += text.substring(startIndex, lastIndex) + (parseInt(v, 10) + n);
                            startIndex = lastIndex + v.length;
                            lastIndex = startIndex;
                          }
                        }
                      });
                      return true;
                    });
                    if (startIndex !== text.length) {
                      result += text.substring(startIndex);
                    }
                    ncell.text = result;
                    // 清空公式的值
                    ncell.formularValue = '';
                    isFormula = true;
                  } else if (ncell.cellProps && ncell.cellProps.cellType !== 'dropdown') {
                    const result = /[\\.\d]+$/.exec(text);
                    // console.log('result:', result);
                    if (result !== null) {
                      const index = Number(result[0]) + n;
                      ncell.text = text.substring(0, result.index) + index;
                    }
                  } else {
                    ncell.text = text;
                  }
                }
                if (helper.isFormulaExpression(ncell.text)) {
                  isFormula = true;
                }
                // console.log('ncell:', nri, nci, ncell);
                const setTextResult = this.setCell(nri, nci, ncell, what);
                if (!(typeof setTextResult === 'boolean')) {
                  resultMsg += setTextResult;
                }
                cb(nri, nci, ncell, isFormula);
              }
            }
          }
        }
      }
    }
    if (resultMsg) {
      this.tipMessage('alert', resultMsg);
    }
  }

  cutPaste(srcCellRange, dstCellRange, cb) {
    const ncellmm = {};
    const formulaDistPosition = [];
    const formulaSrcPosition = [];
    const dropdownRangeDistPosition = [];
    const dropdownRangeSrcPosition = [];
    this.each((ri) => {
      this.eachCells(ri, (ci) => {
        let nri = parseInt(ri, 10);
        let nci = parseInt(ci, 10);
        let isInSource = false;
        if (srcCellRange.includes(ri, ci)) {
          nri = dstCellRange.sri + (nri - srcCellRange.sri);
          nci = dstCellRange.sci + (nci - srcCellRange.sci);
          isInSource = true;
        }
        ncellmm[nri] = ncellmm[nri] || { cells: {} };
        ncellmm[nri].cells[nci] = this._[ri].cells[ci];
        if (isInSource) {
          const ncell = ncellmm[nri].cells[nci];
          if (helper.isFormulaExpression(ncell.text)) {
            formulaDistPosition.push({ row: nri, column: nci });
            formulaSrcPosition.push({ row: parseInt(ri, 10), column: parseInt(ci, 10) });
          }
          if (helper.isDropdownRangeExpression(ncell)) {
            dropdownRangeDistPosition.push({ row: nri, column: nci });
            dropdownRangeSrcPosition.push({ row: parseInt(ri, 10), column: parseInt(ci, 10) });
          }
        }
      });
    });
    this._ = ncellmm;
    if (typeof cb === 'function') {
      cb(formulaDistPosition, formulaSrcPosition, dropdownRangeDistPosition, dropdownRangeSrcPosition);
    }
  }

  insert(sri, n = 1, templateObj) {
    const ndata = {};
    this.each((ri, row) => {
      let nri = parseInt(ri, 10);
      if (nri >= sri) {
        nri += n;
        // linjian 更新已有的公式和下拉项 以及公式依赖(在sheet.js里面更新)
        this.updateFormulaAndLink(sri, n, row, templateObj, nri + 1);
      } else {
        this.updateFormulaAndLink(sri, n, row, templateObj);
      }
      ndata[nri] = row;
    });
    this.len += n;
    // 通过模板行生成插入行的属性
    const needAddMerges = this.getRowCellPropsByTemplateRow(sri, n, ndata, templateObj, 'addRow');
    this._ = ndata;
    return needAddMerges;
  }

  /**
   * @description: 更新插入行下面的那些公式的值
   * @param {type}
   * @return:
   * @Author: linjian
   * @Date: 2019-06-27 13:39:27
   */
  updateFormulaAndLink(sri, n, row, templateObj = {}, currentRow) {
    // if (!templateObj || !templateObj.cells) {
    //   return;
    // }
    const { cells } = row;
    if (!cells) {
      return true;
    }
    // const templateCells = (templateObj || {}).cells;
    const cellsItems = Object.entries(cells);
    cellsItems.forEach((cell) => {
      // const keyIndex = parseInt(cell[0], 10);
      const cellItem = cell[1];
      // 公式继承
      if (helper.isFormulaExpression(cellItem.text)) {
        // let isFormulaEqualTemplate = false;
        // if (cellItem.cellProps && cellItem.cellProps.calculateFormula
        //     && templateCells && templateCells[keyIndex].cellProps.calculateFormula
        //       === cellItem.cellProps.calculateFormula) {
        //   isFormulaEqualTemplate = true;
        // }
        const dataAfterSplit = getDataAfterSplit(cellItem.text);
        cellItem.text = '';
        for (let index2 = 0; index2 < dataAfterSplit.length; index2++) {
          if (typeof dataAfterSplit[index2] === 'string') {
            cellItem.text += dataAfterSplit[index2];
          } else if (dataAfterSplit[index2] > sri) {
            if (currentRow !== undefined && currentRow !== dataAfterSplit[index2]) {
              // 判断是否在流表区域内，如果是，要乘2
              let isInStream = false;
              this.streamCharts.some((item) => {
                const { finalFirstRows, finalLastRows } = item;
                if (finalFirstRows <= row || finalLastRows >= row) {
                  isInStream = true;
                  return true;
                }
                return false;
              });
              if (isInStream) {
                cellItem.text += dataAfterSplit[index2] + 2 * n;
              } else {
                cellItem.text += dataAfterSplit[index2] + n;
              }
            } else {
              cellItem.text += dataAfterSplit[index2] + n;
            }
          } else {
            cellItem.text += dataAfterSplit[index2];
          }
        }
        // typeOptions内值要处理
      } else if (cellItem.typeOptions) {
        const { typeOptions } = cellItem;
        if (typeOptions.link) {
          typeOptions.link = typeOptions.link.map((link) => {
            let {
              // eslint-disable-next-line prefer-const
              row: rowLink, col: colLink, point: pointerLink,
            } = link;
            if (rowLink >= sri) {
              rowLink += n;
            }
            pointerLink = pointerLink.replace(/[0-9]/g, '') + rowLink;
            return { row: rowLink, col: colLink, point: pointerLink };
          });
        }
        if (typeOptions.sourceRange) {
          const { sourceRange } = typeOptions;
          const dataAfterSplit = getDataAfterSplit(sourceRange);
          typeOptions.sourceRange = '';
          for (let index2 = 0; index2 < dataAfterSplit.length; index2++) {
            if (typeof dataAfterSplit[index2] === 'string') {
              typeOptions.sourceRange += dataAfterSplit[index2];
            } else if (dataAfterSplit[index2] > sri) {
              typeOptions.sourceRange += dataAfterSplit[index2] + n;
            } else {
              typeOptions.sourceRange += dataAfterSplit[index2];
            }
          }
        }
        cellItem.typeOptions = typeOptions;
      }
      return true;
    });
    return true;
  }

  /**
   * @description: // linjian 添加行复制功能
   * @param {number} sri 起始行
   * @param {number} n 插件行数
   * @param {Object} ndata 数据容器
   * @param {Object} templateObj 模板行
   * @param {text} type value: addRow / requestData
   * @param {text} textItems
   * @param {number} offsetRowNum 这个参数主要给加载更多数据的时候，模板和实际行数的偏移量
   * @param {Object} isCalCulatorFormula 是否计算公式
   * @param {function} callback 处理公式回调
   * @return: null
   * @Author: linjian
   * @Date: 2019-06-26 14:33:43
   */
  getRowCellPropsByTemplateRow(sri, n, ndata, templateObj, type, textItems, offsetRowNum = 0,
    isCalCulatorFormula, callback) {
    const needAddMerges = [];
    if (!templateObj || !templateObj.cells) {
      return needAddMerges;
    }
    const templateData = {};
    const modifyLink = [];
    const modifyFormula = [];
    for (let index = sri + n - 1; index >= sri; index--) {
      const row = Object.assign({}, templateObj);
      ndata[index] = row;
      const cellsNew = {};
      if (row.cells) {
        const { cells } = row;
        const cellsItems = Object.entries(cells);
        const diff = index - sri + 1 + offsetRowNum;
        // eslint-disable-next-line no-loop-func
        cellsItems.forEach((cell, index2) => {
          const keyIndex = parseInt(cell[0], 10);
          const cellItem = Object.assign({}, cell[1]);
          cellsNew[keyIndex] = cellItem;
          const { merge } = cellItem;
          if (merge) {
            needAddMerges.push({
              sri: index,
              eri: index + merge[0],
              sci: index2,
              eci: index2 + merge[1],
            });
          }
          // 公式继承
          if (helper.isFormulaExpression(cellItem.text)) {
            if (!modifyFormula.includes(keyIndex)) {
              modifyFormula.push(keyIndex);
            }
            if (!templateData[keyIndex]) {
              const dataAfterSplit = [];
              let startIndex = 0;
              let lastIndex;
              templateData[keyIndex] = dataAfterSplit;
              const ranges = getRanges(cellItem.text);
              ranges.forEach(({ content, start, end }) => {
                lastIndex = start;
                content.replace(/([A-Z]+)(\d+)/g, '$1,$2,').split(',').forEach((v) => {
                  if (v.length) {
                    if (isNaN(v)) {
                      lastIndex += v.length;
                    } else {
                      dataAfterSplit.push(cellItem.text.substring(startIndex, lastIndex), parseInt(v, 10));
                      startIndex = lastIndex + v.length;
                      lastIndex = startIndex;
                    }
                  }
                });
              });
              if (startIndex !== cellItem.text.length) {
                dataAfterSplit.push(cellItem.text.substring(startIndex));
              }
            }
            const finalDataAfterSplit = templateData[keyIndex];
            cellItem.text = '';
            for (let index3 = 0; index3 < finalDataAfterSplit.length; index3++) {
              if (typeof finalDataAfterSplit[index3] === 'string') {
                cellItem.text += finalDataAfterSplit[index3];
              } else {
                if (index3 > 0) {
                  const isLogText = (finalDataAfterSplit[index3 - 1] + finalDataAfterSplit[index3]).includes('LOG1');
                  if (isLogText) {
                    cellItem.text += (finalDataAfterSplit[index3]);
                    continue;
                  }
                }
                cellItem.text += (finalDataAfterSplit[index3]) + diff;
              }
            }
            // 置空公式值
            cellItem.formularValue = '';
            if (isCalCulatorFormula) {
              callback(true, { row: index, column: index2 });
            }
          } else {
            if (type === 'addRow') {
              // 值清空
              cellItem.text = '';
              // dropdownText 清空
              if (cellItem.dropdownText) {
                cellItem.dropdownText = '';
              }
            } else if (type === 'requestData') {
              cellItem.text = textItems[index2];
              if (Object.prototype.hasOwnProperty.call(cellItem, 'dropdownText')) {
                cellItem.dropdownText = '';
              }
            }
            // typeOptions内值要处理
            if (cellItem.typeOptions) {
              const typeOptions = Object.assign({}, cellItem.typeOptions);
              if (type === 'addRow') {
                if (typeOptions.images) {
                  typeOptions.images = [];
                  cellItem.text = '';
                }
                if (typeOptions.files) {
                  typeOptions.files = [];
                  cellItem.text = '';
                }
              } else if (type === 'requestData') {
                if (typeOptions.images) {
                  typeOptions.images = JSON.parse(textItems[index2] || '[]');
                }
                if (typeOptions.files) {
                  typeOptions.files = JSON.parse(textItems[index2] || '[]');
                }
              }
              if (typeOptions.link) {
                // 记录有联动的列
                if (!modifyLink.includes(keyIndex)) {
                  modifyLink.push(keyIndex);
                }
                modifyLink[keyIndex] = true;
                const links = [];
                typeOptions.link.map((link) => {
                  let {
                    row: rowLink, col: colLink, point: pointerLink,
                  } = link;
                  rowLink += diff;
                  pointerLink = pointerLink.replace(/0-9/g, '') + rowLink;
                  links.push({
                    row: rowLink, col: colLink, point: pointerLink,
                  });
                  return true;
                });
                typeOptions.link = links;
              }
              cellItem.typeOptions = typeOptions;
            }
          }
          return true;
        });
        row.cells = cellsNew;
      }
    }
    return needAddMerges;
  }

  delete(sri, eri) {
    const n = eri - sri + 1;
    const ndata = {};
    const templateObj = this._[eri];
    this.each((ri, row) => {
      const nri = parseInt(ri, 10);
      if (nri < sri) {
        ndata[nri] = row;
        this.updateFormulaAndLink(eri, -n, row, templateObj);
      } else if (ri >= sri && ri <= eri) {
        this.eachCells(ri, (ci, cell) => {
          if (cell && cell.imageId) {
            const imgObj = document.getElementById(cell.imageId);
            if (imgObj) {
              imgObj.remove();
            }
          }
        });
      } else if (ri > eri) {
        ndata[nri - n] = row;
        this.updateFormulaAndLink(eri, -n, row, templateObj, parseInt(ri, 10) + 1);
      }
    });
    this._ = ndata;
    this.len -= n;
  }

  insertColumn(sci, n = 1) {
    this.each((ri, row) => {
      const rndata = {};
      this.eachCells(ri, (ci, cell) => {
        let nci = parseInt(ci, 10);
        if (nci >= sci) {
          nci += n;
        }
        rndata[nci] = cell;
      });
      row.cells = rndata;
    });
  }

  deleteColumn(sci, eci) {
    const n = eci - sci + 1;
    this.each((ri, row) => {
      const rndata = {};
      this.eachCells(ri, (ci, cell) => {
        const nci = parseInt(ci, 10);
        if (nci < sci) {
          rndata[nci] = cell;
        } else if (nci > eci) {
          rndata[nci - n] = cell;
        }
      });
      row.cells = rndata;
    });
  }

  // what: all | text | format | merge
  deleteCells(cellRange, what = 'all', deleteData, dataProxy, callback) {
    cellRange.each((i, j) => {
      this.deleteCell(i, j, what, deleteData, dataProxy);
      if (typeof callback === 'function') {
        callback(i, j);
      }
    });
  }

  // what: all | text | format | merge
  deleteCell(ri, ci, what = 'all', deleteData, dataProxy) {
    const row = this.get(ri);
    if (row !== null) {
      const cell = this.getCell(ri, ci);
      // linjian 添加只读删除控制，如果是只读的，则不能删除
      if (cell && cell.readOnly && cell.cellProps && cell.cellProps.cellType !== 'image') {
        return false;
      }
      if (cell !== null) {
        if (what === 'all') {
          delete row.cells[ci];
          if (dataProxy) {
            dataProxy.hook.afterSetDataAtCell.call(dataProxy, [ri, ci, undefined, '']);
          }
        } else if (what === 'text') {
          // linjian 这里要重置为 ''
          // if (cell.text) delete cell.text;
          // 清除公式值
          if (helper.isFormulaExpression(cell.text)) {
            this.savaDeleteData(deleteData, ri, ci, { formularValue: cell.formularValue });
            cell.formularValue = '';
            return true;
          }
          if (cell.text || cell.text === 0) {
            this.savaDeleteData(deleteData, ri, ci, { text: cell.text });
            cell.text = '';
          }
          // if (cell.value) delete cell.value;
          if (cell.value) {
            this.savaDeleteData(deleteData, ri, ci, { value: cell.value });
            cell.value = '';
          }
          if (dataProxy) {
            // 这里随便填个原值
            dataProxy.hook.afterSetDataAtCell.call(dataProxy, [ri, ci, undefined, '']);
          }
          if (cell.cellProps) {
            const { imageId } = cell;
            const { cellType } = cell.cellProps;
            const { typeOptions } = cell;
            switch (cellType) {
              case 'dropdown':
                this.savaDeleteData(deleteData, ri, ci, { dropdownText: cell.dropdownText });
                cell.dropdownText = '';
                break;
              case 'image':
                this.savaDeleteData(deleteData, ri, ci, { typeOptions: JSON.parse(JSON.stringify(typeOptions)) });
                typeOptions.images = [];
                // eslint-disable-next-line no-undef
                const imageObj = document.getElementById(imageId);
                if (imageObj) {
                  document.getElementById(imageId).remove();
                }
                break;
              case 'file':
                this.savaDeleteData(deleteData, ri, ci, { typeOptions: JSON.parse(JSON.stringify(typeOptions)) });
                typeOptions.files = [];
                break;
              default:
                break;
            }
          }
        } else if (what === 'format') {
          if (cell.style !== undefined) delete cell.style;
          if (cell.merge) delete cell.merge;
        } else if (what === 'merge') {
          if (cell.merge) delete cell.merge;
        }
      }
    }
    return true;
  }

  savaDeleteData(deleteData, ri, ci, data) {
    if (!deleteData) {
      return false;
    }
    if (!deleteData[ri]) {
      deleteData[ri] = {};
    }
    if (!deleteData[ri][ci]) {
      deleteData[ri][ci] = {};
    }
    Object.assign(deleteData[ri][ci], data);
    return true;
  }

  each(cb) {
    Object.entries(this._).forEach(([ri, row]) => {
      cb(ri, row);
    });
  }

  eachCells(ri, cb) {
    if (this._[ri] && this._[ri].cells) {
      Object.entries(this._[ri].cells).forEach(([ci, cell]) => {
        cb(ci, cell);
      });
    }
  }

  setData(d) {
    if (d.len) {
      this.len = d.len;
      delete d.len;
    }
    this._ = d;
  }

  getData() {
    const { len } = this;
    return Object.assign({ len }, this._);
  }
}

export default {};
export {
  Rows,
};
