/* eslint-disable no-param-reassign */
/* eslint-disable no-underscore-dangle */
import helper from '../core/helper';
import { toLabel, extractLabel, columnIndexToLabel } from './utils';

const { arrayEach, arrayFilter } = helper;
const BARE_CELL_STRICT_REGEX = /^\$?[A-Z]+\$?\d+$/;
const BARE_CELL_REGEX = /\$?[A-Z]+\$?\d+/;
const CELL_REGEX = /(?:[^0-9A-Z$: ]|^)\s*(\$?[A-Z]+\$?\d+)\s*(?![0-9A-Z_: ])/g;
const RANGE_REGEX = /\$?[A-Z]+\$?\d+\s*:\s*\$?[A-Z]+\$?\d+/g;
const COLUMN_RANGE_REGEX = /\$?[A-Z]+\s*:\s*\$?[A-Z]+/g;
const CELL_AND_RANGE_REGEX = /((?:[^0-9A-Z$: ]|^)\s*(\$?[A-Z]+\$?\d+)\s*(?![0-9A-Z_: ]))|(\$?[A-Z]+\$?\d+\s*:\s*\$?[A-Z]+\$?\d+)|(\$?[A-Z]+\s*:\s*\$?[A-Z]+)/g;

/**
 * Component adds an ability to parse and modify
 * formula expressions. It is designed for translating cell
 * coordinates and cell ranges in any direction. By default,
 * component translates only relative coordinates but this
 * behavior can be overwritten by passing custom modifier which controls translating process.
 *
 * @class ExpressionModifier
 * @util
 */
class ExpressionModifier {
  constructor(expression) {
    /**
     * Formula expression to modify.
     *
     * @type {String}
     */
    this.expression = '';
    /**
     * Extracted cells and cells ranges.
     *
     * @type {Array}
     */
    this.cells = [];
    /**
     * Function which can modify default behaviour of how cells and cell ranges will be translated.
     *
     * @type {null|Function}
     */
    this.customModifier = null;

    if (typeof expression === 'string') {
      this.setExpression(expression);
    }
  }

  /**
   * Set formula expression to modify.
   *
   * @param {String} expression Formula expression to process.
   * @returns {ExpressionModifier}
   */
  setExpression(expression) {
    this.cells.length = 0;
    this.expression = expression;

    this._extractCells();
    this._extractCellsRange();
    // 增加 A:B 解析 - xyx
    this._extractColumnsRange();

    return this;
  }

  /**
   * Set function which can modify default behavior of how cells and cell ranges will be translated.
   * The passed function will be called with 4 arguments:
   * - cell, A cell object with structure
   * like this: {start: {row, column}, end: {row, column}, origLabel, type: 'cell|range',
   *  refError, toLabel: () => {}}
   *  - axis, Type of currently processing axis ('row' or 'column')
   *  - delta, Number as distance to translate. Can be positive or negative.
   *  - startFromIndex, Base index which translation will be applied from.
   *
   * the function must return an array with 3 items, where:
   *  [
   *    deltaStart, Number as a delta to translate first part of coordinates.
   *    deltaEnd,   Number as a delta to translate second part of coordinates (if cell range is modified).
   *    refError,   Defines an error which refers to the situation when translated cell overcrossed the data boundary.
   *  ]
   *
   *
   * @param {Function} customModifier Function with custom logic.
   */
  useCustomModifier(customModifier) {
    this.customModifier = customModifier;
  }

  /**
   * Translate formula expression cells.
   *
   * @param {Object} delta Distance to move in proper direction.
   * @param {Object} [startFrom] Coordinates which translation will be applied from.
   * @returns {ExpressionModifier}
   */
  translate({ row: deltaRow, column: deltaColumn }, startFrom = {}) {
    arrayEach(this.cells, (cell) => {
      if (deltaRow !== undefined) {
        this._translateCell(cell, 'row', deltaRow, startFrom.row);
      }
      if (deltaColumn !== undefined) {
        this._translateCell(cell, 'column', deltaColumn, startFrom.column);
      }
    });

    return this;
  }

  /**
   * Translate object into string representation.
   *
   * @returns {String}
   */
  toString() {
    const expression = this.expression.replace(CELL_AND_RANGE_REGEX, (match, p1, p2) => {
      const isSingleCell = match.indexOf(':') === -1;
      let result = match;
      let cellLabel = match;
      let translatedCellLabel = null;

      if (isSingleCell) {
        cellLabel = BARE_CELL_STRICT_REGEX.test(p1) ? p1 : p2;
      }
      const cell = this._searchCell(cellLabel);

      if (cell) {
        translatedCellLabel = cell.refError ? '#REF!' : cell.toLabel();

        if (isSingleCell) {
          result = match.replace(cellLabel, translatedCellLabel);
        } else {
          result = translatedCellLabel;
        }
      }

      return result;
    });

    // if (!expression.startsWith('=')) {
    //   expression = `=${expression}`;
    // }

    return expression;
  }

  /**
   * Translate single cell.
   *
   * @param {Object} cell Cell object.
   * @param {String} axis Axis to modify.
   * @param {Number} delta Distance to move.
   * @param {Number} [startFromIndex] Base index which translation will be applied from.
   * @private
   */
  _translateCell(cell, axis, delta, startFromIndex) {
    const { start, end } = cell;
    const startIndex = start[axis].index;
    const endIndex = end[axis].index;

    let deltaStart = delta;
    let deltaEnd = delta;
    let refError = false;

    if (this.customModifier) {
      [deltaStart, deltaEnd, refError] = this.customModifier(cell, axis, delta, startFromIndex);
    } else {
      // By default only relative cells are translated, if meets absolute reset deltas to 0
      if (start[axis].isAbsolute) {
        deltaStart = 0;
      }
      if (end[axis].isAbsolute) {
        deltaEnd = 0;
      }
    }

    if (deltaStart && !refError) {
      if (startIndex + deltaStart < 0) {
        refError = true;
      }
      start[axis].index = Math.max(startIndex + deltaStart, 0);
      // 发生了改变
      this.changed = true;
    }
    if (deltaEnd && !refError) {
      if (endIndex + deltaEnd < 0) {
        refError = true;
      }
      end[axis].index = Math.max(endIndex + deltaEnd, 0);
      // 发生了改变
      this.changed = true;
    }
    if (refError) {
      cell.refError = true;
    }
  }

  /**
   * Extract all cells from the formula expression.
   *
   * @private
   */
  _extractCells() {
    const matches = this.expression.match(CELL_REGEX);

    if (!matches) {
      return;
    }
    arrayEach(matches, (coord) => {
      coord = coord.match(BARE_CELL_REGEX);

      if (!coord) {
        return;
      }
      const [row, column] = extractLabel(coord[0]);

      this.cells.push(this._createCell({ row, column }, { row, column }, coord[0]));
    });
  }

  /**
   * Extract all cells range from the formula expression.
   *
   * @private
   */
  _extractCellsRange() {
    const matches = this.expression.match(RANGE_REGEX);

    if (!matches) {
      return;
    }
    arrayEach(matches, (match) => {
      const [start, end] = match.split(':');
      const [startRow, startColumn] = extractLabel(start);
      const [endRow, endColumn] = extractLabel(end);
      const startCell = {
        row: startRow,
        column: startColumn,
      };
      const endCell = {
        row: endRow,
        column: endColumn,
      };

      // eslint-disable-next-line no-underscore-dangle
      this.cells.push(this._createCell(startCell, endCell, match));
    });
  }

  // 增加 A:B 匹配 - xyx
  _extractColumnsRange() {
    const matches = this.expression.match(COLUMN_RANGE_REGEX);

    if (!matches) {
      return;
    }
    arrayEach(matches, (label) => {
      const [start, end] = label.split(':');
      const [, startColumn] = extractLabel(`${start}1`);
      const [, endColumn] = extractLabel(`${end}1`);

      // eslint-disable-next-line no-underscore-dangle
      const cell = this._createCell({
        row: { index: -1, isAbsolute: true },
        column: startColumn,
      }, {
        row: { index: -1, isAbsolute: true },
        column: endColumn,
      }, label);
      cell.toLabel = () => {
        const startLabel = (this.start.column.isAbsolute ? '$' : '') + columnIndexToLabel(this.start.column.index);
        const endLabel = (this.end.column.isAbsolute ? '$' : '') + columnIndexToLabel(this.end.column.index);

        return `${startLabel}:${endLabel}`;
      };
      this.cells.push(cell);
    });
  }

  /**
   * Search cell by its label.
   *
   * @param {String} label Cell label eq. `B4` or `$B$6`.
   * @returns {Object|null}
   * @private
   */
  _searchCell(label) {
    // eslint-disable-next-line arrow-body-style
    const [cell] = arrayFilter(this.cells, (cellItem) => {
      return cellItem.origLabel === label;
    });
    return cell || null;
  }

  /**
   * Create object cell.
   *
   * @param {Object} start Start coordinates (top-left).
   * @param {Object} end End coordinates (bottom-right).
   * @param {String} label Original label name.
   * @returns {Object}
   * @private
   */
  _createCell(start, end, label) {
    return {
      start,
      end,
      origLabel: label,
      type: label.indexOf(':') === -1 ? 'cell' : 'range',
      refError: false,
      toLabel: () => {
        let newLabel = toLabel(this.start.row, this.start.column);
        if (this.type === 'range') {
          newLabel = `${newLabel}:${toLabel(this.end.row, this.end.column)}`;
        }
        return newLabel;
      },
    };
  }

  startTranslate(customModifier, delta, cell) {
    this.changed = false;
    this.useCustomModifier(ExpressionModifier[customModifier]);
    this.translate(delta, cell);
  }

  static insertRow(cell, axis, delta, startFromIndex) {
    const { start, end } = cell;
    const { index: startIndex, isAbsolute: startIsAbsolute } = start[axis];
    const { index: endIndex, isAbsolute: endIsAbsolute } = end[axis];
    let deltaStart = delta;
    let deltaEnd = delta;

    if (startIndex === -1) { // A:B 的情况
      deltaStart = 0;
      deltaEnd = 0;
      this.changed = true;
    } else {
      // Do not translate cells above inserted row or on the left of inserted column
      if (startFromIndex > startIndex || startIsAbsolute) {
        deltaStart = 0;
      }
      if (startFromIndex > endIndex || endIsAbsolute) {
        deltaEnd = 0;
      }
    }

    return [deltaStart, deltaEnd, false];
  }

  static removeRow(cell, axis, delta, startFromIndex) {
    const { start, end, type } = cell;
    const { index: startIndex, isAbsolute: startIsAbsolute } = start[axis];
    const { index: endIndex, isAbsolute: endIsAbsolute } = end[axis];
    const indexOffset = Math.abs(delta) - 1;
    let deltaStart = delta;
    let deltaEnd = delta;
    let refError = false;

    // Mark all cells as #REF! which refer to
    // removed cells between startFromIndex and startFromIndex + delta
    if (startIndex >= startFromIndex && endIndex <= startFromIndex + indexOffset) {
      refError = true;
    }

    if (startIndex === -1) { // A:B 的情况
      deltaStart = 0;
      deltaEnd = 0;
      this.changed = true;
    } else {
      // Decrement all cells below startFromIndex
      if (!refError && type === 'cell') {
        if (startFromIndex >= startIndex || startIsAbsolute) {
          deltaStart = 0;
          deltaEnd = 0;
        }
      }

      if (!refError && type === 'range') {
        if (startFromIndex >= startIndex || startIsAbsolute) {
          deltaStart = 0;
        }
        if (startFromIndex > endIndex || endIsAbsolute) {
          deltaEnd = 0;
        } else if (endIndex <= startFromIndex + indexOffset) {
          deltaEnd -= Math.min(endIndex - (startFromIndex + indexOffset), 0);
        }
      }

      if (startIndex + deltaStart < 0) {
        deltaStart -= startIndex + deltaStart;
      }
    }
    return [deltaStart, deltaEnd, refError];
  }

  static insertCol(cell, axis, delta, startFromIndex) {
    const { start, end } = cell;
    const { index: startIndex, isAbsolute: startIsAbsolute } = start[axis];
    const { index: endIndex, isAbsolute: endIsAbsolute } = end[axis];
    let deltaStart = delta;
    let deltaEnd = delta;

    // Do not translate cells above inserted row or on the left of inserted column
    if (startFromIndex > startIndex || startIsAbsolute) {
      deltaStart = 0;
    }
    if (startFromIndex > endIndex || endIsAbsolute) {
      deltaEnd = 0;
    }

    return [deltaStart, deltaEnd, false];
  }

  static removeCol(cell, axis, delta, startFromIndex) {
    const { start, end, type } = cell;
    const { index: startIndex, isAbsolute: startIsAbsolute } = start[axis];
    const { index: endIndex, isAbsolute: endIsAbsolute } = end[axis];
    const indexOffset = Math.abs(delta) - 1;
    let deltaStart = delta;
    let deltaEnd = delta;
    let refError = false;

    // Mark all cells as #REF!
    // which refer to removed cells between startFromIndex and startFromIndex + delta
    if (startIndex >= startFromIndex && endIndex <= startFromIndex + indexOffset) {
      refError = true;
    }

    // Decrement all cells below startFromIndex
    if (!refError && type === 'cell') {
      if (startFromIndex >= startIndex || startIsAbsolute) {
        deltaStart = 0;
        deltaEnd = 0;
      }
    }

    if (!refError && type === 'range') {
      if (startFromIndex >= startIndex || startIsAbsolute) {
        deltaStart = 0;
      }
      if (startFromIndex > endIndex || endIsAbsolute) {
        deltaEnd = 0;
      } else if (endIndex <= startFromIndex + indexOffset) {
        deltaEnd -= Math.min(endIndex - (startFromIndex + indexOffset), 0);
      }
    }

    if (startIndex + deltaStart < 0) {
      deltaStart -= startIndex + deltaStart;
    }

    return [deltaStart, deltaEnd, refError];
  }
}

export default ExpressionModifier;
