// 公式注解
const formulasInfo = {
  ABS: { cn: '返回数的绝对值', note: '用途：返回某一参数的绝对值。\n语法：ABS(number)\n参数：number是需要计算其绝对值的一个实数。\n实例：如果A1=-16，则公式“=ABS(A1)”返回16。' }, ACCRINT: { cn: '返回定期付息有价证券的应计利息', note: '用途： 返回定期付息有价证券的应计利息。\n语法： ACCRINT(issue ， first_interest ， settlement ， rate ， par ， frequency ， basis)\n参数： Issue 为有价证券的发行日\n\t\tFirst_interest 是证券的起息日\n\t\tSettlement 是证券的成交日 (即发行日之后证券卖给购买者的日期 )\n\t\tRate 为有价证券的年息票利率\n\t\tPar 为有价证券的票面价值 (如果省略 par，函数 ACCRINT 将 par 看作 $1000)\n\t\tFrequency 为年付息次数 (如果按年支付 frequency = 1; 按半年期支付，frequency = 2; 按季支付， frequency = 4) 。' }, ACOS: { cn: '返回数的反余弦', note: '用途：返回以弧度表示的参数的反余弦值，范围是0~π。\n语法：ACOS(number)\n参数：number是某一角度的余弦值，大小在-1～1之间。\n实例：如果A1=0.5，则公式“=ACOS(A1)”返回1.047197551(即π/3弧度，也就是600);而公式“=ACOS(-0.5)*180/PI()”返回120°。' }, ACOSH: { cn: '返回数的反双曲余弦值', note: '用途：返回参数的反双曲余弦值。\n语法：ACOSH(number)\n参数：number必须大于或等于1。\n实例：公式“=ACOSH(1)”的计算结果等于0;“=ACOSH(10)”的计算结果等于2.993223。' }, ACOT: { cn: '返回数字的反余切值的主值', note: '用途：返回数字的反余切值的主值。\n语法：ACOT(number)\n参数：Number 为所需的角度的余切值。 它必须是一个实数。\n注意: 返回的角度以弧度表示，弧度值在 0（零）到 pi 之间。 如果 Number 为非数值，则 ACOT 返回错误值 #VALUE! 。 使用 DEGREES 函数或乘以 180/PI() 将结果从弧度转换为度。\n实例：ACOT(2)返回 2 的反余切值（以弧度表示），即 0.4636。' }, ACOTH: { cn: '返回数字的反双曲余切值', note: '用途：返回数字的反双曲余切值。\n语法：ACOTH(number)\n参数：Number 的绝对值必须大于 1。\n实例：ACOTH(6)返回 6 的双曲反余切值 (0.168)' }, AGGREGATE: { cn: '返回列表或数据库中的合计', note: '用途：返回列表或数据库中的合计。\n语法：AGGREGATE(function_num, options, ref1, [ref2], …)\n参数：Function_num 一个介于 1 到 19 之间的数字，指定要使用的函数。\n\t\t\t1 - AVERAGE\n\t\t\t2 - COUNT\n\t\t\t3 - COUNTA\n\t\t\t4 - MAX\n\t\t\t5 - MIN\n\t\t\t6 - PRODUCT\n\t\t\t7 - STDEV.S\n\t\t\t8 - STDEV.P\n\t\t\t9 - SUM\n\t\t\t10 - VAR.S\n\t\t\t11 - VAR.P\n\t\t\t12 - MEDIAN\n\t\t\t13 - MODE.SNGL\n\t\t\t14 - LARGE\n\t\t\t15 - SMALL\n\t\t\t16 - PERCENTILE.INC\n\t\t\t17 - QUARTILE.INC\n\t\t\t18 - PERCENTILE.EXC\n\t\t\t19 - QUARTILE.EXC\n\t\tOptions 一个数值，决定在函数的计算区域内要忽略哪些值。\n\t\t\t0或省略 - 忽略嵌套 SUBTOTAL 和 AGGREGATE 函数\n\t\t\t1 - 忽略隐藏行、嵌套 SUBTOTAL 和 AGGREGATE 函数\n\t\t\t2 - 忽略错误值、嵌套 SUBTOTAL 和 AGGREGATE 函数\n\t\t\t3 - 忽略隐藏行、错误值、嵌套 SUBTOTAL 和 AGGREGATE 函数\n\t\t\t4 - 忽略空值\n\t\t\t5 - 忽略隐藏行\n\t\t\t6 - 忽略错误值\n\t\t\t7 - 忽略隐藏行和错误值\n\t\tRef1 函数的第一个数值参数，这些函数具有要计算聚合值的多个数值参数。\n\t\tRef2,... 要计算聚合值的 2 至 253 个数值参数。\n实例：AGGREGATE(4, 6, A1:A11)计算最大值，同时忽略区域中的错误值，结果96。\n\t\tAGGREGATE(14, 6, A1:A11, 3)计算第 3 个最大值，同时忽略区域中的错误值，结果72。' }, AND: { cn: '如果所有参数为TRUE，则返回TRUE', note: '用途：所有参数的计算结果为 TRUE 时，返回 TRUE；只要有一个参数的计算结果为 FALSE，即返回 FALSE。\n语法：AND(logical1, [logical2,] ...)\n参数：logical1 要测试的第一个条件，其计算结果可以为 TRUE 或 FALSE。\n\t\tlogical2 要测试的其他条件，其计算结果可以为 TRUE 或 FALSE。\n实例：AND(TRUE, TRUE) => TRUE\n\t\tAND(TRUE, FALSE) => FALSE\n\t\tAND(2+2=4, 2+3=5) => TRUE' }, ARABIC: { cn: '将罗马数字转换为阿拉伯数字', note: '用途：将罗马数字转换为阿拉伯数字。\n语法：ARABIC(text)\n参数：text 用引号括起来的字符串、空字符串 ("") 或对包含文本的单元格的引用。\n实例：ARABIC("LVII") => 57' }, ASIN: { cn: '返回数的反正弦值', note: '用途：返回参数的反正弦值。\n语法：ASIN(number)\n参数：Number为某一角度的正弦值，其大小介于-1～1之间。\n实例：如果A1=-0.5，则公式“=ASIN(A1)”返回-0.5236(-π/6弧度);而公式“=ASIN(A1)*180/PI()”返回-300。' }, ASINH: { cn: '返回数的反双曲正弦值', note: '用途：返回参数的反双曲正弦值。\n语法：ASINH(number)\n参数：number为任意实数。\n实例：公式“=ASINH(-2.5)”返回-1.64723;“=ASINH(10)”返回2.998223。' }, ATAN: { cn: '返回数的反正切', note: '用途：返回参数的反正切值。返回的数值以弧度表示，大小在-π/2～π/2之间。\n语法：ATAN(number)\n参数：number为某一角度的正切值。如果要用度表示返回的反正切值，需将结果乘以180/PI()。\n实例：公式“=ATAN(1)”返回0.785398(π/4弧度);=ATAN(1)*180/PI()返回450。' }, ATAN2: { cn: '从X和Y坐标返回反正切', note: '用途：返回直角坐标系中给定X及Y的反正切值。它等于X轴与过原点和给定点(x_num，y_num)的直线之间的夹角，并介于-π～π之间(以弧度表示，不包括-π)。\n语法：ATAN2(x_num，y_num)\n参数：X_num为给定点的X坐标，Y_num为给定点的Y坐标。\n实例：公式“=ATAN2(1，1)”返回0.785398(即π/4弧度);=ATAN2(-1，-1)返回-2.35619(-3π/4弧度);=ATAN2(-1，-1)*180/PI()返回-1350。' }, ATANH: { cn: '返回参数的反双曲正切值', note: '用途：返回参数的反双曲正切值，参数必须在-1～1之间(不包括-1和1)。\n语法：ATANH(number)\n参数：number是-1\n实例：公式“=ATANH(0.5)”返回0.549306144;=ATANH(-0.1)返回-0.10034。' }, AVEDEV: { cn: '返回一组数据与其均值的绝对偏差的平均值', note: '用途： 返回一组数据与其平均值的绝对偏差的平均值，该函数可以评测数据 (例如学生的某科考试成绩 )的离散度。\n语法： AVEDEV(number1 ， number2 ， ...)\n参数： Number1 、 number2 、 ... 是用来计算绝对偏差平均值的一组参数，其个数可以在 1～ 30 个之间。\n实例： 如果 A1=79 、 A2=62 、 A3=45 、 A4=90 、 A5=25 ，则公式 “=AVEDEV(A1:A5) ” 返回 20.16 。' }, AVERAGE: { cn: '返回参数的平均值', note: '用途： 计算所有参数的算术平均值。\n语法： AVERAGE(number1 ， number2 ， ...) 。\n参数： Number1 、 number2 、 ... 是要计算平均值的 1～ 30 个参数。\n实例： 如果 A1:A5 区域命名为分数， 其中的数值分别为 100 、 70、 92、 47 和 82， 则公式 “=AVERAGE( 分数) ”返回 78.2 。' }, AVERAGEA: { cn: '返回参数的平均值，包括数字、文本和逻辑值', note: '用途： 计算参数清单中数值的平均值。 它与 AVERAGE 函数的区别在于不仅数字， 而且文本和逻辑值 (如TRUE 和 FALSE) 也参与计算。\n语法： AVERAGEA(value1 ， value2 ， ...)\n参数： value1 、 value2 、 ... 为需要计算平均值的 1 至 30 个单元格、单元格区域或数值。\n实例： 如果 A1=76 、 A2=85 、 A3=TRUE ，则公式 “=AVERAGEA(A1:A3) ” 返回 54( 即 76+85+1/3=54) 。' }, AVERAGEIF: { cn: '返回某个区域内满足给定条件的所有单元格的平均值（算术平均值）', note: '用途：返回某个区域内满足给定条件的所有单元格的平均值（算术平均值）。\n语法：AVERAGEIF(range, criteria, [average_range])\n参数：Range 要计算平均值的一个或多个单元格，其中包含数字或包含数字的名称、数组或引用。\n\t\tCriteria 形式为数字、表达式、单元格引用或文本的条件，用来定义将计算平均值的单元格。例如，条件可以表示为 32、"32"、">32"、"苹果" 或 B4。\n\t\tAverage_range 计算平均值的实际单元格组。如果省略，则使用 range。\n实例：AVERAGEIF(B2:B5,"<23000") => 求所有小于 23000 的平均值。' }, AVERAGEIFS: { cn: '返回满足多个条件的所有单元格的平均值（算术平均值）', note: '用途：返回满足多个条件的所有单元格的平均值（算术平均值）。\n语法：AVERAGEIFS(average_range, criteria_range1, criteria1, [criteria_range2, criteria2], ...)\n参数：Average_range 要计算平均值的一个或多个单元格，其中包含数字或包含数字的名称、数组或引用。\n\t\tCriteria_range1、criteria_range2等 Criteria_range1 在其中计算关联条件的 1 至 127 个区域。\n\t\tCriteria1、criteria2等 形式为数字、表达式、单元格引用或文本的 1 至 127 个条件，用来定义将计算平均值的单元格。 例如，条件可以表示为 32、"32"、">32"、"苹果" 或 B4。\n实例：AVERAGEIFS(B2:B5, B2:B5, ">70", B2:B5, "<90") => 求70到90之间的数据的平均值' }, BASE: { cn: '将数字转换为具备给定基数的文本表示', note: '用途：将数字转换为具备给定基数的文本表示。\n语法：BASE(Number, Radix [Min_length])\n参数：Number 要转换的数字。 必须为大于或等于 0 并小于 2^53 的整数。\n\t\tRadix 要将数字转换成的基本基数。 必须为大于或等于 2 且小于或等于 36 的整数。\n\t\tMin_length 返回字符串的最小长度。 必须为大于或等于 0 的整数。\n实例：BASE(7,2) => 将十进制数 7 转换为以 2 为基数的数字（二进制数）。 结果等于 111。\n\t\tBASE(15,2,10) => 将十进制数 15 转换为以 2 为基数的数字（二进制数），其最小长度为 10。 结果等于 0000001111。' }, BESSELI: { cn: '返回经过修改的贝塞尔函数IN(X)', note: '用途： 返回修正 Bessel 函数值，它与用纯虚数参数运算时的 Bessel 函数值相等。\n语法： BESSELI(x ， n)\n参数： X 为参数值。 N 为函数的阶数。如果 n 非整数，则截尾取整。' }, BESSELJ: { cn: '返回贝塞尔函数JN(X)', note: '用途： 返回 Bessel 函数值。\n语法： BESSELJ(x ， n)\n参数： X 为参数值。 N 为函数的阶数。如果 n 非整数，则截尾取整。' }, BESSELK: { cn: '返回经过修改的贝塞尔函数KN(X)', note: '用途： 返回修正 Bessel 函数值，它与用纯虚数参数运算时的 Bessel 函数值相等。\n语法： BESSELK(x ， n)23\n参数： X 为参数值。 N 为函数的阶数。如果 n 非整数，则截尾取整。' }, BESSELY: { cn: '返回贝塞尔函数YN(X)', note: '用途： 返回 Bessel 函数值，也称为 Weber 函数或 Neumann 函数。\n语法： BESSELY(x ， n)\n参数： X 为参数值。 N 为函数的阶数。如果 n 非整数，则截尾取整。' }, 'BETA.DIST': { cn: '返回Beta分布', note: '用途：返回 Beta 分布。Beta 分布通常用于研究样本中一定部分的变化情况，例如，人们一天中看电视的时间比率。\n语法：BETA.DIST(x,alpha,beta,cumulative,[A],[B])\n参数：X 用来计算其函数的值，介于值 A 和 B 之间。\n\t\tAlpha 分布参数。\n\t\tBeta 分布参数。\n\t\tcumulative 决定函数形式的逻辑值。 如果 cumulative 为 TRUE，则 BETA.DIST 返回累积分布函数；如果为 FALSE，则返回概率密度函数。\n\t\tA x 所属区间的下界。\n\t\tB x 所属区间的上界。\n实例：BETA.DIST(2,8,10,TRUE,1,3) => 0.6854706\n\t\tBETA.DIST(2,8,10,FALSE,1,3) => 1.4837646' }, 'BETA.INV': { cn: '返回Beta累积概率密度函数(BETA.DIST)的反函数', note: '用途：返回 Beta 累积概率密度函数 (BETA.DIST) 的反函数。如果 probability = BETA.DIST(x,...TRUE)，则 BETA.INV(probability,...) = x。\n语法：BETA.INV(probability,alpha,beta,[A],[B])\n参数：Probability 与 beta 分布相关的概率。\n\t\tAlpha 分布参数。\n\t\tBeta 分布参数。\n\t\tA x 所属区间的下界。\n\t\tB x 所属区间的上界。\n实例：BETA.INV(0.685470581,8,10,1,3)' }, BETADIST: { cn: '返回BETA分布累积函数的函数值', note: '用途：返回 Beta 分布。Beta 分布通常用于研究样本中一定部分的变化情况，例如，人们一天中看电视的时间比率。\n语法：BETADIST(x,alpha,beta,cumulative,[A],[B])\n参数：X 用来计算其函数的值，介于值 A 和 B 之间。\n\t\tAlpha 分布参数。\n\t\tBeta 分布参数。\n\t\tcumulative 决定函数形式的逻辑值。 如果 cumulative 为 TRUE，则 BETA.DIST 返回累积分布函数；如果为 FALSE，则返回概率密度函数。\n\t\tA x 所属区间的下界。\n\t\tB x 所属区间的上界。\n实例：BETADIST(2,8,10,TRUE,1,3) => 0.6854706\n\t\tBETADIST(2,8,10,FALSE,1,3) => 1.4837646' }, BETAINV: { cn: '返回BETA分布累积函数的反函数值', note: '用途：返回 Beta 累积概率密度函数 (BETADIST) 的反函数。如果 probability = BETADIST(x,...TRUE)，则 BETAINV(probability,...) = x。\n语法：BETAINV(probability,alpha,beta,[A],[B])\n参数：Probability 与 beta 分布相关的概率。\n\t\tAlpha 分布参数。\n\t\tBeta 分布参数。\n\t\tA x 所属区间的下界。\n\t\tB x 所属区间的上界。\n实例：BETA.INV(0.685470581,8,10,1,3)' }, BIN2DEC: { cn: '将二进制数转换为十进制数', note: '用途： 将二进制数转换为十进制数。\n语法： BIN2DEC(number)\n参数： Number 待转换的二进制数。 Number 的位数不能多于 10 位 (二进制位 )，最高位为符号位，后 9位为数字位。负数用二进制数补码表示。' }, BIN2HEX: { cn: '将二进制数转换为十六进制数', note: '用途： 将二进制数转换为十六进制数。\n语法： BIN2HEX(number ， places)\n参数： Number 为待转换的二进制数。 Number 的位数不能多于 10 位( 二进制位 )，最高位为符号位，后9位为数字位。负数用二进制数补码表示 ;Places 为所要使用的字符数。如果省略 places ，函数 DEC2BIN 用能表示此数的最少字符来表示。' }, BIN2OCT: { cn: '将二进制数转换为八进制数', note: '用途： 将二进制数转换为八进制数。\n语法： BIN2OCT(number ， places)\n参数： Number 为待转换的二进制数 ;Places 为所要使用的字符数。' }, 'BINOM.DIST': { cn: '返回一元二项式分布的概率', note: '用途：返回一元二项式分布的概率。BINOM.DIST 用于处理固定次数的试验或实验问题，前提是任意试验的结果仅为成功或失败两种情况，实验是独立实验，且在整个试验过程中成功的概率固定不变。 例如，BINOM.DIST 可以计算三个即将出生的婴儿中两个是男孩的概率。\n语法：BINOM.DIST(number_s,trials,probability_s,cumulative)\n参数：Number_s 试验的成功次数。\n\t\tTrials 独立试验次数。\n\t\tProbability_s 每次试验成功的概率。\n\t\tcumulative 决定函数形式的逻辑值。 如果 cumulative 为 TRUE，则 BINOM.DIST 返回累积分布函数，即最多存在 number_s 次成功的概率；如果为 FALSE，则返回概率密度函数，即存在 number_s 次成功的概率。\n实例：BINOM.DIST(6,10,0.5,FALSE) => 10 次试验正好成功 6 次的概率。结果是0.2050781' }, 'BINOM.DIST.RANGE': { cn: '使用二项式分布返回试验结果的概率', note: '用途：使用二项式分布返回试验结果的概率。\n语法：BINOM.DIST.RANGE(trials,probability_s,number_s,[number_s2])\n参数：Trials 独立试验次数。 必须大于或等于 0。\n\t\tProbability_s 每次试验成功的概率。 必须大于或等于 0 并小于或等于 1。\n\t\tNumber_s 试验成功次数。 必须大于或等于 0 并小于或等于 Trials。\n\t\tNumber_s2 如提供，则返回试验成功次数将介于 Number_s 和 number_s2 之间的概率。 必须大于或等于 Number_s 并小于或等于 Trials。\n实例：BINOM.DIST.RANGE(60,0.75,48) => 当成功概率为 75% 时，在 60 次试验之中出现 48 次成功的概率（0.084 或 8.4%）' }, 'BINOM.INV': { cn: '返回一个数值，它是使得累积二项式分布的函数值大于等于临界值的最小整数', note: '用途：返回一个数值，它是使得累积二项式分布的函数值大于等于临界值的最小整数。\n语法：BINOM.INV(trials,probability_s,alpha)\n参数：Trials 贝努利试验次数。\n\t\tProbability_s 一次试验中成功的概率。\n\t\tAlpha 临界值。\n实例：BINOM.INV(6,0.5,0.75) => 返回使得累积二项式分布大于等于临界值的最小值。结果为 4' }, BINOMDIST: { cn: '返回单独项二项式分布概率', note: '用途： 返回一元二项式分布的概率值。 BINOMDIST 函数适用于固定次数的独立实验，实验的结果只包含成功或失败二种情况，且成功的概率在实验期间固定不变。例如，它可以计算掷 10 次硬币时正面朝上 6 次的概率。\n语法： BINOMDIST(number_s ， trials ， probability_s ， cumulative)\n参数： Number_s 为实验成功的次数， Trials 为独立实验的次数， Probability_s 为一次实验中成功的概率， Cumulative 是一个逻辑值，用于确定函数的形式。如果 cumulative 为 TRUE ，则 BINOMDIST 函数返回累积分布函数， 即至多 number_s 次成功的概率 ;如果为 FALSE ， 返回概率密度函数， 即 number_s 次成功的概率。\n实例： 抛硬币的结果不是正面就是反面， 第一次抛硬币为正面的概率是 0.5 。 则掷硬币 10 次中 6 次的计算公式为 “=BINOMDIST(6 ， 10， 0.5 ， FALSE) ” ，计算的结果等于 0.205078' }, BITAND: { cn: '返回两个数的按位“与”', note: '用途：返回两个数的按位“与”。\n语法：BITAND( number1, number2)\n参数：Number1 必须为十进制格式并大于或等于 0。\n\t\tNumber2 必须为十进制格式并大于或等于 0。\n实例：BITAND(13,25) => 13 的二进制表示形式是 1101，25 的二进制表示形式是 11001。 他们的二进制数字在最右端和从右侧开始第四个位置相匹配。结果是 9' }, BITLSHIFT: { cn: '返回向左移动指定位数后的数值', note: '用途：返回向左移动指定位数后的数值。\n语法：BITLSHIFT(number, shift_amount)\n参数：Number Number 必须为大于或等于 0 的整数。\n\t\tShift_amount Shift_amount 必须为整数。\n实例：BITLSHIFT(4,2) => 4 以二进制表示为 100。 在右侧添加两个数字 0 将得到 10000，即十进制值的 16。' }, BITOR: { cn: '返回两个数的按位“或”', note: '用途：返回两个数的按位“或”。\n语法：BITOR(number1, number2)\n参数：Number1 必须为十进制格式并大于或等于 0。\n\t\tNumber2 必须为十进制格式并大于或等于 0。\n实例：BITOR(23,10) => 数值 23 的二进制形式为 10111，数值 10 的二进制形式为 1010。 在这两个数字中任一数字的所有 5 个位的任一位置可找到值 1。 您可以将 1010 表示为 01010，以使这两个数值具有相同的位数。 将对数字 2^0、2^1、2^2、2^3 和 2^4 求和，总计为 31。' }, BITRSHIFT: { cn: '返回向右移动指定位数后的数值', note: '用途：返回向右移动指定位数后的数值。\n语法：BITRSHIFT(number, shift_amount)\n参数：Number 必须为大于或等于 0 的整数。\n\t\tShift_amount 必须为整数。\n实例：BITRSHIFT(13,2) => 13 以二进制表示为 1101。 删除最右边的两位数将得到 11，即十进制值 3。' }, BITXOR: { cn: '返回两个数值的按位“异或”结果', note: '用途：返回两个数值的按位“异或”结果。\n语法：BITXOR(number1, number2)\n参数：Number1 必须大于或等于 0。\n\t\tNumber2 必须大于或等于 0。\n实例：BITXOR(5,3) => 数值 5 的二进制形式为 101，数值 3 的二进制形式为 11。 您可以将 11 表示为 011，以使这两个数值都具有三位数。 按位“异或”比较运算符检查确定每一位的数是否不相等，如果为 TRUE，则为该位返回一个正值。结果为 6。' }, CEILING: { cn: '对数字取整为最接近的整数或最接近的多个有效数字', note: '用途：将参数Number沿绝对值增大的方向，返回一个最接近的整数或基数significance的最小倍数。\n语法：CEILING(number，significance)\n参数：number为待返回的数值，Significance为待返回的最小倍数。\n注意:无论number的正负如何，都是按远离0点的方向返回结果。如果number是Significance的倍数，则返回的数值是其自身。\n实例：如果A1=3.1416，则公式“=CEILING(A1，1)”返回的结果是4;=CEILING(-2.5，-2)返回的结果为–4。' }, CEILINGMATH: { cn: '将数字向上舍入为最接近的整数或最接近的指定基数的倍数', note: '用途：将数字向上舍入为最接近的整数或最接近的指定基数的倍数。\n语法：CEILINGMATH(number, [significance], [mode])\n参数：Number 数字必须小于 9.99E+307 并大于 -2.229E-308。\n\t\tSignificance 要将 Number 舍入的倍数。\n\t\tMode 对于负数，控制 Number 是按朝向 0 还是远离 0 的方向舍入。\n实例：CEILINGMATH(24.3,5) => 将 24.3 向上舍入到最接近的 5 的倍数的整数 (25)。' }, CEILINGPRECISE: { cn: '返回一个数字，该数字向上舍入为最接近的整数或最接近的有效位的倍数', note: '用途：返回一个数字，该数字向上舍入为最接近的整数或最接近的有效位的倍数。 无论该数字的符号如何，该数字都向上舍入。 但是，如果该数字或有效位为 0，则返回 0。\n语法：CEILINGPRECISE(number, [significance])\n参数：Number 要进行舍入的值。\n\t\tsignificance 要将数字舍入的倍数。如果省略 significance，则其默认值为 1。\n实例：CEILINGPRECISE(4.3) => 将 4.3 向上舍入为最接近的 1 的倍数（5）。' }, CHAR: { cn: '返回由编码号码所指定的字符', note: '用途：返回对应于数字代码的字符。 使用 CHAR 可将从其他类型计算机上的文件中获取的代码页数字转换为字符。\n语法：CHAR(number)\n参数：Number 介于 1 到 255 之间的数字，指定所需的字符。 使用的是当前计算机字符集中的字符。\n实例：CHAR(65) => A' }, 'CHISQ.DIST': { cn: '返回χ2分布', note: '用途：返回 χ2 分布。χ2 分布通常用于研究样本中某些事物变化的百分比，例如人们一天中用来看电视的时间所占的比例。\n语法：CHISQ.DIST(x,deg_freedom,cumulative)\n参数：X 用来计算分布的数值。\n\t\tDeg_freedom 自由度数。\n\t\tcumulative 决定函数形式的逻辑值。 如果 cumulative 为 TRUE，则 CHISQ.DIST 返回累积分布函数；如果为 FALSE，则返回概率密度函数。\n实例：CHISQ.DIST(0.5,1,TRUE) => 作为累积分布函数返回的 χ2 分布概率为 0.5，其自由度为 1。结果为 0.52049988。' }, 'CHISQ.DIST.RT': { cn: '返回χ2分布的右尾概率', note: '用途：返回 χ2 分布的右尾概率。χ2 分布与 χ2 测试相关联。 使用 χ2 测试可比较观察值和预期值。 例如，某项遗传学实验可能假设下一代植物将呈现出某一组颜色。 通过使用该函数比较观察结果和理论值，可以确定初始假设是否有效。\n语法：CHISQ.DIST.RT(x,deg_freedom)\n参数：X 用来计算分布的数值。\n\t\tDeg_freedom 自由度数。\n实例：CHISQ.DIST.RT(18.307,10) => 0.0500006' }, 'CHISQ.INV': { cn: '返回χ2分布的左尾概率的反函数', note: '用途：返回 χ2 分布的左尾概率的反函数。χ2 分布通常用于研究样本中某些事物变化的百分比，例如人们一天中用来看电视的时间所占的比例。\n语法：CHISQ.INV(probability,deg_freedom)\n参数：Probability 与 χ2 分布相关联的概率。\n\t\tDeg_freedom 自由度数。\n实例：CHISQ.INV(0.93,1) => 5.318520074' }, 'CHISQ.INV.RT': { cn: '返回χ2分布的右尾概率的反函数', note: '用途：返回 χ2 分布的右尾概率的反函数。如果 probability = CHISQ.DIST.RT(x,...)，则 CHISQ.INV(probability,...) = x。 使用此函数可比较观察结果与理论值，以确定初始假设是否有效。\n语法：CHISQ.INV.RT(probability,deg_freedom)\n参数：Probability 与 χ2 分布相关联的概率。\n\t\tDeg_freedom    必需。 自由度数。\n实例：CHISQ.INV.RT(0.050001,10) => 18.306973' }, CHOOSE: { cn: '从值的列表中选择一个值', note: '用途：使用 index_num 返回数值参数列表中的数值。\n语法：CHOOSE(index_num, value1, [value2], ...)\n参数：index_num 用于指定所选定的数值参数。\n\t\tvalue1, value2, ...    Value1 是必需的，后续值是可选的。 1 到 254 个数值参数，CHOOSE 将根据 index_num 从中选择一个数值或一项要执行的操作。 参数可以是数字、单元格引用、定义的名称、公式、函数或文本。\n实例：SUM(CHOOSE(2,A1:A10,B1:B10,C1:C10)) 相当于 SUM(B1:B10)' }, CLEAN: { cn: '删除文本中的所有不可打印字符', note: '用途：删除文本中所有不能打印的字符。 \n语法：CLEAN(text)\n参数：text 要从中删除非打印字符的任何工作表信息。\n实例：A2 => =CHAR(9)&"Monthly report"&CHAR(10)\n\t\tCLEAN(A2) => Monthly report' }, CODE: { cn: '返回文本串中第一个字符的数字编码', note: '用途：返回文本字符串中第一个字符的数字代码。 返回的代码对应于本机所使用的字符集。\n语法：CODE(text)\n参数：Text 要为其获取第一个字符的代码的文本。\n实例：CODE("A") => 65' }, COLUMN: { cn: '返回引用的列号', note: '用途：返回指定单元格引用的列号。 例如，公式 =COLUMN(D10) 返回 4，因为列 D 为第四列。\n语法：COLUMN([reference])\n参数：reference 要返回其列号的单元格或单元格范围。\n实例：COLUMN(B6) => 2' }, COLUMNS: { cn: '返回引用中的列数', note: '用途：返回数组或引用的列数。\n语法：COLUMNS(array)\n参数：Array 要计算列数的数组、数组公式或是对单元格区域的引用。\n实例：COLUMNS(C1:E4) => 3\n\t\tCOLUMNS({1,2,3;4,5,6}) => 数组常量 {1,2,3;4,5,6} 中的列数。 其中有两行，每行 3 列，第一行中包含 1、2、3，第二行中包含 4、5、6。结果为 3' }, COMBIN: { cn: '返回给定数目对象的组合数', note: '用途：返回一组对象所有可能的组合数目。\n语法：COMBIN(number，number_chosen)\n参数：number是某一对象的总数量，number_chosen则是每一组合中对象的数量。\n注意:函数中的参数按照截尾取整的原则参与运算，并且要求number>0、number_chosen>0以及number>number_chosen。\n实例：假设有10名乒乓球队员，从中选出任意两人搭配参加双打，则计算公式为“=COMBIN(10，2)”，可以得出45种搭配方案。' }, COMBINA: { cn: '返回给定数目的项的组合数（包含重复）', note: '用途：返回给定数目的项的组合数（包含重复）。\n语法：COMBINA(number, number_chosen)\n参数：Number 必须大于或等于 0 并大于或等于 Number_chosen。 非整数值将被截尾取整。\n\t\tNumber_chosen 必须大于或等于 0。 非整数值将被截尾取整。\n实例：COMBINA(4,3) => 20' }, COMPLEX: { cn: '将实系数和虚系数转换为复数', note: '用途： 将实系数及虚系数转换为 x+yi 或 x+yj 形式的复数。\n语法： COMPLEX(real_num ， i_num ， suffix)\n参数： Real_num 为复数的实部， I_num 为复数的虚部， Suffix 为复数中虚部的后缀，省略时则认为它为 i。' }, CONCATENATE: { cn: '将多个文本项连接到一个文本项中', note: '用途：使用 CONCATENATE（其中一个文本函数）将两个或多个文本字符串联接为一个字符串。\n语法：CONCATENATE(text1, [text2], ...)\n参数：text1 要联接的第一个项目。项目可以是文本值、数字或单元格引用。\n\t\tText2,... 要联接的其他文本项目。\n实例：CONCATENATE("Andreas", " ", "Hauser") => "Andreas Hauser"' }, CONFIDENCE: { cn: '返回总体平均值的置信区间', note: '用途： 返回总体平均值的置信区间，它是样本平均值任意一侧的区域。例如，某班学生参加考试，依照给定的置信度，可以确定该次考试的最低和最高分数。\n语法： CONFIDENCE(alpha ， standard_dev ， size) 。\n参数： Alpha 是用于计算置信度 (它等于 100*(1-alpha)% ，如果 alpha 为 0.05 ，则置信度为 95%) 的显著水平参数， Standard_dev 是数据区域的总体标准偏差， Size 为样本容量。3\n实例： 假设样本取自 46 名学生的考试成绩，他们的平均分为 60，总体标准偏差为 5 分，则平均分在下列区域内的置信度为 95% 。公式 “=CONFIDENCE(0.05 ， 5， 46)”返回 1.44 ，即考试成绩为 60±1.44 分。' }, 'CONFIDENCE.NORM': { cn: '使用正态分布返回总体平均值的置信区间。', note: '用途：置信区间为某一范围的值。 样本平均值 x 位于此范围的中心，此范围为 x ± CONFIDENCE.NORM。 例如，如果 x 是通过邮件订购的产品交付时间的样本平均值，x ± CONFIDENCE.NORM 为总体平均值范围。 对于在此范围中的任意总体平均值 μ0，从 μ0 而非 x 中获取样本平均值的概率大于 alpha；对于不在此范围内的任意总体平均值 μ0，从 μ0 而非 x 中获取样本平均值的概率小于 alpha。 换言之，假设我们使用 x、standard_dev 和字号在显著性水平 alpha 上构建一个双尾测试，其中假设总体平均值为 μ0。 则如果 μ0 处于置信区间，我们将不会拒绝该假设；如果 μ0 未处于置信区间，则将拒绝该假设。 置信区间无法使我们推断出概率 1 - alpha，即我们的下一个包的发送时间处于置信区间内。\n语法：CONFIDENCE.NORM(alpha,standard_dev,size)\n参数：Alpha 用来计算置信水平的显著性水平。 置信水平等于 100*(1 - alpha)%，亦即，如果 alpha 为 0.05，则置信水平为 95%。\n\t\tstandard_dev 数据区域的总体标准偏差，假定为已知。\n\t\tsize 样本大小。\n实例：CONFIDENCE.NORM(0.05,2.5,50) => 总体平均值的置信区间。 换言之，花在上班路上的基础总体平均时间的置信区间为 30 ± 0.692952 分钟，或 29.3 到 30.7 分钟。结果为 0.692952' }, 'CONFIDENCE.T': { cn: '使用学生的t分布返回总体平均值的置信区间', note: '用途：使用学生的 t 分布返回总体平均值的置信区间。\n语法：CONFIDENCE.T(alpha,standard_dev,size)\n参数：Alpha 用来计算置信水平的显著性水平。 置信水平等于 100*(1 - alpha)%，亦即，如果 alpha 为 0.05，则置信水平为 95%。\n\t\tstandard_dev 数据区域的总体标准偏差，假定为已知。\n\t\tsize 样本大小。\n实例：CONFIDENCE.T(0.05,1,50) => 0.284196855' }, CONVERT: { cn: '将一种度量单位制中的数字转换为另一种度量单位制', note: '用途： 将数字从一个度量系统转换到另一个度量系统中。\n语法： CONVERT(number ， from_unit ， to_unit)\n参数： Number 是以 from_units 为单位的需要进行转换的数值。 From_unit 是数值 number 的单位。To_unit 是结果的单位。' }, CORREL: { cn: '返回两个数据集之间的相关系数', note: '用途： 返回单元格区域 array1 和 array2 之间的相关系数。它可以确定两个不同事物之间的关系，例如检测学生的物理与数学学习成绩之间是否关联。\n语法： CORREL(array1 ， array2)\n参数： Array1 第一组数值单元格区域。 Array2 第二组数值单元格区域。\n实例： 如果 A1=90 、 A2=86 、 A3=65 、 A4=54 、 A5=36 、 B1=89 、 B2=83 、 B3=60 、 B4=50 、 B5=32 ，则公式 “=CORREL(A1:A5 ， B1:B5)”返回 0.998876229 ，可以看出 A、 B 两列数据具有很高的相关性。' }, COS: { cn: '返回数的余弦', note: '用途：返回某一角度的余弦值。\n语法：COS(number)\n参数：number为需要求余弦值的一个角度，必须用弧度表示。如果number的单位是度，可以乘以PI()/180转换为弧度。\n实例：如果A1=1，则公式“=COS(A1)”返回0.540302;若A2=60，则公式“=COS(A2*PI()/180)”返回0.5。' }, COSH: { cn: '返回数的双曲线余弦', note: '用途：返回参数的双曲余弦值。\n语法：COSH(number)\n参数：number为任意实数。\n实例：如果A1=5、A3=6，则公式“=COSH(A1+A3)”返回29937.07087;若C1=60，则公式“=COSH(COS(C1*PI()/180))”返回1.127625965。' }, COT: { cn: '返回以弧度表示的角度的余切值', note: '用途：返回以弧度表示的角度的余切值。\n语法：COT(number)\n参数：Number 要获得其余切值的角度，以弧度表示。\n实例：COT(30) => -0.156' }, COTH: { cn: '返回一个双曲角度的双曲余切值', note: '用途：返回一个双曲角度的双曲余切值。\n语法：COTH(number)\n参数：Number    必需。\n实例：COTH(2) => 1.037' }, COUNT: { cn: '计算参数列表中的数字多少', note: '用途： 返回数字参数的个数。它可以统计数组或单元格区域中含有数字的单元格个数。\n语法： COUNT(value1 ， value2 ， ...) 。\n参数： value1 ， value2 ， ... 是包含或引用各种类型数据的参数 (1～ 30 个 )，其中只有数字类型的数据才能被统计。\n实例： 如果 A1=90 、 A2= 人数、 A3= 〞〞、 A4=54 、 A5=36 ，则公式 “=COUNT(A1:A5) ” 返回 3。' }, COUNTA: { cn: '计算参数列表中的值多少', note: '用途： 返回参数组中非空值的数目。利用函数 COUNTA 可以计算数组或单元格区域中数据项的个数。\n语法： COUNTA(value1 ， value2 ， ...)\n参数： value1 ， value2 ， ... 所要计数的值，参数个数为 1～ 30 个。在这种情况下的参数可以是任何类型，它们包括空格但不包括空白单元格。如果参数是数组或单元格引用，则数组或引用中的空白单元格将被忽略。如果不需要统计逻辑值、文字或错误值，则应该使用 COUNT 函数。\n实例： 如果 A1=6.28 、 A2=3.74 ，其余单元格为空，则公式 “=COUNTA(A1:A7) ” 的计算结果等于 2。' }, COUNTBLANK: { cn: '计算区域中空单元格的个数', note: '用途： 计算某个单元格区域中空白单元格的数目。\n语法： COUNTBLANK(range)\n参数： Range 为需要计算其中空白单元格数目的区域。\n实例： 如果 A1=88 、 A2=55 、 A3="" 、 A4=72 、 A5="" ，则公式 “=COUNTBLANK(A1:A5) ” 返回 2。' }, COUNTIF: { cn: '计算符合给定条件的区域中的非空单元格数', note: '用途：统计某一区域中符合条件的单元格数目。\n语法：COUNTIF(range，criteria)\n参数：range为需要统计的符合条件的单元格数目的区域;Criteria为参与计算的单元格条件，其形式可以为数字、表达式或文本(如36、">160"和"男"等)。其中数字可以直接写入，表达式和文本必须加引号。\n实例：假设A1:A5区域内存放的文本分别为女、男、女、男、女，则公式“=COUNTIF(A1:A5，"女")”返回3。' }, COUNTIFS: { cn: '将条件应用于跨多个区域的单元格，然后统计满足所有条件的次数', note: '用途：将条件应用于跨多个区域的单元格，然后统计满足所有条件的次数。\n语法：COUNTIFS(criteria_range1, criteria1, [criteria_range2, criteria2],…)\n参数：criteria_range1 在其中计算关联条件的第一个区域。\n\t\tcriteria1 条件的形式为数字、表达式、单元格引用或文本，它定义了要计数的单元格范围。 例如，条件可以表示为 32、">32"、B4、"apples"或 "32"。\n\t\tcriteria_range2, criteria2, ... 附加的区域及其关联条件。\n实例：B2 => 是 \n\t\tC2 => 不支持\n\t\tD2 => 否\n\t\tCOUNTIFS(B2:D2,"=是") => 1' }, 'COVARIANCE.P': { cn: '返回总体协方差，即两个数据集中每对数据点的偏差乘积的平均数', note: '用途：返回总体协方差，即两个数据集中每对数据点的偏差乘积的平均数。利用协方差确定两个数据集之间的关系。例如，您可检查教育程度与收入是否成正比。\n语法：COVARIANCE.P(array1,array2)\n参数：Array1 整数的第一个单元格区域。\n\t\tArray2 整数的第二个单元格区域。\n实例：COVARIANCE.P(A2:A6, B2:B6)' }, 'COVARIANCE.S': { cn: '返回样本协方差，即两个数据集中每对数据点的偏差乘积的平均值。', note: '用途：返回样本协方差，即两个数据集中每对数据点的偏差乘积的平均值。\n语法：COVARIANCE.S(array1,array2)\n参数：Array1 整数的第一个单元格区域。\n\t\tArray2 整数的第二个单元格区域。\n实例：COVARIANCE.S({2,4,8},{5,11,12}) => 9.666666667' }, CSC: { cn: '返回角度的余割值，以弧度表示', note: '用途：返回角度的余割值，以弧度表示。\n语法：CSC(number)\n参数：Number    必需。\n实例：CSC(15) => 1.538' }, CSCH: { cn: '返回角度的双曲余割值，以弧度表示', note: '用途：返回角度的双曲余割值，以弧度表示。\n语法：CSCH(number)\n参数：Number    必需。\n实例：CSCH(1.5) => 0.4696' }, CUMIPMT: { cn: '返回两个期间之间累计偿还的利息数额', note: '用途： 返回一笔贷款在给定的 start-period 到 end-period 期间累计偿还的利息数额。\n语法： CUMIPMT(rate ， nper ， pv ， start_period ， end_period ， type)\n参数： Rate 为利率， Nper 为总付款期数， Pv 为现值， Start_period 为计算中的首期 (付款期数从 1 开始计数 )， End_period 为计算中的末期， Type 为付款时间类型 (0( 零 )为期末付款， 1 为期初付款 )。' }, CUMPRINC: { cn: '返回两个期间之间累计偿还的本金数额', note: '用途： 返回一笔贷款在给定的 start-period 到 end-period 期间累计偿还的本金数额。\n语法： CUMPRINC(rate ， nper ， pv， start_period ， end_period ， type)\n参数： Rate 为利率， Nper 为总付款期数， Pv 为现值， Start_period 为计算中的首期 (付款期数从 1 开始计数 )， End_period 为计算中的末期， Type 为付款时间类型 (0( 零 )为期末付款， 1 为期初付款 )。' }, DATE: { cn: '返回特定时间的系列数', note: '用途：返回代表特定日期的序列号。\n语法：DATE(year，month，day)\n参数：year为一到四位，根据使用的日期系统解释该参数。\n\t\tMonth代表每年中月份的数字。如果所输入的月份大于12，将从指定年份的一月份执行加法运算。\n\t\tDay代表在该月份中第几天的数字。如果 day 大于该月份的最大天数时，将从指定月份的第一天开始往上累加。\n实例：DATE(2001，1，1) => 36892。' }, DATEVALUE: { cn: '将文本格式的日期转换为系列数', note: '用途：返回date_text所表示的日期的序列号。该函数的主要用途是将文字表示的日期转换成一个序列号。\n语法：DATEVaLUE(date_text)\n参数：Date_text是用Excel日期格式表示日期的文本。如果省略参数date_text中的年代，则函数DATEVALUE使用电脑系统内部时钟的当前年代，且date_text中的时间信息将被忽略。\n实例：DATEVALUE("2001/3/5") => 36955\n\t\tDATEVALUE("2-26") => 43157' }, DAY: { cn: '将系列数转换为月份中的日', note: '用途：返回用序列号(整数1到31)表示的某日期的天数，用整数 1 到 31 表示。\n语法：DAY(serial_number)\n参数：Serial_number 是要查找的天数日期，它有多种输入方式:带引号的文本串(如"1998/01/30")、序列号(如1900日期系统的35825表示 的1998年1月30日)，以及其他公式或函数的结果(如DATEVALUE("1998/1/30"))。\n实例：DAY("2001/1/27") => 27\n\t\tDAY(35825) => 30\n\t\tDAY(DATEVALUE("2001/1/25")) => 25。' }, DAYS: { cn: '返回两个日期之间的天数', note: '用途：返回两个日期之间的天数。\n语法：DAYS(end_date, start_date)\n参数：End_date Start_date 和 End_date 是用于计算期间天数的起止日期。\n\t\tStart_date Start_date 和 End_date 是用于计算期间天数的起止日期。\n实例：DAYS("2011-3-15","2011-2-1") => 42' }, DAYS360: { cn: '按每年360天计算两个日期之间的天数', note: '用途：按照一年360天的算法(每个月30天，一年共计12 个月)，返回两日期间相差的天数。\n语法：DAYS360(start_date，end_date，method)\n参数：Start_date和end_date 是用于计算期间天数的起止日期。如果start_date在end_date之后，则DAYS360将返回一个负数。日期可以有多种输入方式:带引号的文本串(例如:"1998/01/30")、序列号(例如:如果使用1900日期系统，则35825表示1998年1月30日)或其他公式或函数的结果(例如，DATEVALUE("1998/1/30"))。\n\t\tMethod是一个逻辑值，它指定了在计算中是采用欧洲方法还是美国方法。若为FALSE或忽略，则采用美国方法(如果起始日期是一个月的31日，则等于同月的30日。如果终止日期是一个月的31日，并且起始日期早于30日，则终止日期等于下一个月的1日，否则，终止日期等于本月的30日)。 若为TRUE则采用欧洲方法(无论是起始日期还是终止日期为一个月的 31 号，都将等于本月的 30 号)。\n实例：DAYS360("1998/2/1"，"2001/2/1") => 1080' }, DB: { cn: '使用固定余额递减法，返回一笔资产在指定期间内的折旧值', note: '用途： 使用固定余额递减法，计算一笔资产在给定期间内的折旧值。\n语法： DB(cost ， salvage ， life ， period ， month)\n参数： Cost 为资产原值， Salvage 为资产在折旧期末的价值 (也称为资产残值 )， Life 为折旧期限 (有时也称作资产的使用寿命 )， Period 为需要计算折旧值的期间。 Period 必须使用与 life 相同的单位， Month 为第一年的月份数 (省略时假设为 12)。' }, DDB: { cn: '使用双倍余额递减法或其他指定方法，返回一笔资产在指定期间内的折旧值', note: '用途： 使用双倍余额递减法或其他指定方法，计算一笔资产在给定期间内的折旧值。\n语法： DDB(cost ， salvage ， life， period ， factor)\n参数： Cost 为资产原值， Salvage 为资产在折旧期末的价值 (也称为资产残值 )， Life 为折旧期限 (有时也称作资产的使用寿命 )， Period 为需要计算折旧值的期间。 Period 必须使用与 life 相同的单位， Factor 为余额递减速率 (如果 factor 省略，则假设为 2) 。' }, DEC2BIN: { cn: '将十进制数转换为二进制数', note: '用途： 将十进制数转换为二进制数。\n语法： DEC2BIN(number ， places)\n参数： Number 是待转换的十进制数。 Places 是所要使用的字符数，如果省略 places ，函数 DEC2OCT用能表示此数的最少字符来表示。' }, DEC2HEX: { cn: '将十进制数转换为十六进制数', note: '用途： 将十进制数转换为十六进制数。\n语法： DEC2HEX(number ， places)\n参数： Number 为待转换的十进制数。如果参数 number 是负数，则省略 places 。 Places 是所要使用的字符数。' }, DEC2OCT: { cn: '将十进制数转换为八进制数', note: '用途： 将十进制数转换为八进制数。\n语法： DEC2OCT(number ， places)24\n参数： Number 为待转换的十进制数。如果参数 number 是负数，则省略 places 。 Places 是所要使用的字符数。' }, DECIMAL: { cn: '按给定基数将数字的文本表示形式转换成十进制数', note: '用途：按给定基数将数字的文本表示形式转换成十进制数。\n语法：DECIMAL(text, radix)\n参数：Text    必需。\n\t\tRadix    必需。Radix 必须是整数。\n实例：DECIMAL("FF",16) => 将十六进制（基数为 16）数值 FF 转换为十进制（基数为 10）的等效值 (255)。' }, DEGREES: { cn: '将弧度转换为度', note: '用途：将弧度转换为度。\n语法：DEGREES(angle)\n参数：angle是采用弧度单位的一个角度。\n实例：公式“=DEGREES(1)返回57.29577951”，=DEGREES(PI()/3)返回60。' }, DELTA: { cn: '检测两个值是否相等', note: '用途： 测试两个数值是否相等。如果 number1=number2 ，则返回 1，否则返回 0。\n语法： DELTA(number1 ， number2)\n参数： Number1 为第一个参数。 Number2 为第二个参数。如果省略，假设 Number2 的值为零。' }, DEVSQ: { cn: '返回偏差的平方和', note: '用途： 返回数据点与各自样本平均值的偏差的平方和。\n语法： DEVSQ(number1 ， number2 ， ...)\n参数： Number1 、 number2 、 ... 是用于计算偏差平方和的 1 到 30 个参数。它们可以是用逗号分隔的数值，也可以是数组引用。\n实例： 如果 A1=90 、 A2=86 、 A3=65 、 A4=54 、 A5=36 ，则公式 “=DEVSQ(A1:A5) ” 返回 2020.8 。' }, DOLLAR: { cn: '将数字转换为货币格式使用小数位数舍入到您指定的位数的文本', note: '用途：将数字转换为货币格式使用小数位数舍入到您指定的位数的文本。\n语法：DOLLAR(number, [decimals])\n参数：Number 数字、对包含数字的单元格的引用或是计算结果为数字的公式。\n\t\tdecimals 数值小数点右边的位数。如果这是负数，则将数字四舍五入到小数点左侧。如果省略 decimals，则假设为 2。\n实例：DOLLAR(2123,3) => $2,123.000' }, DOLLARDE: { cn: '将按分数表示的价格转换为按小数表示的价格', note: '用途： 将按分数表示的价格转换为按小数表示的价格，如证券价格，转换为小数表示的数字。\n语法： DOLLARDE(fractional_dollar ， fraction)\n参数： Fractional_dollar 以分数表示的数字， Fraction 分数中的分母 (整数 )。' }, DOLLARFR: { cn: '将按小数表示的价格转换为按分数表示的价格', note: '用途： 将按小数表示的价格转换为按分数表示的价格。\n语法： DOLLARFR(decimal_dollar ， fraction)\n参数： Decimal_dollar 为小数， Fraction 分数中的分母 (整数 )。' }, EDATE: { cn: '返回在开始日期之前或之后指定月数的某个日期的系列数', note: '用途：返回表示某个日期的序列号，该日期与指定日期 (start_date) 相隔（之前或之后）指示的月份数。 使用函数 EDATE 可以计算与发行日处于一月中同一天的到期日的日期。\n语法：EDATE(start_date, months)\n参数：Start_date 一个代表开始日期的日期。应使用 DATE 函数输入日期，或将日期作为其他公式或函数的结果输入。例如，使用函数 DATE(2008,5,23) 输入 2008 年 5 月 23 日。如果日期以文本形式输入，则会出现问题。\n\t\tMonths start_date 之前或之后的月份数。 months 为正值将生成未来日期；为负值将生成过去日期。\n实例：EDATE("2011-1-15",1) => 2011-2-15' }, EFFECT: { cn: '返回实际年利率', note: '用途： 利用给定的名义年利率和一年中的复利期次，计算实际年利率。\n语法： EFFECT(nominal_rate ， npery)\n参数： Nominal_rate 为名义利率， Npery 为每年的复利期数。' }, EOMONTH: { cn: '返回指定月份数之前或之后某月的最后一天的系列数', note: '用途：返回某个月份最后一天的序列号，该月份与 start_date 相隔（之后或之后）指示的月份数。 使用函数 EOMONTH 可以计算正好在特定月份中最后一天到期的到期日。\n语法：EOMONTH(start_date, months)\n参数：Start_date 一个代表开始日期的日期。应使用 DATE 函数输入日期，或将日期作为其他公式或函数的结果输入。例如，使用函数 DATE(2008,5,23) 输入 2008 年 5 月 23 日。如果日期以文本形式输入，则会出现问题。\n\t\tMonths start_date 之前或之后的月份数。 months 为正值将生成未来日期；为负值将生成过去日期。\n实例：EOMONTH(2011-1-1,1) => 2011-2-28' }, ERF: { cn: '返回误差函数', note: '用途： 返回误差函数在上下限之间的积分。\n语法： ERF(lower_limit ， upper_limit)\n参数： Lower_limit 是 ERF 函数的积分下限。 Upper_limit 是 ERF 函数的积分上限。如果省略， ERF 将在零到下限之间进行积分。' }, ERFC: { cn: '返回余误差函数', note: '用途： 返回从 x 到 ∞(无穷 )积分的 ERF 函数的余误差函数\n语法： ERFC(x)\n参数： X?是 ERF 函数积分的下限。' }, EVEN: { cn: '将数向上取整至最接近的偶数整数', note: '用途：返回沿绝对值增大方向，将一个数值取整为最接近的偶数。\n语法：EVEN(number)\n参数：number是要取整的一个数值。\n实例：如果A1=-2.6则公式“=EVEN(A1)”返回-4;=EVEN(-4.56+6.87)返回4。' }, EXACT: { cn: '检查两个文本值是否相同', note: '用途：比较两个文本字符串，如果它们完全相同，则返回 TRUE，否则返回 FALSE。 函数 EXACT 区分大小写，但忽略格式上的差异。 使用 EXACT 可以检验在文档中输入的文本。\n语法：EXACT(text1, text2)\n参数：Text1 第一个文本字符串。\n\t\ttext2 第二个文本字符串。\n实例：A1 => 物理 \n\t\tA2 => 化学\n\t\tEXACT(A1，A2) => FALSE' }, EXP: { cn: '返回E的指定数乘幂', note: '用途：返回e的n次幂。\n语法：EXP(number)\n参数：Number为底数e的指数。\n注意:EXP函数是计算自然对数的LN函数的反函数。\n实例：如果A1=3，则公式“=EXP(A1)”返回20.085537即e3。' }, 'EXPON.DIST': { cn: '返回指数分布', note: '用途：返回指数分布。 使用 EXPON.DIST 可以建立事件之间的时间间隔模型，如银行自动提款机支付一次现金所花费的时间。 例如，可通过 EXPON.DIST 来确定这一过程最长持续一分钟的发生概率。\n语法：EXPON.DIST(x,lambda,cumulative)\n参数：X 函数值。\n\t\tLambda 参数值。\n\t\tCumulative 逻辑值，用于指定指数函数的形式。 如果 cumulative 为 TRUE，则 EXPON.DIST 返回累积分布函数；如果为 FALSE，则返回概率密度函数。\n实例：EXPON.DIST(0.2,10,TRUE) => 0.86466472' }, EXPONDIST: { cn: '返回指数分布', note: '用途： 返回指数分布。该函数可以建立事件之间的时间间隔模型，如估计银行的自动取款机支付一次现金所花费的时间，从而确定此过程最长持续一分钟的发生概率。\n语法： EXPONDIST(x ， lambda ， cumulative) 。\n参数： X 函数的数值， Lambda 参数值， Cumulative 为确定指数函数形式的逻辑值。如果 cumulative为 TRUE ， EXPONDIST 返回累积分布函数 ;如果 cumulative 为 FALSE ，则返回概率密度函数。\n实例： 公式 “=EXPONDIST(0.2 ， 10， TRUE) ” 返回 0.864665 ， =EXPONDIST(0.2 ， 10， FALSE) 返回1.353353 。' }, 'F.DIST': { cn: '返回F概率分布函数的函数值', note: '用途：返回 F 概率分布函数的函数值。 使用此函数可以确定两组数据是否存在变化程度上的不同。 例如，分析进入中学的男生、女生的考试分数，来确定女生分数的变化程度是否与男生不同。\n语法：F.DIST(x,deg_freedom1,deg_freedom2,cumulative)\n参数：X 用来计算函数的值。\n\t\tDeg_freedom1 分子自由度。\n\t\tDeg_freedom2 分母自由度。\n\t\tCumulative 决定函数形式的逻辑值。 如果 cumulative 为 TRUE，则 F.DIST 返回累积分布函数；如果为 FALSE，则返回概率密度函数。\n实例：F.DIST(15.2069,6,4,TRUE) => 0.0012238' }, 'F.DIST.RT': { cn: '返回两个数据集的（右尾）F概率分布（变化程度）', note: '用途：返回两个数据集的（右尾）F 概率分布（变化程度）。 使用此函数可以确定两组数据是否存在变化程度上的不同。 例如，分析进入中学的男生、女生的考试分数，来确定女生分数的变化程度是否与男生不同。\n语法：F.DIST.RT(x,deg_freedom1,deg_freedom2)\n参数：X 用来计算函数的值。\n\t\tDeg_freedom1 分子自由度。\n\t\tDeg_freedom2 分母自由度。\n实例：F.DIST.RT(15.2068649,6,4) => 0.01' }, 'F.INV': { cn: '返回F概率分布函数的反函数值', note: '用途：返回 F 概率分布函数的反函数值。 如果 p = F.DIST(x,...)，则 F.INV(p,...) = x。 在 F 检验中，可以使用 F 分布比较两组数据中的变化程度。 例如，可以分析美国和加拿大的收入分布，判断两个国家/地区是否有相似的收入变化程度。\n语法：F.INV(probability,deg_freedom1,deg_freedom2)\n参数：Probability F 累积分布的概率值。\n\t\tDeg_freedom1 分子自由度。\n\t\tDeg_freedom2 分母自由度。\n实例：F.INV(0.01,6,4) => 0.10930991' }, 'F.INV.RT': { cn: '返回（右尾）F概率分布函数的反函数值', note: '用途：返回（右尾）F 概率分布函数的反函数值。 如果 p = F.DIST.RT(x,...)，则 F.INV.RT(p,...) = x。 在 F 检验中，可以使用 F 分布比较两组数据中的变化程度。 例如，可以分析美国和加拿大的收入分布，判断两个国家/地区是否有相似的收入变化程度。\n语法：F.INV.RT(probability,deg_freedom1,deg_freedom2)\n参数：Probability F 累积分布的概率值。\n\t\tDeg_freedom1 分子自由度。\n\t\tDeg_freedom2 分母自由度。\n实例：F.INV.RT(0.01,6,4) => 15.20686' }, FACT: { cn: '返回数的阶乘', note: '用途：返回一个数的阶乘，即1*2*3*...*该数。\n语法：FACT(number)\n注意:Number是计算其阶乘的非负数。如果输入的Number不是整数，则截去小数部分取整数。\n实例：如果A1=3，则公式“=FACT(A1)”返回6;=FACT(5.5)返回1*2*3*4*5.5即120。' }, FACTDOUBLE: { cn: '返回参数NUMBER的半阶乘', note: '用途：返回参数Number的半阶乘。\n语法：FACTDOUBLE(number)\nNumber要计算其半阶乘的数值，如果参数Number为非整数，则截尾取整。\n注意:如果该函数不存在，应当运行“安装”程序加载“分析工具库”。\n实例：公式“=FACTDOUBLE(4)”返回8。' }, FALSE: { cn: '返回逻辑值FALSE', note: '用途：返回逻辑值 FALSE。\n语法：FALSE\n参数：FALSE 函数语法没有参数。' }, FDIST: { cn: '返回F概率分布', note: '用途： 返回 F 概率分布，它可以确定两个数据系列是否存在变化程度上的不同。例如，通过分析某一班级男、女生的考试分数，确定女生分数的变化程度是否与男生不同。\n语法： FDIST(x ， degrees_freedom1 ， degrees_freedom2)\n参数： X 是用来计算概率分布的区间点， Degrees_freedom1 是分子自由度， Degrees_freedom2 是分母自由度。\n实例： 公式 “=FDIST(1， 90， 89)”返回 0.500157305 。' }, FDISTRT: { cn: '返回F概率分布函数的函数值', note: '用途：返回两个数据集的（右尾）F 概率分布（变化程度）。 使用此函数可以确定两组数据是否存在变化程度上的不同。 例如，分析进入中学的男生、女生的考试分数，来确定女生分数的变化程度是否与男生不同。\n语法：FDISTRT(x,deg_freedom1,deg_freedom2)\n参数：X 用来计算函数的值。\n\t\tDeg_freedom1 分子自由度。\n\t\tDeg_freedom2 分母自由度。\n实例：FDISTRT(15.2068649,6,4) => 0.01' }, FIND: { cn: '在其他文本值中查找文本值（区分大小写）', note: '用途：用于在第二个文本串中定位第一个文本串，并返回第一个文本串的起始位置的值，该值从第二个文本串的第一个字符算起。\n语法：FIND(find_text, within_text, [start_num])\n参数：find_text 要查找的文本。\n\t\twithin_text 包含要查找文本的文本。\n\t\tstart_num 指定开始进行查找的字符。 within_text 中的首字符是编号为 1 的字符。 如果省略 start_num，则假定其值为 1。\n实例：FIND("M",A2,3) => 从单元格 A2 的第三个字符开始查找第一个“M”的位置' }, FINV: { cn: '返回反F概率分布', note: '用途： 返回 F 概率分布的逆函数值，即 F 分布的临界值。如果 p=FDIST(x ， …) ，则 FINV(p ， …)=x 。\n语法： FINV(probability ， degrees_freedom1 ， degrees_freedom2)\n参数： Probability 是累积 F 分布的概率值， Degrees_freedom1 是分子自由度， Degrees_freedom2 是分母自由度。\n实例： 公式 “=FINV(0.1， 86， 74)”返回 1.337888023 。' }, FINVRT: { cn: '返回（右尾）F概率分布函数的反函数值', note: '用途：返回（右尾）F 概率分布函数的反函数值。 如果 p = F.DIST.RT(x,...)，则 F.INV.RT(p,...) = x。 在 F 检验中，可以使用 F 分布比较两组数据中的变化程度。 例如，可以分析美国和加拿大的收入分布，判断两个国家/地区是否有相似的收入变化程度。\n语法：F.INV.RT(probability,deg_freedom1,deg_freedom2)\n参数：Probability F 累积分布的概率值。\n\t\tDeg_freedom1 分子自由度。\n\t\tDeg_freedom2 分母自由度。\n实例：FINVRT(0.01,6,4) => 15.20686' }, FISHER: { cn: '返回FISHER变换', note: '用途： 返回点 x 的 Fisher 变换。 该变换生成一个近似正态分布而非偏斜的函数， 使用此函数可以完成相关系数的假设性检验。\n语法： FISHER(x)\n参数： X 为一个数字，在该点进行变换。\n实例： 公式 “=FISHER(0.55)”返回 0.618381314 。' }, FISHERINV: { cn: '返回反FISHER变换', note: '用途： 返回 Fisher 变换的逆函数值，如果 y=FISHER(x) ，则 FISHERINV(y)=x 。上述变换可以分析数据区域或数组之间的相关性。\n语法： FISHERINV(y)\n参数： Y 为一个数值，在该点进行反变换。\n实例： 公式 “=FISHERINV(0.765)” 返回 0.644012628 。' }, FIXED: { cn: '使用固定的十进制数将数字设置为文本格式', note: '用途：将数字舍入到指定的小数位数，使用句点和逗号，以十进制数格式对该数进行格式设置，并以文本形式返回结果。\n语法：FIXED(number, [decimals], [no_commas])\n参数：Number 要进行舍入并转换为文本的数字。\n\t\tdecimals 小数点右边的位数。\n\t\tno_commas 一个逻辑值，如果为 TRUE，则会禁止 FIXED 在返回的文本中包含逗号。\n实例：FIXED(1234.567, 1) => 1,234.6\n\t\tFIXED(-1234.567, -1, TRUE) => -1230' }, FLOOR: { cn: '将参数NUMBER沿绝对值减小的方向取整', note: '用途：将参数Number沿绝对值减小的方向去尾舍入，使其等于最接近的significance的倍数。\n语法：FLOOR(number，significance)\n参数：Number为要舍入的某一数值，Significance为该数值的倍数。\n实例：如果A1=22.5，则公式“=FLOOR(A1，1)”返回22;=FLOOR(-2.5，-2)返回-2。' }, FORECAST: { cn: '根据给定的数据计算或预测未来值', note: '用途： 根据一条线性回归拟合线返回一个预测值。使用此函数可以对未来销售额、库存需求或消费趋势进行预测。\n语法： FORECAST(x ， known_y’s， known_x’ s)。5\n参数： X 为需要进行预测的数据点的 X 坐标 (自变量值 )。 Known_y’s 是从满足线性拟合直线 y=kx+b 的点集合中选出的一组已知的 y 值， Known_x’s 是从满足线性拟合直线 y=kx+b 的点集合中选出的一组已知的 x 值。\n实例： 公式 “=FORECAST(16 ， {7 ， 8， 9， 11， 15} ， {21 ， 26 ， 32， 36， 42}) ”返回 4.378318584 。' }, FREQUENCY: { cn: '返回作为矢量数组的频率分布', note: '用途： 以一列垂直数组返回某个区域中数据的频率分布。它可以计算出在给定的值域和接收区间内，每个区间包含的数据个数。\n语法： FREQUENCY(data_array ， bins_array)\n参数： Data_array 是用来计算频率一个数组，或对数组单元区域的引用。 Bins_array 是数据接收区间，为一数组或对数组区域的引用，设定对 data_array 进行频率计算的分段点。' }, FV: { cn: '返回投资的未来值', note: '用途： 基于固定利率及等额分期付款方式，返回某项投资的未来值。\n语法： FV(rate ， nper ， pmt ， pv ， type)\n参数： Rate 为各期利率， Nper 为总投资期 (即该项投资的付款期总数 )， Pmt 为各期所应支付的金额，Pv 为现值 (即从该项投资开始计算时已经入帐的款项， 或一系列未来付款的当前值的累积和， 也称为本金 ) ， Type为数字 0 或 1(0 为期末， 1 为期初 )。' }, FVSCHEDULE: { cn: '基于一系列复利返回本金的未来值', note: '用途： 基于一系列复利返回本金的未来值，用于计算某项投资在变动或可调利率下的未来值。\n语法： FVSCHEDULE(principal ， schedule)\n参数： Principal 为现值， Schedule 为利率数组。' }, GAMMA: { cn: '返回gamma函数值', note: '用途：返回 gamma 函数值。\n语法：GAMMA(number)\n参数：Number 返回一个数字。\n实例：GAMMA(-3.75) => 0.268' }, 'GAMMA.DIST': { cn: '返回伽玛分布函数的函数值', note: '用途：返回伽玛分布函数的函数值。 可以使用此函数来研究呈斜分布的变量。 伽玛分布通常用于排队分析。\n语法：GAMMA.DIST(x,alpha,beta,cumulative)\n参数：X 用来计算分布的数值。\n\t\tAlpha 分布参数。\n\t\tBeta 分布参数。 如果 beta = 1，则 GAMMA.DIST 返回标准伽玛分布。\n\t\tCumulative 决定函数形式的逻辑值。 如果 cumulative 为 TRUE，则 GAMMA.DIST 返回累积分布函数；如果为 FALSE，则返回概率密度函数。\n实例：GAMMA.DIST(10.00001131,9,2,FALSE) => 0.032639\n\t\tGAMMA.DIST(10.00001131,9,2,TRUE) => 0.068094' }, 'GAMMA.INV': { cn: '返回伽玛累积分布函数的反函数值', note: '用途：返回伽玛累积分布函数的反函数值。 如果 p = GAMMA.DIST(x,...)，则 GAMMA.INV(p,...) = x。 使用此函数可以研究有可能呈斜分布的变量。\n语法：GAMMA.INV(probability,alpha,beta)\n参数：Probability 伽玛分布相关的概率。\n\t\tAlpha 分布参数。\n\t\tBeta 分布参数。如果 beta = 1，则 GAMMA.INV 返回标准伽玛分布。\n实例：GAMMA.INV(0.068094,9,2) => 10.0000112' }, GAMMADIST: { cn: '返回伽玛分布', note: '用途： 返回伽玛分布。可用它研究具有偏态分布的变量，通常用于排队分析。\n语法： GAMMADIST(x ， alpha ， beta ， cumulative) 。\n参数： X 为用来计算伽玛分布的数值， Alpha 是 γ分布参数， Betaγ分布的一个参数。如果 beta=1 ，GAMMADIST 函数返回标准伽玛分布。 Cumulative 为一逻辑值，决定函数的形式。如果 cumulative 为 TRUE ，GAMMADIST 函数返回累积分布函数 ;如果为 FALSE ，则返回概率密度函数。\n实例： 公式 “=GAMMADIST(10 ， 9， 2， FALSE) ” 的计算结果等于 0.032639 ， =GAMMADIST(10 ， 9， 2，TRUE) 返回 0.068094 。' }, GAMMAINV: { cn: '返回反伽玛累积分布', note: '用途： 返回具有给定概率的伽玛分布的区间点，用来研究出现分布偏斜的变量。如果P=GAMMADIST(x ， ...) ，则 GAMMAINV(p ， ...)=x 。\n语法： GAMMAINV(probability ， alpha ， beta)\n参数： Probability 为伽玛分布的概率值， Alphaγ分布参数， Betaγ 分布参数。如果 beta=1 ，函数GAMMAINV 返回标准伽玛分布。\n实例： 公式 “=GAMMAINV(0.05 ， 8， 2)”返回 7.96164386 。' }, GAMMALN: { cn: '返回伽玛函数的自然对数，Γ(X)', note: '用途： 返回伽玛函数的自然对数 Γ(x)。\n语法： GAMMALN(x)\n参数： X 为需要计算 GAMMALN 函数的数值。\n实例： 公式 “=GAMMALN(6) ” 返回 4.787491743 。' }, 'GAMMALN.PRECISE': { cn: '返回伽玛函数的自然对数，Γ(x)', note: '用途：返回伽玛函数的自然对数，Γ(x)。\n语法：GAMMALN.PRECISE(x)\n参数：X 要计算其 GAMMALN.PRECISE 的数值。\n实例：GAMMALN.PRECISE(4) => 1.7917595' }, GAUSS: { cn: '计算标准正态总体的成员处于平均值与平均值的z倍标准偏差之间的概率', note: '用途：计算标准正态总体的成员处于平均值与平均值的 z 倍标准偏差之间的概率。\n语法：GAUSS(z)\n参数：z 返回一个数字。\n实例：GAUSS(2) => 标准正态总体的成员处于平均值与平均值的 2 倍标准偏差之间的概率（结果为 0.47725）。' }, GCD: { cn: '返回最大公约数', note: '用途：返回两个或多个整数的最大公约数。\n语法：GCD(number1，number2，...)\n参数：Number1，number2，...为1到29个数值，如果数值为非整数，则截尾取整。\n说明:如果该函数不存在，必须运行“安装”程序加载“分析工具库”。\n实例：如果A1=16、A2=28、A3=46，则公式“=GCD(A1:A3)”返回2。' }, GEOMEAN: { cn: '返回几何平均数', note: '用途： 返回正数数组或数据区域的几何平均值。可用于计算可变复利的平均增长率。\n语法： GEOMEAN(number1 ， number2 ， ...)\n参数： Number1 ， number2 ， ... 为需要计算其平均值的 1 到 30 个参数，除了使用逗号分隔数值的形式外，还可使用数组或对数组的引用。\n实例： 公式 “=GEOMEAN(1.2 ， 1.5， 1.8 ， 2.3 ， 2.6 ， 2.8， 3)”的计算结果是 2.069818248 。' }, GESTEP: { cn: '检测数字是否大于某个阈值', note: '用途： 如果 Number 大于等于 step ，返回 1，否则返回 0。使用该函数可筛选数据。\n语法： GESTEP(number ， step)\n参数： Number 是待测试的数值。 Step 是阈值。如果省略 step ，则函数 GESTEP 假设其为零。' }, GROWTH: { cn: '根据给定的数据预测指数增长值', note: "用途：使用现有数据计算预测的指数等比。 GROWTH 通过使用现有的 x 值和 y 值，返回指定的一系列的新 x 值的 y 值。 您也可以使用 GROWTH 工作表函数以拟合指数曲线与现有的 x 值和 y 值。\n语法：GROWTH(known_y's, [known_x's], [new_x's], [const])\n参数：Known_y's 关系表达式 y = b*m^x 中已知的 y 值集合。\n\t\t\t如果数组 known_y's 在单独一列中，则 known_x's 的每一列被视为一个独立的变量。\n\t\t\t如果数组 known_y's 在单独一行中，则 known_x's 的每一行被视为一个独立的变量。\n\t\t\t如果 known_y's 中的任何数为零或为负数，则 GROWTH 返回。 错误值 #NUM!。\n\t\tKnown_x's 关系表达式 y=b*m^x 中已知的 x 值集合，为可选参数。\n\t\t\t数组 known_x's 可以包含一组或多组变量。 如果仅使用一个变量，那么只要 known_x's 和 known_y's 具有相同的维数，则它们可以是任何形状的区域。 如果用到多个变量，则 known_y's 必须为向量（即必须为一行或一列）。\n\t\t\t如果省略 known_x's，则假设该数组为 {1,2,3,...}，其大小与 known_y's 相同。\n\t\tNew_x's 需要 GROWTH 返回对应 y 值的新 x 值\n\t\t\tNew_x's 与 known_x's 一样，对每个自变量必须包括单独的一列（或一行）。 因此，如果 known_y's 是单列的，known_x's 和 new_x's 应该有同样的列数。 如果 known_y's 是单行的，known_x's 和 new_x's 应该有同样的行数。\n\t\t\t如果省略 new_x's，则假设它和 known_x's 相同。\n\t\t\t如果 known_x's 与 new_x's 都被省略，则假设它们为数组 {1,2,3,...}，其大小与 known_y's 相同。\n\t\tConst 一个逻辑值，用于指定是否将常量 b 强制设为 1。\n\t\t\t如果 const 为 TRUE 或省略，b 将按正常计算。\n\t\t\t如果 const 为 FALSE，b 将设为 1，m 值将被调整以满足 y = m^x。\n实例：GROWTH(B2:B7,A2:A7,A9:A10)" }, HARMEAN: { cn: '返回数据集合的调和平均值', note: '用途： 返回数据集合的调和平均值。调和平均值与倒数的算术平均值互为倒数。调和平均值总小于几何平均值，而几何平均值总小于算术平均值。\n语法： HARMEAN(number1 ， number2 ， ...)\n参数： Number1 ， number2 ， ... 是需要计算其平均值的 1 到 30 个参数。可以使用逗号分隔参数的形式，还可以使用数组或数组的引用。\n实例： 公式 “=HARMEAN(66 ， 88， 92)”返回 80.24669604 。' }, HEX2BIN: { cn: '将十六进制数转换为二进制数', note: '用途： 将十六进制数转换为二进制数。\n语法： HEX2BIN(number ， places)\n参数： Number 是待转换的十六进制数， Places 是所要使用的字符数。' }, HEX2DEC: { cn: '将十六进制数转换为十进制数', note: '用途： 将十六进制数转换为十进制数。\n语法： HEX2DEC(number)\n参数： Number 是待转换的十六进制数。 参数 number 的位数不能多于 10 位 (40 位二进制 )， 最高位为符号位，其余 39 位是数字位。负数用二进制数的补码表示。' }, HEX2OCT: { cn: '将十六进制数转换为八进制数', note: '用途： 将十六进制数转换为八进制数。\n语法： HEX2OCT(number ， places)\n参数： Number 是待转换的十六进制数， Places 是所要使用的字符数。' }, HOUR: { cn: '将系列数转换为小时', note: '用途：返回时间值的小时数。 小时数是介于 0 (12:00 A.M.) 到 23 (11:00 P.M.) 之间的整数。\n语法：HOUR(serial_number)\n参数：Serial_number 时间值，其中包含要查找的小时数。 时间值有多种输入方式：带引号的文本字符串（例如 "6:45 PM"）、十进制数（例如 0.78125 表示 6:45 PM）或其他公式或函数的结果（例如 TIMEVALUE("6:45 PM")）。\n实例：HOUR(0.75) => 返回 24 小时的 75%，结果为 18\n\t\tHOUR("2011-7-18 7:45") => 返回日期/时间值的小时部分。结果为 7\n\t\tHOUR("2012-4-21") => 未指定时间部分的日期被视作上午 12:00 或 0 小时。结果为 0' }, 'HYPGEOM.DIST': { cn: '返回超几何分布', note: '用途：返回超几何分布。 如果已知样本量、总体成功次数和总体大小，则 HYPGEOM.DIST 返回样本取得已知成功次数的概率。 Use HYPGEOM.DIST 用于处理以下的有限总体问题，在该有限总体中，每次观察结果或为成功或为失败，并且已知样本量的每个子集的选取是等可能的。\n语法：HYPGEOM.DIST(sample_s,number_sample,population_s,number_pop,cumulative)\n参数：Sample_s 样本中成功的次数。\n\t\tNumber_sample 样本量。\n\t\tPopulation_s 总体中成功的次数。\n\t\tNumber_pop 总体大小。\n\t\tcumulative 决定函数形式的逻辑值。 如果 cumulative 为 TRUE，则 HYPGEOM.DIST 返回累积分布函数；如果为 FALSE，则返回概率密度函数。\n实例：HYPGEOM.DIST(1,4,8,20,TRUE) => 0.4654' }, HYPGEOMDIST: { cn: '返回超几何分布', note: '用途： 返回超几何分布。给定样本容量、样本总体容量和样本总体中成功的次数， HYPGEOMDIST 函数返回样本取得给定成功次数的概率。\n语法： HYPGEOMDIST(sample_s ， number_sample ， population_s ， number_population)\n参数： Sample_s 为样本中成功的次数， Number_sample 为样本容量。 Population_s 为样本总体中成功的次数， Number_population 为样本总体的容量。\n实例： 如果某个班级有 42 名学生。其中 22 名是男生， 20 名是女生。如果随机选出 6 人，则其中恰好有三名女生的概率公式是 : “=HYPGEOMDIST(3 ， 6， 20， 42)”，返回的结果为 0.334668627 。' }, IF: { cn: '指定要执行的逻辑检测', note: '用途：对值和期待值进行逻辑比较。\n语法：IF(logical_test, value_if_true, [value_if_false])\n参数：ogical_test 要测试的条件。\n\t\tvalue_if_true logical_test 的结果为 TRUE 时，您希望返回的值。\n\t\tvalue_if_false logical_test 的结果为 FALSE 时，您希望返回的值。\n实例：IF(A2>B2,"超出预算","正常")\n\t\tIF(A2=B2,B4-A4,"")' }, IMABS: { cn: '返回复数的绝对值（模）', note: '用途： 返回以 x+yi 或 x+yj 文本格式表示的复数的绝对值 (模 )。\n语法： IMABS(inumber)\n参数： Inumber 为需要计算其绝对值的复数。' }, IMAGINARY: { cn: '返回复数的虚系数', note: '用途： 返回以 x+yi 或 x+yj 文本格式表示的复数的虚系数。\n语法： IMAGINARY(inumber)\n参数： Inumber 为需要计算其虚系数的复数。' }, IMARGUMENT: { cn: '返回参数THETA，一个以弧度表示的角', note: '用途： 返回以弧度表示的角。\n语法： IMARGUMENT(inumber)\n参数： Inumber 为用来计算角度值的复数。25' }, IMCONJUGATE: { cn: '返回复数的共轭复数', note: '用途：返回以 x+yi 或 x+yj 文本格式表示的复数的共轭复数。\n语法：IMCONJUGATE(inumber)\n参数：Inumber 需要计算其共轭数的复数。\n实例：IMCONJUGATE("3+4i") => 3-4i' }, IMCOS: { cn: '返回复数的余弦', note: '用途： 返回以 x+yi 或 x+yj 文本格式表示的复数的余弦。\n语法： MCOS(inumber)\n参数： Inumber 为需要计算其余弦值的复数。' }, IMCOSH: { cn: '返回以x+yi或x+yj文本格式表示的复数的双曲余弦值', note: '用途：返回以 x+yi 或 x+yj 文本格式表示的复数的双曲余弦值。\n语法：IMCOSH(inumber)\n参数：Inumber 需要计算其双曲余弦值的复数。\n实例：IMCOSH("4+3i") => -27.0349456030742+3.85115333481178i' }, IMCOT: { cn: '返回以x+yi或x+yj文本格式表示的复数的余切值', note: '用途：返回以 x+yi 或 x+yj 文本格式表示的复数的余切值。\n语法：IMCOT(inumber)\n参数：Inumber 需要计算其余切值的复数。\n实例：IMCOT("4+3i") => 0.00490118239430447-0.999266927805902i' }, IMCSC: { cn: '返回以x+yi或x+yj文本格式表示的复数的余割值', note: '用途：返回以 x+yi 或 x+yj 文本格式表示的复数的余割值。\n语法：IMCSC(inumber)\n参数：Inumber 需要计算其余割值的复数。\n实例：IMCSC("4+3i") => -0.0754898329158637+0.0648774713706355i' }, IMCSCH: { cn: '返回以x+yi或x+yj文本格式表示的复数的双曲余割值', note: '用途：返回以 x+yi 或 x+yj 文本格式表示的复数的双曲余割值。\n语法：IMCSCH(inumber)\n参数：Inumber 需要计算其双曲余割值的复数。\n实例：IMCSCH("4+3i") => -0.036275889628626-0.0051744731840194i' }, IMDIV: { cn: '返回两个复数的商', note: '用途： 返回以 x+yi 或 x+yj 文本格式表示的两个复数的商。\n语法： IMDIV(inumber1 ， inumber2)\n参数： Inumber1 为复数分子 (被除数 )， Inumber2 为复数分母 (除数 )。' }, IMEXP: { cn: '返回复数的指数', note: '用途： 返回以 x+yi 或 x+yj 文本格式表示的复数的指数。\n语法： IMEXP(inumber)\n参数： Inumber 为需要计算其指数的复数。' }, IMLN: { cn: '返回复数的自然对数', note: '用途： 返回以 x+yi 或 x+yj 文本格式表示的复数的自然对数。\n语法： IMLN(inumber)\n参数： Inumber 为需要计算其自然对数的复数。' }, IMLOG10: { cn: '返回复数的常用对数', note: '用途： 返回以 x+yi 或 x+yj 文本格式表示的复数的常用对数 (以 10 为底数 )。\n语法： IMLOG10(inumber)\n参数： Inumber 为需要计算其常用对数的复数。' }, IMLOG2: { cn: '返回复数的以2为底数的对数', note: '用途： 返回以 x+yi 或 x+yj 文本格式表示的复数的以 2 为底数的对数。\n语法： IMLOG2(inumber)\n参数： Inumber 为需要计算以 2 为底数的对数值的复数。' }, IMPOWER: { cn: '返回复数的整数幂', note: '用途： 返回以 x+yi 或 x+yj 文本格式表示的复数的 n 次幂。\n语法： IMPOWER(inumber ， number)\n参数： Inumber 为需要计算其幂值的复数， Number 为需要计算的幂次。' }, IMPRODUCT: { cn: '返回两个复数的乘积', note: '用途： 返回以 x+yi 或 x+yj 文本格式表示的 2 至 29 个复数的乘积。\n语法： IMPRODUCT(inumber1 ， inumber2 ， ...)\n参数： Inumber1 ， inumber2 ， … 为 1 到 29 个用来相乘的复数。' }, IMREAL: { cn: '返回复数的实系数', note: '用途： 返回以 x+yi 或 x+yj 文本格式表示的复数的实系数。\n语法： IMREAL(inumber)\n参数： Inumber 为需要计算其实系数的复数。' }, IMSEC: { cn: '返回以x+yi或x+yj文本格式表示的复数的正割值', note: '用途：返回以 x+yi 或 x+yj 文本格式表示的复数的正割值。\n语法：IMSEC(inumber)\n参数：Inumber 需要计算其双曲余割值的复数。\n实例：IMSEC("4+3i") => -0.0652940278579471-0.0752249603027732i' }, IMSECH: { cn: '返回以x+yi或x+yj文本格式表示的复数的双曲正割值', note: '用途：返回以 x+yi 或 x+yj 文本格式表示的复数的双曲正割值。\n语法：IMSECH(inumber)\n参数：Inumber 需要计算其双曲正割值的复数。\n实例：IMSECH("4+3i") => -0.0362534969158689-0.00516434460775318i' }, IMSIN: { cn: '返回复数的正弦', note: '用途： 返回以 x+yi 或 x+yj 文本格式表示的复数的正弦值。\n语法： IMSIN(inumber)\n参数： Inumber 为需要计算其正弦的复数。' }, IMSINH: { cn: '返回以x+yi或x+yj文本格式表示的复数的双曲正弦值', note: '用途：返回以 x+yi 或 x+yj 文本格式表示的复数的双曲正弦值。\n语法：IMSINH(inumber)\n参数：Inumber 需要计算其双曲正弦值的复数。\n实例：IMSINH("4+3i") => -7.61923172032141-6.548120040911i' }, IMSQRT: { cn: '返回复数的平方根', note: '用途： 返回以 x+yi 或 x+yj 文本格式表示的复数的平方根。\n语法： IMSQRT(inumber)\n参数： Inumber 为需要计算其平方根的复数。' }, IMSUB: { cn: '返回两个复数的差', note: '用途： 返回以 x+yi 或 x+yj 文本格式表示的两个复数的差。\n语法： IMSUB(inumber1 ， inumber2)\n参数： Inumber1 是被减 (复 )数， Inumber2 是为减 (复 ) 数。\n36 、 IMSUM\n用途： 返回以 x+yi 或 x+yj 文本格式表示的两个或多个复数的和。\n语法： IMSUM(inumber1 ， inumber2 ， ...)\n参数： Inumber1 ， inumber2 ， ... 为 1 到 29 个需要相加的复数。' }, IMSUM: { cn: '返回以x+yi或x+yj文本格式表示的两个或多个复数的和', note: '用途：返回以 x+yi 或 x+yj 文本格式表示的两个或多个复数的和。\n语法：IMSUM(inumber1, [inumber2], ...)\n参数：Inumber1, [inumber2], ...    Inumber1 是必需的，后续数值不是必须的。\n实例：IMSUM("3+4i","5-3i") => 8+i' }, IMTAN: { cn: '返回以x+yi或x+yj文本格式表示的复数的正切值', note: '用途：返回以 x+yi 或 x+yj 文本格式表示的复数的正切值。\n语法：IMTAN(inumber)\n参数：Inumber 需要计算其正切值的复数。\n实例：IMTAN("4+3i") => 0.00490825806749606+1.00070953606723i' }, INT: { cn: '将数向下取整至最接近的整数', note: '用途：将任意实数向下取整为最接近的整数。\n语法：INT(number)\n参数：Number为需要处理的任意一个实数。\n实例：如果A1=16.24、A2=-28.389，则公式“=INT(A1)”返回16，=INT(A2)返回-29。' }, INTERCEPT: { cn: '返回回归线截距', note: '用途： 利用已知的 x 值与 y 值计算直线与 y 轴的截距。当已知自变量为零时，利用截距可以求得因变量的值。\n语法： INTERCEPT(known_y ’s ， known_x’ s)\n参数： Known_y’s 是一组因变量数据或数据组， Known_x’s 是一组自变量数据或数据组。\n实例： 如果 A1=71 、 A2=83 、 A3=76 、 A4=49 、 A5=92 、 A6=88 、 A7=96 ， B1=59 、 B2=70 、 B3=80 、B4=90 、 B5=89 、 B6=84 、 B7=92 ，则公式 “=INTERCEPT(A1:A7 ， B1:B7) ”返回 87.61058785 。' }, IPMT: { cn: '返回给定期间内投资的利息偿还额', note: '用途： 基于固定利率及等额分期付款方式，返回投资或贷款在某一给定期限内的利息偿还额。\n语法： IPMT(rate ， per ， nper ， pv， fv， type)\n参数： Rate 为各期利率， Per 用于计算其利息数额的期数 (1 到 nper 之间 ) ， Nper 为总投资期， Pv 为现值( 本金 ) ， Fv 为未来值 (最后一次付款后的现金余额。如果省略 fv，则假设其值为零 )， Type 指定各期的付款时间是在期初还是期末 (0 为期末， 1 为期初 )。' }, IRR: { cn: '返回一组现金流的内部收益率', note: '用途： 返回由数值代表的一组现金流的内部收益率。\n语法： IRR(values ， guess)\n参数： values 为数组或单元格的引用，包含用来计算返回的内部收益率的数字。 Guess 为对函数 IRR计算结果的估计值。' }, ISBLANK: { cn: '如果值为空，则返回TRUE。', note: '用途：如果值为空，则返回TRUE。\n语法：ISBLANK(value)\n参数：value 指的是要测试的值。 参数 value 可以是空白（空单元格）、错误值、逻辑值、文本、数字、引用值，或者引用要测试的以上任意值的名称。\n实例：ISBLANK("") => FALSE' }, ISEVEN: { cn: '如果数为偶数，则返回TRUE。', note: '用途：如果参数 number 为偶数，返回 TRUE，否则返回 FALSE。\n语法：ISEVEN(number)\n参数：Number 要测试的值。 如果 number 不是整数，将被截尾取整。\n实例：ISEVEN(5) => FALSE' }, ISLOGICAL: { cn: '如果值为逻辑值，则返回TRUE。', note: '用途：如果值为逻辑值，则返回 TRUE\n语法：ISLOGICAL(value)\n参数：value 指的是要测试的值。 参数 value 可以是空白（空单元格）、错误值、逻辑值、文本、数字、引用值，或者引用要测试的以上任意值的名称。\n实例：ISLOGICAL("TRUE") => FALSE' }, ISNONTEXT: { cn: '如果值不是文本，则返回TRUE。', note: '用途：如果值为不是文本的任意项，则返回 TRUE\n语法：ISNONTEXT(value)\n参数：value 指的是要测试的值。 参数 value 可以是空白（空单元格）、错误值、逻辑值、文本、数字、引用值，或者引用要测试的以上任意值的名称。\n实例：ISNONTEXT(1) => true' }, ISNUMBER: { cn: '如果值为数字，则返回TRUE。', note: '用途：如果值为数字，则返回 TRUE\n语法：ISNUMBER(value)\n参数：value 指的是要测试的值。 参数 value 可以是空白（空单元格）、错误值、逻辑值、文本、数字、引用值，或者引用要测试的以上任意值的名称。\n实例：ISNUMBER(1) => true' }, ISODD: { cn: '如果数字为奇数，则返回TRUE。', note: '用途：如果参数 number 为奇数，返回 TRUE，否则返回 FALSE。\n语法：ISODD(number)\n参数：Number 要测试的值。 如果 number 不是整数，将被截尾取整。\n实例：ISODD(2.5) => FALSE' }, ISOWEEKNUM: { cn: '返回给定日期在全年中的ISO周数', note: '用途：返回给定日期在全年中的 ISO 周数。\n语法：ISOWEEKNUM(date)\n参数：date 日期是 Excel 用于日期和时间计算的日期-时间代码。\n实例：ISOWEEKNUM("2012-3-9") => 2012-3-9 所在一年中的周数，一周开始于星期一（默认值）(10)' }, ISPMT: { cn: '计算在投资的特定期间内支付的利息', note: '用途： 计算特定投资期内要支付的利息。\n语法： ISPMT(rate ， per ， nper ， pv)\n参数： Rate 为投资的利率， Per 为要计算利息的期数 (在 1 到 nper 之间 )， Nper 为投资的总支付期数，Pv 为投资的当前值 (对于贷款来说 pv 为贷款数额 )。' }, ISTEXT: { cn: '如果值为文本，则返回TRUE。', note: '用途：如果值为文本，则返回 TRUE\n语法：ISTEXT(value)\n参数：value 指的是要测试的值。 参数 value 可以是空白（空单元格）、错误值、逻辑值、文本、数字、引用值，或者引用要测试的以上任意值的名称。\n实例：ISTEXT("123") => TRUE' }, KURT: { cn: '返回数据集的峰值', note: '用途： 返回数据集的峰值。它反映与正态分布相比时某一分布的尖锐程度或平坦程度，正峰值表示相对尖锐的分布，负峰值表示相对平坦的分布。\n语法： KURT(number1 ， number2 ， ...)\n参数： Number1 ， number2 ， ... 为需要计算其峰值的 1 到 30 个参数。它们可以使用逗号分隔参数的形式，也可以使用单一数组，即对数组单元格的引用。\n实例： 如果某次学生考试的成绩为 A1=71 、 A2=83 、 A3=76 、 A4=49 、 A5=92 、 A6=88 、 A7=96 ，则公式“=KURT(A1:A7)” 返回 -1.199009798 ，说明这次的成绩相对正态分布是一比较平坦的分布。' }, LARGE: { cn: '返回数据集中第K个最大值', note: '用途： 返回某一数据集中的某个最大值。可以使用 LARGE 函数查询考试分数集中第一、第二、第三等的得分。\n语法： LARGE(array ， k)\n参数： Array 为需要从中查询第 k 个最大值的数组或数据区域， K 为返回值在数组或数据单元格区域里的位置 (即名次 ) 。7\n实例： 如果 B1=59 、 B2=70 、 B3=80 、 B4=90 、 B5=89 、 B6=84 、 B7=92 ，，则公式 “=LARGE(B1， B7 ，2) ”返回 90。' }, LCM: { cn: '返回最小公倍数', note: '用途：返回整数的最小公倍数。最小公倍数是所有整数参数number1、number2、…，的最小正整数倍数。用函数LCM可以将分母不同的分数相加。\n语法：LCM(number1，number2，...)\n参数：Number1，number2，...是要计算最小公倍数的1到29个参数。如果参数不是整数，则自动截去小数部分取整。\n说明:该函数需要加载“分析工具库”才能使用。\n实例：如果A1=4、A2=16、A3=8，则公式“=LCM(A1:A3)”返回16。' }, LEFT: { cn: '返回文本值中最左边的字符', note: '用途：从文本字符串的第一个字符开始返回指定个数的字符。\n语法：LEFT(text, [num_chars])\n参数：Text 包含要提取的字符的文本字符串。\n\t\tnum_chars 指定要由 LEFT 提取的字符的数量。\n\t\t\tNum_chars 必须大于或等于零。\n\t\t\t如果 num_chars 大于文本长度，则 LEFT 返回全部文本。\n\t\t\t如果省略 num_chars，则假定其值为 1。\n实例：LEFT("好书法",2) => "好书"' }, LEN: { cn: '返回文本串中字符的个数', note: '用途：返回文本字符串中的字符个数。\n语法：LEN(text)\n参数：Text 要查找其长度的文本。 空格将作为字符进行计数。\n实例：LEN("Phoenix, AZ") => 11' }, LINEST: { cn: '返回线条趋势的参数', note: '用途： 使用最小二乘法对已知数据进行最佳直线拟合，并返回描述此直线的数组。\n语法： LINEST(known_y’s ， known_x’s， const ， stats)\n参数： Known_y’s 是表达式 y=mx+b 中已知的 y 值集合\n\t\tKnown_x’s 是关系表达式 y=mx+b 中已知的可选 x 值集合\n\t\tConst 为一逻辑值，指明是否强制使常数 b 为 0，如果 const 为 TRUE 或省略， b 将参与正常计算。如果 const 为 FALSE ， b 将被设为 0，并同时调整 m 值使得 y=mx\n\t\tStats 为一逻辑值，指明是否返回附加回归统计值。如果 stats 为 TRUE ，函数 LINEST 返回附加回归统计值。如果 stats 为 FALSE 或省略，函数 LINEST只返回系数 m 和常数项 b。\n实例： 如果 A1=71 、 A2=83 、 A3=76 、 A4=49 、 A5=92 、 A6=88 、 A7=96 ， B1=59 、 B2=70 、 B3=80 、B4=90 、 B5=89 、 B6=84 、 B7=92 ，则数组公式 “{=LINEST(A1:A7 ， B1:B7)} ”返回 -0.174244885 、 -0.174244885 、-0.174244885 、 -0.174244885 、 -0.174244885 、 -0.174244885 、 -0.174244885 。' }, LN: { cn: '返回数的自然对数', note: '用途：返回一个数的自然对数，即以e(2.71828182845904)为底的对数(LN函数是EXP函数的反函数)。\n语法：LN(number)\n参数：Number是待计算其自然对数的正实数。\n实例：如果A1=100、A2=67，则公式“=LN(A1+A2)”返回5.117993812;=LN(EXP(3))返回3;=EXP(LN(4))返回4。' }, LOG: { cn: '返回数的指定底数的对数', note: '用途：按所指定的底数，返回某个数的对数。\n语法：LOG(number，base)\n参数：Number是计算对数的任意实数，Base是对数的底数。如果省略底数，则默认它的值为10。\n实例：如果A1=8，则公式“=LOG(A1，2)”返回3;=LOG(100，10)返回2。' }, LOG10: { cn: '返回以10为底的对数', note: '用途：返回以10为底的对数。\n语法：LOG10(number)\n参数：Number是待计算常用对数的一个正实数。\n实例：如果A1=1000，则公式“=LOG10(A1)”返回3;=LOG10(10^5)返回5。' }, LOGEST: { cn: '返回指数趋势的参数', note: '用途： 在回归分析中，计算最符合观测数据组的指数回归拟合曲线，并返回描述该曲线的数组。\n语法： LOGEST(known_y ’s ， known_x’s， const ， stats)\n参数： Known_y’s 是一组符合 y=b*m^x 函数关系的 y 值的集合\n\t\tKnown _x’s是一组符合 y=b*m^x 运算关系的可选 x 值集合\n\t\tConst 是指定是否要设定常数 b 为 1 的逻辑值，如果 const 设定为 TRUE 或省略，则常数项 b 将通过计算求得。\n实例： 如果某公司的新产品销售额呈指数增长， 依次为 A1=33100 、A2=47300 、A3=69000 、 A4=102000 、A5=150000 和 A6=220000 ，同时 B1=11 、 B2=12 、 B3=13 、 B4=14 、 B5=15 、 B6=16 。则使用数组公式“{=LOGEST(A1:A6 ， B1:B6 ， TRUE ， TRUE)}”，在 C1:D5 单元格内得到的计算结果是 :1.463275628 、495.3047702 、 0.002633403 、 0.035834282 、 0.99980862 、 0.011016315 、 20896.8011 、 4、 2.53601883 和0.000485437 。' }, 'LOGNORM.DIST': { cn: '返回x的对数分布函数', note: '用途：返回 x 的对数分布函数，此处的 ln(x) 是含有 Mean 与 Standard_dev 参数的正态分布。使用此函数可以分析经过对数变换的数据。\n语法：LOGNORM.DIST(x,mean,standard_dev,cumulative)\n参数：X 用来计算函数的值。\n\t\tMean ln(x) 的平均值。\n\t\tstandard_dev ln(x) 的标准偏差。\n\t\tcumulative 决定函数形式的逻辑值。 如果 cumulative 为 TRUE，则 LOGNORM.DIST 返回累积分布函数；如果为 FALSE，则返回概率密度函数。\n实例：LOGNORM.DIST(4,3.5,1.2,TRUE) => 0.0390836' }, 'LOGNORM.INV': { cn: '返回x的对数累积分布函数的反函数值', note: '用途：返回 x 的对数累积分布函数的反函数值，此处的 ln(x) 是服从参数 Mean 和 Standard_dev 的正态分布。 如果 p = LOGNORM.DIST(x,...)，则 LOGNORM.INV(p,...) = x。\n语法：LOGNORM.INV(probability, mean, standard_dev)\n参数：Probability 与对数分布相关的概率。\n\t\tMean ln(x) 的平均值。\n\t\tstandard_dev ln(x) 的标准偏差。\n实例：LOGNORM.INV(0.039084, 3.5, 1.2) => 4.0000252' }, LOGNORMDIST: { cn: '返回对数正态分布的累积函数', note: '用途： 返回 x 的对数正态分布的累积函数， 其中 ln(x) 是服从参数为 mean 和 standard_dev 的正态分布。使用此函数可以分析经过对数变换的数据。\n语法： LOGNORMDIST(x ， mean ， standard_dev)\n参数： X 是用来计算函数的数值， \n\t\tMean 是 ln(x)的平均值\n\t\tStandard_dev 是 ln(x) 的标准偏差。\n实例： 公式 “=LOGNORMDIST(2 ， 5.5 ， 1.6) ”返回 0.001331107 。' }, LOGNORMINV: { cn: '返回x的对数累积分布函数的反函数值', note: '用途：返回 x 的对数累积分布函数的反函数值，此处的 ln(x) 是服从参数 Mean 和 Standard_dev 的正态分布。 如果 p = LOGNORM.DIST(x,...)，则 LOGNORM.INV(p,...) = x。\n语法：LOGNORMINV(probability, mean, standard_dev)\n参数：Probability 与对数分布相关的概率。\n\t\tMean ln(x) 的平均值。\n\t\tstandard_dev ln(x) 的标准偏差。\n实例：LOGNORMINV(0.039084, 3.5, 1.2) => 4.0000252' }, LOWER: { cn: '将文本转换为小写', note: '用途：将一个文本字符串中的所有大写字母转换为小写字母。\n语法：LOWER(text)\n参数：Text 要转换为小写字母的文本。 LOWER 不改变文本中的非字母字符。\n实例：LOWER("E. E. Cummings") => "e. e. cummings"' }, MATCH: { cn: '在引用或数组中查找值', note: '用途：在范围单元格中搜索特定的项，然后返回该项在此区域中的相对位置。例如，如果 A1:A3 区域中包含值 5、25 和 38，那么公式 =MATCH(25,A1:A3,0) 返回数字 2，因为 25 是该区域中的第二项。\n语法：MATCH(lookup_value, lookup_array, [match_type])\n参数：lookup_value 要在 lookup_array 中匹配的值。例如，如果要在电话簿中查找某人的电话号码，则应该将姓名作为查找值，但实际上需要的是电话号码。\n\t\t\tlookup_value 参数可以为值（数字、文本或逻辑值）或对数字、文本或逻辑值的单元格引用。\n\t\tlookup_array 要搜索的单元格区域。\n\t\tmatch_type 数字 -1、0 或 1。match_type 参数指定 Excel 如何将 lookup_value 与 lookup_array 中的值匹配。此参数的默认值为 1。\n\t\t\tMatch_type 为 1。MATCH 查找小于或等于 lookup_value 的最大值。lookup_array 参数中的值必须以升序排序，例如：...-2, -1, 0, 1, 2, ..., A-Z, FALSE, TRUE。\n\t\t\tMatch_type 为 0。MATCH 查找完全等于 lookup_value 的第一个值。lookup_array 参数中的值可按任何顺序排列。\n\t\t\tMatch_type 为 -1。MATCH 查找大于或等于 lookup_value 的最小值。lookup_array 参数中的值必须按降序排列，例如：TRUE, FALSE, Z-A, ...2, 1, 0, -1, -2, ... 等等。\n实例：B2:B5 => 25,38,40,41\n\t\tMATCH(39,B2:B5,1) => 由于此处无精确匹配项，因此函数会返回单元格区域 B2:B5 中最接近的下个最小值 (38) 的位置。结果为 2' }, MAX: { cn: '返回参数列表中的最大值', note: '用途： 返回数据集中的最大数值。\n语法： MAX(number1 ， number2 ， ...)\n参数： Number1 ， number2 ， ... 是需要找出最大数值的 1 至 30 个数值。\n实例： 如果 A1=71 、 A2=83 、 A3=76 、 A4=49 、 A5=92 、 A6=88 、 A7=96 ，则公式 “=MAX(A1:A7)” 返回\n96。' }, MAXA: { cn: '返回参数列表中的最大值，包括数字、文本和逻辑值', note: '用途：返回参数列表中的最大值。\n语法：MAXA(value1,[value2],...)\n参数：Value1 要从中找出最大值的第一个数值参数。\n\t\tValue2,... 要从中找出最大值的数值参数。\n实例：A2:A6 => 0, 0.2, 0.5, 0.4, TRUE\n\t\tMAXA(A2:A6) => 区域 A2:A6 中的最大数字。 因为 TRUE 值的计算结果为 1，这就是最大值。' }, MEDIAN: { cn: '返回给定数字的中位数', note: '用途： 返回给定数值集合的中位数 (它是在一组数据中居于中间的数。换句话说，在这组数据中，有一半的数据比它大，有一半的数据比它小 )。\n语法： MEDIAN(number1 ， number2 ， ...)\n参数： Number1 ， number2 ， ... 是需要找出中位数的 1 到 30 个数字参数。\n实例： MEDIAN(11 ， 12 ， 13 ， 14， 15) 返回 13;MEDIAN(1 ， 2， 3， 4， 5， 6)返回 3.5 ，即 3 与 4 的平均值。' }, MID: { cn: '从文本串中的指定位置开始返回特定数目的字符', note: '用途：MID 返回文本字符串中从指定位置开始的特定数目的字符，该数目由用户指定。\n语法：MID(text, start_num, num_chars)\n参数：Text 包含要提取字符的文本字符串。\n\t\tstart_num 文本中要提取的第一个字符的位置。 文本中第一个字符的 start_num 为 1，以此类推。\n\t\tnum_chars 指定希望 MID 从文本中返回字符的个数。\n\t\tNum_bytes 指定希望 MIDB 从文本中返回字符的个数（字节数）。\n实例：MID("Fluid Flow",1,5) => "Fluid"' }, MIN: { cn: '返回参数列表的最小值', note: '用途： 返回给定参数表中的最小值。\n语法： MIN(number1 ， number2 ， ...) 。\n参数： Number1 ， number2 ， ... 是要从中找出最小值的 1 到 30 个数字参数。\n实例： 如果 A1=71 、 A2=83 、 A3=76 、 A4=49 、 A5=92 、 A6=88 、 A7=96 ，则公式 “=MIN(A1:A7)”返回49; 而 =MIN(A1:A5 ， 0， -8) 返回 -8 。' }, MINA: { cn: '返回参数列表中的最小值，包括数字、文本和逻辑值', note: '用途： 返回参数清单中的最小数值。它与 MIN 函数的区别在于文本值和逻辑值 (如 TRUE 和 FALSE) 也作为数字参与计算。\n语法： MINA(value1 ， value2 ， ...)\n参数： value1 ， value2 ， ... 为需要从中查找最小数值的 1 到 30 个参数。\n实例： 如果 A1=71 、 A2=83 、 A3=76 、 A4=49 、 A5=92 、 A6=88 、 A7=FALSE ，则公式 “=MINA(A1:A7)”返回 0。' }, MINUTE: { cn: '将系列数转换为分钟', note: '用途：返回时间值中的分钟。 分钟是一个介于 0 到 59 之间的整数。\n语法：MINUTE(serial_number)\n参数：Serial_number 一个时间值，其中包含要查找的分钟。 时间值有多种输入方式：带引号的文本字符串（例如 "6:45 PM"）、十进制数（例如 0.78125 表示 6:45 PM）或其他公式或函数的结果（例如 TIMEVALUE("6:45 PM")）。\n实例：MINUTE("12:45:00") => 45' }, MIRR: { cn: '返回正负现金流使用不同利率的修正内部收益率', note: '用途： 返回某一期限内现金流的修正内部收益率。\n语法： MIRR(values ， finance_rate ， reinvest_rate)\n参数： values 为一个数组或对包含数字的单元格的引用 (代表着各期的一系列支出及收入，其中必须至少包含一个正值和一个负值，才能计算修正后的内部收益率 )， Finance_rate 为现金流中使用的资金支付的利率，Reinvest_rate 为将现金流再投资的收益率。' }, MOD: { cn: '返回两数相除的余数', note: '用途：返回两数相除的余数，其结果的正负号与除数相同。\n语法：MOD(number，divisor)\n参数：Number为被除数，Divisor为除数(divisor不能为零)。\n实例：如果A1=51，则公式“=MOD(A1，4)”返回3;=MOD(-101，-2)返回–1。' }, 'MODE.MULT': { cn: '返回一组数据或数据区域中出现频率最高或重复出现的数值的垂直数组', note: '用途：返回一组数据或数据区域中出现频率最高或重复出现的数值的垂直数组。对于水平数组，请使用 TRANSPOSE(MODE.MULT(number1,number2,...))。如果有多个众数，则将返回多个结果。因为此函数返回数值数组，所以它必须以数组公式的形式输入。\n语法：MODE.MULT((number1,[number2],...)\n参数：Number1 要计算其众数的第一个数字参数。\n\t\tNumber2, ... 要计算其众数的数字参数。也可以用单一数组或对某个数组的引用来代替用逗号分隔的参数。\n实例：MODE.MULT(A2:A13)' }, 'MODE.SNGL': { cn: '返回在某一数组或数据区域中出现频率最多的数值', note: '用途：返回在某一数组或数据区域中出现频率最多的数值。\n语法：MODE.SNGL(number1,[number2],...)\n参数：Number1 要计算其众数的第一个参数。\n\t\tNumber2, ... 要计算其众数的参数。也可以用单一数组或对某个数组的引用来代替用逗号分隔的参数。\n实例：A2:A7 => 5.6, 4, 4, 3, 2, 4\n\t\tMODE.SNGL(A2:A7) => 4' }, MODEMULT: { cn: '返回一组数据或数据区域中出现频率最高或重复出现的数值的垂直数组', note: '用途：返回一组数据或数据区域中出现频率最高或重复出现的数值的垂直数组。对于水平数组，请使用 TRANSPOSE(MODE.MULT(number1,number2,...))。如果有多个众数，则将返回多个结果。因为此函数返回数值数组，所以它必须以数组公式的形式输入。\n语法：MODEMULT((number1,[number2],...)\n参数：Number1 要计算其众数的第一个数字参数。\n\t\tNumber2, ... 要计算其众数的数字参数。也可以用单一数组或对某个数组的引用来代替用逗号分隔的参数。\n实例：MODEMULT(A2:A13)' }, MODESNGL: { cn: '返回在某一数组或数据区域中出现频率最多的数值', note: '用途：返回在某一数组或数据区域中出现频率最多的数值。\n语法：MODESNGL(number1,[number2],...)\n参数：Number1 要计算其众数的第一个参数。\n\t\tNumber2, ... 要计算其众数的参数。也可以用单一数组或对某个数组的引用来代替用逗号分隔的参数。\n实例：A2:A7 => 5.6, 4, 4, 3, 2, 4\n\t\tMODESNGL(A2:A7) => 4' }, MONTH: { cn: '将系列数转换为月', note: '用途：返回日期（以序列数表示）中的月份。 月份是介于 1（一月）到 12（十二月）之间的整数。\n语法：MONTH(serial_number)\n参数：Serial_number 要查找的月份日期。应使用 DATE 函数输入日期，或将日期作为其他公式或函数的结果输入。例如，使用函数 DATE(2008,5,23) 输入 2008 年 5 月 23 日。如果日期以文本形式输入，则会出现问题。\n实例：MONTH("2011-4-15") => 4' }, MROUND: { cn: '返回参数按指定基数取整后的数值', note: '用途：返回参数按指定基数舍入后的数值。\n语法：MROUND(number，significance)\n参数：Number是将要舍入的数值，Significance是要对参数Number进行舍入运算的基数。\n注意:如果参数number除以基数Significance的余数大于或等于基数Significance的一半，则函数MROUND向远离零的方向舍入。另外，该函数只有加载了“分析工具库”方可使用。\n实例：如果A1=6.6876，则公式“=MROUND(A1，4)”的计算结果是8。' }, MULTINOMIAL: { cn: '返回一组数的多项式', note: '用途：返回参数和的阶乘与各参数阶乘乘积的比值，例如MULTINOMIAL(2，3，4)执行的运算为9!/2!*3!*4!。\n语法：MULTINOMIAL(number1，number2，...)\n参数：Number1，number2，...是用于进行函数Multinomial运算的1到29个数值参数。\n注意:该函数只有加载“分析工具库”方可使用。\n实例：MULTINOMIAL(2，3，4)返回的结果为1260。' }, 'NEGBINOM.DIST': { cn: '返回负二项式分布', note: '用途：返回负二项式分布，即当成功概率为 Probability_s 时，在 Number_s 次成功之前出现 Number_f 次失败的概率。此函数与二项式分布相似，只是它的成功次数固定，试验次数为变量。 与二项式分布相同的是，二者均假定试验是独立的。例如，如果要找 10 个反应敏捷的人，且已知候选人符合相关条件的概率为 0.3。 NEGBINOM.DIST 将计算出在找到 10 个合格候选人之前，需面试特定数目的不合格候选人的概率。\n语法：NEGBINOM.DIST(number_f,number_s,probability_s,cumulative)\n参数：Number_f    必需。 失败的次数。\n\t\tNumber_s 成功次数的阈值。\n\t\tProbability_s 成功的概率。\n\t\tcumulative 决定函数形式的逻辑值。 如果 cumulative 为 TRUE，则 NEGBINOM.DIST 返回累积分布函数；如果为 FALSE，则返回概率密度函数。\n实例：NEGBINOM.DIST(10,5,0.25,TRUE) => 0.3135141' }, NEGBINOMDIST: { cn: '返回负二项式分布', note: '用途： 返回负二项式分布。当成功概率为常数 probability_s 时，函数 NEGBINOMDIST 返回在到达number_s 次成功之前，出现 number_f 次失败的概率。此函数与二项式分布相似，只是它的成功次数固定，试验总数为变量。与二项分布类似的是，试验次数被假设为自变量。\n语法： NEGBINOMDIST(number_f ， number_s ， probability_s)\n参数： Number_f 是失败次数\n\t\tNumber_s 为成功的临界次数\n\t\tProbability_s 是成功的概率\n实例： 如果要找 10 个反应敏捷的人，且已知具有这种特征的候选人的概率为 0.3 。那么，找到 10 个合格候选人之前，需要对不合格候选人进行面试的概率公式为 “=NEGBINOMDIST(40 ， 10 ， 0.3) ”，计算结果是0.007723798 。' }, NETWORKDAYS: { cn: '返回两个日期之间的完整工作日数', note: '用途：返回参数 start_date 和 end_date 之间完整的工作日数值。 工作日不包括周末和专门指定的假期。 可以使用函数 NETWORKDAYS，根据某一特定时期内雇员的工作天数，计算其应计的报酬。\n语法：NETWORKDAYS(start_date, end_date, [holidays])\n参数：Start_date 一个代表开始日期的日期。\n\t\tEnd_date 一个代表终止日期的日期。\n\t\tHolidays 不在工作日历中的一个或多个日期所构成的可选区域，例如：省/市/自治区和国家/地区的法定假日以及其他非法定假日。该列表可以是包含日期的单元格区域，或是表示日期的序列号的数组常量。\n实例：NETWORKDAYS("2012-10-1","2013-3-1") => 开始日期 (2012-10-1) 和终止日期 (2013-3-1) 之间工作日的天数。结果为 110\n\t\tNETWORKDAYS("2012-10-1","2013-3-1", "2012-11-22") => 开始日期 (2012-10-1) 和终止日期 (2013-3-1)之间工作日的天数，其中 2012-11-22 为非工作日。结果为 109' }, NOMINAL: { cn: '返回名义年利率', note: '用途： 基于给定的实际利率和年复利期数，返回名义年利率。\n语法： NOMINAL(effect_rate ， npery)\n参数： Effect_rate 为实际利率， Npery 为每年的复利期数。' }, 'NORM.DIST': { cn: '返回指定平均值和标准偏差的正态分布函数', note: '用途：返回指定平均值和标准偏差的正态分布函数。 此函数在统计方面应用范围广泛（包括假设检验）。\n语法：NORM.DIST(x,mean,standard_dev,cumulative)\n参数：X 需要计算其分布的数值。\n\t\tMean 分布的算术平均值。\n\t\tstandard_dev 分布的标准偏差。\n\t\tcumulative 决定函数形式的逻辑值。 如果 cumulative 为 TRUE，则 NORM.DIST 返回累积分布函数；如果为 FALSE，则返回概率密度函数。\n实例：NORM.DIST(42,40,1.5,TRUE) => 0.9087888' }, 'NORM.INV': { cn: '返回指定平均值和标准偏差的正态累积分布函数的反函数值', note: '用途：返回指定平均值和标准偏差的正态累积分布函数的反函数值。\n语法：NORM.INV(probability,mean,standard_dev)\n参数：Probability 对应于正态分布的概率。\n\t\tMean 分布的算术平均值。\n\t\tstandard_dev 分布的标准偏差。\n实例：NORM.INV(0.908789,40,1.5) => 42.000002' }, 'NORM.S.DIST': { cn: '返回标准正态分布函数（该分布的平均值为0，标准偏差为1）', note: '用途：返回标准正态分布函数（该分布的平均值为 0，标准偏差为 1）。可以使用此函数代替标准正态曲线面积表。\n语法：NORM.S.DIST(z,cumulative)\n参数：Z 需要计算其分布的数值。\n\t\tcumulative Cumulative 是决定函数形式的逻辑值。 如果 cumulative 为 TRUE，则 NORMS.DIST 返回累积分布函数；如果为 FALSE，则返回概率密度函数。\n实例：NORM.S.DIST(1.333333,TRUE) => 0.908788726' }, 'NORM.S.INV': { cn: '返回标准正态累积分布函数的反函数值', note: '用途：返回标准正态累积分布函数的反函数值。 该分布的平均值为 0，标准偏差为 1。\n语法：NORM.S.INV(probability)\n参数：Probability 对应于正态分布的概率。\n实例：NORM.S.INV(0.908789) => 1.3333347' }, NORMDIST: { cn: '返回普通累积分布', note: '用途： 返回给定平均值和标准偏差的正态分布的累积函数。\n语法： NORMDIST(x ， mean ， standard_dev ， cumulative)\n参数： X 为用于计算正态分布函数的区间点\n\t\tMean 是分布的算术平均值\n\t\tStandard_dev 是分布的标准方差\n\t\tCumulative 为一逻辑值，指明函数的形式。如果 cumulative 为 TRUE ，则 NORMDIST 函数返回累积分布函数 ;如果为 FALSE ，则返回概率密度函数。\n实例： 公式 “=NORMDIST(46 ， 35， 2.5， TRUE) ” 返回 0.999994583 。' }, NORMINV: { cn: '返回反普通累积分布', note: '用途：返回指定平均值和标准偏差的正态累积分布函数的反函数值。\n语法：NORMINV(probability,mean,standard_dev)\n参数：Probability 对应于正态分布的概率。\n\t\tMean 分布的算术平均值。\n\t\tstandard_dev 分布的标准偏差。\n实例：NORMINV(0.908789,40,1.5) => 42.000002' }, NORMSDIST: { cn: '返回标准普通累积分布', note: '用途： 返回标准正态分布的累积函数，该分布的平均值为 0，标准偏差为 1。\n语法： NORMSDIST(z)\n参数： Z 为需要计算其分布的数值。\n实例： 公式 “=NORMSDIST(1.5) ” 的计算结果为 0.933192771 。' }, NORMSINV: { cn: '返回反标准普通累积分布', note: '用途： 返回标准正态分布累积函数的逆函数。该分布的平均值为 0，标准偏差为 1。\n语法： NORMSINV(probability)\n参数： Probability 是正态分布的概率值。\n实例： 公式 “=NORMSINV(0.933192771) ” 返回 1.499997779( 即 1.5) 。' }, NOT: { cn: '反转参数的逻辑值', note: '用途：求出一个逻辑值或逻辑表达式的相反值。\n语法：NOT(logical)\n参数：Logical 是一个可以得出 TRUE 或 FALSE 结论的逻辑值或逻辑表达式。 如果逻辑值或表达式的结果为 FALSE ， 则 NOT 函数返回 TRUE; 如果逻辑值或表达式的结果为 TRUE ， 那么 NOT 函数返回的结果为 FALSE 。\n实例：NOT(6<8) => FALSE' }, NOW: { cn: '返回当前日期和时间的系列数', note: '用途：返回当前日期和时间的序列号。 \n语法：Now()\n参数：NOW 函数语法没有参数。\n实例：NOW()' }, NPER: { cn: '返回投资的期数', note: '用途： 基于固定利率及等额分期付款方式，返回某项投资 ( 或贷款 )的总期数。\n语法： NPER(rate ， pmt ， pv ， fv， type)\n参数： Rate 为各期利率， Pmt 为各期所应支付的金额， Pv 为现值 (本金 )， Fv 为未来值 (即最后一次付款后希望得到的现金余额 )， Type 可以指定各期的付款时间是在期初还是期末 (0 为期末， 1 为期初 )。' }, NPV: { cn: '基于一系列现金流和固定的各期贴现率，返回一项投资的净现值', note: '用途： 通过使用贴现率以及一系列未来支出 (负值 )和收入 (正值 )，返回一项投资的净现值。\n语法： NPV(rate ， value1 ， value2 ， ...)\n参数： Rate 为某一期间的贴现率， value1 ， value2 ， ...为 1 到 29 个参数，代表支出及收入。' }, OCT2BIN: { cn: '将八进制数转换为二进制数', note: '用途： 将八进制数转换为二进制数。\n语法： OCT2BIN(number ， places)\n参数： Number 是待转换的八进制数。 Places 是所要使用的字符数。' }, OCT2DEC: { cn: '将八进制数转换为十进制数', note: '用途： 将八进制数转换为十进制数。\n语法： OCT2DEC(number)\n参数： Number? 是待转换的八进制数。' }, OCT2HEX: { cn: '将八进制数转换为十六进制数', note: '用途： 将八进制数转换为十六进制数。\n语法： OCT2HEX(number ， places)\n参数： Number 是待转换的八进制数。 Places 是所要使用的字符数。' }, ODD: { cn: '将数取整至最接近的奇数整数', note: '用途：将一个正(负数)向上(向下)舍入为最接近的奇数。\n语法：ODD(number)\n参数：Number是待计算的一个数值。\n注意:参数number必须是一个数值参数，不论它的正负号如何，其结果均按远离0的方向舍入。如果number恰好是奇数，则保持原来的数值不变。\n实例：如果A1=31.5，则公式“=ODD(A1)”返回33;=ODD(3)返回3;=ODD(-26.38)返回–27。' }, OR: { cn: '如果任何参数为TRUE，则返回TRUE', note: '用途：如果 OR 函数的任意参数计算为 TRUE，则其返回 TRUE；如果其所有参数均计算机为 FALSE，则返回 FALSE。\n语法：OR(logical1, [logical2], ...)\n参数：Logical1 第一个想要测试且计算结果可为 TRUE 或 FALSE 的条件。\n\t\tLogical2, ... 其他想要测试且计算结果可为 TRUE 或 FALSE 的条件。\n实例：OR(A2>1,A2<100)' }, PDURATION: { cn: '返回投资到达指定值所需的期数', note: '用途：返回投资到达指定值所需的期数。\n语法：PDURATION(rate, pv, fv)\n参数：Rate 为每期利率。\n\t\tPv 为投资的现值。\n\t\tFv 为所需的投资未来值。\n实例：PDURATION(2.5%,2000,2200) => 3.86' }, PEARSON: { cn: '返回PEARSON乘积矩相关系数', note: '用途： 返回 Pearson( 皮尔生 ) 乘积矩相关系数 r，它是一个范围在 -1.0 到 1.0 之间 (包括 -1.0 和 1.0 在内 )的无量纲指数，反映了两个数据集合之间的线性相关程度。\n语法： PEARSON(array1 ， array2)\n参数： Array1 为自变量集合， Array2 为因变量集合。\n实例： 如果 A1=71 、 A2=83 、 A3=71 、 A4=49 、 A5=92 、 A6=88 ， B1=69 、 B2=80 、 B3=76 、 B4=40 、B5=90 、 B6=81 ，则公式 “=PEARSON(A1:A6 ， B1:B6) ”返回 0.96229628 。' }, PERCENTILEEXC: { cn: '返回区域中数值的第K个百分点的值，其中k为0到1之间的值，不包含0和1', note: '用途：返回区域中数值的第 K 个百分点的值，其中 k 为 0 到 1 之间的值，不包含 0 和 1。\n语法：PERCENTILEEXC(array,k)\n参数：Array 定义相对位置的数组或数据区域。\n\t\tK 0 到 1 之间的百分点值，不包含 0 和 1。\n实例：A2:A10 => 1, 2, 3, 4, 5, 6, 7, 8, 9\n\t\tPERCENTILEEXC(A2:A10, 0.25) => 当指定百分点的值位于数组中的两个值之间时，将插入值。结果为 2.5' }, PERCENTILEINC: { cn: '返回区域中数值的第K个百分点的值，其中k为0到1之间的值，包含0和1', note: '用途：返回区域中数值的第 K 个百分点的值，K 为 0 到 1 之间的百分点值，包含 0 和 1。\n语法：PERCENTILEINC(array,k)\n参数：Array 定义相对位置的数组或数据区域。\n\t\tK 0 到 1 之间的百分点值，包含 0 和 1。\n实例：(A2:A5 => 1,2,3,4\n\t\tPERCENTILE.INC(A2:A5,0.3) => 区域 A2:A5 中的数据在第 30 个百分点的值。结果为 1.9' }, PERCENTRANKEXC: { cn: '返回某个数值在一个数据集中的百分比（0到1，不包括0和1）排位', note: '用途：返回某个数值在一个数据集中的百分比（0 到 1，不包括 0 和 1）排位。\n语法：PERCENTRANKEXC(array,x,[significance])\n参数：Array 定义相对位置的数值数组或数值数据区域。\n\t\tX 需要得到其排位的值。\n\t\tsignificance 用于标识返回的百分比值的有效位数的值。 如果省略，则 PERCENTRANKEXC 使用 3 位小数 (0.xxx)。\n实例：A2:A10 => 1,2,3,6,6,6,7,8,9\n\t\tPERCENTRANKEXC(A2:A10,7) => 从 A2:A10 中包含的数组返回数值 7 的排位。结果为 0.7' }, PERCENTRANKINC: { cn: '将某个数值在数据集中的排位作为数据集的百分比值返回，此处的百分比值的范围为0到1（含0和1）', note: '用途：将某个数值在数据集中的排位作为数据集的百分比值返回，此处的百分比值的范围为 0 到 1（含 0 和 1）。此函数可用于计算值在数据集内的相对位置。 例如，可以使用 PERCENTRANKINC 计算能力测试得分在所有测试得分中的位置。\n语法：PERCENTRANKINC(array,x,[significance])\n参数：Array 定义相对位置的数值数组或数值数据区域。\n\t\tX 需要得到其排位的值。\n\t\tsignificance 用于标识返回的百分比值的有效位数的值。 如果省略，则 PERCENTRANKINC 使用 3 位小数 (0.xxx)。\n实例：A2:A11 => 13,12,11,8,4,3,2,1,1,1\n\t\tPERCENTRANK.INC(A2:A11,2) => 2 在区域 A2:A11 中的百分比排位（0.333，因为该数据集中小于 2 的值有 3 个，而大于 2 的值有 6 个，因此为 3/(3+6)=0.333）。' }, PERMUT: { cn: '返回对象给定数的排列数', note: '用途： 返回从给定数目的元素集合中选取的若干元素的排列数。\n语法： PERMUT(number ， number_chosen)\n参数： Number 为元素总数\n\t\tNumber_chosen 是每个排列中的元素数目。10\n实例： 如果某种彩票的号码有 9 个数，每个数的范围是从 0 到 9(包括 0 和 9)。则所有可能的排列数量用公式 “=PERMUT(10 ， 9)”计算，其结果为 3628800 。' }, PERMUTATIONA: { cn: '返回可从对象总数中选择的给定数目对象（含重复）的排列数', note: '用途：返回可从对象总数中选择的给定数目对象（含重复）的排列数。\n语法：PERMUTATIONA(number, number-chosen)\n参数：Number 表示对象总数的整数。\n\t\tNumber_chosen 表示每个排列中对象数目的整数。\n实例：PERMUTATIONA(3,2) => 假设组中有 3 个对象，即 [4,5,6]。 对其中的 2 个对象使用 PERMUTATIONA，那么共有 9 种数字排列方式（有重复）' }, PHI: { cn: '返回标准正态分布的密度函数值', note: '用途：返回标准正态分布的密度函数值。\n语法：PHI(x)\n参数：X 是所需的标准正态分布密度值。\n实例：PHI(0.75) => 0.301137432' }, PI: { cn: '返回PI值', note: '用途：返回圆周率π，精确到小数点后14位。\n语法：PI()\n参数：不需要\n实例：公式“=PI()”返回3.14159265358979。' }, PMT: { cn: '返回投资或贷款的每期付款额', note: '用途： 基于固定利率及等额分期付款方式，返回贷款的每期付款额。\n语法： PMT(rate ， nper， pv， fv， type)\n参数： Rate 贷款利率， Nper 该项贷款的付款总数， Pv 为现值 ( 也称为本金 ) ， Fv 为未来值 (或最后一次付款后希望得到的现金余额 )， Type 指定各期的付款时间是在期初还是期末 (1 为期初。 0 为期末 )。' }, 'POISSON.DIST': { cn: '返回泊松分布', note: '用途：返回泊松分布。 泊松分布的一个常见应用是预测特定时间内的事件数，例如 1 分钟内到达收费停车场的汽车数。\n语法：POISSON.DIST(x,mean,cumulative)\n参数：X 事件数。\n\t\tMean 期望值。\n\t\tcumulative 一逻辑值，确定所返回的概率分布的形式。 如果 cumulative 为 TRUE，则 POISSON.DIST 返回发生的随机事件数在零（含零）和 x（含 x）之间的累积泊松概率；如果为 FALSE，则 POISSON 返回发生的事件数正好是 x 的泊松概率密度函数。\n实例：POISSON.DIST(2,5,TRUE) => 0.124652' }, POISSONDIST: { cn: '返回泊松分布', note: '用途：返回泊松分布。 泊松分布的一个常见应用是预测特定时间内的事件数，例如 1 分钟内到达收费停车场的汽车数。\n语法：POISSONDIST(x,mean,cumulative)\n参数：X 事件数。\n\t\tMean 期望值。\n\t\tcumulative 一逻辑值，确定所返回的概率分布的形式。 如果 cumulative 为 TRUE，则 POISSONDIST 返回发生的随机事件数在零（含零）和 x（含 x）之间的累积泊松概率；如果为 FALSE，则 POISSON 返回发生的事件数正好是 x 的泊松概率密度函数。\n实例：POISSONDIST(2,5,TRUE) => 0.124652' }, POWER: { cn: '返回数的乘幂结果', note: '用途：返回给定数字的乘幂。\n语法：POWER(number，power)\n参数：其中Number为底数，Power为指数，均可以为任意实数。\n注意:可以用“^”运算符代替POWER函数执行乘幂运算，例如公式“=5^2”与“=POWER(5，2)”等价。\n实例：如果A1=25.37，则公式“=POWER(A1，7)”返回6764617901;=POWER(4，5/4)返回5.656854。' }, PPMT: { cn: '返回投资在某一给定期次内的本金偿还额', note: '用途： 基于固定利率及等额分期付款方式，返回投资在某一给定期间内的本金偿还额。\n语法： PPMT(rate ， per ， nper ， pv ， fv， type)\n参数： Rate 为各期利率， Per 用于计算其本金数额的期数 (介于 1 到 nper 之间 )， Nper 为总投资期 (该项投资的付款期总数 )， Pv 为现值 (也称为本金 )， Fv 为未来值， Type 指定各期的付款时间是在期初还是期末 (1 为期初。 0 为期末 )。' }, PROB: { cn: '返回区域中的值在两个限制之间的概率', note: '用途： 返回一概率事件组中落在指定区域内的事件所对应的概率之和。\n语法： PROB(x_range ， prob_range ， lower_limit ， upper_limit)\n参数： X_range 是具有各自相应概率值的 x 数值区域\n\t\tProb_range 是与 x_range 中的数值相对应的一组概率值\n\t\tLower_limit 是用于概率求和计算的数值下界\n\t\tUpper_limit 是用于概率求和计算的数值可选上界\n实例： 公式 “=PROB({0， 1， 2， 3}， {0.2 ， 0.3 ， 0.1 ， 0.4}， 2)”返回 0.1， =PROB({0 ， 1， 2， 3} ， {0.2 ，0.3 ， 0.1 ， 0.4} ， 1， 3)返回 0.8 。' }, PRODUCT: { cn: '将所有以参数形式给出的数字相乘', note: '用途：将所有数字形式给出的参数相乘，然后返回乘积值。\n语法：PRODUCT(number1，number2，...)\n参数：Number1，number2，...为1到30个需要相乘的数字参数。\n实例：如果单元格A1=24、A2=36、A3=80，则公式“=PRODUCT(A1:A3)”返回69120;=PRODUCT(12，26，39)返回12168。' }, PROPER: { cn: '将文本值中每个单词的首字母设置为大写', note: '用途：将文本字符串的首字母以及文字中任何非字母字符之后的任何其他字母转换成大写。 将其余字母转换为小写。\n语法：PROPER(text)\n参数：text 用引号括起来的文本、返回文本值的公式，或者对包含要进行部分大写转换文本的单元格的引用。\n实例：PROPER("this is a TITLE") => This Is A Title' }, PV: { cn: '返回投资的现值', note: '用途： 返回投资的现值 (即一系列未来付款的当前值的累积和 )，如借入方的借入款即为贷出方贷款的现值。\n语法： PV(rate ， nper ， pmt ， fv， type)\n参数： Rate 为各期利率， Nper 为总投资 (或贷款 )期数， Pmt 为各期所应支付的金额， Fv 为未来值， Type指定各期的付款时间是在期初还是期末 (1 为期初。 0 为期末 )。' }, 'QUARTILE.EXC': { cn: '基于0到1之间（不包括0和1）的百分点值返回数据集的四分位数', note: '用途：基于 0 到 1 之间（不包括 0 和 1）的百分点值返回数据集的四分位数。\n语法：QUARTILE.EXC(array,quart)\n参数：Array 要求得四分位数值的数组或数字型单元格区域。\n\t\tQuart 指定返回哪一个值。\n实例：QUARTILE.EXC(A2:A12,1)' }, 'QUARTILE.INC': { cn: '根据0到1之间的百分点值（包含0和1）返回数据集的四分位数', note: '用途：根据 0 到 1 之间的百分点值（包含 0 和 1）返回数据集的四分位数。四分位点通常用于销售和调查数据，以对总体进行分组。 例如，您可以使用 QUARTILE.INC 查找总体中前 25% 的收入值。\n语法：QUARTILE.INC(array,quart)\n参数：Array 要求得四分位数值的数组或数字型单元格区域。\n\t\tQuart 指定返回哪一个值。\n\t\t\tquart= 0 最小值\n\t\t\tquart= 1 第一个四分位数（第 25 个百分点值）\n\t\t\tquart= 2 中分位数（第 50 个百分点值）\n\t\t\tquart= 3 第三个四分位数（第 75 个百分点值）\n\t\t\tquart= 4 最大值\n实例：QUARTILE.INC(A2:A9,1)' }, QUARTILEEXC: { cn: '基于0到1之间（不包括0和1）的百分点值返回数据集的四分位数', note: '用途：基于 0 到 1 之间（不包括 0 和 1）的百分点值返回数据集的四分位数。\n语法：QUARTILEEXC(array,quart)\n参数：Array 要求得四分位数值的数组或数字型单元格区域。\n\t\tQuart 指定返回哪一个值。\n实例：QUARTILEEXC(A2:A12,1)' }, QUARTILEINC: { cn: '根据0到1之间的百分点值（包含0和1）返回数据集的四分位数', note: '用途：根据 0 到 1 之间的百分点值（包含 0 和 1）返回数据集的四分位数。四分位点通常用于销售和调查数据，以对总体进行分组。 例如，您可以使用 QUARTILEINC 查找总体中前 25% 的收入值。\n语法：QUARTILEINC(array,quart)\n参数：Array 要求得四分位数值的数组或数字型单元格区域。\n\t\tQuart 指定返回哪一个值。\n\t\t\tquart= 0 最小值\n\t\t\tquart= 1 第一个四分位数（第 25 个百分点值）\n\t\t\tquart= 2 中分位数（第 50 个百分点值）\n\t\t\tquart= 3 第三个四分位数（第 75 个百分点值）\n\t\t\tquart= 4 最大值\n实例：QUARTILEINC(A2:A9,1)' }, QUOTIENT: { cn: '返回商的整数部分', note: '用途：返回商的整数部分，即舍去商的小数部分。\n语法：QUOTIENT(numerator，denominator)\n参数：Numerator为被除数，Denominator为除数。\n注意:该函数只有加载“分析工具库”方可使用。\n实例：如果A1=86、A2=9，则公式“=QUOTIENT(A1，A2)”返回9;=QUOTIENT(-10，3)返回–3。' }, RADIANS: { cn: '将度转换为弧度', note: '用途：将一个表示角度的数值或参数转换为弧度。\n语法：RADIANS(angle)\n参数：Angle为需要转换成弧度的角度。\n实例：如果A1=90，则公式“=RADIANS(A1)”返回1.57，=RADIANS(360)返回6.28(均取两位小数)。' }, RAND: { cn: '返回0和1之间的随机数', note: '用途：返回一个大于等于0小于1的随机数，每次计算工作表(按F9键)将返回一个新的数值。\n语法：RAND()\n参数：不需要\n注意:如果要生成a，b之间的随机实数，可以使用公式“=RAND()*(b-a)+a”。如果在某一单元格内应用公式“=RAND()”，然后在编辑状态下按住F9键，将会产生一个变化的随机数。\n实例：公式“=RAND()*1000”返回一个大于等于0、小于1000的随机数。' }, RANDBETWEEN: { cn: '返回指定数之间的随机数', note: '用途：产生位于两个指定数值之间的一个随机数，每次重新计算工作表(按F9键)都将返回新的数值。\n语法：RANDBETWEEN(bottom，top)\n参数：Bottom是RANDBETWEEN函数可能返回的最小随机数，Top是RANDBETWEEN函数可能返回的最大随机数。\n注意:该函数只有在加载了“分析工具库”以后才能使用。\n实例：公式“=RANDBETWEEN(1000，9999)”将返回一个大于等于1000、小于等于9999的随机数。' }, 'RANK.AVG': { cn: '返回一列数字的数字排位', note: '用途：返回一列数字的数字排位：数字的排位是其大小与列表中其他值的比值；如果多个值具有相同的排位，则将返回平均排位。\n语法：RANK.AVG(number,ref,[order])\n参数：Number 要找到其排位的数字。\n\t\tRef 数字列表的数组，对数字列表的引用。 Ref 中的非数字值会被忽略。\n\t\tOrder 一个指定数字排位方式的数字。\n实例：RANK.AVG(34,B2:B8)' }, 'RANK.EQ': { cn: '返回一列数字的数字排位', note: '用途：返回一列数字的数字排位。 其大小与列表中其他值相关；如果多个值具有相同的排位，则返回该组值的最高排位。如果要对列表进行排序，则数字排位可作为其位置。\n语法：RANK.EQ(number,ref,[order])\n参数：Number 要找到其排位的数字。\n\t\tRef 数字列表的数组，对数字列表的引用。 Ref 中的非数字值会被忽略。\n\t\tOrder 一个指定数字排位方式的数字。\n实例：A2:A6 => 7, 3.5, 3.5, 1, 2\n\t\tRANK.EQ(A2,A2:A6,1) => 7 在区域 A2:A6 的表中的排位。 因为 Order 参数 (1) 是非 0 值，按照从小到大的顺序对列表进行排序。结果为 5' }, RANKAVG: { cn: '返回一列数字的数字排位', note: '用途：返回一列数字的数字排位：数字的排位是其大小与列表中其他值的比值；如果多个值具有相同的排位，则将返回平均排位。\n语法：RANKAVG(number,ref,[order])\n参数：Number 要找到其排位的数字。\n\t\tRef 数字列表的数组，对数字列表的引用。 Ref 中的非数字值会被忽略。\n\t\tOrder 一个指定数字排位方式的数字。\n实例：RANKAVG(34,B2:B8)' }, RANKEQ: { cn: '返回一列数字的数字排位', note: '用途：返回一列数字的数字排位。 其大小与列表中其他值相关；如果多个值具有相同的排位，则返回该组值的最高排位。如果要对列表进行排序，则数字排位可作为其位置。\n语法：RANKEQ(number,ref,[order])\n参数：Number 要找到其排位的数字。\n\t\tRef 数字列表的数组，对数字列表的引用。 Ref 中的非数字值会被忽略。\n\t\tOrder 一个指定数字排位方式的数字。\n实例：A2:A6 => 7, 3.5, 3.5, 1, 2\n\t\tRANKEQ(A2,A2:A6,1) => 7 在区域 A2:A6 的表中的排位。 因为 Order 参数 (1) 是非 0 值，按照从小到大的顺序对列表进行排序。结果为 5' }, RATE: { cn: '返回年金的各期利率', note: '用途： 返回年金的各期利率。函数 RATE 通过迭代法计算得出，并且可能无解或有多个解。\n语法： RATE(nper ， pmt ， pv ， fv， type ， guess)\n参数： Nper 为总投资期 (即该项投资的付款期总数 )， Pmt 为各期付款额， Pv 为现值 (本金 )， Fv 为未来值， Type 指定各期的付款时间是在期初还是期末 (1 为期初。 0 为期末 )。' }, REPLACE: { cn: '替换文本中的字符', note: '用途：根据指定的字符数，REPLACE 将部分文本字符串替换为不同的文本字符串。\n语法：REPLACE(old_text, start_num, num_chars, new_text)\n参数：old_text 要替换其部分字符的文本。\n\t\tstart_num old_text 中要替换为 new_text 的字符位置。\n\t\tnum_chars old_text 中希望 REPLACE 使用 new_text 来进行替换的字符数。\n\t\tnew_text 将替换 old_text 中字符的文本。\n实例：REPLACE("abcdefghijk",6,5,"*") => abcde*k' }, REPT: { cn: '按给定次数重复文本', note: '用途：将文本重复一定次数。 使用 REPT 来在单元格中填充文本字符串的大量实例。\n语法：REPT(text, number_times)\n参数：ext 需要重复显示的文本。\n\t\tNumber_times 用于指定文本重复次数的正数。\n实例：REPT("*-", 3) => "*-*-*-"' }, RIGHT: { cn: '返回文本值中最右边的字符', note: '用途：RIGHT 根据所指定的字符数返回文本字符串中最后一个或多个字符。\n语法：RIGHT(text,[num_chars])\n参数：文本 包含要提取字符的文本字符串。\n\t\tnum_chars 指定希望 RIGHT 提取的字符数。\n实例：RIGHT("Hello,world",5) => "world"' }, ROMAN: { cn: '将阿拉伯数字转换为文本形式的罗马数字', note: '用途：将阿拉伯数字转换为文本形式的罗马数字。\n语法：ROMAN(number，form)\n参数：Number为需要转换的阿拉伯数字。form则是一个数字，它指定要转换的罗马数字样式。可以从经典到简化，随着form值的增加趋于简单。\n实例：公式“=ROMAN(499，0)”返回“CDXCIX”;=ROMAN(499，1)返回“LDVLIV”。' }, ROUND: { cn: '将数取整至指定数', note: '用途：按指定位数四舍五入某个数字。\n语法：ROUND(number，num_digits)\n参数：Number是需要四舍五入的数字;Num_digits为指定的位数，Number按此位数进行处理。\n注意:如果num_digits大于0，则四舍五入到指定的小数位;如果num_digits等于0，则四舍五入到最接近的整数;如果num_digits小于0，则在小数点左侧按指定位数四舍五入。\n实例：如果A1=65.25，则公式“=ROUND(A1，1)”返回65.3;=ROUND(82.149，2)返回82.15;=ROUND(21.5，-1)返回20。' }, ROUNDDOWN: { cn: '将数向下*近0值取整', note: '用途：按绝对值减小的方向舍入某一数字。\n语法：ROUNDDOWN(number，num_digits)\n参数：Number是需要向下舍入的任意实数，Num_digits指定计算的小数位数。\n注意:ROUNDDOWN函数和ROUND函数的用途相似，不同之处是ROUNDDOWN函数总是向下舍入数字。\n实例：如果A1=65.251，则公式“=ROUNDDOWN(A1，0)”返回65;=ROUNDDOWN(A1，2)返回65.25;=ROUNDDOWN(3.14159，3)返回3.141;=ROUNDDOWN(-3.14159，1)返回-3.1;=ROUNDDOWN(31415.92654，-2)返回31400。' }, ROUNDUP: { cn: '将数向上远离0值取整', note: '用途：按绝对值增大的方向舍入一个数字。\n语法：ROUNDUP(number，num_digits)\n参数：Number为需要舍入的任意实数，Num_digits指定舍入的数字位数。\n注意:如果num_digits为0或省略，则将数字向上舍入到最接近的整数。如果num_digits小于0，则将数字向上舍入到小数点左边的相应位数。\n实例：如果A1=65.251，则公式“=ROUNDUP(A1，0)”返回66;=ROUNDUP(A1，1)返回66;=ROUNDUP(A1，2)返回65.26;=ROUNDUP(-3.14159，1)返回-3.2;=ROUNDUP(31415.92654，-2)返回31500。' }, ROW: { cn: '返回引用的行号', note: '用途：返回引用的行号。\n语法：ROW([reference])\n参数：Reference 需要得到其行号的单元格或单元格区域。\n\t\t\t如果省略 reference，则假定是对函数 ROW 所在单元格的引用。\n\t\t\t如果 reference 为一个单元格区域，并且 ROW 作为垂直数组输入，则 ROW 将以垂直数组的形式返回 reference 的行号。\n\t\t\tReference 不能引用多个区域。\n实例：ROW(C10) => 10' }, ROWS: { cn: '返回引用中的行数', note: '用途：返回引用或数组的行数。\n语法：ROWS(array)\n参数：Array 需要得到其行数的数组、数组公式或对单元格区域的引用。\n实例：ROWS(C1:E4) => 4' }, RRI: { cn: '返回投资增长的等效利率', note: '用途：返回投资增长的等效利率。\n语法：RRI(nper, pv, fv)\n参数：Nper 为投资的期数。\n\t\tPv 为投资的现值。\n\t\tFv 为投资的未来值。\n实例：RRI(96,10000,11000) => 0.0009933' }, RSQ: { cn: '返回PEARSON乘积力矩相关系数的平方', note: '用途： 返回给定数据点的 Pearson 乘积矩相关系数的平方。\n语法： RSQ(known_y ’s ， known_x’ s)\n参数： Known_y’s 为一个数组或数据区域\n\t\tKnown_x’s 也是一个数组或数据区域\n实例： 公式 “=RSQ({22， 23， 29， 19， 38， 27， 25} ， {16， 15， 19， 17， 15， 14 ， 34}) ”返回 0.013009334 。' }, SEARCH: { cn: '在其他文本值中查找文本值（不区分大小写）', note: '用途：可在第二个文本字符串中查找第一个文本字符串，并返回第一个文本字符串的起始位置的编号，该编号从第二个文本字符串的第一个字符算起。例如，若要查找字母“n”在单词“printer”中的位置，可以使用以下函数：=SEARCH("n","printer")此函数会返回 4，因为“n”是单词“printer”的第四个字符。也可以在一个单词中搜索另一个单词。例如，以下函数：=SEARCH("base","database")会返回 5，因为单词“base”是从单词“database”的第五个字符开始的。使用 SEARCH 和 SEARCHB 函数可以确定某个字符或文本字符串在另一个文本字符串中的位置，然后可使用 MID 和 MIDB 函数返回文本，或使用 REPLACE 和 REPLACEB 函数更改文本。\n语法：SEARCH(find_text,within_text,[start_num])\n参数：find_text 要查找的文本。\n\t\twithin_text 要在其中搜索 find_text 参数的值的文本。\n\t\tstart_num within_text 参数中从之开始搜索的字符编号。\n实例：SEARCH("e","Statements",6) => 7' }, SEC: { cn: '返回角度的正割值', note: '用途：返回角度的正割值。\n语法：SEC(number)\n参数：Number 为要获得其正割值的角度，以弧度表示。\n实例：SEC(45) => 返回 45 度角的正割值。结果为 1.90359' }, SECH: { cn: '返回角度的双曲正割值', note: '用途：返回角度的双曲正割值。\n语法：SECH(number)\n参数：Number 为对应所需双曲正割值的角度，以弧度表示。\n实例：SECH(45) => 返回 45 度角的双曲正割值。结果为 5.73E-20' }, SECOND: { cn: '将系列数转换为秒', note: '用途：返回时间值的秒数。 秒数是 0（零）到 59 范围内的整数。\n语法：SECOND(serial_number)\n参数：Serial_number 一个时间值，其中包含要查找的秒数。 时间值有多种输入方式：带引号的文本字符串（例如 "6:45 PM"）、十进制数（例如 0.78125 表示 6:45 PM）或其他公式或函数的结果（例如 TIMEVALUE("6:45 PM")）。\n实例：SECOND(0.1111) => 59' }, SERIESSUM: { cn: '返回基于公式的幂级数的和', note: '用途：返回幂级数的和。\n语法：SERIESSUM(x，n，m，coefficients)\n参数：X幂级数的输入值\n\t\tN为x的首项乘幂\n\t\tM为级数中每一项的乘幂n的步长增加值\n\t\tCoefficients为一系列与x各级乘幂相乘的系数\n\t\tCoefficients的值决定了幂级数的项数\n实例：如果单元格A1=65.25，则公式“=SERIESSUM(A1，3，2，6)”返回1666835.719。' }, SIGN: { cn: '返回数的正负号', note: '用途：返回数字的符号。正数返回1，零返回0，负数时返回-1。\n语法：SIGN(number)\n参数：Number是需要返回符号的任意实数。\n实例：如果A1=65.25，则公式“=SIGN(A1)”返回1;=SIGN(6-12)返回-1;=SIGN(9-9)返回0。' }, SIN: { cn: '返回给定角度的正弦', note: '用途：返回某一角度的正弦值。\n语法：SIN(number)\n参数：Number是待求正弦值的一个角度(采用弧度单位)，如果它的单位是度，则必须乘以PI()/180转换为弧度。\n实例：如果A1=60，则公式“=SIN(A1*PI()/180)”返回0.866，即60度角的正弦值。' }, SINH: { cn: '返回数的双曲正弦值', note: '用途：返回任意实数的双曲正弦值。\n语法：SINH(number)\n参数：Number为任意实数。\n实例：公式“=SINH(10)”返回11013.23287，=SINH(-6)返回-201.7131574。' }, SKEW: { cn: '返回分布的偏斜度', note: '用途： 返回一个分布的不对称度。它反映以平均值为中心的分布的不对称程度，正不对称度表示不对称边的分布更趋向正值。负不对称度表示不对称边的分布更趋向负值。\n语法： SKEW(number1 ， number2 ， ...)\n参数： Number1 ， number2... 是需要计算不对称度的 1 到 30 个参数。包括逗号分隔的数值、单一数组和名称等。\n实例： 公式 “=SKEW({22 ， 23， 29， 19， 38， 27， 25} ， {16 ， 15 ， 19， 17， 15， 14， 34}) ”返回 0.854631382 。' }, 'SKEW.P': { cn: '返回基于样本总体的分布不对称度', note: '用途：返回基于样本总体的分布不对称度：表明分布相对于平均值的不对称程度。\n语法：SKEW.P(number 1, [number 2],…)\n参数：Number 1, number 2,…    Number 1 是必选项，后续数字是可选项。Number 1、number 2、… 是数字，或包含数字的名称、数组或引用，您要以此函数获得其样本总体的分布不对称度。\n实例：A2:A11 => 3,4,5,2,3,4,5,6,2,7\n\t\tSKEW.P(A2:A11)' }, SKEWP: { cn: '返回基于样本总体的分布不对称度', note: '用途：返回基于样本总体的分布不对称度：表明分布相对于平均值的不对称程度。\n语法：SKEW.P(number 1, [number 2],…)\n参数：Number 1, number 2,…    Number 1 是必选项，后续数字是可选项。Number 1、number 2、… 是数字，或包含数字的名称、数组或引用，您要以此函数获得其样本总体的分布不对称度。\n实例：A2:A11 => 3,4,5,2,3,4,5,6,2,7\n\t\tSKEWP(A2:A11)' }, SLN: { cn: '返回一项资产每期的直线折旧费', note: '用途： 返回某项资产在一个期间中的线性折旧值。\n语法： SLN(cost ， salvage ， life)\n参数： Cost 为资产原值， Salvage 为资产在折旧期末的价值 (也称为资产残值 )， Life 为折旧期限 (有时也称作资产的使用寿命 )。' }, SLOPE: { cn: '返回线性回归直线的斜率', note: '用途： 返回经过给定数据点的线性回归拟合线方程的斜率 (它是直线上任意两点的垂直距离与水平距离的比值，也就是回归直线的变化率 )。\n语法： SLOPE(known_y ’s ， known_x’ s)\n参数： Known_y’s 为数字型因变量数组或单元格区域\n\t\tKnown_x’s 为自变量数据点集合。\n实例： 公式 “=SLOPE({22， 23 ， 29， 19， 38， 27， 25}， {16， 15 ， 19， 17， 15， 14， 34}) ”返回 -0.100680934 。' }, SMALL: { cn: '返回数据集中的第K个最小值', note: '用途： 返回数据集中第 k 个最小值，从而得到数据集中特定位置上的数值。\n语法： SMALL(array ， k)\n参数： Array 是需要找到第 k 个最小值的数组或数字型数据区域\n\t\tK 为返回的数据在数组或数据区域里的位置 (从小到大 )。\n实例： 如果如果 A1=78 、 A2=45 、 A3=90 、 A4=12 、 A5=85 ，则公式 “=SMALL(A1:A5 ， 3)”返回 78。' }, SQRT: { cn: '返回正平方根', note: '用途：返回某一正数的算术平方根。\n语法：SQRT(number)\n参数：Number为需要求平方根的一个正数。\n实例：如果A1=81，则公式“=SQRT(A1)”返回9;=SQRT(4+12)返回6。' }, SQRTPI: { cn: '返回某数与PI的乘积的平方根', note: '用途：返回一个正实数与π的乘积的平方根。\n语法：SQRTPI(number)\n参数：Number是用来与π相乘的正实数。\n注意:SQRTPI函数只有加载“分析工具库”以后方能使用。如果参数number<0，则函数SQRTPI返回错误值#NUM!。\n实例：公式“=SQRTPI(1)”返回1.772454，=SQRTPI(2)返回2.506628。' }, STANDARDIZE: { cn: '返回正态化数值', note: '用途： 返回以 mean 为平均值，以 standard-dev 为标准偏差的分布的正态化数值。\n语法： STANDARDIZE(x ， mean ， standard_dev)\n参数： X 为需要进行正态化的数值\n\t\tMean 分布的算术平均值\n\t\tStandard_dev 为分布的标准偏差。\n实例： 公式 “=STANDARDIZE(62 ， 60， 10)”返回 0.2 。' }, 'STDEV.P': { cn: '计算基于以参数形式给出的整个样本总体的标准偏差（忽略逻辑值和文本）', note: '用途：计算基于以参数形式给出的整个样本总体的标准偏差（忽略逻辑值和文本）。标准偏差可以测量值在平均值（中值）附近分布的范围大小。\n语法：STDEV.P(number1,[number2],...)\n参数：Number1 对应于总体的第一个数值参数。\n\t\tNumber2, ... 对应于总体的第 1 个之后数值参数。也可以用单一数组或对某个数组的引用来代替用逗号分隔的参数。\n实例：A3:A11 => 1345,1301,1368,1322,1310,1370,1318,1350,1303,1299\n\t\tSTDEV.P(A3:A12) => 假定只生产了 10 个工具的情况下，断裂强度的标准偏差。结果为 26.05455814' }, 'STDEV.S': { cn: '基于样本估算标准偏差（忽略样本中的逻辑值和文本）', note: '用途：基于样本估算标准偏差（忽略样本中的逻辑值和文本）。标准偏差可以测量值在平均值（中值）附近分布的范围大小。\n语法：STDEV.S(number1,[number2],...)\n参数：Number1 对应于总体样本的第一个数值参数。也可以用单一数组或对某个数组的引用来代替用逗号分隔的参数。\nNumber2, ... 对应于总体样本的第 1 个之后数值参数。也可以用单一数组或对某个数组的引用来代替用逗号分隔的参数。\n实例：A2:A11 => "强度",1345,1301,1368,1322,1310,1370,1318,1350,1303,1299\n\t\tSTDEV.S(A2:A11) => 断裂强度的标准偏差。结果为 27.46391572' }, STDEVA: { cn: '估计样本的标准偏差，包括数字、文本和逻辑值', note: '用途： 计算基于给定样本的标准偏差。它与 STDEV 函数的区别是文本值和逻辑值 (TRUE 或 FALSE) 也将参与计算。\n语法： STDEVA(value1 ， value2 ， ...)\n参数： value1 ， value2 ， ... 是作为总体样本的 1 到 30 个参数。可以使用逗号分隔参数的形式，也可以使用单一数组，即对数组单元格的引用。\n实例： 假设某次考试的部分成绩为 A1=78 、 A2=45 、 A3=90 、 A4=12 、 A5=85 ，则估算所有成绩标准偏差的公式为 “=STDEVA(A1:A5) ” ，其结果等于 33.00757489 。' }, STDEVP: { cn: '计算整个样本总体的标准偏差', note: '用途： 返回整个样本总体的标准偏差。它反映了样本总体相对于平均值 (mean) 的离散程度。\n语法： STDEVP(number1 ， number2 ， ...)\n参数： Number1 ， number2 ， ... 为对应于样本总体的 1 到 30 个参数。可以使用逗号分隔参数的形式，也可以使用单一数组，即对数组单元格的引用。12\n注意： STDEVP 函数在计算过程中忽略逻辑值 (TRUE 或 FALSE) 和文本。如果逻辑值和文本不能忽略，应当使用 STDEVPA 函数。同时 STDEVP 函数假设其参数为整个样本总体。如果数据代表样本总体中的样本，应使用函数 STDEV来计算标准偏差。当样本数较多时， STDEV 和 STDEVP 函数的计算结果相差很小。\n实例： 如果某次考试只有 5 名学生参加，成绩为 A1=78 、 A2=45 、 A3=90 、 A4=12 、 A5=85 ，则计算的所有成绩的标准偏差公式为 “=STDEVP(A1:A5) ” ，返回的结果等于 29.52287249 。' }, STDEVPA: { cn: '计算整个样本总体的标准偏差，包括数字、文本和逻辑值', note: '用途： 计算样本总体的标准偏差。它与 STDEVP 函数的区别是文本值和逻辑值 (TRUE 或 FALSE) 参与计算。\n语法： STDEVPA(value1 ， value2 ， ...)\n参数： value1 ， value2 ， ... 作为样本总体的 1 到 30 个参数。可以使用逗号分隔参数的形式，也可以使用单一数组 (即对数组单元格的引用 )。\n注意： STDEVPA 函数假设参数为样本总体。如果数据代表的是总体的部分样本，则必须使用 STDEV 函数来估算标准偏差。\n实例： 如果某次考试只有 5 名学生参加，成绩为 A1=78 、 A2=45 、 A3=90 、 A4=12 、 A5=85 ，则计算的所有成绩的标准偏差公式为 “=STDEVP(A1:A5) ” ，返回的结果等于 29.52287249 。' }, STDEVS: { cn: '基于样本估算标准偏差（忽略样本中的逻辑值和文本）', note: '用途：基于样本估算标准偏差（忽略样本中的逻辑值和文本）。标准偏差可以测量值在平均值（中值）附近分布的范围大小。\n语法：STDEVS(number1,[number2],...)\n参数：Number1 对应于总体样本的第一个数值参数。也可以用单一数组或对某个数组的引用来代替用逗号分隔的参数。\nNumber2, ... 对应于总体样本的第 1 个之后数值参数。也可以用单一数组或对某个数组的引用来代替用逗号分隔的参数。\n实例：A2:A11 => "强度",1345,1301,1368,1322,1310,1370,1318,1350,1303,1299\n\t\tSTDEVS(A2:A11) => 断裂强度的标准偏差。结果为 27.46391572' }, STEYX: { cn: '返回通过线性回归法计算Y预测值时所产生的标准误差', note: '用途： 返回通过线性回归法计算 y 预测值时所产生的标准误差。标准误差用来度量根据单个 x 变量计算出的 y 预测值的误差量。\n语法： STEYX(known_y ’s ， known_x’ s)\n参数： Known_y’s 为因变量数据点数组或区域\n\t\tKnown_x’s 为自变量数据点数组或区域。\n实例： 公式 “=STEYX({22 ， 13， 29， 19， 18， 17， 15} ， {16， 25， 11， 17， 25， 14， 17}) ”返回 4.251584755 。' }, SUBSTITUTE: { cn: '在文本串中使用新文本替换旧文本', note: '用途：在文本字符串中用 new_text 替换 old_text。 如果需要在某一文本字符串中替换指定的文本，请使用函数 SUBSTITUTE；如果需要在某一文本字符串中替换特定位置处的任意文本，请使用函数 REPLACE。\n语法：SUBSTITUTE(text, old_text, new_text, [instance_num])\n参数：text 需要替换其中字符的文本，或对含有文本（需要替换其中字符）的单元格的引用。\n\t\told_text 需要替换的文本。\n\t\tnew_text 用于替换 old_text 的文本。\n\t\tinstance_num 指定要用 new_text 替换 old_text 的事件。 如果指定了 instance_num，则只有满足要求的 old_text 被替换。 否则，文本中出现的所有 old_text 都会更改为 new_text。\n实例：SUBSTITUTE("销售数据", "销售额", "成本") => "成本数据"' }, SUBTOTAL: { cn: '返回清单或数据库中的分类汇总', note: '用途：返回数据清单或数据库中的分类汇总。如果用户使用“数据”菜单中的“分类汇总”命令创建了分类汇总数据清单，即可编辑SUBTOTAL函数对其进行修改。\n语法：SUBTOTAL(function_num，ref1，ref2…)\n参数：Function_num为1到11之间的自然数，用来指定分类汇总计算使用的函数(1是AVERAGE;2是COUNT;3是COUNTA;4是MAX;5是MIN;6是PRODUCT;7是STDEV;8是STDEVP;9是SUM;10是VAR;11是VARP)\n\t\tRef1、ref2…则是需要分类汇总的1到29个区域或引用。\n实例：如果A1=1、A2=2、A3=3，则公式“=SUBTOTAL(9，A1:A3)”将使用SUM函数对“A1:A3”区域进行分类汇总，其结果为6。' }, SUM: { cn: '对区域中的数值求和', note: '用途：返回某一单元格区域中所有数字之和。\n语法：SUM(number1，number2，...)。\n参数：Number1，number2，...为1到30个需要求和的数值(包括逻辑值及文本表达式)、区域或引用。\n注意:参数表中的数字、逻辑值及数字的文本表达式可以参与计算，其中逻辑值被转换为1、文本被转换为数字。如果参数为数组或引用，只有其中的数字将被计算，数组或引用中的空白单元格、逻辑值、文本或错误值将被忽略。\n实例：如果A1=1、A2=2、A3=3，则公式“=SUM(A1:A3)”返回6;=SUM("3"，2，TRUE)返回6，因为"3"被转换成数字3，而逻辑值TRUE被转换成数字1。' }, SUMIF: { cn: '对区域中符合指定条件的数值求和', note: '用途：根据指定条件对若干单元格、区域或引用求和。\n语法：SUMIF(range，criteria，sum_range)\n参数：Range为用于条件判断的单元格区域\n\t\tCriteria是由数字、逻辑表达式等组成的判定条件\n\t\tSum_range为需要求和的单元格、区域或引用\n实例：某单位统计工资报表中职称为“中级”的员工工资总额。假设工资总额存放在工作表的F列，员工职称存放在工作表B列。则公式为“=SUMIF(B1:B1000，"中级"，F1:F1000)”，其中“B1:B1000”为提供逻辑判断依据的单元格区域，"中级"为判断条件，就是仅仅统计B1:B1000区域中职称为“中级”的单元格，F1:F1000为实际求和的单元格区域。' }, SUMIFS: { cn: '用于计算其满足多个条件的全部参数的总量', note: '用途：用于计算其满足多个条件的全部参数的总量。 例如，可以使用 SUMIFS 计算一个国家/地区内 (1) 邮政编码为同一个且 (2) 利润超过了特定美元值的零售商的总量。\n语法：SUMIFS(sum_range, criteria_range1, criteria1, [criteria_range2, criteria2], ...)\n参数：Sum_range 要求和的单元格区域。\n\t\tCriteria_range1 使用 Criteria1 测试的区域。\n\t\t\tCriteria_range1 和 Criteria1 设置用于搜索某个区域是否符合特定条件的搜索对。 一旦在该区域中找到了项，将计算 Sum_range 中的相应值的和。\n\t\tCriteria1 定义将计算 Criteria_range1 中的哪些单元格的和的条件。 例如，可以将条件输入为 32、">32"、B4、"苹果" 或 "32"。\n\t\tCriteria_range2, criteria2, …  附加的区域及其关联条件。\n实例：A2:A9 => 5,4,15,3,22,12,10,33\n\t\tB2:B9 => 苹果,苹果,香梨,香梨,香蕉,香蕉,胡萝卜,胡萝卜\n\t\tC2:C9 => 卢宁,Sarah,卢宁,Sarah,卢宁,Sarah,卢宁,Sarah\n\t\tSUMIFS(A2:A9, B2:B9, "=香*", C2:C9, "卢宁") => 计算以“香”开头并由“卢宁”售出的产品的总量。 该公式在 Criteria1 中使用通配符 *（即 "=香*"）在 Criterial_range1 B2:B9 中查找匹配的产品名，在 Criterial_range2 C2:C9 中查找姓名"卢宁"。 然后计算 Sum_range A2:A9 中同时满足这两个条件的单元格的总量。 结果为 20。' }, SUMPRODUCT: { cn: '返回相对应的数组部分的乘积和', note: '用途：在给定的几组数组中，将数组间对应的元素相乘，并返回乘积之和。\n语法：SUMPRODUCT(array1，array2，array3，...)\n参数：Array1，array2，array3，...为2至30个数组，其相应元素需要进行相乘并求和。\n实例：公式“=SUMPRODUCT({3，4;8，6;1，9}，{2，7;6，7;5，3})”的计算结果是156。' }, SUMSQ: { cn: '返回参数的平方和', note: '用途：返回所有参数的平方和。\n语法：SUMSQ(number1，number2，...)\n参数：Number1，number2，...为1到30个需要求平方和的参数，它可以是数值、区域、引用或数组。\n实例：如果A1=1、A2=2、A3=3，则公式“=SUMSQ(A1:A3)返回14(即12+22+32=14)。' }, SUMX2MY2: { cn: '返回两个数组中相对应值的平方差之和', note: '用途：返回两数组中对应数值的平方差之和。\n语法：SUMX2MY2(array_x，array_y)\n参数：Array_x为第一个数组或数值区域\n\t\tArray_y为第二个数组或数值区域。\n实例：如果A1=1、A2=2、A3=3、B1=4、B2=5、B3=6，则公式“=SUMX2MY2(A1:A3，B1:B3)”返回-63。' }, SUMX2PY2: { cn: '返回两个数组中相对应值的平方和之和', note: '用途：返回两数组中对应数值的平方和的总和，此类运算在统计中经常遇到。\n语法：SUMX2PY2(array_x，array_y)\n参数：Array_x为第一个数组或数值区域\n\t\tArray_y为第二个数组或数值区域。\n实例：如果A1=1、A2=2、A3=3、B1=4、B2=5、B3=6，则公式“=SUMX2PY2(A1:A3，B1:B3)”返回91。' }, SUMXMY2: { cn: '返回两个数组中相对应值差的平方之和', note: '用途：返回两数组中对应数值之差的平方和。\n语法：SUMXMY2(array_x，array_y)\n参数：Array_x为第一个数组或数值区域\n\t\tArray_y为第二个数组或数值区域。\n实例：如果A1=1、A2=2、A3=3、B1=4、B2=5、B3=6，则公式“=SUMXMY2(A1:A3，B1:B3)”返回27。' }, SWITCH: { cn: '根据值列表计算一个值（称为表达式），并返回与第一个匹配值对应的结果', note: '用途：根据值列表计算一个值（称为表达式），并返回与第一个匹配值对应的结果。如果不匹配，则可能返回可选默认值。\n语法：SWITCH(表达式, value1, result1, [default 或 value2, result2],…[default 或 value3, result3])\n参数：表达式 是将与 value1…value126 比较的值（如数字、日期或文某些本）。\n\t\tvalue1… ValueN 的值将与表达式比较。\n\t\tresult1… ResultN 是在对应 valueN 参数与表达式匹配时返回的值。ResultN 必须为每个对应 valueN 参数提供。\n\t\tdefault Default 是当在 valueN 表达式中没有找到匹配值时要返回的值。当没有对应的 resultN 表达式时，则标识为 Default 参数（请参阅示例）。Default 必须是函数中的最后一个参数。\n实例：SWITCH(A3,1,"星期天",2,"星期一",3,"星期二")' }, SYD: { cn: '返回某项资产按年限总和折旧法计算的某期的折旧值', note: '用途： 返回某项资产按年限总和折旧法计算的指定期间的折旧值。\n语法： SYD(cost ， salvage ， life， per)\n参数： Cost 为资产原值， Salvage 为资产在折旧期末的价值 (也称为资产残值 )， Life 为折旧期限 (有时也称作资产的使用寿命 )， Per 为期间 (单位与 life 相同 )。' }, T: { cn: '将参数转换为文本', note: '用途：返回值引用的文字。\n语法：T(value)\n参数：value 要测试的值。\n实例：T("黄昏") => "黄昏"\n\t\tT(12) => ""\n\t\tT(TRUE) => ""' }, 'T.DIST': { cn: '返回学生t分布的百分点（概率）', note: '用途：返回学生 t 分布的百分点（概率），其中，数字值 (x) 是用来计算百分点的 t 的计算值。 t 分布用于小型样本数据集的假设检验。 可以使用该函数代替 t 分布的临界值表。\n语法：T.DIST(x,deg_freedom,tails)\n参数：X 需要计算分布的数值。\n\t\tDeg_freedom 一个表示自由度数的整数。\n\t\ttails 指定返回的分布函数是单尾分布还是双尾分布。 如果 Tails = 1，则 TDIST 返回单尾分布。 如果Tails = 2，则 TDIST 返回双尾分布。\n实例：T.DIST(1.959999998,60,2) => 5.46%' }, 'T.DIST.2T': { cn: '返回学生的双尾t分布', note: '用途：返回学生的双尾 t 分布。学生的 t 分布用于小样本数据集的假设检验。 可以使用该函数代替 t 分布的临界值表。\n语法：T.DIST.2T(x,deg_freedom)\n参数：X 需要计算分布的数值。\n\t\tDeg_freedom 一个表示自由度数的整数。' }, 'T.DIST.RT': { cn: '返回学生的右尾t分布', note: '用途：返回学生的右尾 t 分布。t 分布用于小型样本数据集的假设检验。 可以使用该函数代替 t 分布的临界值表。\n语法：T.DIST.RT(x,deg_freedom)\n参数：X 需要计算分布的数值。\n\t\tDeg_freedom 一个表示自由度数的整数。\n实例：T.DIST.RT(1.959999998,60) => 0.027322' }, 'T.INV': { cn: '返回学生的t分布的左尾反函数', note: '用途：返回学生的 t 分布的左尾反函数。\n语法：T.INV(probability,deg_freedom)\n参数：Probability 与学生的 t 分布相关的概率。\n\t\tDeg_freedom 代表分布的自由度数。\n实例：T.INV(0.75,2) => 0.8164966' }, 'T.INV.2T': { cn: '返回学生t分布的双尾反函数', note: '用途：返回学生 t 分布的双尾反函数。\n语法：T.INV.2T(probability,deg_freedom)\n参数：Probability 与学生的 t 分布相关的概率。\n\t\tDeg_freedom 代表分布的自由度数。\n实例：T.INV.2T(0.546449,60) => 0.606533' }, TAN: { cn: '返回数的正切', note: '用途：返回某一角度的正切值。\n语法：TAN(number)\n参数：Number为需要求正切的角度，以弧度表示。如果参数的单位是度，可以乘以P1()/180转换为弧度。\n实例：如果A1=60，则公式“=TAN(A1*PI()/180)”返回1.732050808;TAN(1)返回1.557407725。' }, TANH: { cn: '返回数的双曲正切值', note: '用途：返回任意实数的双曲正切值。\n语法：TANH(number)\n参数：Number为任意实数。\n实例：如果A1=60，则公式“=TANH(A1)”返回1，=TANH(0.5)返回0.462117。' }, TBILLEQ: { cn: '返回国库券的债券等效收益率', note: '用途： 返回国库券的等效收益率。\n语法： TBILLEQ(settlement ， maturity ， discount)\n参数： Settlement 为国库券的成交日 (即在发行日之后，国库券卖给购买者的日期 )， Maturity 为国库券的到期日， Discount 为国库券的贴现率。' }, TBILLPRICE: { cn: '返回面值0的国库券的价格', note: '用途： 返回面值 $100 的国库券的价格。\n语法： TBILLPRICE(settlement ， maturity ， discount)\n参数： Settlement 为国库券的成交日， Maturity 为国库券的到期日， Discount 为国库券的贴现率。' }, TBILLYIELD: { cn: '返回国库券的收益率', note: '用途： 返回国库券的收益率。\n语法： TBILLYIELD(settlement ， maturity ， pr)\n参数： Settlement 为国库券的成交日， Maturity 为国库券的到期日， Pr 为面值 $100 的国库券的价格。' }, TDIST: { cn: '返回学生氏-T分布', note: '用途： 返回学生氏 t-分布的百分点 (概率 )， t 分布中的数值 (x) 是 t 的计算值 (将计算其百分点 ) 。 t 分布用于小样本数据集合的假设检验，使用此函数可以代替 t 分布的临界值表。\n语法： TDIST(x ， degrees_freedom ， tails)\n参数： X 为需要计算分布的数字\n\t\tDegrees_freedom 为表示自由度的整数\n\t\tTails 指明返回的分布函数是单尾分布还是双尾分布。如果 tails=1 ，函数 TDIST 返回单尾分布。如果 tails=2 ，函数 TDIST 返回双尾分布。\n实例： 公式 “=TDIST(60， 2， 1)”返回 0.000138831 。' }, TDIST2T: { cn: '返回学生的双尾t分布', note: '用途：返回学生的双尾 t 分布。学生的 t 分布用于小样本数据集的假设检验。 可以使用该函数代替 t 分布的临界值表。\n语法：TDIST2T(x,deg_freedom)\n参数：X 需要计算分布的数值。\n\t\tDeg_freedom 一个表示自由度数的整数。\n实例：TDIST2T(1.959999998,60) => 5.46%' }, TDISTRT: { cn: '返回学生的右尾t分布', note: '用途：返回学生的右尾 t 分布。t 分布用于小型样本数据集的假设检验。 可以使用该函数代替 t 分布的临界值表。\n语法：TDISTRT(x,deg_freedom)\n参数：X 需要计算分布的数值。\n\t\tDeg_freedom 一个表示自由度数的整数。\n实例：TDISTRT(1.959999998,60) => 0.027322' }, TEXT: { cn: '设置数字的格式并将其转换为文本', note: "用途：将数值转换为按指定数字格式表示的文本。\n语法：TEXT(value, format_text)\n参数：value 要转换为文本的数值。\n\t\tformat_text 一个文本字符串，定义要应用于所提供值的格式。\n实例：TEXT(1234.567,\"$#,##0.00\") => $1,234.57\n\t\tTEXT(TODAY()，\"MM/DD/YY\") => 06/08/18\n\t\tTEXT(TODAY(),\"DDDD\") => Friday\n\t\tTEXT(NOW(),\"H:MM AM/PM\") => 9:38 AM\n\t\tTEXT(0.285,\"0.0%\") => 28.5%\n\t\tTEXT(4.34 ,\"# ?/?\") => 4 1/3\n\t\tTEXT(12200000,\"0.00E+00\") => 1.22E+07\n\t\tTEXT(1234567898,\"[<=9999999]###-####;(###) ###-####\") => (123) 456-7898\n\t\tTEXT(1234,\"0000000\") => 0001234\n\t\tTEXT(123456,\"##0° 00' 00''\") => 12° 34' 56''" }, TIME: { cn: '返回特定时间的系列数', note: '用途：返回特定时间的十进制数字。 由 TIME 返回的十进制数字是一个范围在 0（零）到 0.99988426 之间的值，表示 0:00:00 (12:00:00 AM) 到 23:59:59 (11:59:59 P.M.) 之间的时间。\n语法：TIME(hour, minute, second)\n参数：hour 代表小时。 任何大于 23 的值都会除以 24，余数将作为小时值。 例如，TIME(27,0,0) = TIME(3,0,0) = .125 或 3:00 AM。\n\t\tminute 代表分钟。 任何大于 59 的值将转换为小时和分钟。 例如，TIME(0,750,0) = TIME(12,30,0) = .520833 或 12:30 PM。\n\t\tsecond 代表秒。 任何大于 59 的值将转换为小时、分钟和秒。 例如，TIME(0,0,2000) = TIME(0,33,22) = .023148 或 12:33:20 AM\n实例：TIME(12,0,0) => 0.5' }, TIMEVALUE: { cn: '将文本格式的时间转换为系列数', note: '用途：返回由文本字符串表示的时间的十进制数字。 十进制数字是一个范围在 0（零）到 0.99988426 之间的值，表示 0:00:00 (12:00:00 AM) 到 23:59:59 (11:59:59 P.M.) 之间的时间。\n语法：TIMEVALUE(time_text)\n参数：time_text 一个文本字符串，代表以任一 Microsoft Excel 时间格式表示的时间（例如，代表时间的具有引号的文本字符串 "6:45 PM" 和 "18:45"）。\n实例：TIMEVALUE("2:24 AM") => 0.10' }, TINV: { cn: '返回反学生氏-T分布', note: '用途： 返回作为概率和自由度函数的学生氏 t 分布的 t 值。\n语法： TINV(probability ， degrees_freedom)\n参数： Probability 为对应于双尾学生氏 -t 分布的概率\n\t\tDegrees_freedom 为分布的自由度。\n实例： 公式 “=TINV(0.5， 60)”返回 0.678600713 。' }, TINV2T: { cn: '返回学生t分布的双尾反函数', note: '用途：返回学生 t 分布的双尾反函数。\n语法：TINV2T(probability,deg_freedom)\n参数：Probability 与学生的 t 分布相关的概率。\n\t\tDeg_freedom 代表分布的自由度数。\n实例：TINV2T(0.546449,60) => 0.606533' }, TODAY: { cn: '返回当天日期的系列数', note: '用途：返回当前日期的序列号。 \n语法：TODAY()\n参数：TODAY 函数语法没有参数。\n实例：TODAY()+5 => 2018/6/13' }, TRANSPOSE: { cn: '返回数组的转置', note: '用途：返回区域的转置 (所谓转置就是将数组的第一行作为新数组的第一列，数组的第二行作为新数组的第二列，以此类推 )。\n语法：TRANSPOSE(array) \n参数：Array 是需要转置的数组或工作表中的单元格区域。\n实例：' }, TREND: { cn: '返回沿线性趋势的值', note: '用途： 返回一条线性回归拟合线的一组纵坐标值 (y 值 )。 即找到适合给定的数组 known_y’s 和 known_x’s的直线 (用最小二乘法 )，并返回指定数组 new_x’s值在直线上对应的 y 值。\n语法： TREND(known_y ’s ， known_x’s， new_x’s， const)\n参数： Known_y’s 为已知关系 y=mx+b 中的 y 值集合\n\t\tKnown_x’s 为已知关系 y=mx+b 中可选的 x 值的集合\n\t\tNew_x’s 为需要函数 TREND 返回对应 y 值的新 x 值\n\t\tConst 为逻辑值指明是否强制常数项 b 为 0。' }, TRIM: { cn: '删除文本中的空格', note: '用途：除了单词之间的单个空格之外，移除文本中的所有空格。 \n语法：TRIM(text)\n参数：Text 要从中移除空格的文本。\n实例：TRIM(" First Quarter Earnings ") => "First Quarter Earnings"' }, TRIMMEAN: { cn: '返回数据集的内部平均值', note: '用途： 返回数据集的内部平均值。 TRIMMEAN 函数先从数据集的头部和尾部除去一定百分比的数据点，然后再求平均值。当希望在分析中剔除一部分数据的计算时，可以使用此函数。\n语法： TRIMMEAN(array ， percent)\n参数： Array 为需要进行筛选并求平均值的数组或数据区域\n\t\tPercent 为计算时所要除去的数据点的比例。如果 percent=0.2 ，则在 20 个数据中除去 4 个，即头部除去 2 个尾部除去 2 个。如果 percent=0.1 ， 30 个数据点的 10% 等于 3 个数据点。函数 TRIMMEAN 将对称地在数据集的头部和尾部各除去一个数据。\n实例： 如果 A1=78 、 A2=45 、 A3=90 、 A4=12 、 A5=85 ，则公式 “=TRI MMEAN(A1:A5 ， 0.1) ”返回 62。' }, TRUE: { cn: '返回逻辑值TRUE', note: '用途：返回逻辑值 TRUE。\n语法：TRUE\n参数：TRUE 函数语法没有参数。' }, TRUNC: { cn: '将数截尾为整数', note: '用途：将数字的小数部分截去，返回整数。\n语法：TRUNC(number，num_digits)\n参数：Number是需要截去小数部分的数字\n\t\tNum_digits则指定保留小数的精度(几位小数)。\n注意: TRUNC函数可以按需要截取数字的小数部分，而INT函数则将数字向下舍入到最接近的整数。INT和TRUNC函数在处理负数时有所不同:TRUNC(-4.3)返回-4，而INT(-4.3)返回-5。\n实例：如果A1=78.652，则公式“=TRUNC(A1，1)”返回78.6，=TRUNC(A1，2)返回78.65，=TRUNC(-8.963，2)返回–8.96。' }, UNICHAR: { cn: '返回给定数值引用的Unicode字符', note: '用途：返回给定数值引用的 Unicode 字符。\n语法：UNICHAR(number)\n参数：Number 为代表字符的 Unicode 数字。\n实例：UNICHAR(66) => "B"' }, UNICODE: { cn: '返回对应于文本的第一个字符的数字（代码点）', note: '用途：返回对应于文本的第一个字符的数字（代码点）。\n语法：Unicode(text)\n参数：Text 是要获得其 Unicode 值的字符。\n实例：UNICODE(" ") => 32' }, UPPER: { cn: '将文本转换为大写', note: '用途：将文本转换为大写字母。\n语法：UPPER(text)\n参数：Text 要转换为大写字母的文本。 文本可以是引用或文本字符串。\n实例：UPPER("total") => "TOTAL"' }, VALUE: { cn: '将文本参数转换为数字', note: '用途：将表示数字的文本字符串转换为数字。\n语法：VALUE(text)\n参数：text 用引号括起来的文本或包含要转换文本的单元格的引用。\n实例：VALUE("$1,000") => 1000' }, 'VAR.P': { cn: '计算基于整个样本总体的方差（忽略样本总体中的逻辑值和文本）', note: '用途：计算基于整个样本总体的方差（忽略样本总体中的逻辑值和文本）。\n语法：VAR.P(number1,[number2],...)\n参数：Number1 对应于总体的第一个数值参数。\n\t\tNumber2, ... 对应于总体的第一个之后的数值参数。\n实例：A2:A11 => 1345, 1301, 1368, 1322, 1310, 1370, 1318, 1350, 1303, 1299\n\t\tVAR.P(A2:A11) => 678.84\n\t\tVAR.S(A2:A11) => 754.27' }, 'VAR.S': { cn: '估算基于样本的方差（忽略样本中的逻辑值和文本）', note: '用途：估算基于样本的方差（忽略样本中的逻辑值和文本）。\n语法：VAR.S(number1,[number2],...)\n参数：Number1 对应于总体的第一个数值参数。\n\t\tNumber2, ... 对应于总体的第一个之后的数值参数。\n实例：A2:A11 => 1345, 1301, 1368, 1322, 1310, 1370, 1318, 1350, 1303, 1299\n\t\tVAR.S(A2:A11) => 754.27\n\t\tVAR.P(A2:A11) => 678.84' }, VARA: { cn: '估计样本的方差，包括数字、文本和逻辑值', note: '用途： 用来估算给定样本的方差。它与 VAR 函数的区别在于文本和逻辑值 (TRUE 和 FALSE) 也将参与计算。\n语法： VARA(value1 ， value2 ， ...)\n参数： value1 ， value2 ， ... 作为总体的一个样本的 1 到 30 个参数。\n实例： 假设抽取某次考试中的 5 个分数，并将其作为随机样本，用 VAR 函数估算成绩方差，样本值为A1=78 、 A2=45 、 A3=90 、 A4=12 、 A5=85 ，则公式 “=VARA(A1:A5， TRUE)” 返回 1491.766667 。' }, VARP: { cn: '计算整个样本总体的方差', note: '用途：计算基于整个样本总体的方差（忽略样本总体中的逻辑值和文本）。\n语法：VARP(number1,[number2],...)\n参数：Number1 对应于总体的第一个数值参数。\n\t\tNumber2, ... 对应于总体的第一个之后的数值参数。\n实例：A2:A11 => 1345, 1301, 1368, 1322, 1310, 1370, 1318, 1350, 1303, 1299\n\t\tVARP(A2:A11) => 678.84\n\t\tVARS(A2:A11) => 754.27' }, VARPA: { cn: '计算整个样本总体的方差，包括数字、文本和逻辑值', note: '用途： 计算样本总体的方差。 它与 VARP 函数的区别在于文本和逻辑值 (TRUE 和 FALSE) 也将参与计算。\n语法： VARPA(value1 ， value2 ， ...)\n参数： value1 ， value2 ， ... 作为样本总体的 1 到 30 个参数。\n实例： 如果某次补考只有 5 名学生参加，成绩为 A1=88 、 A2=55 、 A3=90 、 A4=72 、 A5=85 ，用 VARPA函数估算成绩方差，则公式 “=VARPA(A1:A5) ” 返回 214.5 。' }, VARS: { cn: '估算基于样本的方差（忽略样本中的逻辑值和文本）', note: '用途：估算基于样本的方差（忽略样本中的逻辑值和文本）。\n语法：VARS(number1,[number2],...)\n参数：Number1 对应于总体的第一个数值参数。\n\t\tNumber2, ... 对应于总体的第一个之后的数值参数。\n实例：A2:A11 => 1345, 1301, 1368, 1322, 1310, 1370, 1318, 1350, 1303, 1299\n\t\tVARS(A2:A11) => 754.27\n\t\tVARP(A2:A11) => 678.84' }, WEEKDAY: { cn: '将系列数转换为星期', note: '用途：返回对应于某个日期的一周中的第几天。 默认情况下，天数是 1（星期日）到 7（星期六）范围内的整数。\n语法：WEEKDAY(serial_number,[return_type])\n参数：Serial_number 一个序列号，代表尝试查找的那一天的日期。 应使用 DATE 函数输入日期，或者将日期作为其他公式或函数的结果输入。 例如，使用函数 DATE(2008,5,23) 输入 2008 年 5 月 23 日。 如果日期以文本形式输入，则会出现问题。\n\t\tReturn_type 用于确定返回值类型的数字。\n\t\t\treturn_type = 1或省略 数字 1（星期日）到 7（星期六）。\n\t\t\treturn_type = 2 数字 1（星期一）到 7（星期日）。\n\t\t\treturn_type = 3 数字 0（星期一）到 6（星期日）。\n\t\t\treturn_type = 11 数字 1（星期一）到 7（星期日）。\n\t\t\treturn_type = 12 数字 1（星期二）到数字 7（星期一）。\n\t\t\treturn_type = 13 数字 1（星期三）到数字 7（星期二）。\n\t\t\treturn_type = 14 数字 1（星期四）到数字 7（星期三）。\n\t\t\treturn_type = 15 数字 1（星期五）到数字 7（星期四）。\n\t\t\treturn_type = 16 数字 1（星期六）到数字 7（星期五）。\n\t\t\treturn_type = 17 数字 1（星期日）到 7（星期六）。\n实例：WEEKDAY(NOW()) => 6' }, WEEKNUM: { cn: '返回特定日期的周数', note: '用途：返回特定日期的周数。 例如，包含 1 月 1 日的周为该年的第 1 周，其编号为第 1 周。\n语法：WEEKNUM(serial_number,[return_type])\n参数：Serial_number 代表一周中的日期。 应使用 DATE 函数输入日期，或者将日期作为其他公式或函数的结果输入。 例如，使用函数 DATE(2008,5,23) 输入 2008 年 5 月 23 日。 如果日期以文本形式输入，则会出现问题。\n\t\tReturn_type 一数字，确定星期从哪一天开始。 默认值为 1。\n\t\t\treturn_type = 1或省略 星期从星期日开始。\n\t\t\treturn_type = 2 星期从星期一开始。\n\t\t\treturn_type = 11 星期从星期一开始。\n\t\t\treturn_type = 12 星期从星期二开始。\n\t\t\treturn_type = 13 星期从星期三开始。\n\t\t\treturn_type = 14 星期从星期四开始。\n\t\t\treturn_type = 15 星期从星期五开始。\n\t\t\treturn_type = 16 星期从星期六开始。\n\t\t\treturn_type = 17 星期从星期日开始。\n实例：WEEKNUM(NOW()) => 23' }, 'WEIBULL.DIST': { cn: '返回Weibull分布', note: '用途：返回 Weibull 分布。 可以将该分布用于可靠性分析，例如计算设备出现故障的平均时间。\n语法：WEIBULL.DIST(x,alpha,beta,cumulative)\n参数：X 用来计算函数的值。\n\t\tAlpha 分布参数。\n\t\tBeta 分布参数。\n\t\tcumulative 确定函数的形式。\n实例：WEIBULL.DIST(105,20,100,TRUE) => 0.929581' }, WEIBULLDIST: { cn: '返回Weibull分布', note: '用途：返回 Weibull 分布。 可以将该分布用于可靠性分析，例如计算设备出现故障的平均时间。\n语法：WEIBULLDIST(x,alpha,beta,cumulative)\n参数：X 用来计算函数的值。\n\t\tAlpha 分布参数。\n\t\tBeta 分布参数。\n\t\tcumulative 确定函数的形式。\n实例：WEIBULLDIST(105,20,100,TRUE) => 0.929581' }, WORKDAY: { cn: '返回指定工作日数之前或之后某日期的系列数', note: '用途：返回在某日期（起始日期）之前或之后、与该日期相隔指定工作日的某一日期的日期值。 工作日不包括周末和专门指定的假日。 在计算发票到期日、预期交货时间或工作天数时，可以使用函数 WORKDAY 来扣除周末或假日。\n语法：WORKDAY(start_date, days, [holidays])\n参数：Start_date 一个代表开始日期的日期。\n\t\tDays start_date 之前或之后不含周末及节假日的天数。 Days 为正值将生成未来日期；为负值生成过去日期。\n\t\tHolidays 一个可选列表，其中包含需要从工作日历中排除的一个或多个日期，例如各种省/市/自治区和国家/地区的法定假日及非法定假日。该列表可以是包含日期的单元格区域，也可以是由代表日期的序列号所构成的数组常量。\n实例：WORKDAY("2008-10-1",151) => 2009-4-30' }, XIRR: { cn: '返回一组不定期发生的现金流的内部收益率', note: '用途：返回一组不一定定期发生的现金流的内部收益率。 若要计算一组定期现金流的内部收益率，请使用函数 IRR。\n语法：XIRR(values, dates, [guess])\n参数：values 与 dates 中的支付时间相对应的一系列现金流。 首期支付是可选的，并与投资开始时的成本或支付有关。 如果第一个值是成本或支付，则它必须是负值。 所有后续支付都基于 365 天/年贴现。 值系列中必须至少包含一个正值和一个负值。\n\t\tdates 与现金流支付相对应的支付日期表。 日期可按任何顺序排列。 应使用 DATE 函数输入日期，或者将日期作为其他公式或函数的结果输入。 例如，使用函数 DATE(2008,5,23) 输入 2008 年 5 月 23 日。 如果日期以文本形式输入，则会出现问题 。\n\t\tGuess 对函数 XIRR 计算结果的估计值。\n实例：A3:A7 => -10000, 2750, 4250, 3250, 2750\n\t\tB3:B7 => 2008-1-1, 2008-3-1, 2008-10-30, 2009-2-15, 2009-4-1\n\t\tXIRR(A3:A7, B3:B7, 0.1)' }, XNPV: { cn: '返回一组不定期发生的现金流的净现值', note: '用途： 返回一组现金流的净现值，这些现金流不一定定期发生。若要计算一组定期现金流的净现值，可\n以使用函数 NPV 。\n语法： XNPV(rate ， values ， dates)\n参数： Rate 应用于现金流的贴现率， values 是与 dates 中的支付时间相对应的一系列现金流转， Dates与现金流支付相对应的支付日期表。' }, XOR: { cn: '返回所有参数的逻辑异或', note: '用途：返回所有参数的逻辑异或。\n语法：XOR(logical1, [logical2],…)\n参数：Logical1、logical2 等    Logical 1 是必需的，后续逻辑值是可选的。您要检验条件，可为 TRUE 或 FALSE，且可为逻辑值、数组或引用。\n实例：XOR(3>0,2<9) => TRUE' }, YEAR: { cn: '将系列数转换为年', note: '用途：返回对应于某个日期的年份。 Year 作为 1900 - 9999 之间的整数返回。\n语法：YEAR(serial_number)\n参数：Serial_number 要查找的年份的日期。 应使用 DATE 函数输入日期，或者将日期作为其他公式或函数的结果输入。 例如，使用函数 DATE(2008,5,23) 输入 2008 年 5 月 23 日。 如果日期以文本形式输入，则会出现问题。\n实例：YEAR(NOW()) => 2018' }, YEARFRAC: { cn: '返回代表START_DATE（开始日期）和END_DATE（结束日期）之间天数的以年为单位的分数', note: '用途：YEARFRAC计算代表整个 （ start_date和end_date） 的两个日期之间的天数的年分数。例如，您可以使用YEARFRAC识别整个年的好处或义务要分配给特定术语的比例。\n语法：YEARFRAC(start_date, end_date, [basis])\n参数：Start_date 一个代表开始日期的日期。\n\t\tEnd_date 一个代表终止日期的日期。\n\t\tBasis 要使用的日计数基准类型。\n\t\t\tbasis = 0或省略 US (NASD) 30/360\n\t\t\tbasis = 1 实际/实际\n\t\t\tbasis = 2 实际/360\n\t\t\tbasis = 3 实际/365\n\t\t\tbasis = 4 欧洲 30/360\n实例：YEARFRAC("2012/1/1","2012/7/30") => 2012-1-1 和 2012-7-30 之间的天数占全年的百分比，结果为 0.58055556' }, VLOOKUP: { cn: '查找数组的第一列并移过行，然后返回单元格的值', note: '用途：查找数组的第一列并移过行，然后返回单元格的值\n语法：VLOOKUP(lookup_value, table_array, col_index_num, range_lookup)\n参数：lookup_value 要查找的值，为数值、引用或文本字符串\n\t\ttable_array 要查找的区域，如 A1:C5\n\t\tcol_index_num 返回数据在查找区域的第几列数，为正整数。如col_index_num 为 1 时，返回 table_array 第一列的数值\n\t\trange_lookup 模糊匹配/精确匹配，TRUE/FALSE\n实例：YEARFRAC("2012/1/1","2012/7/30") => 2012-1-1 和 2012-7-30 之间的天数占全年的百分比，结果为 0.58055556' }, ISERROR: { cn: '返回一个Boolean值，该值是否表达式为错误值', note: '用途：返回一个Boolean值，该值是否表达式为错误值\n语法：ISERROR(expression)\n参数：expression 表达式参数可以是任何有效的表达式\n实例：ISERROR(SUM(A1:B2))\n' },
};
