// 使用了不正确的区域运算符或不正确的单元格引用（例如：引用的单元格区域的交集为空）。
exports.nil = new Error('#NULL!');
// 在包含有除法的公式中，除数引用了零值单元格或空单元格，或者直接使用了零值等。
exports.div0 = new Error('#DIV/0!');
// 函数参数的数值类型不正确，文本类型的数据参与了数值运算，或输入数组公式时忘记按Ctrl+Shift+Enter键。
exports.value = new Error('#VALUE!');
// 使用了无效的单元格引用，公式引用的单元格被删除或者被错误覆盖。
exports.ref = new Error('#REF!');
// 公式中使用了Excel无法识别的文本，例如函数的名称拼写错误，使用了没有被定义的区域或单元格名称，引用文本时没有加引号等。
exports.name = new Error('#NAME?');
// 公式中的数字出现类型错误的问题，函数中使用了无法接受的参数。
exports.num = new Error('#NUM!');
// 引用的数据有遗漏，找不到匹配的值。例如：在使用查找功能函数VLOOKUP时，缺少查询区域。
exports.na = new Error('#N/A');
exports.error = new Error('#ERROR!');
exports.data = new Error('#GETTING_DATA');
