/* eslint-disable no-use-before-define */
/* eslint-disable no-undef */
/* jshint esversion: 6 */
import webWorkObj from '../webworker/webWorker';

function uuID() {
  return 'xxxxxxxxxxxx4xxxyxxxxxxxxxxxxxxx'.replace(/[xy]/g, (c) => {
    const r = Math.random() * 16|0; 
    const v = c === 'x' ? r : (r&0x3|0x8);
    return v.toString(16);
  });
}
function getFullNum(num) {
  // 处理非数字
  if (isNaN(num)) {
    return num;
  }
  // 处理不需要转换的数字
  const str = '' + num;
  if(!/e/i.test(str)){return num;};

  // 先获取到精确的小数位
  let fixed = (''+num).match(/\d+$/)[0];

  // 拿到保留指定的小数
  return new Number(num).toFixed(fixed);
}

/**
 * 防抖函数
 * @param method 事件触发的操作
 * @param delay 多少毫秒内连续触发事件，不会执行
 * @returns {Function}
 */
let timer = null;
function debounce(method, delay) {
  return function () {
    let self = this;
    let args = arguments;
    timer && clearTimeout(timer);
    timer = setTimeout(function () {
        method.apply(self,args);
    }, delay);
  };
}

function workerCalUtil(dataProxy, callback) {
  const worker = webWorkObj.createWebWorker('./js/workerCalc.js', {}, (error, event) => {
    const { data } = event;
    dataProxy.data.cachePosition = data.result || [];
    dataProxy.data.totalHeightValue = data.totalHeight;
    dataProxy.data.totalWidthValue = data.totalWidth;
    if (typeof callback === 'function') {
      callback(data.totalHeight);
    }
    dataProxy.calculateAddRowAndColPosition();
    // console.timeEnd('webworker');
  });
  const { rows, cols, settings } = dataProxy.data;
  const { streamCharts } = settings;
  const [fri] = dataProxy.data.freeze;
  if (worker) {
    worker.postMessage({
      actionApi: 'calcYValue',
      row: rows._,
      defaultRowHeight: rows.height,
      rowLength: rows.len,
      cols,
      fri, // 冻结窗口起始行
      streamCharts,
    });
  }
}

function setSessionStorage(obj) {
  if (!obj) {
    return false;
  }
  let { content, key } = obj;
  if (typeof obj === 'object') {
    content = JSON.stringify(content);
  }
  sessionStorage.setItem(key, content);
  return true;
}

function getSesstionStorage(key) {
  if (!key) {
    return null;
  }
  return sessionStorage.getItem(key);
}

export default {
  getFullNum,
  debounce,
  workerCalUtil,
  uuID,
  setSessionStorage,
  getSesstionStorage,
};
