import { CellRange } from './cell_range';
// operator: all|eq|neq|gt|gte|lt|lte|in|be
// value:
//   in => []
//   be => [min, max]
class Filter {
  constructor(ri, ci, operator, value, isSelectAll, order, searchKey) {
    this.ri = ri;
    this.ci = ci;
    this.operator = operator;
    this.value = value;
    this.isSelectAll = isSelectAll;
    this.order = order;
    this.searchKey = searchKey;
  }

  set(operator, value, isSelectAll, order, searchKey) {
    this.operator = operator;
    this.value = value;
    this.isSelectAll = isSelectAll;
    this.order = order;
    this.searchKey = searchKey;
  }

  includes(v) {
    const { operator, value } = this;
    if (operator === 'all') {
      return true;
    }
    if (operator === 'in') {
      return value.includes(v);
    }
    return false;
  }

  vlength() {
    const { operator, value } = this;
    if (operator === 'in') {
      return value.length;
    }
    return 0;
  }

  getData() {
    const {
      ri, ci, value, isSelectAll, order, searchKey,
    } = this;
    return {
      ri, ci, value, isSelectAll, order, searchKey,
    };
  }
}

class Sort {
  constructor(ri, ci, order) {
    this.ri = ri;
    this.ci = ci;
    this.order = order;
  }

  asc() {
    return this.order === 'asc';
  }

  desc() {
    return this.order === 'desc';
  }
}

export default class AutoFilter {
  constructor() {
    this.ref = [];
    this.filters = [];
    this.sort = null;
  }

  generate(settings = {}, colLen = 0) {
    const { streamCharts, autoFilterRef } = settings;
    if (autoFilterRef) {
      this.ref = autoFilterRef;
    } else if (streamCharts && streamCharts.length > 0) { // linjian 如果外界没有传入指定的筛选范围，可以根据流表生成
      streamCharts.forEach((value) => {
        let { finalFirstRows } = value;
        finalFirstRows = finalFirstRows === 0 ? finalFirstRows : finalFirstRows - 1;
        const cellRange = new CellRange(finalFirstRows, 0, finalFirstRows, colLen - 1);
        this.ref.push(cellRange.toString());
      });
    }
  }

  setData({ ref, filters, sort }) {
    if (ref != null) {
      this.ref = ref;
      this.fitlers = filters.map(it => new Filter(it.ri, it.ci, it.operator, it.value));
      if (sort) {
        this.sort = new Sort(sort.ri, sort.ci, sort.order);
      }
    }
  }

  getData() {
    if (this.active()) {
      const { ref, filters, sort } = this;
      return { ref, filters: filters.map(it => it.getData()), sort };
    }
    return {};
  }

  /**
   * @description: 生成自定义的数据结构
   * @param {type} null
   * @return: conditions = [{
   *  selectAll: true,
   *  sort: 'asc',
   *  values:[]
   * }
   * @Author: linjian
   * @Date: 2019-08-19 16:41:40
   */
  getCustomerFilterData() {
    const conditions = this.filters.map(it => it.getData());
    return conditions;
  }

  addFilter(ri, ci, operator, value, isSelectAll, order, searchKey) {
    const filter = this.getFilter(ri, ci);
    if (filter == null) {
      this.filters.push(new Filter(ri, ci, operator, value, isSelectAll, order, searchKey));
    } else {
      filter.set(operator, value, isSelectAll, order, searchKey);
    }
  }

  setSort(ri, ci, order) {
    this.sort = order ? new Sort(ri, ci, order) : null;
  }

  includes(ri, ci) {
    if (this.active()) {
      return this.hrange().some(({
        sri, sci, eri, eci,
      }) => ri >= sri && ri <= eri && ci >= sci && ci <= eci);
    }
    return false;
  }

  getSort(ri, ci) {
    const { sort } = this;
    if (sort && sort.ci === ci && sort.ri === ri) {
      return sort;
    }
    return null;
  }

  getFilter(ri, ci) {
    const { filters } = this;
    for (let i = 0; i < filters.length; i += 1) {
      if (filters[i].ci === ci && filters[i].ri === ri) {
        return filters[i];
      }
    }
    return null;
  }

  filteredRows(getCell) {
    // const ary = [];
    // let lastri = 0;
    const rset = new Set();
    const fset = new Set();
    if (this.active()) {
      const { sri, eri } = this.range();
      const { filters } = this;
      for (let ri = sri + 1; ri <= eri; ri += 1) {
        for (let i = 0; i < filters.length; i += 1) {
          const filter = filters[i];
          const cell = getCell(ri, filter.ci);
          const ctext = cell ? cell.text : '';
          if (!filter.includes(ctext)) {
            rset.add(ri);
            break;
          } else {
            fset.add(ri);
          }
        }
      }
    }
    return { rset, fset };
  }

  items(ri, ci, getCell) {
    const m = {};
    if (this.active()) {
      const { sri, eri } = this.range({ ri, ci });
      for (let i = sri + 1; i <= eri; i += 1) {
        const cell = getCell(i, ci);
        if (cell !== null && !/^\s*$/.test(cell.text)) {
          const key = cell.text;
          const cnt = (m[key] || 0) + 1;
          m[key] = cnt;
        } else {
          m[''] = (m[''] || 0) + 1;
        }
      }
    }
    return m;
  }

  range(item) {
    return CellRange.valueOf(item);
  }

  hrange() {
    const result = [];
    this.ref.forEach((item) => {
      const r = this.range(item);
      r.eri = r.sri;
      result.push(r);
    });
    return result;
  }

  clear() {
    this.ref = [];
    this.filters = [];
    this.sort = null;
  }

  active() {
    return this.ref.length !== 0;
  }
}
