/* global window document */
function dpr() {
  return window.devicePixelRatio || 1;
}

function thinLineWidth() {
  return dpr() - 0.5;
}

function npx(px) {
  return parseInt(px * dpr(), 10);
}

function npxLine(px) {
  const n = npx(px);
  return n > 0 ? n - 0.5 : 0.5;
}

class DrawBox {
  constructor(x, y, w, h, padding = 0, mergeRealHeight) {
    this.x = x;
    this.y = y;
    this.width = w;
    this.height = h;
    this.padding = padding;
    this.mergeRealHeight = mergeRealHeight;
    this.bgcolor = '#ffffff';
    // border: [width, style, color]
    this.borderTop = null;
    this.borderRight = null;
    this.borderBottom = null;
    this.borderLeft = null;
    this.doubleBottomBorder = null; // 双底边框
    this.slash = null; // 从左到右斜线
  }

  setBorders({
    top, bottom, left, right, dBottom, slash
  }) {
    if (top) this.borderTop = top;
    if (right) this.borderRight = right;
    if (bottom) this.borderBottom = bottom;
    if (dBottom) this.doubleBottomBorder = dBottom;
    if (slash) this.slash = slash;
    if (left) this.borderLeft = left;
  }

  innerWidth() {
    return this.width - (this.padding * 2);
  }

  innerHeight() {
    return this.height - (this.padding * 2);
  }

  textx(align) {
    const { width, padding } = this;
    let { x } = this;
    if (align === 'left') {
      x += padding;
    } else if (align === 'center') {
      x += width / 2;
    } else if (align === 'right') {
      x += width - padding;
    }
    return x;
  }

  texty(align, hoffset) {
    const { height } = this;
    let { y } = this;
    if (align === 'top') {
      // linjian 原来是用padding处理，由于当高度设置过低的时候，会出现文字会被截一半情况
      // y += padding;
      y += 2;
    } else if (align === 'middle') {
      y = y + height / 2 - hoffset + 1;
    } else if (align === 'bottom') {
      // 原来是用padding处理，由于当高度设置过低的时候，会出现文字会被截一半情况
      // y += height - hoffset * 2 - padding;
      y += height - hoffset * 2 - 2;
    }
    return y;
  }

  topxys() {
    const { x, y, width } = this;
    return [[x, y], [x + width, y]];
  }

  rightxys() {
    const {
      x, y, width, height, mergeRealHeight,
    } = this;
    // linjian 这里添加合并单元格处理，当合并单元格一部分处于屏幕可见区域上，另一部分不在
    if (height !== mergeRealHeight) {
      return [[x + width, y], [x + width, y + mergeRealHeight]];
    }
    return [[x + width, y], [x + width, y + height]];
  }

  bottomxys() {
    const {
      x, y, width, height,
    } = this;
    return [[x, y + height], [x + width, y + height]];
  }

  leftxys() {
    const {
      x, y, height, mergeRealHeight,
    } = this;
    // linjian 这里添加合并单元格处理，当合并单元格一部分处于屏幕可见区域上，另一部分不在
    if (height !== mergeRealHeight) {
      return [[x, y], [x, y + mergeRealHeight]];
    }
    return [[x, y], [x, y + height]];
  }

  // 画双底边框
  drawDoubleBottomBorderxys() {
    const { x, y, width, height } = this;
    const lineWidth = npxLine(1);
    return [
      [[x, y + height - lineWidth], [x + width, y + height - lineWidth]],
      [[x, y + height], [x + width, y + height]]
    ];
  }

  // 斜线
  drawSlashxys() {
    const { x, y, width, height } = this;
    return [[x, y], [x + width, y + height]];
  }
}

function drawFontLine(type, tx, ty, align, valign, blheight, blwidth) {
  const floffset = { x: 0, y: 0 };
  if (type === 'underline') {
    if (valign === 'bottom') {
      floffset.y = 0;
    } else if (valign === 'top') {
      floffset.y = -(blheight + 2);
    } else {
      floffset.y = -blheight / 2;
    }
  } else if (type === 'strike') {
    if (valign === 'bottom') {
      floffset.y = blheight / 2;
    } else if (valign === 'top') {
      floffset.y = -((blheight / 2) + 2);
    }
  }

  if (align === 'center') {
    floffset.x = blwidth / 2;
  } else if (align === 'right') {
    floffset.x = blwidth;
  }
  this.line(
    [tx - floffset.x, ty - floffset.y],
    [tx - floffset.x + blwidth, ty - floffset.y],
  );
}

class Draw {
  constructor(el, width, height) {
    this.el = el;
    this.ctx = el.getContext('2d');
    this.resize(width, height);
    this.ctx.scale(dpr(), dpr());
  }

  resize(width, height) {
    // console.log('dpr:', dpr);
    this.el.style.width = `${width}px`;
    this.el.style.height = `${height}px`;
    this.el.width = npx(width);
    this.el.height = npx(height);
  }

  clear() {
    const { width, height } = this.el;
    this.ctx.clearRect(0, 0, width, height);
    return this;
  }

  attr(options) {
    Object.assign(this.ctx, options);
    return this;
  }

  save() {
    this.ctx.save();
    this.ctx.beginPath();
    return this;
  }

  restore() {
    this.ctx.restore();
    return this;
  }

  beginPath() {
    this.ctx.beginPath();
    return this;
  }

  translate(x, y) {
    this.ctx.translate(npx(x), npx(y));
    return this;
  }

  clearRect(x, y, w, h) {
    this.ctx.clearRect(x, y, w, h);
    return this;
  }

  fillRect(x, y, w, h) {
    this.ctx.fillRect(npx(x) - 0.5, npx(y) - 0.5, npx(w), npx(h));
    return this;
  }

  fillText(text, x, y) {
    this.ctx.fillText(text, npx(x), npx(y));
    return this;
  }

  /*
    txt: render text
    box: DrawBox
    attr: {
      align: left | center | right
      valign: top | middle | bottom
      color: '#333333',
      strike: false,
      font: {
        name: 'Arial',
        size: 14,
        bold: false,
        italic: false,
      }
    }
    textWrap: text wrapping
  */
  text(txt, box, attr = {}, textWrap = true) {
    const { ctx } = this;
    const {
      align, valign, font, color, strike, underline,
    } = attr;
    const tx = box.textx(align);
    ctx.save();
    ctx.beginPath();
    this.attr({
      textAlign: align,
      textBaseline: valign,
      font: `${font.italic ? 'italic' : ''} ${font.bold ? 'bold' : ''} ${npx(font.size)}px ${font.name}`,
      fillStyle: color,
      strokeStyle: color,
    });
    const txtWidth = ctx.measureText(txt).width;
    let hoffset = 0;
    const isIncludeLineFeed = txt.toString().includes('\n');
    if (textWrap || isIncludeLineFeed) {
      const n = Math.ceil(txtWidth / box.innerWidth());
      hoffset = ((n - 1) * font.size) / 2;
    }
    let ty = box.texty(valign, hoffset);
    // console.log('tx: ', tx, ', ty:', ty);
    const innerWidth = box.innerWidth();
    if ((textWrap && txtWidth > innerWidth) || isIncludeLineFeed) {
      const textLine = { len: 0, start: 0 };
      for (let i = 0; i < txt.length; i += 1) {
        const txtfragment = txt[i];
        if (textLine.len >= innerWidth || txtfragment === '\n') {
          this.fillText(txt.substring(textLine.start, i), tx, ty);
          if (strike) {
            drawFontLine.call(this, 'strike', tx, ty, align, valign, font.size, textLine.len);
          }
          if (underline) {
            drawFontLine.call(this, 'underline', tx, ty, align, valign, font.size, textLine.len);
          }
          ty += font.size + 2;
          textLine.len = 0;
          // linjian 遇到换行符，则要把换行符去掉
          if (txtfragment === '\n') {
            i++;
          }
          textLine.start = i;
        }
        textLine.len += ctx.measureText(txtfragment).width;
      }
      if (textLine.len > 0) {
        this.fillText(txt.substring(textLine.start), tx, ty);
        if (strike) {
          drawFontLine.call(this, 'strike', tx, ty, align, valign, font.size, textLine.len);
        }
        if (underline) {
          drawFontLine.call(this, 'underline', tx, ty, align, valign, font.size, textLine.len);
        }
      }
    } else {
      this.fillText(txt, tx, ty);
      if (strike) {
        drawFontLine.call(this, 'strike', tx, ty, align, valign, font.size, txtWidth);
      }
      if (underline) {
        drawFontLine.call(this, 'underline', tx, ty, align, valign, font.size, txtWidth);
      }
    }
    ctx.restore();
    return this;
  }

  border(style, color) {
    const { ctx } = this;
    ctx.lineWidth = thinLineWidth();
    ctx.strokeStyle = color;
    // console.log('style:', style);
    if (style === 'medium') {
      ctx.lineWidth = npx(2) - 0.5;
    } else if (style === 'thick') {
      ctx.lineWidth = npx(3);
    } else if (style === 'dashed') {
      ctx.setLineDash([npx(3), npx(2)]);
    } else if (style === 'dotted') {
      ctx.setLineDash([npx(1), npx(1)]);
    } else if (style === 'double') {
      ctx.setLineDash([npx(2), 0]);
    }
    return this;
  }

  line(...xys) {
    const { ctx } = this;
    if (xys.length > 1) {
      const [x, y] = xys[0];
      ctx.moveTo(npxLine(x), npxLine(y));
      for (let i = 1; i < xys.length; i += 1) {
        const [x1, y1] = xys[i];
        ctx.lineTo(npxLine(x1), npxLine(y1));
      }
      ctx.stroke();
    }
    return this;
  }

  // 画双边框（方向取决于传入的参数 linesArr）
  drawDoubleBottomBorder(color, linesArr) {
    const { ctx } = this;
    ctx.strokeStyle = color;
    
    for (let i = 0; i < linesArr.length; i++) {
      const line = linesArr[i];
      const [start, end] = line;
      ctx.beginPath();
      ctx.moveTo(npxLine(start[0]), npxLine(start[1]));
      ctx.lineTo(npxLine(end[0]), npxLine(end[1]));
      ctx.stroke();
    }
    return this;
  }

  // 画斜线（从左上到右下）
  drawSlash(color, poles) {
    const { ctx } = this;
    ctx.strokeStyle = color;
    const [start, end] = poles;
    ctx.beginPath();
    ctx.moveTo(npxLine(start[0]), npxLine(start[1]));
    ctx.lineTo(npxLine(end[0]), npxLine(end[1]));
    ctx.stroke();
    return this;
  }


  // 画边框
  strokeBorders(box) {
    const { ctx } = this;
    ctx.save();
    ctx.beginPath();
    // border
    const {
      borderTop, borderRight, borderBottom, borderLeft,
      doubleBottomBorder, slash,
    } = box;
    if (borderTop) {
      this.border(...borderTop);
      // console.log('box.topxys:', box.topxys());
      this.line(...box.topxys());
    }
    if (borderRight) {
      this.border(...borderRight);
      this.line(...box.rightxys());
    }
    if (borderBottom) {
      this.border(...borderBottom);
      this.line(...box.bottomxys());
    }
    if (doubleBottomBorder) {
      this.drawDoubleBottomBorder(doubleBottomBorder.color, box.drawDoubleBottomBorderxys());
    }
    if (slash) { // TODO:
      this.drawSlash(slash[1], box.drawSlashxys());
    }
    if (borderLeft) {
      this.border(...borderLeft);
      this.line(...box.leftxys());
    }
    ctx.restore();
  }

  customerError(box) {
    const { ctx } = this;
    const { x, y, width } = box;
    const sx = x + width - 1;
    ctx.save();
    ctx.beginPath();
    ctx.moveTo(npx(sx - 12), npx(y - 1));
    ctx.lineTo(npx(sx), npx(y - 1));
    ctx.lineTo(npx(sx), npx(y + 12));
    ctx.closePath();
    ctx.fillStyle = 'rgba(255, 0, 0, .65)';
    ctx.fill();
    ctx.restore();
  }

  dropdown(box, isHasFilter) {
    const { ctx } = this;
    const {
      x, y, width, height,
    } = box;
    const sx = x + width - 15;
    const sy = y + height - 15;
    ctx.save();
    ctx.beginPath();
    ctx.moveTo(npx(sx), npx(sy));
    ctx.lineTo(npx(sx + 8), npx(sy));
    ctx.lineTo(npx(sx + 4), npx(sy + 6));
    ctx.closePath();
    if (isHasFilter) {
      ctx.fillStyle = '#ffbf00';
    } else {
      ctx.fillStyle = 'rgba(0, 0, 0, .45)';
    }
    ctx.fill();
    ctx.restore();
  }

  error(box) {
    const { ctx } = this;
    const { x, y, width } = box;
    const sx = x + width - 1;
    ctx.save();
    ctx.beginPath();
    ctx.moveTo(npx(sx - 8), npx(y - 1));
    ctx.lineTo(npx(sx), npx(y - 1));
    ctx.lineTo(npx(sx), npx(y + 8));
    ctx.closePath();
    ctx.fillStyle = 'rgba(255, 0, 0, .65)';
    ctx.fill();
    ctx.restore();
  }

  rect(box, dtextcb) {
    const { ctx } = this;
    const {
      x, y, width, height, bgcolor,
    } = box;
    ctx.save();
    ctx.beginPath();
    ctx.fillStyle = bgcolor || '#fff';
    ctx.rect(npxLine(x + 1), npxLine(y + 1), npx(width - 2), npx(height - 2));
    ctx.clip();
    ctx.fill();
    dtextcb();
    ctx.restore();
  }

  triangle(x1, y1, x2, y2, x3, y3, color, type = 'fill') {
    const { ctx } = this;
    ctx.save();
    ctx.beginPath();
    ctx.moveTo(npx(x1), npx(y1));
    ctx.lineTo(npxLine(x2 + 1), npxLine(y2 + 1));
    ctx.lineTo(npxLine(x3 + 1), npxLine(y3 + 1));
    if (color) {
      const fillType = `${type}Style`;
      ctx[fillType] = color;
    }
    ctx.closePath();
    ctx[type]();
    ctx.restore();
  }

  // dropdown(box) {
  //   const { ctx } = this;
  //   const {
  //     x, y, width, height,
  //   } = box;
  //   const sx = x + width - 15;
  //   const sy = y + height - 15;
  //   ctx.save();
  //   ctx.beginPath();
  //   ctx.moveTo(npx(sx), npx(sy));
  //   ctx.lineTo(npx(sx + 8), npx(sy));
  //   ctx.lineTo(npx(sx + 4), npx(sy + 6));
  //   ctx.closePath();
  //   ctx.fillStyle = 'rgba(0, 0, 0, .45)';
  //   ctx.fill();
  //   ctx.restore();
  // }

  drawImage(image, box, fileCount, textStyle) {
    const { ctx } = this;
    const {
      x, y, width, height,
    } = box;
    ctx.save();
    if (image.width === 0) {
      image.width = 20;
    }
    if (image.height === 0) {
      image.height = 20;
    }
    let paddingX = 0;
    if (fileCount !== -1) {
      const { font } = textStyle;
      this.attr({
        font: `${font.italic ? 'italic' : ''} ${font.bold ? 'bold' : ''} ${npx(font.size)}px ${font.name}`,
      });
      const txtWidth = ctx.measureText(`(${fileCount})`).width;
      paddingX = (width - image.width - txtWidth) / 2;
    } else {
      paddingX = (width - image.width) / 2;
    }
    const paddingY = (height - image.height) / 2;
    ctx.drawImage(image, npx(x + paddingX), npx(y + paddingY), npx(image.width), npx(image.height));
    ctx.restore();
    if (fileCount !== -1) {
      const textX = x + paddingX + image.width;
      const textWidth = width - (paddingX + image.width);
      const textBox = new DrawBox(textX, y, textWidth, height);
      this.text(`(${fileCount})`, textBox, textStyle, false);
    }
  }

  /**
   * @description: 添加水印方法
   * @param {string} direction: repeat repeat-x repeat-y no-repeat
   * @return: null
   * @Author: linjian
   * @Date: 2019-08-09 14:27:12
   */
  drawWaterMark(direction, waterMarkContent = [], colIndexWidth = 0, rowIndexWidth = 0) {
    if (waterMarkContent.length === 0) {
      return;
    }
    const { ctx } = this;
    const opt = {
      fontStyle: '16px 黑体', // 水印字体设置
      rotateAngle: -30 * Math.PI / 180, // 水印字体倾斜角度设置
      fontColor: 'rgba(100, 100, 100, 0.3)', // 水印字体颜色设置
      firstLinePositionY: 30,
      SecondLinePositionY: 50,
    };
    const waterMark = document.getElementById('waterMark');
    const [company, loginName, lineTwoText] = waterMarkContent;
    const lineOneText = `${company}     ${loginName}`;
    ctx.save();
    ctx.moveTo(colIndexWidth, rowIndexWidth);
    ctx.font = opt.fontStyle;
    const lineOnemeasureText = ctx.measureText(lineOneText);
    const lineTwomeasureText = ctx.measureText(lineTwoText);

    const totalWidth = Math.max(lineOnemeasureText.width, lineTwomeasureText.width);
    const xPoint = totalWidth / 2 - Math.sin(-opt.rotateAngle) * totalWidth / 2;
    waterMark.width = totalWidth * 1.5;
    waterMark.height = Math.sin(-opt.rotateAngle) * totalWidth + 40 + 80;

    const waterMarkctx = waterMark.getContext('2d');
    waterMarkctx.save();
    // waterMarkctx.moveTo(colIndexWidth, rowIndexWidth);
    waterMarkctx.font = opt.fontStyle;
    // 文字倾斜角度
    waterMarkctx.rotate(opt.rotateAngle);

    waterMarkctx.fillStyle = opt.fontColor;
    waterMarkctx.textAlign = 'center';

    waterMarkctx.fillText(lineOneText, xPoint, totalWidth / 2);
    // 第二行文字
    waterMarkctx.fillText(lineTwoText, xPoint, totalWidth / 2 + 40);
    // 坐标系还原
    waterMarkctx.rotate(-opt.rotateAngle);
    waterMarkctx.restore();
    const pat = ctx.createPattern(waterMark, direction);
    ctx.rect(colIndexWidth, rowIndexWidth, ctx.canvas.width, ctx.canvas.height);
    ctx.fillStyle = pat;
    ctx.fill();
    ctx.restore();
  }
}

export default {};
export {
  Draw,
  DrawBox,
  thinLineWidth,
  npx,
};
