import { h } from './element';
import { bindClickoutside, unbindClickoutside } from './event';
import { cssPrefix } from '../config';
import { tf } from '../locale/locale';

const defaultMenuItems = [
  { key: 'copy', title: tf('contextmenu.copy'), label: 'Ctrl+C' },
  { key: 'cut', title: tf('contextmenu.cut'), label: 'Ctrl+X' },
  { key: 'paste', title: tf('contextmenu.paste'), label: 'Ctrl+V' },
  { key: 'paste-value', title: tf('contextmenu.pasteValue'), label: 'Ctrl+Shift+V' },
  { key: 'paste-format', title: tf('contextmenu.pasteFormat'), label: 'Ctrl+Alt+V' },
  { key: 'divider' },
  { key: 'insert-row', title: tf('contextmenu.insertRow') },
  { key: 'insert-column', title: tf('contextmenu.insertColumn') },
  { key: 'divider' },
  { key: 'delete-row', title: tf('contextmenu.deleteRow') },
  { key: 'delete-column', title: tf('contextmenu.deleteColumn') },
  { key: 'delete-cell-text', title: tf('contextmenu.deleteCellText') },
  { key: 'divider' },
  { key: 'validation', title: tf('contextmenu.validation') },
  { key: 'divider' },
  { key: 'cell-printable', title: tf('contextmenu.cellprintable') },
  { key: 'cell-non-printable', title: tf('contextmenu.cellnonprintable') },
  { key: 'divider' },
  { key: 'cell-editable', title: tf('contextmenu.celleditable') },
  { key: 'cell-non-editable', title: tf('contextmenu.cellnoneditable') },
];

function buildMenuItem(item) {
  if (item.key === 'divider') {
    return h('div', `${cssPrefix}-item divider`);
  } else if (item.key === 'paste-options') { // 粘贴选项
    const pasteEls = [];
    item.subItems.forEach(v => {
      pasteEls.push(
        h('span', `${cssPrefix}-item paste-common ${v.key}`)
        .on('click', e => {
          if (typeof v.callback === 'function') {
            this.itemClick(v.key, e, v.callback);
          } else {
            this.itemClick(v.key, e);
          }
          this.hide();
        })
      );
    });

    const pasteTitle = h('div', `${cssPrefix}-item paste-options-title`).children(item.title());
    const pasteCluster = h('div', 'paste-options-cluster').children(...pasteEls);

    return (
      h('div')
      .children(
        pasteTitle,
        pasteCluster
      )
    );
  };

  return (
    h('div', `${cssPrefix}-item lop-menu-item ${item.key}`)
    .on('click', e => {
      if (typeof item.callback === 'function') {
        this.itemClick(item.key, e, item.callback);
      } else {
        this.itemClick(item.key, e);
      }
      this.hide();
    })
    .children(
      item.title(),
      h('div', 'label').child(item.label || ''),
    )
  );
}

function buildMenu(menuItems) {
  return menuItems.map(it => buildMenuItem.call(this, it));
}


// 类：右键菜单
export default class ContextMenu {
  constructor(viewFn, isHide = false, customMenuItems) {
    let terminalMenuItems = defaultMenuItems;
    if (customMenuItems && customMenuItems.length !== 0) {
      customMenuItems.forEach(item => {
        item.title = tf(item.title)
      });
      terminalMenuItems = customMenuItems;
    }
    this.el = h('div', `${cssPrefix}-contextmenu`)
      .children(...buildMenu.call(this, terminalMenuItems))
      .hide();
    this.viewFn = viewFn;
    this.itemClick = () => {};
    this.isHide = isHide;
  }

  hide() {
    const { el } = this;
    el.hide();
    unbindClickoutside(el);
  }

  setPosition(x, y) {
    if (this.isHide) return;
    const { el } = this;
    const { height, width } = el.show().offset();
    const view = this.viewFn();
    let top = y;
    let left = x;
    if (view.height - y <= height) {
      top -= height;
    }
    if (view.width - x <= width) {
      left -= width;
    }
    el.offset({ left, top });
    bindClickoutside(el);
  }
}
