export default class Fx {
  constructor(sheet, fxObj, focusFn, blurFn, fxKeyupFn) {
    this.fxObj = fxObj;
    // 先解绑事件
    this.fxObj.onfocus = null;
    this.fxObj.onblur = null;
    this.fxObj.change = null;
    // 在添加事件
    this.fxObj.onfocus = focusFn;
    this.fxObj.onblur = blurFn;
    this.fxObj.onkeyup = fxKeyupFn;
    this.sheet = sheet;
  }

  setData(value) {
    if (this.fxObj) {
      this.fxObj.value = value;
    }
  }

  setAttribute(key, value) {
    this.fxObj.setAttribute(key, value);
  }

  removeAttribute(key) {
    this.fxObj.removeAttribute(key);
  }

  getData() {
    if (!this.fxObj) {
      return '';
    }
    return this.fxObj.value;
  }
}
