/* global document */
import { h } from './element';
import { cssPrefix } from '../config';

export default class Scrollbar {
  constructor(vertical) {
    this.vertical = vertical;
    this.moveFn = null;
    // linjian 添加 滚动方法监听
    this.moveVerticalFn = null;
    this.el = h('div', `${cssPrefix}-scrollbar ${vertical ? 'vertical' : 'horizontal'} `)
      .child(this.contentEl = h('div', ''))
      .on('mousemove.stop', () => {})
      .on('scroll.stop', (evt) => {
        const {
          scrollTop, scrollLeft, scrollHeight, offsetHeight, scrollWidth, offsetWidth,
        } = evt.target;
        // console.log('scrollTop:'+scrollTop+',scrollHeight:'+scrollHeight+',offsetHeight:'+offsetHeight);
        if (this.moveFn) {
          const isVertical = this.vertical;
          this.moveFn(isVertical ? scrollTop : scrollLeft,
            evt,
            isVertical ? scrollHeight : scrollWidth,
            isVertical ? offsetHeight : offsetWidth);
        }
        // linjian 添加 竖直滚动方法监听
        if (this.moveVerticalFn && this.vertical) {
          this.moveVerticalFn(evt);
        }
        // linjian 不管是横向还是纵向移动  都要把下拉 日期时间 弹框关闭
        const dateComponent = document.querySelector('#layui-laydate1');
        if (dateComponent) {
          dateComponent.remove();// 关闭日期弹框
        }
        const dropdownItems = document.querySelector('.x-spreadsheet-dropdown-items');
        if (dropdownItems) {
          dropdownItems.style.display = 'none';
        }
        // 关闭 筛选排序框
        const sortFilter = document.querySelector('.x-spreadsheet-sort-filter');
        if (sortFilter) {
          sortFilter.style.display = 'none';
        }
        // console.log('evt:::', evt);
      });
  }

  move(v) {
    this.el.scroll(v);
    return this;
  }

  scroll() {
    return this.el.scroll();
  }

  set(distance, contentDistance) {
    const d = distance - 1;
    // console.log('distance:', distance, ', contentDistance:', contentDistance);
    if (contentDistance > d) {
      const cssKey = this.vertical ? 'height' : 'width';
      // eslint-disable-next-line no-undef
      const isMac = navigator.userAgent.indexOf('Macintosh') > -1;
      // console.log('d:', d);
      this.el.css(cssKey, `${d - 15}px`).show();
      this.contentEl
        .css(this.vertical ? 'width' : 'height', isMac ? '10px' : '1px')
        .css(cssKey, `${contentDistance}px`);
    } else {
      this.el.hide();
    }
    return this;
  }
}
