/* eslint-disable eslint-comments/disable-enable-pair */
/* eslint-disable prefer-destructuring */
/* eslint-disable no-param-reassign */
import { trim } from '../../helpers/string';
import formulasObj from './supported-formulas';
import { extractLabel, columnLabelToIndex } from './utils';
import { error as consoleError } from '../../helpers/console';
import parseError from './formulas-implement/error';

const { formulasInfo, formulasImplement } = formulasObj;
const isPlusLevel = value => /^[-+]$/.test(value);
// 两个字符的运算符
const isCombinedOperator = value => value === '<=' || value === '<>' || value === '>=';
const isOperator = value => /^[-+*/%<>=]$/.test(value) || isCombinedOperator(value);
// 运算符优先级
const getOperationLevel = operator => {
  switch (operator) {
    case '=':
    case '>':
    case '<':
    case '>=':
    case '<=':
    case '<>':
      return 0;
    case '+':
    case '-':
      return 1;
    case '*':
    case '/':
    case '%':
      return 2;
    default: {
      consoleError(`无${operator}运算符`);
      throw parseError.error;
    }
  }
};
const transferNullToZero = value => {
  if (value === '' || value === undefined || value === null) {
    return 0;
  }
  return value;
};
// 运算符计算
const operation = function(num1, operator, num2) {
  num1 = transferNullToZero(num1);
  num2 = transferNullToZero(num2);
  switch (operator) {
    case '+': {
      return num1 + num2;
    }
    case '-': {
      return num1 - num2;
    }
    case '*': {
      return num1 * num2;
    }
    case '/': {
      if (num2 === 0) {
        throw parseError.div0;
      }
      return num1 / num2;
    }
    case '%': {
      return num1 % num2;
    }
    case '=': {
      return num1 === num2;
    }
    case '>': {
      return num1 > num2;
    }
    case '>=': {
      return num1 >= num2;
    }
    case '<': {
      return num1 < num2;
    }
    case '<=': {
      return num1 <= num2;
    }
    case '<>': {
      return num1 !== num2;
    }
    default: {
      consoleError(`无${operator}运算符`);
      throw parseError.error;
    }
  }
};

export default function(source, dependent) {
  const sheetInstance = this;
  // 处理 等号
  if (source[0] === '=') {
    source = source.substring(1);
  }
  const operator = /[-+*/%<>=(),"']/;
  let arr = [];
  let result = null;
  let error = null;
  const pushInArr = function(value) {
    value = trim(value);
    if (value) {
      arr.push(value);
    }
  };
  try {
    // 拆解 source 存入 arr 中 —— 'SUM(E1:E3)+5' => ['SUM', '(', 'E1:E3', ')', '+', '5']
    while (source) {
      // 根据操作符拆分，找到操作符的索引
      const temp = source.match(operator);
      if (temp) {
        const operatorIndex = temp.index;
        // 把操作符之前的，当成一个完整的元素，入栈，如 'SUM'
        if (temp.index > 0) {
          // 如 SUM、E1:E3，如果是单元格，则获取单元格值，如果不是，则直接入栈
          pushInArr(source.substring(0, operatorIndex));
        }
        // 操作符特殊处理
        let operatorTmp = source[operatorIndex];
        source = source.substring(operatorIndex + 1);
        // 双引号——字符串处理
        if (operatorTmp === '"') {
          const tmp = source.match(/^([^"]*)"(.*)$/);
          if (tmp) {
            arr.push(`"${tmp[1]}"`);
            source = tmp[2];
          } else {
            throw parseError.error;
          }
        } else if (operatorTmp === "'") {
          // 单引号——实例名处理
          let tmp = source.match(/^([^']*')/);
          let range; // 用于缓存最终范围，形如 '表-1'!E1:E4
          if (tmp) {
            // '表-1'
            range = `'${tmp[1]}`;
            source = source.substr(range.length - 1);
            tmp = source.match(operator);
            if (tmp) {
              range += source.substring(0, tmp.index);
              source = source.substr(tmp.index);
            } else {
              range += source;
              source = '';
            }
            pushInArr(range);
          } else {
            throw parseError.error;
          }
        } else {
          const combinedOperator = operatorTmp + source[0];
          if (isCombinedOperator(combinedOperator)) {
            operatorTmp = combinedOperator;
            source = source.substring(1);
          }
          arr.push(operatorTmp);
        }
      } else {
        pushInArr(source);
        source = null;
      }
    }
    // 处理括号 —— ['SUM', '(', 'E1:E3', ')', '+', '5'] => ['SUM', ['E1:E3'], '+', '5']
    arr = dealDrackets(arr, true);
    // linjian 将解析结果缓存到dependent
    dependent.parseArr = JSON.parse(JSON.stringify(arr));
    // 开始计算
    result = calculate.call(sheetInstance, arr)[0];
  } catch (err) {
    error = err.message;
    if (!isFormulaError(error)) {
      consoleError(err.stack);
    }
  }

  return {
    result,
    error,
  };
}
// 解析字符串格式的单元格范围 rowOffset 专门给流表使用
export function parseRange(rangStr, rowOffset = 0) {
  // (([\u4e00-\u9fa5_\w-]*|'[^']*')\s*!\s*)? 对应 sheetName ! 或 'sheetName' !
  // (\$?[A-Z]+\$?\d+(\s*:\s*\$?[A-Z]+\$?\d+)?|\$?[A-Z]\s*:\s*\$?[A-Z])
  // 表格范围表达式，如 A2、 B2 : E4、 B:E、$A$2、$B:$E
  const matchObj = rangStr.match(
    /^(([\u4e00-\u9fa5_\w-]*|'[^']*')\s*!\s*)?(\$?[A-Z]+\$?\d+(\s*:\s*\$?[A-Z]+\$?\d+)?|\$?[A-Z]\s*:\s*\$?[A-Z])$/,
  );
  if (matchObj) {
    const startParam = {};
    // sheetName
    let sheetName = matchObj[2];
    if (sheetName) {
      // 去除可能存在的单引号
      sheetName = sheetName.replace(/(^')|('$)/g, '');
      startParam.sheetName = sheetName;
    }
    const range = matchObj[3].split(/\s*:\s*/);
    // 判断是否 形如 A:B、$A:B 这种整列选中的格式
    if (/^\$?[A-Z]+$/.test(range[0])) {
      startParam.row = -1;
      startParam.column = columnLabelToIndex(range[0]);
      return [
        startParam,
        {
          row: -1,
          column: columnLabelToIndex(range[1]),
        },
      ];
    }
    const [startRow, startCol] = extractLabel(range[0]);
    if (!startRow.isAbsolute) {
      startParam.row = startRow.index + rowOffset;
    } else {
      startParam.row = startRow.index;
    }
    startParam.column = startCol.index;
    // 结束范围
    if (range[1]) {
      // B2 : E4、$B$2 : $E$4 这种格式
      const [endRow, endCol] = extractLabel(range[1]);
      const endParam = { row: endRow.index, column: endCol.index };
      if (!endRow.isAbsolute) {
        endParam.row = endRow.index + rowOffset;
      }
      return [startParam, endParam];
    }
    // A2、 $A$2 这种格式
    return [startParam];
  }
  return false;
}
// 判断是否单元格范围，如果是，则获取单元格值
// 如果不是单元格，是数字字符串，则转成数字 ([\u4e00-\u9fa5_\w-]*
function getCellValue(value, sheetInstance, rowOffset) {
  const rangeValue = sheetInstance.parseRangeValue(value, rowOffset);
  if (!(rangeValue instanceof Error)) {
    // 从实例获取单元格的值
    value = rangeValue;
    if (value instanceof Array) {
      value.ignore = true;
    }
  } else if (/^\${[^}]}$/.test(value)) {
    // 特殊处理，${key} 格式
    value = '';
  } else if (typeof value === 'string') {
    if (/^-?(([0-9])|([1-9]\d*))(\.\d+)?$/.test(value)) {
      value = parseFloat(value);
    } else if (/^".*"$/.test(value)) {
      value = value.substring(1, value.length - 1);
    }
  }
  return value;
}
// 处理括号
function dealDrackets(source, isFirstTime) {
  const result = [];
  while (source.length > 0) {
    const temp = source.shift();
    // 如果是左括号，则新建一个数组
    if (temp === '(') {
      result.push(dealDrackets(source));
    } else if (temp === ')') {
      return result;
    } else {
      result.push(temp);
    }
  }
  if (isFirstTime) {
    return result;
  }
  throw parseError.error;
}

export { parseError };

export function isFormulaError(type) {
  let result;

  type = `${type}`.replace(/[#!?/]/g, '').toLowerCase();

  if (parseError[type]) {
    result = parseError[type];
  }

  return result || null;
}

// 计算
export function calculate(source, rowOffset) {
  const sheetInstance = this;
  let i = 0;
  // 计算函数，转化
  while (i < source.length) {
    const current = source[i];
    if (current === 'TRUE') {
      source[i] = true;
    } else if (current === 'FALSE') {
      source[i] = false;
    } else if (source[i + 1] instanceof Array) {
      // 下一个是数组，即函数参数，只可能是函数参数
      if (formulasInfo[current]) {
        if (current === 'IF') {
          // 特殊处理，根据条件，只计算对应项
          const params = source[i + 1];
          // 先分离条件
          let temp = params.splice(0, params.indexOf(','));
          // 去除逗号
          params.shift();
          if (calculate.call(sheetInstance, temp, rowOffset)[0]) {
            // TRUE 分支
            temp = params.splice(0, params.indexOf(','));
          } else {
            // FALSE 分支
            temp = params.splice(params.indexOf(',') + 1);
          }
          source[i] = calculate.call(sheetInstance, temp, rowOffset)[0];
        } else if (current === 'ISERROR') {
          try {
            // 先计算好参数里面可能存在的公式
            calculate.call(sheetInstance, source[i + 1], rowOffset);
            source[i + 1] = [];
            source[i] = false;
          } catch (e) {
            source[i] = true;
          }
        } else {
          // 先计算好参数里面可能存在的公式
          source[i + 1] = calculate.call(sheetInstance, source[i + 1], rowOffset);
          // 再计算当前公式，并把计算结果覆盖到 i 位置
          source[i] = formulasImplement[current].apply(this, source[i + 1]);
        }
        // 部分函数会返回 null，暂且当做空字符串处理
        // left("", 2) 会返回 null
        if (source[i] === null) {
          source[i] = '';
        } else if (source[i] === undefined) {
          consoleError(`${current}公式返回了undefined`);
          source[i] = '';
        } else if (source[i].name === 'Error') {
          throw source[i];
        }
        // 删除 i + 1 位置的参数
        source.splice(i + 1, 1);
      } else if (typeof current === 'string' && /[A-Z]/.test(current)) {
        consoleError(`不存在 ${current} 函数`);
        throw parseError.name;
      }
    } else if (typeof current === 'string') {
      source[i] = getCellValue(current, sheetInstance, rowOffset);
    }
    i += 1;
  }
  i = 0;
  // -1 计算完毕，等待下一个运算符
  // 0 前一个是运算符，等待下一个运算符或数据
  // 1 当前和前一个都是运算符，如果碰到数据，要立即计算
  let status = 0;
  // ++++ 格式的待计算数量 如 ++ 为 1
  let waitDataForCalculation = 0;
  // 逗号索引
  const commaIndex = [];
  // source 中只剩下数字和运算符号了
  while (i < source.length && source.length !== 1) {
    let current = source[i];
    const preOperator = source[i - 1];
    // 括号，优先计算，如 (1 + 1)/2 => [[1, '+', 1], '/', 2]
    if (current instanceof Array && current.ignore !== true) {
      source[i] = calculate.call(sheetInstance, current, rowOffset);
      current = source[i];
    }
    // 操作符号
    if (isOperator(current)) {
      if ((status === 0 || status === 1) && isPlusLevel(current)) {
        // 前一个是符号，当前是加减号的情况，即 ++
        waitDataForCalculation += 1;
        status = 1;
      } else {
        // 符号字符串，当成正常数据
        // 或正常数字后的符号
        status = 0;
      }
      i += 1;
    } else if (current === ',') {
      commaIndex.push(i);
      i += 1;
      status = 0;
    } else if (status === 1) { // 后面都是 current是数据的情况处理
      // ++ 格式的计算
      // 先删除当前数据
      source.splice(i, 1);
      // 索引退一位，到符号位
      i -= 1;
      const operator = source[i];
      // 计算结果覆盖到操作符的位置
      if (operator === '+') {
        source[i] = +current;
      } else if (operator === '-') {
        source[i] = -current;
      }
      waitDataForCalculation -= 1;
      // 再无 ++等 立即计算，下一循环进行正常计算
      if (waitDataForCalculation === 0) {
        status = 0;
      } // 如果 waitDataForCalculation 不为0，则说明有 +++A2 这种格式的计算，不改变status，下个循环继续处理
    } else if (status === 0) {
      // 正常计算
      if (isOperator(preOperator)) {
        const preOperatorLevel = getOperationLevel(preOperator);
        const nextOperator = source[i + 1];
        if (isOperator(nextOperator)) {
          // 下一个是运算符号
          const nextOperatorLevel = getOperationLevel(nextOperator);
          // 前计算等级 大于等于 后计算等级，则计算前面的 1 + 1 + 1
          if (preOperatorLevel >= nextOperatorLevel) {
            i -= 2;
            // 特殊处理 >= 和 <=，还有 <>（不等于）
            if (isOperator(source[i]) && (preOperator === '=' || preOperator === '>')) {
              source[i] += preOperator;
              source.splice(i + 1, 1);
              i -= 1;
            }
            const arg = source.splice(i, 3, '');
            source[i] = operation(...arg);
          } else {
            // 格式 1 + 1 * 1，要先计算后面的，所以跳过
            i += 2;
            // status 不变
          }
        } else {
          // 下一个不是运算符
          i -= 2;
          // 特殊处理 >= 和 <=
          if (isOperator(source[i]) && preOperator === '=') {
            source[i] += preOperator;
            source.splice(i + 1, 1);
            i -= 1;
          }
          const arg = source.splice(i, 3, '');
          source[i] = operation(...arg);
        }
      } else {
        // 前一个不是运算符，直接往后走（首位是数据的情况）
        i += 1;
        status = -1;
      }
    } else {
      // 二维数组中的连续数组
      i += 1;
    }
  }
  // 清空逗号
  while (commaIndex.length) {
    source.splice(commaIndex.pop(), 1);
  }
  return source;
}

// linjian 导出计算dropdownRange用
export function calculateDropdownRange(value, sheetInstance, rowOffset) {
  return getCellValue(value, sheetInstance, rowOffset);
}
