import { h } from './element';
import Button from './button';
// import { bindClickoutside, unbindClickoutside } from './event';
import { cssPrefix } from '../config';
import { t } from '../locale/locale';

function buildMenu(clsName) {
  return h('div', `${cssPrefix}-item ${clsName}`);
}

function buildSortItem(it) {
  const spanImage = h('span', `state-${it}-image`);
  const spanText = h('span', `state-${it}-text`, t(`sort.${it}`));
  return buildMenu(`state state-${it}`).children(
    spanImage,
    spanText,
  ).on('click.stop', () => this.itemClick(it));
}

function buildFilterBody(items, filter) {
  const {
    filterbEl, filterValues,
  } = this;
  let { isSelectAll } = filter;
  if (!isSelectAll) {
    ({ isSelectAll } = this);
  }
  filterbEl.html('');
  // const itemKeys = Object.keys(items);
  items.forEach((it, index) => {
    const checkboxDom = h('input', 'xs-spread-sheet-checkbox').attr('type', 'checkbox');
    if (isSelectAll) {
      if (!filterValues.includes(it)) {
        checkboxDom.el.checked = true;
      }
    } else if (filterValues.includes(it)) {
      checkboxDom.el.checked = true;
    }
    const active = !!(filterValues.includes(it) ? 'checked' : '');
    if (!active) {
      checkboxDom.el.removeAttribute('checked');
    }
    filterbEl.child(h('div', `${cssPrefix}-sub-item state`)
      .on('click', (e) => {
        e.stopPropagation();
        if (e.target.classList.contains('xs-spread-sheet-label')) {
          const checkbox = e.target.querySelector('.xs-spread-sheet-checkbox');
          if (checkbox.checked) {
            checkbox.checked = false;
          } else {
            checkbox.checked = true;
          }
        }
        setTimeout(() => {
          this.filterClick(index, it);
        }, 0);
      })
      .child(h('span', 'xs-spread-sheet-label').children(it === '' ? t('filter.empty') : checkboxDom, it)));
  });
}

function resetFilterHeader() {
  const { filterhEl, filterValues, values } = this;
  filterhEl.html(`${filterValues.length} / ${values.length}`);
  filterhEl.checked(filterValues.length === values.length);
}

export default class SortFilter {
  constructor() {
    this.filterbEl = h('div', `${cssPrefix}-body`);
    this.filterhEl = h('div', `${cssPrefix}-header state`);
    this.sortAscEl = buildSortItem.call(this, 'asc');
    this.sortDescEl = buildSortItem.call(this, 'desc');
    this.sortSelectAll = h('span', 'sort-select-all', '全选').on('click.stop', () => this.filterClick(0, 'all'));
    this.sortClearAll = h('span', 'sort-clear-all', '清空').on('click.stop', () => this.filterClick(0, 'none'));
    this.searchTextEl = h('input', 'search-text-input')
      .attr('placeholder', '查找')
      .on('keyup.stop', () => this.conditionChange());
    this.el = h('div', `${cssPrefix}-sort-filter`).children(
      h('div', 'sort-container').children(
        this.sortAscEl,
        this.sortDescEl,
      ),
      buildMenu('divider'),
      h('div', 'search-text', '过滤值'),
      this.searchTextEl,
      h('div', 'sort-action-btn1').children(
        this.sortSelectAll,
        this.sortClearAll,
      ),
      h('div', `${cssPrefix}-filter`).children(
        // this.filterhEl,
        this.filterbEl,
      ),
      h('div', `${cssPrefix}-buttons`).children(
        new Button('cancel').on('click', () => this.btnClick('cancel')),
        new Button('ok', 'primary').on('click', () => this.btnClick('ok')),
      ),
    ).hide();
    // this.setFilters(['test1', 'test2', 'text3']);
    this.ri = null;
    this.ci = null;
    this.sortDesc = null;
    this.values = null;
    this.filterValues = [];
    this.isSelectAll = false;
    this.isClear = false;
    this.searchKey = '';
  }

  btnClick(it) {
    if (it === 'ok') {
      const {
        ri, ci, sort, filterValues, isSelectAll, searchKey,
      } = this;
      if (this.ok) {
        this.ok(ri, ci, sort, 'in', filterValues, isSelectAll, searchKey);
      }
    }
    this.hide();
  }

  itemClick(it) {
    // console.log('it:', it);
    const { sortAscEl, sortDescEl } = this;
    // linjian 这里修改允许用户取消筛选选择
    if (it === 'asc') {
      if (Array.prototype.includes.call(sortAscEl.el.classList, 'checked')) {
        sortAscEl.checked(false);
        this.sort = null;
      } else {
        sortAscEl.checked(it === 'asc');
        sortDescEl.checked(false);
        this.sort = it;
      }
    } else if (it === 'desc') {
      if (Array.prototype.includes.call(sortDescEl.el.classList, 'checked')) {
        sortDescEl.checked(false);
        this.sort = null;
      } else {
        sortDescEl.checked(it === 'desc');
        sortAscEl.checked(false);
        this.sort = it;
      }
    }
  }

  filterClick(index, it) {
    // console.log('index:', index, it);
    const { filterbEl, filterValues } = this;
    const children = filterbEl.children();
    if (it === 'all') {
      this.filterValues = [];
      children.forEach((item) => { item.querySelector('.xs-spread-sheet-checkbox').checked = true; });
      this.isSelectAll = true;
      this.isClear = false;
    } else if (it === 'none') {
      this.filterValues = [];
      children.forEach((item) => { item.querySelector('.xs-spread-sheet-checkbox').checked = false; });
      this.isSelectAll = false;
      this.isClear = true;
    } else {
      // linjian 这里因为禁止了浏览器的默认行为，所以点击的时候CheckBox并不会自动勾选
      const { checked } = children[index].querySelector('.xs-spread-sheet-checkbox');
      // 这里要分情况处理  如果是全选，则应该保存未选择的部分值，如果不是全选则直接选中哪个就保存哪个
      if (this.isSelectAll) {
        if (checked) {
          filterValues.splice(filterValues.findIndex(i => i === it), 1);
        } else {
          filterValues.push(it);
        }
      }
      if (!this.isSelectAll) {
        if (checked) {
          filterValues.push(it);
        } else {
          filterValues.splice(filterValues.findIndex(i => i === it), 1);
        }
      }
    }
    resetFilterHeader.call(this);
  }

  // v: autoFilter
  // items: {value: cnt}
  // sort { ci, order }
  set(ci, items, filter, sort) {
    this.ci = ci;
    const { sortAscEl, sortDescEl } = this;
    if (sort !== null) {
      this.sort = sort.order;
      sortAscEl.checked(sort.asc());
      sortDescEl.checked(sort.desc());
    } else {
      this.sortDesc = null;
      sortAscEl.checked(false);
      sortDescEl.checked(false);
    }
    // this.setFilters(items, filter);
    this.values = Object.keys(items);
    this.filterValues = filter ? Array.from(filter.value) : Object.keys(items);
    buildFilterBody.call(this, items, filter || {});
    resetFilterHeader.call(this);
  }

  // linjian
  // v: autoFilter
  // items: {value: cnt}
  // sort { ci, order } linjian 这里把sort入参注释掉 改为从filter去sort：string（asc/desc）
  // status init或者没有 代表是否是初始化状态
  setItems(ri, ci, items, filter, sort, status) {
    this.ci = ci;
    this.ri = ri;
    const { sortAscEl, sortDescEl, searchTextEl } = this;
    if (status === 'init') {
      let searchKey = '';
      if (filter && filter.searchKey) {
        ({ searchKey } = filter);
      }
      searchTextEl.el.value = searchKey;
      this.sortDesc = null;
      this.values = null;
      this.filterValues = [];
      this.isSelectAll = true;
      this.isClear = false;
      let order = null;
      if (filter) {
        ({ order } = filter);
      }
      if (order !== null) {
        this.sort = order;
        sortAscEl.checked(order === 'asc');
        sortDescEl.checked(order === 'desc');
      } else {
        this.sortDesc = null;
        sortAscEl.checked(false);
        sortDescEl.checked(false);
      }
    }
    // this.setFilters(items, filter);
    this.values = items;
    // this.filterValues = filter ? Array.from(filter.value) : items;
    // linjian这里修改为默认不选择
    this.filterValues = filter ? Array.from(filter.value) : [];
    buildFilterBody.call(this, items, filter || {});
    resetFilterHeader.call(this);
  }

  setOffset(v) {
    this.el.css(v);
    // this.el.offset(v).show();
    // let tindex = 1;
    // bindClickoutside(this.el, () => {
    //   if (tindex <= 0) {
    //     this.hide();
    //   }
    //   tindex -= 1;
    // });
  }

  show() {
    this.el.show();
  }

  hide() {
    this.el.hide();
    // unbindClickoutside(this.el);
  }
}
