function scrollbarMove() {
  const {
    data, verticalScrollbar, horizontalScrollbar,
  } = this;
  const {
    l, t, left, top, width, height,
  } = data.getSelectedRect();
  const tableOffset = this.getTableOffset();
  // console.log(',l:', l, ', left:', left, ', tOffset.left:', tableOffset.width);
  if (Math.abs(left) + width > tableOffset.width) {
    horizontalScrollbar.move({ left: l + width - tableOffset.width });
  } else {
    const fsw = data.freezeTotalWidth();
    if (left < fsw) {
      horizontalScrollbar.move({ left: l - 1 - fsw });
    }
  }
  // console.log('top:', top, ', height:', height, ', tof.height:', tableOffset.height);
  if (Math.abs(top) + height > tableOffset.height) {
    verticalScrollbar.move({ top: t + height - tableOffset.height - 1 });
  } else {
    const fsh = data.freezeTotalHeight();
    if (top < fsh) {
      verticalScrollbar.move({ top: t - 1 - fsh });
    }
  }
}

function selectorSet(multiple, ri, ci, indexesUpdated = true, moving = false) {
  if (ri === -1 && ci === -1) return;
  // console.log(multiple, ', ri:', ri, ', ci:', ci);
  this.editor.isAutoFocus = true;
  this.el.el.querySelector(`#${this.editor.textElID}`).focus();
  const {
    table, selector, toolbar,
  } = this;
  if (multiple) {
    selector.setEnd(ri, ci, moving);
  } else {
    selector.set(ri, ci, indexesUpdated);
  }
  editorFormulaCell.call(this); // 选择单元格（包含范围选择）
  toolbar.reset();
  table.render();
}

// 单元格内编辑公式
function editorFormulaCell() {
  const { editor, selector } = this;
  if (editor.formulaHanging) {
    const { textEl } = editor;
    const formulaTxt = textEl.val();
    const valueRegExp = /^(=[A-Z]+\()(.*)(\))$/;
    const formulaBody = formulaTxt.match(valueRegExp);
    const { sri, sci, eri, eci } = selector.range;

    let selectCell = xy2expr(sci, sri);
    if(!(sri === eri && sci === eci)) {
      selectCell = `${xy2expr(sci, sri)}:${xy2expr(eci, eri)}`;
    }

    const cursorIndex = getCaretPosition(textEl.el); // 光标的位置
    // console.log('光标位置：', cursorIndex);

    const formula_part1 = formulaBody[1];
    const formula_part2 = formulaBody[2].trim();
    const formula_part3 = formulaBody[3];

    let posStr = formula_part2; // 字符串：初始化为原有的行列值
    const hasComma = formula_part2.includes(','); // 按照逗号来判断
    let nextCursorIndex; // 下一次需替换的单元格行列值在光标处

    if (hasComma) { // 有逗号
      const cursorIndex2 = cursorIndex - formula_part1.length; // 排除公式前半部分
      const afterCursorFirstCommaIndex = formula_part2.indexOf(',', cursorIndex2); // 光标后面的第一个逗号位置
      const beforeCursorFirstCommaIndex = formula_part2.lastIndexOf(',', cursorIndex2); // 光标前面的第一个逗号位置
      if (afterCursorFirstCommaIndex !== -1) { // 光标之后有逗号
        if (beforeCursorFirstCommaIndex !== -1) { // 且光标之前有逗号
          posStr = `${posStr.substring(0, beforeCursorFirstCommaIndex + 1)}${selectCell}${posStr.substring(afterCursorFirstCommaIndex, posStr.length)}`;
          nextCursorIndex = formula_part1.length + beforeCursorFirstCommaIndex - 1;
          // console.log('光标1：', nextCursorIndex);
        } else { // 但光标之前没逗号
          posStr = `${selectCell}${posStr.substring(afterCursorFirstCommaIndex, posStr.length)}`;
          nextCursorIndex = formula_part1.length;
          // console.log('光标2：', nextCursorIndex);
        }
      } else if (beforeCursorFirstCommaIndex !== -1) { // 光标之前有逗号但光标之后没有
        posStr = `${posStr.substring(0, beforeCursorFirstCommaIndex + 1)}${selectCell}`;
        nextCursorIndex = formula_part1.length + beforeCursorFirstCommaIndex + 1;
        // console.log('光标3：', nextCursorIndex);
      }
    } else { // 没有逗号
      posStr = selectCell;
    }
    const terminal = `${formula_part1}${posStr}${formula_part3}`;

    editor.setText(terminal, nextCursorIndex || terminal.length - 1); // 设置光标到被替换的部分那个位置！
  }
}

// multiple: boolean
// direction: left | right | up | down | row-first | row-last | col-first | col-last
function selectorMove(multiple, direction) {
  const {
    selector, data,
  } = this;
  const { rows, cols } = data;
  let [ri, ci] = selector.indexes;
  const { eri, eci } = selector.range;
  if (multiple) {
    [ri, ci] = selector.moveIndexes;
  }
  // console.log('selector.move:', ri, ci);
  if (direction === 'left') {
    if (ci > 0) ci -= 1;
  } else if (direction === 'right') {
    if (eci !== ci) ci = eci;
    if (ci < cols.len - 1) ci += 1;
  } else if (direction === 'up') {
    if (ri > 0) ri -= 1;
  } else if (direction === 'down') {
    if (eri !== ri) ri = eri;
    if (ri < rows.len - 1) ri += 1;
  } else if (direction === 'row-first') {
    ci = 0;
  } else if (direction === 'row-last') {
    ci = cols.len - 1;
  } else if (direction === 'col-first') {
    ri = 0;
  } else if (direction === 'col-last') {
    ri = rows.len - 1;
  }
  if (multiple) {
    selector.moveIndexes = [ri, ci];
  }
  selectorSet.call(this, multiple, ri, ci);
  scrollbarMove.call(this);
}

/**
 * @description: 获取下拉树中选中的索引和隐藏项的索引
 * @param {domobj} itemElements 父容器
 * @return: hideIndex selectIndex
 * @Author: linjian
 * @Date: 2019-06-14 15:42:24
 */
function getHideIndexAndSelectIndex(itemElements) {
  const hideIndex = [];
  let selectedIndex = -1;
  for (let index = 0; index < itemElements.length; index++) {
    const element = itemElements[index];
    const toItem = element.querySelector('.to__item');
    if (element.parentElement.style.display !== 'block' && !element.parentElement.classList.contains('to__show')) {
      toItem.style.background = '#fff';
      hideIndex.push(index);
      // eslint-disable-next-line no-continue
      continue;
    }
    if (selectedIndex === -1 && toItem.style.background === 'rgb(245, 245, 245)') {
      selectedIndex = index;
      toItem.style.background = '#fff';
    }
  }
  return { selectedIndex, hideIndex };
}

// direction:  up | down |enter linjian 用于处理下拉上下箭头和enter事件
function selectDropdownItems(direction) {
  const itemElements = document.querySelector('.x-spreadsheet-dropdown-items').getElementsByClassName('item-container');
  let selectedIndex = -1;
  switch (direction) {
    case 'up':
      // eslint-disable-next-line no-case-declarations
      const result = getHideIndexAndSelectIndex(itemElements);
      // eslint-disable-next-line no-case-declarations
      const { hideIndex } = result;
      // eslint-disable-next-line prefer-destructuring
      selectedIndex = result.selectedIndex;
      // eslint-disable-next-line no-case-declarations
      let nextIndex = selectedIndex - 1;
      if (selectedIndex === 0) {
        nextIndex = itemElements.length - 1;
      }
      for (let index = 0; index < itemElements.length; index++) {
        if (hideIndex.includes(nextIndex)) {
          nextIndex--;
        }
      }
      itemElements[nextIndex].querySelector('.to__item').style.background = '#f5f5f5';
      break;
    case 'down':
      // eslint-disable-next-line no-case-declarations
      const resultDown = getHideIndexAndSelectIndex(itemElements);
      // eslint-disable-next-line no-case-declarations
      const hideIndexDown = resultDown.hideIndex;
      // eslint-disable-next-line no-case-declarations
      const selectedIndexDown = resultDown.selectedIndex;
      // eslint-disable-next-line no-case-declarations
      let nextIndexDown = selectedIndexDown + 1;
      if (selectedIndexDown === itemElements.length - 1) {
        nextIndexDown = 0;
      }
      for (let index = 0; index < itemElements.length; index++) {
        if (hideIndexDown.includes(nextIndexDown)) {
          nextIndexDown++;
        }
      }
      itemElements[nextIndexDown].querySelector('.to__item').style.background = '#f5f5f5';
      break;
    case 'enter':
      for (let index = 0; index < itemElements.length; index++) {
        const element = itemElements[index].querySelector('.to__item');
        if (element.style.background === 'rgb(245, 245, 245)') {
          element.querySelector('.to__click_item').click();
          break;
        }
      }
      break;
    default:
      break;
  }
}

// private methods
function overlayerMousemove(evt) {
  // console.log('x:', evt.offsetX, ', y:', evt.offsetY);
  if (evt.buttons !== 0) return;
  if (evt.target.className === `${cssPrefix}-resizer-hover`) return;
  const { offsetX, offsetY } = evt;
  const {
    rowResizer, colResizer, tableEl, data,
  } = this;
  const { rows, cols, imageSwitchBtn } = data;
  if (offsetX > cols.indexWidth && offsetY > rows.height) {
    rowResizer.hide();
    colResizer.hide();
    // linjian 如果是图片或者附件  则鼠标手势变为手势
    const cRect = data.getCellRectByXY(evt.offsetX, evt.offsetY);
    if (cRect.ri >= 0 && cRect.ci >= 0) {
      const cell = data.getCell(cRect.ri, cRect.ci);
      if (cell) {
        const { cellProps } = cell;
        if (cellProps && (cellProps.cellType === 'image' || cellProps.cellType === 'file')) {
          evt.target.style.cursor = 'pointer';
          // 如果是图片 并且图片的张数大于1 ，还要显示可以切换左右的切换按钮
          if (cell.typeOptions && cell.typeOptions.images && cell.typeOptions.images.length > 1) {
            // console.log('left:', cRect.left, 'top:', cRect.top);
            imageSwitchBtn.el.setAttribute('sri', cRect.ri);
            imageSwitchBtn.el.setAttribute('sci', cRect.ci);
            // const rowHeight = data.rows._[cRect.ri].height || data.rows.height;
            const indexRowHeight = data.rows.height;
            imageSwitchBtn.el.style.left = `${(cRect.left - data.cols.indexWidth + cRect.width - 14)}px`;
            imageSwitchBtn.el.style.top = `${(cRect.top - indexRowHeight + 10)}px`;
            imageSwitchBtn.el.style.display = 'block';
          } else {
            imageSwitchBtn.el.style.display = 'none';
          }
        } else {
          evt.target.style.cursor = 'default';
          imageSwitchBtn.el.style.display = 'none';
        }
      } else {
        evt.target.style.cursor = 'default';
        imageSwitchBtn.el.style.display = 'none';
      }
    } else {
      evt.target.style.cursor = 'default';
      imageSwitchBtn.el.style.display = 'none';
    }
    return;
  }
  const tRect = tableEl.box();
  const cRect = data.getCellRectByXY(evt.offsetX, evt.offsetY);
  if (cRect.ri >= 0 && cRect.ci === -1) {
    cRect.width = cols.indexWidth;
    rowResizer.show(cRect, {
      width: tRect.width,
    });
  } else {
    rowResizer.hide();
  }
  if (cRect.ri === -1 && cRect.ci >= 0) {
    cRect.height = rows.height;
    colResizer.show(cRect, {
      height: tRect.height,
    });
  } else {
    colResizer.hide();
  }
}

function overlayerMousescroll(evt) {
  const { verticalScrollbar, data } = this;
  const { top } = verticalScrollbar.scroll();
  // console.log('evt:::', evt.wheelDelta, evt.detail * 40);
  let delta = evt.deltaY;
  const { rows } = data;
  if (evt.detail) delta = evt.detail * 40;
  if (delta > 0) {
    // up
    const ri = data.scroll.ri + 1;
    if (ri < rows.len) {
      verticalScrollbar.move({ top: top + rows.getHeight(ri) - 1 });
    }
  } else {
    // down
    const ri = data.scroll.ri - 1;
    if (ri >= 0) {
      verticalScrollbar.move({ top: ri === 0 ? 0 : top - rows.getHeight(ri) });
    }
  }
}

function overlayerTouch(direction, distance) {
  const { verticalScrollbar, horizontalScrollbar } = this;
  const { top } = verticalScrollbar.scroll();
  const { left } = horizontalScrollbar.scroll();
  // console.log('direction:', direction, ', distance:', distance, left);
  if (direction === 'left' || direction === 'right') {
    horizontalScrollbar.move({ left: left - distance });
  } else if (direction === 'up' || direction === 'down') {
    verticalScrollbar.move({ top: top - distance });
  }
}

function verticalScrollbarSet(val) {
  const { data, verticalScrollbar } = this;
  const { height } = this.getTableOffset();
  let totalHeight;
  if (val !== undefined) {
    totalHeight = val;
  } else {
    totalHeight = data.rows.totalHeight();
  }
  data.totalHeightValue = totalHeight;
  const erth = data.exceptRowTotalHeight(0, -1);
  verticalScrollbar.set(height, totalHeight - erth);
  // linjian 删除后高度没有超出屏幕，则要把range设为起始行，要不显示不全
  if (height > totalHeight - erth) {
    this.data.scroll.y = 0;
    this.data.scroll.ri = 0;
    // 启动webworker 计算垂直距离 linjian
    utils.workerCalUtil(this);
  }
}

function horizontalScrollbarSet() {
  const { data, horizontalScrollbar } = this;
  const { width } = this.getTableOffset();
  if (data) {
    horizontalScrollbar.set(width, data.cols.totalWidth());
  }
}

function sheetFreeze() {
  const {
    selector, data, editor,
  } = this;
  const [ri, ci] = data.freeze;
  if (ri > 0 || ci > 0) {
    const fwidth = data.freezeTotalWidth();
    const fheight = data.freezeTotalHeight();
    editor.setFreezeLengths(fwidth, fheight);
  }
  selector.resetAreaOffset();
}

function sheetReset() {
  const {
    tableEl,
    overlayerEl,
    overlayerCEl,
    table,
    toolbar,
    selector,
    el,
  } = this;
  const tOffset = this.getTableOffset();
  const vRect = this.getRect();
  tableEl.attr(vRect);
  overlayerEl.offset(vRect);
  overlayerCEl.offset(tOffset);
  el.css('width', `${vRect.width}px`);
  verticalScrollbarSet.call(this);
  horizontalScrollbarSet.call(this);

  sheetFreeze.call(this);
  table.render();
  toolbar.reset();
  const { sri, sci } = this.data.selector.range;
  // linjian 插件默认只是选中第一个格子，不管是否有合并单元格 所以这里添加判断是否有合并，把合并单元格一起加上
  this.data.merges._.some((value) => {
    if (value.sri === 0 && value.sci === 0 && sri === 0 && sci === 0) {
      this.data.selector.range = value;
      return true;
    }
    return false;
  });
  selector.reset();
  // 初始化默认选中要更新fx的值
  // linjian fx值更新
  const { fx } = this.data;
  if (fx) {
    fx.setData(this.data.getCellTextOrDefault(sri, sci));
  }
  // linjian
  // 启动webworker 计算垂直距离 linjian
  utils.workerCalUtil(this);
  // getOverlayerElSize.call(this, true);
}
function sheetCustomerReset() {
  const {
    tableEl,
    overlayerEl,
    overlayerCEl,
    table,
    toolbar,
    selector,
    el,
  } = this;
  const tOffset = this.getTableOffset();
  const vRect = this.getRect();
  tableEl.attr(vRect);
  overlayerEl.offset(vRect);
  overlayerCEl.offset(tOffset);
  el.css('width', `${vRect.width}px`);

  verticalScrollbarSet.call(this);

  horizontalScrollbarSet.call(this);

  sheetFreeze.call(this);

  table.customerRender();

  toolbar.reset();
  selector.reset();
  // getOverlayerElSize.call(this, true);
  return this;
}

function clearClipboard() {
  const { data, selector } = this;
  data.clearClipboard();
  selector.hideClipboard();
}

// ctrl + c
function copy() {
  const { data, selector } = this;
  data.copy();
  selector.showClipboard();
}

// ctrl + x
function cut() {
  const { data, selector } = this;
  // linjian 添加是否启用剪切功能，填报页面不需要剪切
  if (data.isNeedCut) {
    data.cut();
    selector.showClipboard();
  }
}

// ctrl + v
function paste(what) {
  const { data } = this;
  if (data.paste(what, msg => data.hook.tipMessage('alert', msg))) {
    sheetReset.call(this);
  }
}
// 设置筛选条件
function autofilter() {
  const { data } = this;
  data.autofilter();
  sheetReset.call(this);
}

function toolbarChangePaintformatPaste() {
  const { toolbar } = this;
  if (toolbar.paintformatActive()) {
    paste.call(this, 'format');
    clearClipboard.call(this);
    toolbar.paintformatToggle();
  }
}

// 获取正在公式编辑的单元格的位置行列值
function formulaEditoringCell() {
  const { areaOffset } = this.editor;
  if (areaOffset) {
    const { left, top } = areaOffset;
    const { ri, ci } = this.data.getCellRectByXY(left, top);
    return { ri: ri + 1, ci: ci + 1 };
  }
  return { ci: 0, ri: 0 };
}

// 遮罩层的鼠标按下事件处理函数
function overlayerMousedown(evt) {
  // console.log(':::::overlayer.mousedown:', evt.detail, evt.button, evt.buttons, evt.shiftKey);
  // console.log('evt.target.className:', evt.target.className);
  const {
    selector, data, table, sortFilter,
  } = this;
  const { offsetX, offsetY } = evt;
  const isAutofillEl = evt.target.className === `${cssPrefix}-selector-corner`;
  const cellRect = data.getCellRectByXY(offsetX, offsetY);
  const {
    left, top, width, height,
  } = cellRect;
  let { ri, ci } = cellRect;
  // sort or filter
  const { autoFilter } = data;
  const isInMergeObj = this.getLastCellMerge(ri, ci);
  const {
    firstCi,
    firstRi,
    isInMerge,
    lastCi,
    lastRi,
  } = isInMergeObj;
  let isAutoFilterInclude = false;
  if (isInMerge) {
    ri = firstRi;
    ci = firstCi;
    isAutoFilterInclude = autoFilter.includes(lastRi, lastCi);
  } else {
    isAutoFilterInclude = autoFilter.includes(ri, ci);
  }
  if (isAutoFilterInclude) {
    if (left + width - 20 < offsetX && top + height - 20 < offsetY
        && left + width >= offsetX && top + height >= offsetY) {
      // linjian 这里把插件自带填充项去掉
      // const items = autoFilter.items(ri, ci, (r, c) => data.rows.getCell(r, c));
      // sortFilter.set(ri, ci, items, autoFilter.getFilter(ri, ci), autoFilter.getSort(ri, ci));
      // 添加钩子选中后可以
      const { requestCondition } = data.hook;
      const filter = autoFilter.getFilter(ri, ci);
      let searchKey = '';
      if (filter && filter.searchKey) {
        ({ searchKey } = filter);
      }
      requestCondition(ri, ci, searchKey, isInMergeObj, (result) => {
        sortFilter.setItems(ri, ci, result, filter, autoFilter.getSort(ri, ci), 'init');
      });
      // 获取容器左上角的位置
      const relativeRect = this.el.el.getBoundingClientRect();
      const finalLeft = left + relativeRect.left;
      const finalTop = top + relativeRect.top;
      // console.log();
      sortFilter.show(); // 先显示，然后计算位置才正确，要不获取到的宽高都是0
      sortFilter.setOffset(helper.getPosition(null, sortFilter.el.el,
        { elemOffsetWidth: 0, elemOffsetHeight: height + 2, cellRect: { left: finalLeft, top: finalTop } }));
      return;
    }
  }
  sortFilter.hide();
  // 添加选中后钩子
  const { afterSelection, beforeChangeFile } = data.hook;
  // linjian 添加全选点击事件
  if (ri === -1 && ci === -1 && evt.target.classList.contains(`${cssPrefix}-overlayer`)) {
    const eri = this.data.rows.len - 1;
    const eci = this.data.cols.len - 1;
    this.data.selector.setRange({
      sri: 0, sci: 0, eri, eci,
    });
    this.refresh();
    afterSelection.call(data, 0, 0, eri, eci); // 添加钩子
  }
  // 只要一个为-1就说明 用户进行了行或者列选中
  if (ri === -1 || ci === -1) {
    this.data.isSelectPartOfAll = true;
  } else {
    this.data.isSelectPartOfAll = false;
  }
  // console.log('ri:', ri, ', ci:', ci);
  if (!evt.shiftKey) {
    // console.log('selectorSetStart:::');
    if (isAutofillEl) {
      selector.showAutofill(ri, ci);
    } else {
      // linjian 这里要添加 图片和附件的钩子
      const cellProps = this.data.getCellProps(ri, ci);
      if (cellProps) {
        if (!this.data.isReadOnlySheet && (cellProps.cellType === 'image' || cellProps.cellType === 'file')) {
          beforeChangeFile(this.data.getCell(ri, ci), ri, ci, 0);
        }
        if (!this.data.isReadOnlySheet && cellProps.cellType === 'checkbox') {
          const cell = this.data.getCell(ri, ci);
          if (cell.text) {
            this.data.setCellText(ri, ci, false, 'finished');
          } else {
            this.data.setCellText(ri, ci, true, 'finished');
          }
          this.refresh();
        }
      }
      selectorSet.call(this, false, ri, ci);
    }

    // mouseMoveUp(window, (e) => {
    //   let direction = '';
    //   if (!e.target.classList.contains('x-spreadsheet-overlayer')) {
    //     if (!this.lastOffset) {
    //       this.lastOffset = {
    //         offsetX: e.offsetX,
    //         offsetY: e.offsetY,
    //       };
    //     } else {
    //       const offDistanceX = e.offsetX - this.lastOffset.offsetX;
    //       const offDistanceY = e.offsetY - this.lastOffset.offsetY;
    //       if (offDistanceX > 10) {
    //         direction = 'right';
    //       }
    //       if (offDistanceX < -10) {
    //         direction = 'left';
    //       }
    //       if (offDistanceY > 10) {
    //         direction = 'down';
    //       }
    //       if (offDistanceY < -10) {
    //         direction = 'up';
    //       }
    //     }
    //     if (!this.increaseTime) {
    //       this.increaseTime = setInterval(() => {
    //         selectorMove.call(this, true, direction);
    //       }, 500);
    //     }
    //   }
    // }, (e) => {
    //   clearInterval(this.increaseTime);
    //   this.increaseTime = null;
    //   this.lastOffset = null;
    // });
    // mouse move up
    mouseMoveUp(this.targetEl.el, (e) => {
      // console.log('mouseMoveUp::::');
      ({ ri, ci } = data.getCellRectByXY(e.offsetX, e.offsetY));
      if (isAutofillEl) {
        selector.showAutofill(ri, ci);
      } else if (e.buttons === 1 && !e.shiftKey) {
        selectorSet.call(this, true, ri, ci, true, true);
      }
    }, () => {
      if (isAutofillEl) {
        // 这里自动填充修改为text
        if (data.autofill(selector.arange, 'text', msg => data.hook.tipMessage('alert', msg))) {
          table.render();
        }
      }
      selector.hideAutofill();
      toolbarChangePaintformatPaste.call(this);
    });
  }

  if (!isAutofillEl && evt.buttons === 1) {
    if (evt.shiftKey) {
      // console.log('shiftKey::::');
      selectorSet.call(this, true, ri, ci);
    }
  }
}

function editorSetOffset() {
  const { editor, data } = this;
  const sOffset = data.getSelectedRect();
  const tOffset = this.getTableOffset();
  let sPosition = 'top';
  // console.log('sOffset:', sOffset, ':', tOffset);
  if (sOffset.top > tOffset.height / 2) {
    sPosition = 'bottom';
  }
  editor.setOffset(sOffset, sPosition);
}

// TODO: FIXME: 关键函数
function editorSet(isNeedWriteToCell) {
  const { editor, data } = this;
  editor.isAutoFocus = false;
  editorSetOffset.call(this);
  editor.setCell(data.getSelectedCell(), data.getSelectedValidator(), isNeedWriteToCell);
  clearClipboard.call(this);
  const { hook } = this.data;
  const { isInEditStatus } = hook;
  isInEditStatus(true);
}

function verticalScrollbarMove(distance, evt, scrollHeight, offsetHeight) {
  const {
    data, table, selector, addRow,
  } = this;
  // linjian distance在数据量大于134w的时候会出现滚动条实际总长度和获取到长度不一致，所以这里做了同等比例的处理
  const scale = data.totalHeightValue / scrollHeight;
  let realDistance = scale * distance;
  let isLast = false;

  // 由于scale计算出来存在误差，所以拉不到最底部，下面判断如果scale不一样时，处理拉倒最底部的情况
  if (scale !== 1) {
    if (distance + offsetHeight >= scrollHeight - 5) {
      realDistance = data.totalHeightValue - data.settings.view.height() + 70;
      isLast = true;
    }
  } else if (distance + offsetHeight === scrollHeight) { // linjian 这里加20主要是给加号（加行按钮）腾出位置
    realDistance += 30;
    isLast = true;
  }
  // 记录scrollDistance，用来计算加号相对位置
  this.data.scrollDistance = realDistance;
  const { showAddRow } = this.data.settings;
  if (showAddRow) {
    if (isLast) {
      h(addRow.el).show();
    } else {
      h(addRow.el).hide();
    }
  }
  data.scrolly(realDistance, (sri) => {
    // linjian 这里做了顺序替换将 table.render放在最前面，原来是放在最后一行，原因是未render之前不会对startPosition进行更新
    // let viewRange = this.data.viewRange();
    // this.data.startPostion.sri = viewRange.sri;
    table.render();
    selector.resetBRLAreaOffset(sri);
    editorSetOffset.call(this, sri);
  });
}
// linjian 添加竖直滚动监听处理 row作为条件变化后，改变的行
function verticalScrollbarMoveVertical(e, actionType = 'loadNext', row) {
  const { data } = this;
  const { rows } = data;
  const cellRange = data.viewRange();
  const { sri, eri } = cellRange;
  const conditions = data.autoFilter.getCustomerFilterData();

  //  linjian 先判断哪些行需要从后台请求数据，因为这些行
  let startRow = -1;
  for (let index = sri; index < eri; index++) {
    const rowObj = rows._[index];
    if (!rowObj || !rowObj.cells) {
      startRow = index;
      break;
    }
  }
  if (actionType === 'loadNext') {
    // 如果为 -1 ，则说明这个视角是都已经加载完成数据了
    if (startRow === -1) {
      return;
    }
    if (data.isexcuteOptimize) { // 是否执行优化策略 ，如果为true，则在数据量过大状态删除多余数据
      const totalLen = rows.len;
      const clipboardRange = this.data.clipboard.range;
      for (let index = 51; index < totalLen; index++) {
        if (index <= sri - 10 && index >= eri + 10) {
          if (index >= clipboardRange.sri && index <= clipboardRange.eri) {
            continue;
          }
          if (!this.data.isRowInStream(index)) {
            continue;
          }
          // delete rows.get(index).cells;
          delete rows.get(index);
        }
      }
    }
  } else {
    startRow = row + 1;// linjian 因为筛选项是放在表头上的
  }
  // 执行回调外部方法
  this.data.hook.requestData({
    startRow, endRow: eri, conditions, actionType,
  }, (/* loaddingDatas */) => {
    // this.data.setData(loaddingDatas);
    sheetCustomerReset.call(this);
  });
}

function horizontalScrollbarMove(distance, evt, scrollHeight, offsetHeight) {
  const {
    data, table, selector, addCol,
  } = this;
  const { showAddCol } = this.data.settings;
  if (distance + offsetHeight === scrollHeight && showAddCol) { // linjian 这里加20主要是给加号（加行按钮）腾出位置
    h(addCol.el).css({ right: '12px', display: 'block' });
  } else {
    h(addCol.el).hide();
  }
  data.scrollx(distance, () => {
    selector.resetBRTAreaOffset();
    editorSetOffset.call(this);
    table.render();
  });
}

function rowResizerFinished(cRect, distance) {
  const { ri } = cRect;
  const { table, selector, data } = this;
  data.rows.setHeight(ri, distance);
  table.render();
  // 启动webworker 计算垂直距离 linjian
  utils.workerCalUtil(this, (val) => {
    verticalScrollbarSet.call(this, val);
  });
  selector.resetAreaOffset();
  editorSetOffset.call(this);
}

function colResizerFinished(cRect, distance) {
  const { ci } = cRect;
  const { table, selector, data } = this;
  data.cols.setWidth(ci, distance);
  // console.log('data:', data);
  table.render();
  selector.resetAreaOffset();
  horizontalScrollbarSet.call(this);
  editorSetOffset.call(this);
}

// TODO: FIXME: 用 canvas 绘画 输入的值
function dataSetCellText(text, state = 'finished', changeSource, formulaEditorRC) {
  const { data, table, editor } = this;
  // const [ri, ci] = selector.indexes;
  data.setSelectedCellText(text, state, changeSource, formulaEditorRC);
  if (state === 'finished') {
    editor.formulaHanging = false;
    table.render();
  }
}

function insertDeleteRowColumn(type) {
  const { data } = this;
  if (type === 'insert-row') {
    data.insert('row');
  } else if (type === 'delete-row') {
    data.delete('row');
  } else if (type === 'insert-column') {
    data.insert('column');
  } else if (type === 'delete-column') {
    data.delete('column');
  } else if (type === 'delete-cell') {
    data.deleteCell();
  } else if (type === 'delete-cell-format') {
    data.deleteCell('format');
  } else if (type === 'delete-cell-text') {
    data.deleteCell('text');
  }
  clearClipboard.call(this);
  setTimeout(() => {
    sheetReset.call(this);
  }, 0);
}

function toolbarChange(type, value) {
  const { data } = this;
  if (type === 'undo') {
    this.undo();
  } else if (type === 'redo') {
    this.redo();
  } else if (type === 'print') {
    // print
  } else if (type === 'paintformat') {
    if (value === true) copy.call(this);
    else clearClipboard.call(this);
  } else if (type === 'clearformat') {
    insertDeleteRowColumn.call(this, 'delete-cell-format');
  } else if (type === 'link') {
    // link
  } else if (type === 'chart') {
    // chart
  } else if (type === 'autofilter') {
    // filter
    autofilter.call(this);
  } else if (type === 'copy') {
    copy.call(this);
  } else if (type === 'cut') {
    cut.call(this);
  } else if (type === 'freeze') {
    if (value) {
      const { ri, ci } = data.selector;
      this.freeze(ri, ci);
    } else {
      this.freeze(0, 0);
    }
  } else {
    data.setSelectedCellAttr(type, value);
    if (type === 'formula' && !data.selector.multiple()) {
      editorSet.call(this);
    }
    sheetReset.call(this);
  }
}

function sortFilterChange(ri, ci, order, operator, value, isSelectAll, searchKey) {
  // console.log('sort:', sortDesc, operator, value);
  this.data.setAutoFilter(ri, ci, order, operator, value, isSelectAll, searchKey);
  this.data.requestData();
  // sheetReset.call(this);
}

function isCanEditText() {
  const cell = this.data.getCell(this.data.selector.ri, this.data.selector.ci);
  const { cellProps } = cell || {};
  if (this.data.isReadOnlySheet) {
    return false;
  }
  if (cell && cell.readOnly) {
    return false;
  }
  if (cellProps && cellProps.cellType !== 'text'
    && cellProps.cellType !== 'numeric' && cellProps.cellType !== 'formula') {
    if (cellProps.cellType === 'dropdown') {
      const { typeOptions } = cell;
      // linjian 判断下拉类型是否要支持自定义如果不支持，就是只读
      if (typeOptions && typeOptions.allowCustomValue) {
        return true;
      }
    }
    return false;
  }
  return true;
}

function windowKeyDownEvt(evt) {
  if (!this.focusing) return;
  const {
    editor,
    contextMenu,
    data,
    toolbar,
  } = this;
  const keyCode = evt.keyCode || evt.which;
  const {
    key, ctrlKey, shiftKey, altKey, metaKey,
  } = evt;
  // console.log('keydown.evt: ', keyCode);
  if (ctrlKey || metaKey) {
    // const { sIndexes, eIndexes } = selector;
    let what = 'all';
    if (this.data.isOnlyCopeText) {
      what = 'text';
    }
    if (shiftKey) what = 'text';
    if (altKey) what = 'format';
    const { clipboard } = this.data;
    switch (keyCode) {
      case 90:
        // undo: ctrl + z
        this.undo();
        evt.preventDefault();
        break;
      case 89:
        // redo: ctrl + y
        this.redo();
        evt.preventDefault();
        break;
      case 67:
        // ctrl + c
        copy.call(this);
        evt.preventDefault();
        break;
      case 88:
        // ctrl + x
        cut.call(this);
        evt.preventDefault();
        break;
      case 85:
        // ctrl + u
        toolbar.trigger('underline');
        evt.preventDefault();
        break;
      case 86:
        // ctrl + v
        // 只有系统内的复制不为空的时候，才会执行，这里系统外的粘贴命令放在构造函数那里监听
        if (!clipboard.isClear()) {
          paste.call(this, what);
          evt.preventDefault();
        }
        break;
      case 37:
        // ctrl + left
        selectorMove.call(this, shiftKey, 'row-first');
        evt.preventDefault();
        break;
      case 38:
        // ctrl + up
        selectorMove.call(this, shiftKey, 'col-first');
        evt.preventDefault();
        break;
      case 39:
        // ctrl + right
        selectorMove.call(this, shiftKey, 'row-last');
        evt.preventDefault();
        break;
      case 40:
        // ctrl + down
        selectorMove.call(this, shiftKey, 'col-last');
        evt.preventDefault();
        break;
      case 32:
        // ctrl + space, all cells in col
        selectorSet.call(this, false, -1, data.selector.ci, false);
        evt.preventDefault();
        break;
      case 66:
        // ctrl + B
        toolbar.trigger('bold');
        break;
      case 73:
        // ctrl + I
        toolbar.trigger('italic');
        break;
      default:
        break;
    }
  } else {
    // console.log('evt.keyCode:', evt.keyCode);
    const dropdownItems = document.querySelector(`.${cssPrefix}-dropdown-items`);
    switch (keyCode) {
      case 32:
        if (shiftKey) {
          // shift + space, all cells in row
          selectorSet.call(this, false, data.selector.ri, -1, false);
        }
        break;
      case 27: // esc
        contextMenu.hide();
        clearClipboard.call(this);
        break;
      case 37: // left
        selectorMove.call(this, shiftKey, 'left');
        evt.preventDefault();
        break;
      case 38: // up
        // linjian 添加下拉键监听
        if (dropdownItems && dropdownItems.style.display === 'block') {
          selectDropdownItems('up');
        } else {
          selectorMove.call(this, shiftKey, 'up');
        }
        evt.preventDefault();
        break;
      case 39: // right
        selectorMove.call(this, shiftKey, 'right');
        evt.preventDefault();
        break;
      case 40: // down
        if (dropdownItems && dropdownItems.style.display === 'block') {
          selectDropdownItems('down');
        } else {
          selectorMove.call(this, shiftKey, 'down');
        }
        evt.preventDefault();
        break;
      case 9: // tab
        editor.clear();
        // shift + tab => move left
        // tab => move right
        selectorMove.call(this, false, shiftKey ? 'left' : 'right');
        evt.preventDefault();
        break;
      case 13: // enter
        if (dropdownItems && dropdownItems.style.display === 'block') {
          selectDropdownItems('enter');
        } else {
          // linjian 这里传入一个参数 用来指定enter事件，用户输入空值也可以清空
          editor.clear(true);
          // shift + enter => move up
          // enter => move down
          selectorMove.call(this, false, shiftKey ? 'up' : 'down');
        }
        evt.preventDefault();
        break;
      case 8: // backspace
        insertDeleteRowColumn.call(this, 'delete-cell-text');
        evt.preventDefault();
        break;
      default:
        break;
    }
    if (key === 'Delete') {
      insertDeleteRowColumn.call(this, 'delete-cell-text');
      evt.preventDefault();
    } else if ((keyCode >= 65 && keyCode <= 90)
      || (keyCode >= 48 && keyCode <= 57)
      || (keyCode >= 96 && keyCode <= 105)
      || evt.key === '='
    ) { // 大写 数字 小键盘的数字
      // linjian 只读单元格 不编辑
      if (!isCanEditText.call(this)) {
        return;
      }
      dataSetCellText.call(this, evt.key, 'input');
      editorSet.call(this);
    } else if (keyCode === 113) {
      // F2
      editorSet.call(this);
    }
  }
}

function showContextMenu(evt, data, contextMenu) {
  const isFreeze = data.freeze[0] !== 0 || data.freeze[1] !== 0;
  const { hook } = this.data;
  const { beforeContextMenuShow } = hook;
  if (typeof beforeContextMenuShow === 'function') {
    beforeContextMenuShow(contextMenu);
  }
  contextMenu.setPosition(evt.offsetX, evt.offsetY, { isFreeze });
  evt.stopPropagation();
}

// TODO: FIXME: 初始化数据表
function sheetInitEvents() {
  const {
    overlayerEl,
    rowResizer,
    colResizer,
    verticalScrollbar,
    horizontalScrollbar,
    editor,
    contextMenu,
    data,
    toolbar,
    modalValidation,
    sortFilter,
  } = this;
  // overlayer
  overlayerEl
    .on('mousemove', (evt) => {
      overlayerMousemove.call(this, evt);
    })
    .on('mousedown', (evt) => {
      // the left mouse button: mousedown → mouseup → click
      // the right mouse button: mousedown → contenxtmenu → mouseup
      // const [ri, ci] = this.selector.indexes;
      if (evt.buttons === 2) { // 鼠标右键 TODO: FIXME:
        // 下面右键逻辑，如果用户进行了行或者列选中，右键表头，直接显示菜单，如果没有行列选中，则判断，当前右键或者是不是在选中单元格范围，如果是，直接显示，不是的话则是选中当前右键范围，然后再显示右键菜单
        if (data.xyInSelectedRect(evt.offsetX, evt.offsetY)) {
          showContextMenu.call(this, evt, data, contextMenu);
        } else {
          const { hook } = this.data;
          const { isInEditStatus } = hook;
          isInEditStatus(false);
          editor.clear();
          overlayerMousedown.call(this, evt);
          contextMenu.hide();
          showContextMenu.call(this, evt, data, contextMenu);
        }
      } else if (evt.detail === 2) { // overlayerEl 层，双击
        // e.detail 包含一个数值，表示在给定位置上发生了几次单击
        // 在同一个元素上，相继的发生一次mousedown与一次mouseup算作一次单击
        // 如果之间变换位置，则detail值清零。

        // 如果在表头双击  不做处理
        const { sri, eri } = this.selector.range;
        if (this.data.rows.len === (eri - sri + 1)) {
          return;
        }
        /**
         * @description: 添加单元格只读
         * @Author: linjian
         * @Date: 2019-04-24 21:08:14
         */
        if (!isCanEditText.call(this)) {
          return;
        }
        setTimeout(() => {
          editorSet.call(this);
        }, 0);
      } else { // 判断当前的 Editor 是否处于公式编辑状态
        const { hook } = this.data;
        const { isInEditStatus } = hook;
        const textareaVal = editor.textEl.val();
        if (textareaVal && /^=[A-Z]+\(.*\)$/.test(textareaVal)) {
          const { el } = editor.textEl;
          const cursorIndex = getCaretPosition(el); // 光标的位置
          const leftBracketIndex = textareaVal.indexOf('('); // 左括号位置
          const rightBracketIndex = textareaVal.lastIndexOf(')'); //  右括号位置
          if (leftBracketIndex <= cursorIndex && cursorIndex <= rightBracketIndex) { // 光标在括号之内表示编辑状态
            isInEditStatus(true);
            editor.formulaHanging = true;
          } else {
            isInEditStatus(false);
            editor.clear();
          }
        } else {
          isInEditStatus(false);
          editor.clear();
        }
        overlayerMousedown.call(this, evt);
      }
    }).on('mousewheel.stop', (evt) => {
      overlayerMousescroll.call(this, evt);
    });

  // slide on mobile
  bindTouch(overlayerEl.el, {
    move: (direction, d) => {
      overlayerTouch.call(this, direction, d);
    },
  });

  // toolbar change
  toolbar.change = (type, value) => toolbarChange.call(this, type, value);

  // sort filter ok
  sortFilter.ok = (ri, ci, order, operator, values, isSelectAll, searchKey) => sortFilterChange.call(this, ri, ci, order, operator, values, isSelectAll, searchKey);
  sortFilter.conditionChange = () => {
    utils.debounce(() => {
      const text = document.querySelector('.search-text-input').value;
      const { ri, ci } = sortFilter;
      // sortFilter
      const { requestCondition } = this.data.hook;
      sortFilter.searchKey = text;
      requestCondition(ri, ci, text, result => sortFilter.setItems(ri, ci, result, this.data.autoFilter.getFilter(ri, ci), this.data.autoFilter.getSort(ri, ci)));
    }, 300).call(this);
  };

  // 筛选条件改变的时候，请求表格数据
  // conditions：当前的筛选条件， type:resetAutoFilter重置筛选条件 type这里没有用到，暂时先屏蔽
  data.requestData = (type, row) => {
    verticalScrollbarMoveVertical.call(this, null, 'conditionChange', row);
  };
  // resizer finished callback
  rowResizer.finishedFn = (cRect, distance) => {
    rowResizerFinished.call(this, cRect, distance);
  };
  colResizer.finishedFn = (cRect, distance) => {
    colResizerFinished.call(this, cRect, distance);
  };
  // scrollbar move callback
  verticalScrollbar.moveFn = (distance, evt, scrollHeight, offsetHeight) => {
    verticalScrollbarMove.call(this, distance, evt, scrollHeight, offsetHeight);
  };
  horizontalScrollbar.moveFn = (distance, evt, scrollHeight, offsetHeight) => {
    horizontalScrollbarMove.call(this, distance, evt, scrollHeight, offsetHeight);
  };
  // linjian scrollbar move vertical callback
  verticalScrollbar.moveVerticalFn = (evt) => {
    // 添加防抖动处理
    utils.debounce(verticalScrollbarMoveVertical, 300)
      .call(this, evt);
  };
  editor.change = (state, itext) => {
    // 如果单元格是在进行公式编辑，则将 Editor 所在位置的 cell 传入
    const editoringCellRC = editor.formulaHanging ? formulaEditoringCell.call(this) : null;
    dataSetCellText.call(this, itext, state, 'editorChange', editoringCellRC);
  };
  // modal validation
  modalValidation.change = (action, ...args) => {
    if (action === 'save') {
      data.addValidation(...args);
    } else {
      data.removeValidation();
    }
  };

  // contextmenu linjian 添加callback ，如果存在callback说明就是自定义的
  contextMenu.itemClick = (type, e, callback) => {
    // console.log('type:', type);
    if (typeof callback === 'function') {
      callback(type, this.data.selector, e);
      return true;
    }

    if (type === 'copy') {
      copy.call(this);
    } else if (type === 'cut') {
      cut.call(this);
    } else if (type === 'paste') {
      paste.call(this, 'all');
    } else if (type === 'paste-value') {
      paste.call(this, 'text');
    } else if (type === 'paste-format') {
      paste.call(this, 'format');
    }

    /* else if (type === 'validation') {
      modalValidation.setValue(data.getSelectedValidation());
    } else if (type === 'freezePanes') {
      const { freeze } = this.data;
      const { ri, ci } = this.data.selector;
      if (freeze[0] === 0 && freeze[1] === 0) {
        this.freeze(ri, ci);
        // e.target.textContent = '取消冻结窗格';
      } else {
        this.freeze(0, 0);
        // e.target.textContent = '冻结窗格';
      }
    } else {
      insertDeleteRowColumn.call(this, type);
    } */
    return true;
  };

  bind(window, 'resize', () => {
    utils.debounce(() => {
      // 重新计算窗口高度
      const screenHeight = document.documentElement.clientHeight;
      const screenWidth = document.documentElement.clientWidth;
      utils.setSessionStorage({ key: 'screenHeight', content: screenHeight });
      utils.setSessionStorage({ key: 'screenWidth', content: screenWidth });
      this.reload();
    }, 300).call(this);
  });

  bind(window, 'click', (evt) => {
    this.focusing = overlayerEl.contains(evt.target);
  });

  // for selector
  bind(window, 'keydown', (evt) => { windowKeyDownEvt.call(this, evt); });
}

