import helper from './helper';

class Cols {
  constructor({
    len, width, indexWidth, minWidth, colAttr = {},
  }, rowLength, hideCol) {
    // 添加hideCoL 隐藏了参数 便于计算宽度
    this._ = {};
    // linjian 传入列属性  目前放着列宽 width
    this._ = colAttr;
    this.len = len;
    this.width = width;
    this.indexWidth = indexWidth;
    this.minWidth = minWidth;
    // 处理一下 indexWidth ,用于处理行表头宽度，根据row大小动态计算应该宽度大小，如果不够则直接使用计算出来的值
    const calIndexWidth = Math.log10(Math.ceil(rowLength / 100)) * 10 + 22;
    if (indexWidth < calIndexWidth) {
      this.indexWidth = calIndexWidth;
    }
    // 隐藏列
    this.hideCol = hideCol;
  }

  setData(d) {
    if (d.len) {
      this.len = d.len;
      delete d.len;
    }
    this._ = d;
  }

  getData() {
    const { len } = this;
    return Object.assign({ len }, this._);
  }

  getWidth(i) {
    if (this.hideCol.includes(i)) {
      return 0;
    }
    const col = this._[i];
    if (col && col.width) {
      return col.width;
    }
    return this.width;
  }

  getOrNew(ci) {
    this._[ci] = this._[ci] || {};
    return this._[ci];
  }

  setWidth(ci, width) {
    const col = this.getOrNew(ci);
    col.width = width;
  }

  setStyle(ci, style) {
    const col = this.getOrNew(ci);
    col.style = style;
  }

  sumWidth(min, max) {
    return helper.rangeSum(min, max, i => this.getWidth(i));
  }

  totalWidth() {
    return this.sumWidth(0, this.len);
  }

  isHideCol(col) {
    return this.hideCol.includes(col);
  }
}

export default {};
export {
  Cols,
};
