/* global document */
// "typeOptions":{
//   "link":[{"point":"A3","col":0,"row":2},{"point":"B3","col":1,"row":2}],
//   "allowTree":false,
//   "allowCustomValue":true,
//   "allowParentSelection":false,
//   "allowMultiSelect":true,
//   "level":0,
//   "validation":{}
// },
export default class Tree {
  constructor(dom, typeOptions, data) {
    const {
      allowMultiSelect, allowParentSelection, link, level,
    } = typeOptions;
    this.html = '';
    this.level = 1;
    this.dom = dom;
    this.allowMultiSelect = allowMultiSelect;
    this.allowParentSelection = allowParentSelection;
    this.link = link;
    this.data = data;
    dom.innerHTML = '';
    if (link) {
      this.linkLevel = level; // 这个level用来处理枚举联动 当前格子处于联动的第几级
      if (level !== 0) {
        const preLink = link[level - 1];
        const { row, col } = preLink;
        this.preCellValue = this.data.getCell(row, col).text;
      }
    }
  }

  // 遍历树形结构
  getData(source, dom, parentObj) {
    if (Object.prototype.toString.call(source) === '[object Array]') {
      for (let i = 0; i < source.length; i++) {
        // 添加标题
        const item = document.createElement('div');
        item.className = 'item-container';
        let arrow = `
          <span class="to__dropdownList">
            <i click-type="dropClick" class="to__roate">
              <svg t="1550632829702" class="icon" style="" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="1783" xmlns:xlink="http://www.w3.org/1999/xlink" width="100%" height="100%">
                <defs>
                  <style type="text/css"></style>
                </defs>
                <path d="M959.52557 254.29773 511.674589 702.334953 63.824631 254.29773Z" p-id="1784"></path>
              </svg>
            </i>
          </span>
        `;
        let paddingLeftStyle = '';
        if (!source[i].children || source[i].children.length === 0) {
          paddingLeftStyle = 'to__padding_left0';
          arrow = '';
        }
        if (this.level === 1 || this.link) {
          paddingLeftStyle = 'to__padding_left10';
        }
        let checkboxHtml = '';
        let isTranversal = true;
        const isLeaf = !source[i].children || source[i].children.length === 0;
        // 只有非联动才能多选，联动不能多选
        if (!this.link && this.allowMultiSelect) {
          // 允许选中父节点
          if (this.allowParentSelection) {
            checkboxHtml = `<input type="checkbox" name="cName" value="${source[i].id}" text="${source[i].text}"/>`;
          // 不允许选中父节点
          } else if (isLeaf) {
            checkboxHtml = `<input type="checkbox" name="cName" value="${source[i].id}" text="${source[i].text}"/>`;
          }
          // 如果是联动，则要处理联动关系
        } else if (this.link && this.linkLevel === 0) {
          // 第一级联动，直接取树第一层的source
          isTranversal = false;
          arrow = '';
        } else if (this.link) {
          // 不是处理第一级联动，要根据上级的值，获取当前联动的值
          arrow = '';
          if (this.preCellValue) {
            if (parentObj && parentObj.id === this.preCellValue) {
              isTranversal = false;
            } else {
              this.level++;
              this.getData(source[i].children, dom, source[i]);
              continue;
            }
          } else {
            item.innerHTML = `<div class="to__item ${paddingLeftStyle}">
                <span class="to__click_item" click-type="domClick" is-leaf="${isLeaf}"  value="" text="">
                  <div class="to__name to__padding_left2">没有匹配到选项</div>
                </span>
              </div>`;
            dom.appendChild(item);
            break;
          }
        }
        item.innerHTML = `<div class="to__item level-${this.level} ${paddingLeftStyle}">${arrow}
                            <span class="to__click_item" click-type="domClick" is-leaf="${isLeaf}"  value="${source[i].id}" text="${source[i].text}">
                              ${checkboxHtml}
                              <div class="to__name ${!checkboxHtml ? 'to__padding_left2' : ''}">${source[i].text}</div>
                            </span>
                          </div>`;
        dom.appendChild(item);

        // 添加子元素
        const subItem = document.createElement('div');
        subItem.className = 'to__subItem to__show';
        item.appendChild(subItem);

        if (!isLeaf && isTranversal) {
          this.level++;
          this.getData(source[i].children, subItem);
        } else if (i === source.length - 1) {
          this.level--;
        }
      }
    }
  }
}
