import { arrayEach, arrayFilter } from '../../helpers/array';
import BaseCell from './_base';

/**
 * Class which indicates formula expression precedents cells at specified cell
 * coordinates (CellValue). This object uses visual cell coordinates.
 *
 * @class CellReference
 * @util
 */
class CellReference extends BaseCell {
  constructor(row, column, sheetName) {
    super(row, column);

    this.sheetName = sheetName || undefined;
    // 依赖当前单元格的单元格
    this.dependents = [];
  }

  // 增加依赖者
  add(newCell) {
    let getIt = false;
    arrayEach(this.dependents, (cell) => {
      if (cell.isEqual(newCell)) {
        getIt = true;
        return false;
      }
      return true;
    });
    if (!getIt) {
      this.dependents.push(newCell);
    }
  }

  // 删除依赖者
  remove(cellToBeRemoved) {
    this.dependents = arrayFilter(this.dependents, (cell) => !cell.isEqual(cellToBeRemoved));
  }

  setSheetName(name) {
    this.sheetName = name;
  }

  pushDependentsTo(arr = []) {
    arrayEach(this.dependents, (dependent) => {
      if (arr.indexOf(dependent) === -1) {
        arr.push(dependent);
      }
    });
    return arr;
  }
}

export default CellReference;
