import { arrayEach } from '../../../helpers/array';
import BaseCell from './_base';

/**
 * Class which indicates formula expression precedents cells at specified cell
 * coordinates (CellValue). This object uses visual cell coordinates.
 *
 * @class CellReference
 * @util
 */
class CellReference extends BaseCell {
  // 增加实例id - xyx =============================
  constructor(row, column, sheetName) {
    super(row, column);

    this.sheetName = sheetName || undefined;
    // 依赖当前单元格的单元格
    this.next = [];
  }

  // 增加被依赖
  addNext(newCell) {
    let getIt = false;
    arrayEach(this.next, cell => {
      if (cell.isEqual(newCell)) {
        getIt = true;
        return false;
      }
      return true;
    });
    if (!getIt) {
      this.next.push(newCell);
    }
  }

  // 删除被依赖
  removeNext(cellToBeRemoved) {
    arrayEach(this.next, (cell, index) => {
      if (cell.isEqual(cellToBeRemoved)) {
        this.next.splice(index, 1);
        return false;
      }
      return true;
    });
  }
}

export default CellReference;
