/* eslint-disable no-undef */
/* eslint-disable func-names */
import helper from './helper';

const hooks = {
  inputHooks: {},
};

hooks.beforeChangeFile = (cellMeta, row, col, index) => {
  if (hooks.inputHooks.beforeChangeFile) {
    hooks.inputHooks.beforeChangeFile(cellMeta, row, col, index);
  }
};
hooks.afterSetDataAtCell = function (changes/* state */) {
  if (hooks.inputHooks.afterSetDataAtCell) {
    hooks.inputHooks.afterSetDataAtCell(changes);
  }
  // 如果是两个值相等，则不处理
  if (changes[2] === changes[3]) {
    return true;
  }

  // 如果是公式的话，设置此单元格的属性值
  if (/^(=[A-Z]+\()(.*)(\))$/.test(changes[3])) {
    const approachingCell = { row: changes[0], column: changes[1], text: changes[3] };
    const formularType = approachingCell.text.match(/^=([A-Z]+)\((.*)\)$/);
    const tarCell = this.rows._[approachingCell.row].cells[approachingCell.column];

    tarCell.noCalcFormula = true;
    tarCell.cellProps.cellType = 'formula';

    // formularRangeText RegExpress：对公式中括号内容字符串的格式校验
    const frtRegExp = /^[A-Z]+\d+(\:[A-Z]+\d+)?(\,[A-Z]+\d+(\:[A-Z]+\d+)?)*$/;

    tarCell.formularValue = formularType ? (frtRegExp.test(formularType[2]) ? formularType[1] : 'ErrorValue' ) : 'Formula';
    this.setCellStyle(approachingCell.row, approachingCell.column, { color: '#cb2b29' });
  }

  if (this.isCalCulatorFormula || this.isCalculatorDropdownRange) {
    const spreadSheetObj = window.xsObj.getInstanceByName(this.name);
    const changedCell = { row: changes[0], column: changes[1] };
    // 如果是公式，需要update一下
    this.dependences.all.forEach((dependence, dependenceName) => {
      const referenceString = helper.getReferenceString(this, changedCell);
      if (referenceString) {
        this.dependences.update(dependenceName, changedCell, spreadSheetObj.sheet);
      } else {
        dependence.removeDependent(changedCell);
      }
    });
    const { streamCharts } = spreadSheetObj.data.settings;
    let rowOffset = 0;
    streamCharts.some((streamItem) => {
      if (changes[0] >= streamItem.finalFirstRows && changes[0] <= streamItem.finalLastRows) {
        rowOffset = changes[0] - streamItem.finalFirstRows;
        changedCell.row = streamItem.finalFirstRows;
        return true;
      }
      return false;
    });
    // 这里只是更新当前sheet内，那个格子的依赖
    this.dependences.onAfterReferenceValueChange([changedCell], spreadSheetObj.sheet, rowOffset);
   
  }
  return true;
};

hooks.afterSelection = function (sri, sci, eri, eci) {
  setTimeout(() => {
    const cell = this.getCell(sri, sci);
    if (this.fx) {
      if (cell && cell.readOnly) {
        this.fx.setAttribute('readonly', 'readonly');
      } else {
        this.fx.removeAttribute('readonly');
      }
    }
    if (hooks.inputHooks.afterSelection) {
      hooks.inputHooks.afterSelection(sri, sci, eri, eci);
    }
  }, 0);
};

hooks.insertRow = function (sri, sci, eri, eci) {
  if (hooks.inputHooks.insertRow) {
    hooks.inputHooks.insertRow(sri, sci, eri, eci);
  }
};

hooks.insertCol = function (sri, sci, eri, eci) {
  if (hooks.inputHooks.insertCol) {
    hooks.inputHooks.insertCol(sri, sci, eri, eci);
  }
};

hooks.afterMultiSelection = function (sri, sci, eri, eci) {
  if (hooks.inputHooks.afterMultiSelection) {
    hooks.inputHooks.afterMultiSelection(sri, sci, eri, eci);
  }
};
hooks.requestData = function ({
  startRow, endRow, conditions, actionType,
}, callback) {
  if (hooks.inputHooks.requestData) {
    hooks.inputHooks.requestData({
      startRow, endRow, conditions, actionType,
    }, callback);
  }
};
hooks.tipMessage = function (tipType, msg) {
  if (hooks.inputHooks.tipMessage) {
    hooks.inputHooks.tipMessage(tipType, msg);
  }
};
hooks.beforeContextMenuShow = function (contextMenu) {
  if (hooks.inputHooks.beforeContextMenuShow) {
    hooks.inputHooks.beforeContextMenuShow(contextMenu);
  }
};

hooks.afterTriggerHistoryAction = function (historyType, params) {
  if (hooks.inputHooks.afterTriggerHistoryAction) {
    hooks.inputHooks.afterTriggerHistoryAction(historyType, params);
  }
};

hooks.requestCondition = function (ri, ci, conditionItems, isInMergeObj, afterGetConditionItemsCallback) {
  if (hooks.inputHooks.requestCondition) {
    hooks.inputHooks.requestCondition(ri, ci, conditionItems, isInMergeObj, afterGetConditionItemsCallback);
  }
};
hooks.isInEditStatus = function (flag) {
  if (hooks.inputHooks.isInEditStatus) {
    hooks.inputHooks.isInEditStatus(flag);
  }
};
hooks.customerValidate = function (cellProps, data, ri, ci) {
  if (hooks.inputHooks.customerValidate) {
    return hooks.inputHooks.customerValidate(cellProps, data, ri, ci);
  }
  return [true];
};

hooks.afterRender = function () {
  if (hooks.inputHooks.afterRender) {
    hooks.inputHooks.afterRender();
  }
};

export default () => hooks;
