// src: cellRange
// dst: cellRange
function canPaste(src, dst, error = () => {}) {
  const { merges } = this;
  const cellRange = dst.clone();
  const [srn, scn] = src.size();
  const [drn, dcn] = dst.size();
  if (srn > drn) {
    cellRange.eri = dst.sri + srn - 1;
  }
  if (scn > dcn) {
    cellRange.eci = dst.sci + scn - 1;
  }
  if (merges.intersects(cellRange)) {
    error(t('error.pasteForMergedCell'));
    return false;
  }
  return true;
}
function copyPaste(srcCellRange, dstCellRange, what, autofill = false) {
  const { rows, merges } = this;
  // delete dest merge
  if (what === 'all' || what === 'format') {
    rows.deleteCells(dstCellRange, what);
    merges.deleteWithin(dstCellRange);
  }
  rows.copyPaste(srcCellRange, dstCellRange, what, autofill, (ri, ci, cell, isFormula) => {
    if (cell) {
      // console.log('cell:', ri, ci, cell);
      if (cell.merge) {
        const [rn, cn] = cell.merge;
        if (rn <= 0 && cn <= 0) return;
        merges.add(new CellRange(ri, ci, ri + rn, ci + cn));
      }
      // 更新公式依赖
      if (isFormula && this.isCalCulatorFormula) {
        this.calculateZackValue({ row: ri, column: ci });
        // this.dependences.update('formulas', { row: ri, column: ci }, null);
      }
      if (helper.isDropdownRangeExpression(cell) && this.isCalculatorDropdownRange) {
        this.calculateZackValue({ row: ri, column: ci }, 'dropdownRange');
      }
    }
  });
}

function cutPaste(srcCellRange, dstCellRange) {
  const { clipboard, rows, merges } = this;
  rows.cutPaste(srcCellRange, dstCellRange, (formulaDistPosition, formulaSrctPosition, dropdownRangeDistPosition, dropdownRangeSrcPosition) => {
    // 更新公式依赖
    if (this.isCalCulatorFormula) {
      if (Array.isArray(formulaDistPosition) && formulaDistPosition.length > 0) {
        formulaDistPosition.forEach((position) => {
          this.calculateZackValue(position);
          // this.dependences.update('formulas', position, null);
        });
      }
      if (Array.isArray(formulaDistPosition) && formulaDistPosition.length > 0) {
        formulaSrctPosition.forEach((position) => {
          const { row, column } = position;
          helper.removeDependents(this, row, column);
        });
      }
    }
    if (this.isCalculatorDropdownRange) {
      if (Array.isArray(dropdownRangeDistPosition) && dropdownRangeDistPosition.length > 0) {
        dropdownRangeDistPosition.forEach((position) => {
          this.calculateZackValue(position, 'dropdownRange');
          // this.dependences.update('formulas', position, null);
        });
      }
      if (Array.isArray(dropdownRangeSrcPosition) && dropdownRangeSrcPosition.length > 0) {
        dropdownRangeSrcPosition.forEach((position) => {
          const { row, column } = position;
          helper.removeDependents(this, row, column);
        });
      }
    }
  });
  merges.move(srcCellRange,
    dstCellRange.sri - srcCellRange.sri,
    dstCellRange.sci - srcCellRange.sci);
  clipboard.clear();
}


// bss: { top, bottom, left, right }
function setStyleBorder(ri, ci, bss) {
  const { rows } = this;
  const cell = rows.getCellOrNew(ri, ci);
  let cstyle = {};
  if (cell.style !== undefined) {
    // cstyle = helper.cloneDeep(styles[cell.style]);
    // modified by linjian
    cstyle = cell.style;
  }
  Object.assign(cstyle, { border: bss });
  cell.style = this.addStyle(cstyle);
}

function setStyleBorders({ mode, style, color }) {
  const { selector, rows } = this;
  const {
    sri, sci, eri, eci,
  } = selector.range;
  const multiple = !this.isSignleSelected();
  if (!multiple) {
    if (mode === 'inside' || mode === 'horizontal' || mode === 'vertical') {
      return;
    }
  }
  if (mode === 'outside' && !multiple) {
    setStyleBorder.call(this, sri, sci, {
      top: [style, color], bottom: [style, color], left: [style, color], right: [style, color],
    });
  } else if (mode === 'none') {
    selector.range.each((ri, ci) => {
      const cell = rows.getCell(ri, ci);
      if (cell && cell.style !== undefined) {
        // const ns = helper.cloneDeep(styles[cell.style]);
        // modified by linjian
        const ns = helper.cloneDeep(cell.style);
        delete ns.border;
        // ['bottom', 'top', 'left', 'right'].forEach((prop) => {
        //   if (ns[prop]) delete ns[prop];
        // });
        cell.style = this.addStyle(ns);
      }
    });
  } else if (mode === 'all' || mode === 'inside' || mode === 'outside'
    || mode === 'horizontal' || mode === 'vertical') {
    const merges = [];
    for (let ri = sri; ri <= eri; ri += 1) {
      for (let ci = sci; ci <= eci; ci += 1) {
        // jump merges -- start
        const mergeIndexes = [];
        for (let ii = 0; ii < merges.length; ii += 1) {
          const [mri, mci, rn, cn] = merges[ii];
          if (ri === mri + rn + 1) mergeIndexes.push(ii);
          if (mri <= ri && ri <= mri + rn) {
            if (ci === mci) {
              ci += cn + 1;
              break;
            }
          }
        }
        mergeIndexes.forEach(it => merges.splice(it, 1));
        if (ci > eci) break;
        // jump merges -- end
        const cell = rows.getCell(ri, ci);
        let [rn, cn] = [0, 0];
        if (cell && cell.merge) {
          [rn, cn] = cell.merge;
          merges.push([ri, ci, rn, cn]);
        }
        const mrl = rn > 0 && ri + rn === eri;
        const mcl = cn > 0 && ci + cn === eci;
        let bss = {};
        if (mode === 'all') {
          bss = {
            bottom: [style, color],
            top: [style, color],
            left: [style, color],
            right: [style, color],
          };
        } else if (mode === 'inside') {
          if (!mcl && ci < eci) bss.right = [style, color];
          if (!mrl && ri < eri) bss.bottom = [style, color];
        } else if (mode === 'horizontal') {
          if (!mrl && ri < eri) bss.bottom = [style, color];
        } else if (mode === 'vertical') {
          if (!mcl && ci < eci) bss.right = [style, color];
        } else if (mode === 'outside' && multiple) {
          if (sri === ri) bss.top = [style, color];
          if (mrl || eri === ri) bss.bottom = [style, color];
          if (sci === ci) bss.left = [style, color];
          if (mcl || eci === ci) bss.right = [style, color];
        }
        if (Object.keys(bss).length > 0) {
          setStyleBorder.call(this, ri, ci, bss);
        }
        ci += cn;
      }
    }
  } else if (mode === 'top' || mode === 'bottom'
    || mode === 'dBottom' || mode ==='slash') {
    for (let ci = sci; ci <= eci; ci += 1) {
      if (mode === 'top') {
        setStyleBorder.call(this, sri, ci, { top: [style, color] });
        ci += rows.getCellMerge(sri, ci)[1];
      }
      if (mode === 'bottom') {
        setStyleBorder.call(this, eri, ci, { bottom: [style, color] });
        // setStyleBorder.call(this, eri, ci, { dBottom: [style, color] });
        ci += rows.getCellMerge(eri, ci)[1];
      }
      if (mode === 'dBottom') { // 双底边框
        setStyleBorder.call(this, eri, ci, { dBottom: [style, color] });
        ci += rows.getCellMerge(eri, ci)[1];
      }
      if (mode === 'slash') { // 从左到右的斜线
        setStyleBorder.call(this, eri, ci, { slash: [style, color] });
        ci += rows.getCellMerge(eri, ci)[1];
      }
    }
  } else if (mode === 'left' || mode === 'right') {
    for (let ri = sri; ri <= eri; ri += 1) {
      if (mode === 'left') {
        setStyleBorder.call(this, ri, sci, { left: [style, color] });
        ri += rows.getCellMerge(ri, sci)[0];
      }
      if (mode === 'right') {
        setStyleBorder.call(this, ri, eci, { right: [style, color] });
        ri += rows.getCellMerge(ri, eci)[0];
      }
    }
  }
}

function getCellRowByY(y, scrollOffsety) {
  const { rows, startPostion } = this;
  const fsh = this.freezeTotalHeight();
  // console.log('y:', y, ', fsh:', fsh);
  // 这里计算距离是从行表头开始算 所以初始值是默认的行高。
  let inits = rows.height;
  let min = 0;
  // linjian 修改通过startPosition相对位置来计算格子
  const frset = this.exceptRowSet;
  if (y > fsh) {
    if (startPostion && startPostion.sri !== undefined) {
      min = startPostion.sri;
      inits = fsh + inits;
    } else if (fsh + rows.height < y) {
      inits -= scrollOffsety;
    }
  }
  const [ri, top, height] = helper.rangeReduceIf(
    min,
    rows.len,
    inits,
    rows.height,
    y,
    i => rows.getHeight(i),
    frset,
  );
  if (top <= 0) {
    return { ri: -1, top: 0, height };
  }
  return { ri: ri - 1, top, height };
}

function getCellColByX(x, scrollOffsetx) {
  const { cols } = this;
  const fsw = this.freezeTotalWidth();
  let inits = cols.indexWidth;
  if (fsw + cols.indexWidth < x) inits -= scrollOffsetx;
  const [ci, left, width] = helper.rangeReduceIf(
    0,
    cols.len,
    inits,
    cols.indexWidth,
    x,
    i => cols.getWidth(i),
  );
  if (left <= 0) {
    return { ci: -1, left: 0, width: cols.indexWidth };
  }
  return { ci: ci - 1, left, width };
}

export default class DataProxy {
  constructor(settings) {
    this.settings = helper.merge(defaultSettings, settings || {});
    // save data begin
    this.name = settings.name || 'sheet';
    this.freeze = [0, 0];
    this.styles = []; // Array<Style>
    this.merges = new Merges(); // [CellRange, ...]
    // linjian 钩子初始化
    const hook = hooks();
    hook.inputHooks = settings.hooks;
    this.hook = hook;
    const { afterTriggerHistoryAction, tipMessage } = this.hook;
    this.rows = new Rows(this.settings.row, tipMessage, settings.streamCharts);
    /**
     * @description: 增加入参 this.settings.row.len 用于处理行表头宽度，根据row大小动态增加宽度
     * @param { type }
     * @return:
     * @Author: linjian
     * @Date: 2019-05-07 11:22:26
     */
    this.cols = new Cols(this.settings.col, this.settings.row.len, settings.hideCol || []);
    // linjian 因为indexWidth在Cols初始化的时候重新计算了，所以这里要重新复制
    this.settings.col.indexWidth = this.cols.indexWidth;
    this.validations = new Validations();
    this.hyperlinks = {};
    this.comments = {};
    // save data end
    // don't save object
    this.selector = new Selector();
    this.scroll = new Scroll();
    this.clipboard = new Clipboard();
    this.change = () => {};
    this.cachePosition = []; // linjian 用来存储数据量大的时候计算格子位置一个缓存
    this.startPostion = {};// linjian 用来存储每次页面刷新的相对位置
    //  下面是图片和附件的默认图
    this.imageImg = settings.imageImg;
    this.imageFile = settings.imageFile;
    //  下面是bool类型的选中、未选中图片
    this.imageCheckedBox = settings.imageCheckedBox;
    this.imageUncheckedBox = settings.imageUncheckedBox;
    // linjian 添加公式
    this.dependenceState = STATE_NEED_FULL_REBUILD;
    this.dependences = new Dependences(this);
    // linjian 这里添加触发添加历史 前进回退的页面回调
    this.history = new History(afterTriggerHistoryAction);
    this.initing = true;// 用于判断是否处于初始化状态，如果是初始化要判断公式

    // 每条操作栈最大操作行数
    this.historyNumCount = settings.historyNumCount || historyNumCount;
    // 是否启用公式
    this.isCalCulatorFormula = settings.isCalCulatorFormula === undefined
      ? isCalCulatorFormula : settings.isCalCulatorFormula;
    this.isCalculatorDropdownRange = settings.isCalculatorDropdownRange === undefined
      ? isCalculatorDropdownRange : settings.isCalculatorDropdownRange;
    this.isNeedCut = settings.isNeedCut; // 是否启用剪切
    this.isOnlyCopeText = settings.isOnlyCopeText; // 是否在复制、剪切、自动填充的时候，只处理文本
    this.waterMarkContent = settings.waterMarkContent; // 水印内容
    this.isShowWatermark = !!settings.isShowWatermark; // 是否展示水印
    this.isReadOnlySheet = settings.isReadOnlySheet || false; // 此sheet是否只读
    this.autoFilter = new AutoFilter();
    if (settings.isNeedAutoFilter) {
      // linjian 以下是筛选排序
      this.autoFilter.clear();
      // 初始化生成最新的筛选排序范围
      setTimeout(() => {
        this.autoFilter.generate(settings, this.cols.len);
      }, 0);
    }
    this.exceptRowSet = new Set();
    this.sortedRowMap = new Map();
    this.unsortedRowMap = new Map();
    // 添加校验对象
    this.customerValidator = new CustomerValidator();
    // 增加自定义公式由外部指定值
    this.customerFormular = settings.customerFormular;
  }

  // 更新流表标识
  setStreamcharts(streamCharts) {
    if (!streamCharts) {
      return false;
    }
    this.settings.streamCharts = streamCharts;
    return true;
  }

  // 这里做下拉项范围获取运算
  recalculateDropdownRange(sheet) {
    const dependence = this.dependences.getDependence('dropdownRange');
    dependence.reset();
    // const cellToValidate = [];
    const rowLen = this.rows.len;
    const colLen = this.cols.len;
    const { streamCharts } = this.settings;
    for (let row = 0; row < rowLen; row++) {
      const streamItem = helper.getStreamItem(streamCharts, row);
      for (let col = 0; col < colLen; col++) {
        const cell = this.getCell(row, col);
        if (cell && cell.typeOptions && cell.typeOptions.sourceRange) {
          // const { sourceRange } = cell.typeOptions;
          if (!streamItem) {
            this.dependences.update('dropdownRange', { row, column: col }, null);
          } else {
            const { finalFirstRows, finalLastRows } = streamItem;
            const dependences = this.dependences.update('dropdownRange', { row, column: col }, null);
            this.calculateStreamRowDropdownRangeValue({
              dependences, finalFirstRows, finalLastRows,
            }, sheet);
            row = finalLastRows;
          }
          // cellToValidate.push(cell);
        }
      }
    }
    this.dependenceState = STATE_UP_TO_DATE;
  }

  /**
   * @description: 用于计算流表剩余行的值
   * @param {type}
   * @return
   * @Author: linjian
   * @Date: 2019-07-29 14:22:04
   */
  calculateStreamRowDropdownRangeValue({ dependences, finalFirstRows, finalLastRows }, sheet) {
    dependences.then((dependenceItem) => {
      dependenceItem.dependents.some((dependent) => {
        if (dependent.row === finalFirstRows && !dependent.getError()) {
          dependent.finalLastRows = finalLastRows;
          for (let rowd = finalFirstRows + 1; rowd <= finalLastRows; rowd++) {
            const rowOffset = rowd - finalFirstRows;
            const cellRow = this.rows._[rowd];
            if (!cellRow || !cellRow.cells) {
              if (rowd === finalLastRows) {
                // eslint-disable-next-line no-unused-expressions
                sheet && sheet.refresh();
              }
              // eslint-disable-next-line no-continue
              continue;
            }
            if (rowd === finalLastRows) {
              // 最后一个sheet才刷新页面
              this.calculateDropdownRangeValue(dependent, sheet, rowOffset);
            } else {
              this.calculateDropdownRangeValue(dependent, null, rowOffset);
            }
          }
          return true;
        }
        return false;
      });
    });
  }

  calculateDropdownRangeValue(dependent, sheet, rowOffset = 0) {
    const { row, column } = dependent;
    if (!dependent.getError()) {
      dependent.setState(CellValue.STATE_COMPUTING);

      // 用于给自定义函数获取自身信息
      // this._cell = dependent;
      // linjian 如果rowoffset不为0，则说明是在计算流表的剩余行数
      const cell = this.getCell(row + rowOffset, column);
      if (!cell) {
        return false;
      }
      const { sourceRange } = cell.typeOptions;
      const result = calculateDropdownRange(sourceRange, this, rowOffset);
      const source = [];
      const tempArr = [];// 用来去重
      if (Array.isArray(result)) {
        result.forEach((itemone) => {
          itemone.forEach((item) => {
            if (item !== '' && item !== undefined && item.toString().trim() !== '') {
              if (!tempArr.includes(item)) {
                const obj = { id: item, text: item };
                source.push(obj);
                tempArr.push(item);
              }
            }
          });
        });
      }
      cell.source = source;
      // eslint-disable-next-line no-unused-expressions
      sheet && sheet.refresh();
      dependent.setValue(source);
      dependent.setState(CellValue.STATE_UP_TO_DATE);
    }
    return true;
  }

  /**
   * 公式开始
   */
  recalculate(sheet) {
    switch (this.dependenceState) {
      case STATE_NEED_FULL_REBUILD:
        this.recalculateFull(sheet);
        break;
      case STATE_NEED_REBUILD:
        this.recalculateOptimized();
        break;
      default:
        break;
    }
  }

  /**
   * Recalculate sheet using optimized methods (fast recalculation).
   */
  recalculateOptimized() {
    const cells = this.dependence.getOutOfDateCells();

    arrayEach(cells, (cellValue) => {
      const value = this.dataProvider.getSourceDataAtCell(cellValue.row, cellValue.column);

      if (helper.isFormulaExpression(value)) {
        this.calculateFormularsValue(cellValue, value.substr(1));
      }
    });

    // eslint-disable-next-line no-underscore-dangle
    this._state = STATE_UP_TO_DATE;
    this.runLocalHooks('afterRecalculate', cells, 'optimized');
  }

  /**
   * Recalculate whole table by building dependencies from scratch (slow recalculation).
   */
  recalculateFull(sheet) {
    const dependence = this.dependences.getDependence('formulas');
    dependence.reset();
    // const cellToValidate = [];
    const rowLen = this.rows.len;
    const colLen = this.cols.len;
    const { streamCharts } = this.settings;
    for (let row = 0; row < rowLen; row++) {
      const streamItem = helper.getStreamItem(streamCharts, row);
      for (let col = 0; col < colLen; col++) {
        const cell = this.getCell(row, col);
        if (cell) {
          const value = cell.text;
          if (helper.isFormulaExpression(value)) {
            if (!streamItem) {
              this.dependences.update('formulas', { row, column: col }, null);
            } else {
              const { finalFirstRows, finalLastRows } = streamItem;
              const dependences = this.dependences.update('formulas', { row, column: col }, null);
              this.calculateStreamRowFormulaValue({
                dependences, finalFirstRows, finalLastRows,
              }, sheet);
              row = finalLastRows;
            }
            // cellToValidate.push(cell);
          }
        }
      }
    }
    this.dependenceState = STATE_UP_TO_DATE;
  }

  /**
   * @description: 用于计算流表剩余行的值
   * @param {type}
   * @return:
   * @Author: linjian
   * @Date: 2019-07-29 14:22:04
   */
  calculateStreamRowFormulaValue({ dependences, finalFirstRows, finalLastRows }, sheet) {
    dependences.then((dependenceItem) => {
      dependenceItem.dependents.some((dependent) => {
        if (dependent.row === finalFirstRows && !dependent.getError()) {
          dependent.finalLastRows = finalLastRows;
          for (let rowd = finalFirstRows + 1; rowd <= finalLastRows; rowd++) {
            const rowOffset = rowd - finalFirstRows;
            const cellRow = this.rows._[rowd];
            if (!cellRow || !cellRow.cells) {
              if (rowd === finalLastRows) {
                // eslint-disable-next-line no-unused-expressions
                sheet && sheet.refresh();
              }
              // eslint-disable-next-line no-continue
              continue;
            }
            if (rowd === finalLastRows) {
              // 最后一个sheet才刷新页面
              this.calculateFormularsValue(dependent, sheet, rowOffset);
            } else {
              this.calculateFormularsValue(dependent, null, rowOffset);
            }
          }
          return true;
        }
        return false;
      });
    });
  }

  calculateFormularsValue(dependent, sheet, rowOffset = 0) {
    const { row, column } = dependent;
    if (!dependent.getError()) {
      dependent.setState(CellValue.STATE_COMPUTING);

      // 用于给自定义函数获取自身信息
      // this._cell = dependent;
      // linjian 如果rowoffset不为0，则说明是在计算流表的剩余行数
      let { error, result } = {};
      if (rowOffset !== 0) {
        const parseArr = JSON.parse(JSON.stringify(dependent.parseArr));
        [result] = calculate.call(this, parseArr, rowOffset);
      } else {
        const formula = this.getCellTextOrDefault(row, column);
        ({ error, result } = newParse.call(this, formula, dependent));
      }
      this.setCellFormulaValue(row + rowOffset, column, result);
      // eslint-disable-next-line no-unused-expressions
      sheet && sheet.refresh();
      dependent.setValue(result);
      dependent.setError(error);
      dependent.setState(CellValue.STATE_UP_TO_DATE);
    }
  }

  // 实时计算操作栈前进和后退的公式值
  calculateZackValue({ row, column }, type = 'formulas') {
    const { streamCharts } = this.settings;
    let rowOffset = 0;
    const changedCell = { row, column };
    streamCharts.some((streamItem) => {
      if (row >= streamItem.finalFirstRows && row <= streamItem.finalLastRows) {
        rowOffset = row - streamItem.finalFirstRows;
        changedCell.row = streamItem.finalFirstRows;
        return true;
      }
      return false;
    });
    // 这里只是更新当前sheet内，那个格子的依赖
    this.dependences.update(type, changedCell, null, rowOffset);
  }

  /**
   * Get cell value object at specified physical coordinates.
   *
   * @param {Number} row Physical row index.
   * @param {Number} column Physical column index.
   * @returns {CellValue|undefined}
   */
  getCellAt(row, column) {
    return this.dependence.getDependent({ row, column });
  }

  /**
   * Listener for parser cell value.
   *
   * 增加sheetName - linjian
   *
   * @private
   * @param {Object} cell Cell coordinates. 类似： cell = {row: 3, column: 1, sheetName:sheetName}
   */
  onCallCellValue(cell) {
    const { sheetName } = cell;
    let sheetInstance = this;
    let instance;
    if (sheetName) {
      instance = window.xsObj.getInstanceByName(sheetName);
      if (instance) {
        sheetInstance = instance.data;
      } else {
        throw parseError.ref;
      }
    }
    return this.getCellValue(sheetInstance, cell);
  }

  getCellValue(sheetInstance, { row, column }) {
    // 是否在当前表格内
    if (!sheetInstance.isInDataRange(row, column)) {
      throw parseError.ref;
    }

    let cellValue = sheetInstance.getCellTextOrDefault(row, column);

    if (isFormulaError(cellValue)) {
      const computedCell = sheetInstance.dependences.getDependence('formulas').getDependent({ row, column });
      if (computedCell && computedCell.hasError()) {
        throw Error(cellValue);
      }
    }
    const dependence = sheetInstance.dependences.getDependence('formulas');
    if (isFormulaExpression(cellValue)) {
      let needComputingCell = dependence.getDependent({ row, column });
      if (needComputingCell) {
        const { constructor } = needComputingCell;
        if (needComputingCell.state === constructor.STATE_OUT_OFF_DATE) {
          needComputingCell.setState(constructor.STATE_COMPUTING);
          const { error, result } = newParse.call(sheetInstance, cellValue.substr(1));
          needComputingCell.setState(constructor.STATE_UP_TO_DATE);
          if (error) {
            throw Error(error);
          }
          return result;
        }
        if (needComputingCell.isState(constructor.STATE_COMPUTING)) {
          throw Error('#CIRCLE_REF!');
        } else if (needComputingCell.isState(constructor.STATE_UP_TO_DATE)) {
          return needComputingCell.getValue();
        }
      } else {
        // 可能是还没计算过，所以matrix.data中没值
        needComputingCell = dependence.getDependent({ row, column }, true);
        sheetInstance.calculateFormularsValue(needComputingCell);
        return needComputingCell.getValue();
      }
    } else if (cellValue === undefined || cellValue === null) {
      cellValue = '';
    } else if (typeof cellValue === 'string' && cellValue[0] === '\'') {
      cellValue = cellValue.substr(1);
    } else if (cellValue === 'TRUE') {
      cellValue = true;
    } else if (cellValue === 'FALSE') {
      cellValue = false;
    }
    return cellValue;
  }

  /**
   * Listener for parser cells (range) value.
   *
   * @private
   * @param {Object} startCell Cell coordinates (top-left corner coordinate).
   * @param {Object} endCell Cell coordinates (bottom-right corner coordinate).
   */
  onCallRangeValue({ row: startRow, column: startColumn, sheetName },
    { row: endRow, column: endColumn }) {
    let sheetInstance = this;
    let instance;
    if (sheetName) {
      instance = window.xsObj.getInstanceByName(sheetName);
      if (instance) {
        sheetInstance = instance.data;
      } else {
        throw parseError.ref;
      }
    }
    // 整列，如 G:H
    if (startRow === -1 && endRow === -1) {
      startRow = 0;
      endRow = sheetInstance.rows.len - 1;
    }
    const cellValues = sheetInstance.getRawDataByRange(startRow, startColumn, endRow, endColumn);

    const mapRowData = (rowData, rowIndex) => arrayMap(rowData, (cellData, columnIndex) => {
      const rowCellCoord = startRow + rowIndex;
      const columnCellCoord = startColumn + columnIndex;
      const cell = new CellReference(rowCellCoord, columnCellCoord, sheetName);
      return sheetInstance.getCellValue(sheetInstance, cell);
    });

    return arrayMap(cellValues, (rowData, rowIndex) => mapRowData(rowData, rowIndex));
  }

  // 把范围字符串转成对应值
  parseRangeValue(rangeStr, rowOffset) {
    const range = parseRange(rangeStr, rowOffset);
    if (range) {
      if (range.length === 1) {
        return this.onCallCellValue(...range);
      }
      return this.onCallRangeValue(...range);
    }
    return new Error();
  }

  /**
   * Check if provided coordinates match to the table range data.
   *
   * @param {Number} visualRow Visual row index.
   * @param {Number} visualColumn Visual row index.
   * @returns {Boolean}
   */
  isInDataRange(visualRow, visualColumn) {
    const flagRow = visualRow >= 0 && visualRow < this.rows.len;
    const flagCol = visualColumn >= 0 && visualColumn < this.cols.len;
    return flagRow && flagCol;
  }

  /**
   * Get source data at specified visual range.
   *
   * @param {Number} [sri] start row index.
   * @param {Number} [sci] start column index.
   * @param {Number} [eri] end row index.
   * @param {Number} [eci] end column index.
   * @returns {Array}
   */
  getRawDataByRange(sri, sci, eri, eci) {
    const data = [];

    rangeEach2(sri, eri, (row) => {
      const value = [];
      rangeEach2(sci, eci, (col) => {
        value.push(this.getCellTextOrDefault(row, col));
      });
      data.push(value);
    });

    return data;
  }

  /**
   * 公式结束
   */

  addValidation(mode, ref, validator) {
    // console.log('mode:', mode, ', ref:', ref, ', validator:', validator);
    this.changeData('add-validations', () => {
      this.validations.add(mode, ref, validator);
    });
  }

  removeValidation() {
    const { range } = this.selector;
    this.changeData('remove-validations', () => {
      this.validations.remove(range);
    });
  }

  getSelectedValidator() {
    const { ri, ci } = this.selector;
    const v = this.validations.get(ri, ci);
    return v ? v.validator : null;
  }

  getSelectedValidation() {
    const { ri, ci, range } = this.selector;
    const v = this.validations.get(ri, ci);
    const ret = { ref: range.toString() };
    if (v !== null) {
      ret.mode = v.mode;
      ret.validator = v.validator;
    }
    return ret;
  }

  canUndo() {
    return this.history.canUndo();
  }

  canRedo() {
    return this.history.canRedo();
  }

  // linjian 新增callback事件处理公式回退等事件
  undo(callback) {
    this.history.undo(this.getData(), (d) => {
      // 'insert-row', 'insert-column', 'delete-row', 'delete-column'
      const {
        actionType, start, n, eri, sri, dData, ri, ci, rsize, tempRowObj,
        data, range, distData, distRange, srcRange, srcData, dependencesArr,
      } = d;
      const colLen = this.cols.len;
      const range2 = new CellRange(start, 0, start + n - 1, colLen - 1); // 这里range是给删除用
      switch (actionType) {
        case 'insert-row': // 历史是加行，回退就是删除行
          this.delete('row', true, range2);
          callback({
            actionType: 'delete-row',
            start,
            n,
          });
          setTimeout(() => {
            this.settings.streamCharts = helper.updateStreamCharts({
              streamCharts: this.settings.streamCharts,
              actionType: 'delete-row',
              start,
              n,
            });
          }, 0);
          break;
        case 'delete-row': // 历史是删除行，回退就是增加行   actionType,sri,sci,eri,eci,rsize,csize,dData,
          this.undoInsert('row', dData, sri, eri);
          callback({
            actionType: 'undo-insert-row',
            dependencesArr,
            start: sri,
            n: rsize,
          });
          this.settings.streamCharts = helper.updateStreamCharts({
            streamCharts: this.settings.streamCharts,
            actionType: 'insert-row',
            start: tempRowObj['1'] === undefined ? sri : tempRowObj['1'],
            n: rsize,
          });
          break;
        case 'modify-data': // 历史是修改数据，回退就是还原之前的数据
          const cell = this.rows.getCellOrNew(ri, ci);
          if (cell.dropdownText) {
            cell.dropdownText = '';
          }
          const { redoItems } = this.history;
          // 处理下图片和附件
          if (cell.cellProps) {
            const { cellType } = cell.cellProps;
            if (cellType === 'file') {
              const initFiles = cell.typeOptions.files;
              cell.typeOptions.files = data.text;
              data.currentData = data.text;
              data.text = initFiles;
              redoItems[redoItems.length - 1] = JSON.stringify(d);
              return;
            }
            if (cellType === 'image') {
              const initImages = cell.typeOptions.images;
              cell.typeOptions.images = data.text;
              data.currentData = data.text;
              data.text = initImages;
              redoItems[redoItems.length - 1] = JSON.stringify(d);
              return;
            }
          }
          const initText = cell.text || '';
          Object.assign(cell, data);
          this.hook.afterSetDataAtCell.call(this, [ri, ci, initText, data.text]);
          d.data.currentData = data.text;
          d.data.text = initText;
          redoItems[redoItems.length - 1] = JSON.stringify(d);
          break;
        case 'delete-text': // 历史是删除内容，回退就是增加内容
          const range1 = new CellRange(range.sri, range.sci, range.eri, range.eci);
          range1.each((i, j) => {
            if (data[i][j]) {
              const cells = this.rows.getCellOrNew(i, j);
              Object.assign(cells, data[i][j]);
              this.hook.afterSetDataAtCell.call(this, [i, j, undefined, data[i][j].text]);
            }
          });
          break;
        case 'paste-action-copy': // 历史是复制，回退就是还原原来range的值
          this.undoDistPaste(distRange, distData);
          break;
        case 'paste-action-cut': // 历史是剪切，回退就是还原src range值和dist range的值
          this.undoSrcPaste(srcRange, srcData);
          this.undoDistPaste(distRange, distData);
          break;
        default:
          this.setData(d);
          break;
      }
    });
  }

  redo(callback) {
    this.history.redo(this.getData(), (d) => {
      const {
        actionType, start, n, templateObj, rsize,
        sci, eci, sri, eri, ri, ci, data, range, distRange, srcRange,
      } = d;
      const rangeProto = Object.getPrototypeOf(this.selector.range);
      const range2 = new CellRange(sri, sci, eri, eci);
      switch (actionType) {
        case 'insert-row': // 历史是加行，前进就是加行
          this.insert('row', n, start, templateObj, true);
          callback({
            actionType: 'insert-row',
            start,
            n,
          });
          this.settings.streamCharts = helper.updateStreamCharts({
            streamCharts: this.settings.streamCharts,
            actionType: 'insert-row',
            start,
            n,
          });
          break;
        case 'delete-row': // 历史是删除行，回退就是删除行
          this.delete('row', true, range2);
          callback({
            actionType: 'redo-delete-row',
            start: sri,
            n: rsize,
          });
          // setTimeout(() => {
          //   this.settings.streamCharts = helper.updateStreamCharts({
          //     streamCharts: this.settings.streamCharts,
          //     actionType: 'delete-row',
          //     start: sri,
          //     n: rsize,
          //   });
          // }, 0);
          break;
        case 'delete-text': // 历史是删除内容，前进还是删除内容
          const range1 = new CellRange(range.sri, range.sci, range.eri, range.eci);
          this.deleteCell('text', true, range1);
          break;
        case 'modify-data': // 历史是修改数据，前进还是还原之前的数据
          const cell = this.rows.getCellOrNew(ri, ci);
          const { undoItems } = this.history;
          // 处理下图片和附件
          if (cell.cellProps) {
            const { cellType } = cell.cellProps;
            if (cellType === 'file') {
              const initFiles = cell.typeOptions.files;
              cell.typeOptions.files = data.text;
              data.currentData = data.text;
              data.text = initFiles;
              undoItems[undoItems.length - 1] = JSON.stringify(d);
              return;
            }
            if (cellType === 'image') {
              cell.typeOptions.images = data.text;
              const initImages = cell.typeOptions.images;
              cell.typeOptions.images = data.text;
              data.currentData = data.text;
              data.text = initImages;
              undoItems[undoItems.length - 1] = JSON.stringify(d);
              return;
            }
          }
          if (cell.dropdownText) {
            cell.dropdownText = '';
          }
          const initText = cell.text || '';
          Object.assign(cell, data);
          this.hook.afterSetDataAtCell.call(this, [ri, ci, initText, data.text]);
          d.data.currentData = data.text;
          d.data.text = initText;
          undoItems[undoItems.length - 1] = JSON.stringify(d);
          break;
        case 'paste-action-copy':
          Object.setPrototypeOf(distRange, rangeProto);
          Object.setPrototypeOf(srcRange, rangeProto);
          copyPaste.call(this, srcRange, distRange, 'text');
          break;
        case 'paste-action-cut':
          Object.setPrototypeOf(distRange, rangeProto);
          Object.setPrototypeOf(srcRange, rangeProto);
          cutPaste.call(this, srcRange, distRange, 'text');
          break;
        default:
          this.setData(d);
          break;
      }
      // this.setData(d);
    });
  }

  copy() {
    this.clipboard.copy(this.selector.range);
  }

  cut() {
    this.clipboard.cut(this.selector.range);
  }

  // what: all | text | format
  paste(what = 'all', error = () => {}) {
    // console.log('sIndexes:', sIndexes);
    const { clipboard, selector } = this;
    if (clipboard.isClear()) return false;
    if (!canPaste.call(this, clipboard.range, selector.range, error)) return false;
    const [selectorSize] = clipboard.range.size();
    if (selectorSize > this.historyNumCount) {
      this.hook.tipMessage('alert', '粘贴数据量过大，此操作不记录到历史记录');
      if (clipboard.isCopy()) {
        copyPaste.call(this, clipboard.range, selector.range, what);
      } else if (clipboard.isCut()) {
        cutPaste.call(this, clipboard.range, selector.range);
      }
      return false;
    }
    // linjian 添加操作栈
    const dataBak = {};
    if (clipboard.isCopy()) {
      this.pasteBak('copy', clipboard.range, selector.range, dataBak);
    } else if (clipboard.isCut()) {
      this.pasteBak('cut', clipboard.range, selector.range, dataBak);
    }
    this.changeData(dataBak, () => {
      if (clipboard.isCopy()) {
        copyPaste.call(this, clipboard.range, selector.range, what);
      } else if (clipboard.isCut()) {
        cutPaste.call(this, clipboard.range, selector.range);
      }
    });
    return true;
  }

  // 专门用来处理外部粘贴
  outPaste(evt, cb = () => {}) {
    // console.log('sIndexes:', sIndexes);
    evt.preventDefault();
    const clipData = (evt.clipboardData || window.clipboardData).getData('text/plain').split(/\t/g);
    if (!clipData || (Array.isArray(clipData) && !clipData[0])) {
      return false;
    }
    const copeClipData = [];
    let copeClipDataItems = [];
    clipData.forEach((item) => {
      // 先把excel里面格子内的换行符去掉，然后剩下的如果有换行符就是真正的换行。
      if (/"[\s\S]*"/g.test(item)) {
        const tempItem = item.replace(/"[\s\S]*"/g, '');
        if (tempItem.includes('\n')) {
          copeClipDataItems.push({ text: item.match(/"[\s\S]*"/g)[0].replace(/"/g, '') });
          copeClipData.push(copeClipDataItems);
          copeClipDataItems = [{ text: tempItem.replace(/\n/, '') }];
        } else {
          copeClipDataItems.push({ text: item.replace(/"/g, '') });
        }
      } else if (/\n/.test(item)) {
        const tempItems = item.split(/\n/);
        copeClipDataItems.push({ text: tempItems[0] });
        copeClipData.push(copeClipDataItems);
        copeClipDataItems = [{ text: tempItems[1] }];
      } else {
        copeClipDataItems.push({ text: item });
      }
    });
    // 这里只是模拟了一个srcRange
    const srcRange = new CellRange(0, 0, copeClipData.length - 1, copeClipData[0].length - 1);
    const { selector } = this;
    // linjian 添加操作栈
    const dataBak = {};
    this.pasteBak('copy', srcRange, selector.range, dataBak);
    this.changeData(dataBak, () => {
      this.rows.outCopyPaste(srcRange, selector.range, copeClipData);
      cb();
    });
    return true;
  }

  /**
   * @description: 这个方法专门处理剪切粘贴回退时的处理步骤
   * @param {CellRange} srcRange
   * @param {Obj} srcData
   * @return: null
   * @Author: linjian
   * @Date: 2019-07-09 11:13:38
   */
  undoSrcPaste(srcRange, srcData) {
    const ndata = this.rows._;
    // 先删除现在的值，这里把格式和值，以及合并单元格一起删除了，因为复制的时候，操作栈保存了值和格式，所以这里，直接指定是 all类型。
    // linjian 这里因为操作栈记录的时候使用了JSON.stringify,在用JSON.parse就不是cellRange对象了，所以这里
    const rangeProto = Object.getPrototypeOf(this.selector.range);
    Object.setPrototypeOf(srcRange, rangeProto);

    srcRange.each((i, j) => {
      if (srcData[i] && srcData[i].cells && srcData[i].cells[j]) {
        const cell = srcData[i].cells[j];
        if (!ndata[i]) {
          ndata[i] = {};
        }
        if (!ndata[i].cells) {
          ndata[i].cells = {};
        }
        ndata[i].cells[j] = cell;
        if (helper.isFormulaExpression(cell.text) && this.isCalCulatorFormula) {
          this.calculateZackValue({ row: i, column: j });
          // this.dependences.update('formulas', { row: i, column: j }, null);
        }
        if (helper.isDropdownRangeExpression(cell) && this.isCalculatorDropdownRange) {
          this.calculateZackValue({ row: i, column: j }, 'dropdownRange');
        }
        if (cell && cell.merge) {
          // console.log('cell:', ri, ci, cell);
          const [rn, cn] = cell.merge;
          if (rn <= 0 && cn <= 0) return;
          this.merges.add(new CellRange(i, j, i + rn, j + cn));
        }
      }
    });
  }

  /**
   * @description: 这个方法专门处理复制粘贴回退时的处理步骤
   * @param {CellRange} distRange
   * @param {Obj} distData
   * @return: null
   * @Author: linjian
   * @Date: 2019-07-09 11:13:38
   */
  undoDistPaste(distRange, distData) {
    const ndata = this.rows._;
    // 先删除现在的值，这里把格式和值，以及合并单元格一起删除了，因为复制的时候，操作栈保存了值和格式，所以这里，直接指定是 all类型。
    // linjian 这里因为操作栈记录的时候使用了JSON.stringify,在用JSON.parse就不是cellRange对象了，所以这里
    const rangeProto = Object.getPrototypeOf(this.selector.range);
    Object.setPrototypeOf(distRange, rangeProto);
    // 这里回调先移出了现在的公式依赖
    this.rows.deleteCells(distRange, 'all', null, null, (ri, ci) => {
      helper.removeDependents(this, ri, ci);
    });
    this.merges.deleteWithin(distRange);

    distRange.each((i, j) => {
      if (distData[i] && distData[i].cells && distData[i].cells[j]) {
        const cell = distData[i].cells[j];
        if (!ndata[i]) {
          ndata[i] = {};
        }
        if (!ndata[i].cells) {
          ndata[i].cells = {};
        }
        ndata[i].cells[j] = cell;
        if (helper.isFormulaExpression(cell.text) && this.isCalCulatorFormula) {
          this.calculateZackValue({ row: i, column: j });
          // this.dependences.update('formulas', { row: i, column: j }, null);
        }
        if (helper.isDropdownRangeExpression(cell) && this.isCalculatorDropdownRange) {
          this.calculateZackValue({ row: i, column: j }, 'dropdownRange');
        }
        if (cell && cell.merge) {
          // console.log('cell:', ri, ci, cell);
          const [rn, cn] = cell.merge;
          if (rn <= 0 && cn <= 0) return;
          this.merges.add(new CellRange(i, j, i + rn, j + cn));
        }
      }
    });
  }

  /**
   * @description: 用于复制、剪切、自动填充 操作栈记录数据生成
   * @param {string} type : cut copy autofill
   * @param {Object} srcRange 数据源
   * @param {Object} distRange 目标数据范围
   * @param {Object} dataBak 最后生成的存储数据
   * @return: null
   * @Author: linjian
   * @Date: 2019-07-08 16:02:33
   */
  pasteBak(type, srcRange, distRange, dataBak) {
    distRange = new CellRange(distRange.sri, distRange.sci, distRange.eri, distRange.eci);
    dataBak.actionType = `paste-action-${type}`;
    dataBak.distData = {};
    dataBak.srcData = {};
    dataBak.srcRange = srcRange;
    dataBak.distRange = distRange;
    const { distData, srcData } = dataBak;
    const ndata = this.rows._;
    // linjian 这里一定要保证目标范围比源范围大
    const [srcRowCount, srcColCount] = srcRange.size();
    const [distRowCount, distColCount] = distRange.size();
    const rowOffset = srcRowCount - distRowCount;
    const colOffset = srcColCount - distColCount;
    if (rowOffset > 0) {
      distRange.eri += rowOffset;
    }
    if (colOffset > 0) {
      distRange.eci += colOffset;
    }
    const rowLen = this.rows.len;
    const colLen = this.cols.len;
    if (distRange.eri + 1 > rowLen) {
      distRange.eri = rowLen - 1;
    }
    if (distRange.eci + 1 > colLen) {
      distRange.eci = colLen - 1;
    }
    // 不管是哪种情况，都要记录目标源数据的原始值
    distRange.each((i, j) => {
      if (ndata[i] && ndata[i].cells && ndata[i].cells[j]) {
        if (!distData[i]) {
          distData[i] = {};
        }
        if (!distData[i].cells) {
          distData[i].cells = {};
        }
        distData[i].cells[j] = ndata[i].cells[j];
        // 如果是公式 移出依赖
        if (helper.isFormulaExpression(ndata[i].cells[j].text)) {
          helper.removeDependents(this, i, j);
        }
      }
    });
    // 如果是剪切，还要记录原数据
    if (type === 'cut') {
      srcRange.each((i, j) => {
        if (ndata[i] && ndata[i].cells && ndata[i].cells[j]) {
          if (!srcData[i]) {
            srcData[i] = {};
          }
          if (!srcData[i].cells) {
            srcData[i].cells = {};
          }
          srcData[i].cells[j] = ndata[i].cells[j];
        }
      });
    }
  }

  autofill(cellRange, what, error = () => {}) {
    const srcRange = this.selector.range;
    if (!canPaste.call(this, srcRange, cellRange, error)) return false;
    const dataBak = {};
    this.pasteBak('copy', srcRange, cellRange, dataBak);
    this.changeData(dataBak, () => {
      copyPaste.call(this, srcRange, cellRange, what, true);
    });
    return true;
  }

  clearClipboard() {
    this.clipboard.clear();
  }

  calSelectedRangeByEnd(ri, ci) {
    const {
      selector, rows, cols, merges,
    } = this;
    let {
      sri, sci, eri, eci,
    } = selector.range;
    const cri = selector.ri;
    const cci = selector.ci;
    let [nri, nci] = [ri, ci];
    if (ri < 0) nri = rows.len - 1;
    if (ci < 0) nci = cols.len - 1;
    // row index
    if (nri <= cri) [sri, eri] = [nri, cri];
    else eri = nri;
    // col index
    if (nci <= cci) [sci, eci] = [nci, cci];
    else eci = nci;
    // linjian 因为选中列表头，如果有和并列 就会同时选中多个列 所以这里做了区分
    if (ri === -1) {
      selector.range = new CellRange(
        sri, sci, eri, eci,
      );
    } else {
      selector.range = merges.union(new CellRange(
        sri, sci, eri, eci,
      ));
    }
    // console.log('selector.range:', selector.range);
    return selector.range;
  }

  calSelectedRangeByStart(ri, ci) {
    const {
      selector, rows, cols, merges,
    } = this;
    let cellRange = merges.getFirstIncludes(ri, ci);
    // console.log('cellRange:', cellRange, ri, ci, merges);
    if (cellRange === null) {
      cellRange = new CellRange(ri, ci, ri, ci);
      if (ri === -1) {
        cellRange.sri = 0;
        cellRange.eri = rows.len - 1;
      }
      if (ci === -1) {
        cellRange.sci = 0;
        cellRange.eci = cols.len - 1;
      }
    }
    selector.range = cellRange;
    return cellRange;
  }

  setSelectedCellAttr(property, value) {
    this.changeData(`set-cell-Attr-${property}`, () => {
      const { selector, rows } = this;
      if (property === 'merge') {
        if (value) this.merge();
        else this.unmerge();
      } else if (property === 'border') {
        setStyleBorders.call(this, value);
      } else if (property === 'formula') {
        // console.log('>>>', selector.multiple());
        const { ri, ci, range } = selector;
        if (selector.multiple()) {
          const [rn, cn] = selector.size();
          const {
            sri, sci, eri, eci,
          } = range;
          if (rn > 1) {
            for (let i = sci; i <= eci; i += 1) {
              const cell = rows.getCellOrNew(eri + 1, i);
              cell.text = `=${value}(${xy2expr(i, sri)}:${xy2expr(i, eri)})`;
            }
          } else if (cn > 1) {
            const cell = rows.getCellOrNew(ri, eci + 1);
            cell.text = `=${value}(${xy2expr(sci, ri)}:${xy2expr(eci, ri)})`;
          }
        } else {
          const cell = rows.getCellOrNew(ri, ci);
          cell.text = `=${value}()`;
        }
      } else {
        selector.range.each((ri, ci) => {
          const cell = rows.getCellOrNew(ri, ci);
          let cstyle = {};
          if (cell.style !== undefined) {
            // cstyle = helper.cloneDeep(styles[cell.style]);
            // modified by linjian ,because the style has Changed
            // and now is an Object than store all style,eg:{bold:true}
            cstyle = helper.cloneDeep(cell.style);
          }
          if (property === 'format') {
            cstyle.format = value;
            cell.style = this.addStyle(cstyle);
          } else if (property === 'font-bold' || property === 'font-italic'
            || property === 'font-name' || property === 'font-size') {
            const nfont = {};
            nfont[property.split('-')[1]] = value;
            cstyle.font = Object.assign(cstyle.font || {}, nfont);
            cell.style = this.addStyle(cstyle);
          } else if (property === 'strike' || property === 'textwrap'
            || property === 'underline'
            || property === 'align' || property === 'valign'
            || property === 'color' || property === 'bgcolor') {
            cstyle[property] = value;
            cell.style = this.addStyle(cstyle);
          } else if (property === 'cellType') {
            const {
              cellType,
              format,
              type,
              scale,
              ...remainCellProps
            } = cell.cellProps || {};
            cell.cellProps = {
              ...value,
              ...remainCellProps,
            };
          }
        });
      }
    });
  }

  setCellStyle(ri, ci, style = {}) {
    const { rows } = this;
    const cell = rows.getCellOrNew(ri, ci);
    let cstyle = {};
    if (cell.style !== undefined) {
      // cstyle = helper.cloneDeep(styles[cell.style]);
      // modified by linjian
      cstyle = cell.style;
    }
    Object.assign(cstyle, style);
    cell.style = this.addStyle(cstyle);
  }

  // state: input | finished
  setSelectedCellText(text, state = 'input', changeSource, formulaEditorRC) {
    const { autoFilter, selector, rows } = this;
    const { ri: selectorRi, ci: selectorCi } = selector;
    let ri = selectorRi;
    let ci = selectorCi;
    if (formulaEditorRC) { // 如果单元格是在进行公式编辑，则将 Editor 中的内容写入 Editor 所在位置的 cell
      const { ri: formulaEditorRi, ci: formulaEditorCi } = formulaEditorRC;
      ri = formulaEditorRi;
      ci = formulaEditorCi
    }
    let nri = ri;
    if (this.unsortedRowMap.has(ri)) {
      nri = this.unsortedRowMap.get(ri);
    }
    const oldCell = rows.getCell(nri, ci);
    const oldText = oldCell ? oldCell.text : '';
    this.setCellText(nri, ci, text, state, changeSource);
    // replace filter.value
    if (autoFilter.active()) {
      const filter = autoFilter.getFilter(ci);
      if (filter) {
        const vIndex = filter.value.findIndex(v => v === oldText);
        if (vIndex >= 0) {
          filter.value.splice(vIndex, 1, text);
        }
        // console.log('filter:', filter, oldCell);
      }
    }
    // this.resetAutoFilter();
  }

  isRowInStream(row) {
    return helper.isRowInStream(this.streamCharts, row);
  }

  getSelectedCell() {
    const { ri, ci } = this.selector;
    return this.rows.getCell(ri, ci);
  }

  xyInSelectedRect(x, y) {
    const {
      left, top, width, height,
    } = this.getSelectedRect();
    const x1 = x - this.cols.indexWidth;
    const y1 = y - this.rows.height;
    // 说明是在表头右键
    if (x1 <= 0 || y1 <= 0) {
      return this.isSelectPartOfAll;
    }
    // console.log('x:', x, ',y:', y, 'left:', left, 'top:', top);
    return x1 > left && x1 < (left + width)
      && y1 > top && y1 < (top + height);
  }

  // TODO:
  getSelectedRect() {
    return this.getRect(this.selector.range);
  }

  getClipboardRect() {
    const { clipboard } = this;
    if (!clipboard.isClear()) {
      return this.getRect(clipboard.range);
    }
    return { left: -100, top: -100 };
  }

  getRect(cellRange) {
    const {
      scroll, rows, cols, exceptRowSet, freeze,
    } = this;
    const {
      sri, sci, eri, eci,
    } = cellRange;
    const fsh = this.freezeTotalHeight();
    const fsw = this.freezeTotalWidth();
    // console.log('sri:', sri, ',sci:', sci, ', eri:', eri, ', eci:', eci);
    // no selector
    if (sri < 0 && sci < 0) {
      return {
        left: 0, l: 0, top: 0, t: 0, scroll,
      };
    }
    const viewRange = this.viewRange();
    const startPostion = { sri: viewRange.sri, top: 0 };
    const viewRangeOffset = Math.abs(viewRange.eri - viewRange.sri);
    if (fsh !== 0) {
      startPostion.top = fsh;
    }
    let [left, top] = [-1000, -1000];
    let height = 'notInit';
    // linjian 添加相对位置计算
    if (((startPostion.sri - viewRangeOffset) <= sri && eri <= (this.viewRange().eri + viewRangeOffset)) || (freeze[0] > sri)) {
      const calPosition = this.cellPosition(sri, sci, startPostion, viewRangeOffset);
      ({ left, top } = calPosition);
      // linjian 这个判断处理合并单元格一半已经滚动上去，一半还留在视口范围内，选中范围的起始位置
      if (sri < viewRange.sri && eri <= viewRange.eri && eri >= viewRange.sri) {
        top = 0;
        if (fsh !== 0) {
          top = fsh;
        }
      }
    }
    // 如果是选择范围超过视口范围，然后
    if (Math.abs(eri - sri) > viewRangeOffset) {
      let calPosition;
      // 如果选中范围大于窗口起始点，小于窗口结束点
      if (sri >= viewRange.sri && sri <= viewRange.eri) {
        calPosition = this.cellPosition(sri, sci, startPostion, viewRangeOffset);
        ({ left, top } = calPosition);
      }
      // 如果选中范围结束点大于窗口起始点，小于窗口结束点
      if (eri >= viewRange.sri && eri <= viewRange.eri) {
        calPosition = this.cellPosition(startPostion.sri, sci, startPostion, viewRangeOffset);
        ({ left, top } = calPosition);
      }
    }

    if (sri < viewRange.sri && eri > viewRange.eri) {
      height = rows.sumHeight(viewRange.sri - 1, viewRange.eri + 1, exceptRowSet);
    } else if (sri === viewRange.sri && eri > viewRange.eri) {
      height = rows.sumHeight(viewRange.sri, viewRange.eri + 1, exceptRowSet);
    } else if (sri < viewRange.sri && eri === viewRange.eri) {
      height = rows.sumHeight(viewRange.sri - 1, viewRange.eri, exceptRowSet);
    } else if (sri < viewRange.sri && eri < viewRange.eri) {
      height = rows.sumHeight(viewRange.sri, eri + 1, exceptRowSet);
    } else if (sri >= viewRange.sri && eri <= viewRange.eri) {
      height = rows.sumHeight(sri, eri + 1, exceptRowSet);
    } else if (sri >= viewRange.sri && eri > viewRange.eri) {
      height = rows.sumHeight(sri, viewRange.eri + 1, exceptRowSet);
    } else if (height === 'notInit') {
      height = rows.sumHeight(sri, eri + 1, exceptRowSet);
      // 如果是从头
    }
    const width = cols.sumWidth(sci, eci + 1);
    // console.log('sri:', sri, ', sci:', sci, ', eri:', eri, ', eci:', eci);
    let left0 = left - scroll.x;
    // linjian 这里使用了相对定位  所以不用滚动位置
    // let top0 = top - scroll.y;
    let top0 = top;

    if (fsw > 0 && fsw > left) {
      left0 = left;
    }
    if (fsh > 0 && fsh > top) {
      top0 = top;
    }
    return {
      l: left,
      t: top,
      left: left0,
      top: top0,
      height,
      width,
      scroll,
    };
  }

  // 通过左/上偏移量 确定 行列值
  getCellRectByXY(x, y) {
    const {
      scroll, merges, rows, cols,
    } = this;
    let { ri, top, height } = getCellRowByY.call(this, y, scroll.y);
    let { ci, left, width } = getCellColByX.call(this, x, scroll.x);
    let mergeRealHeight;
    if (ci === -1) {
      width = cols.totalWidth();
    }
    if (ri === -1) {
      if (this.totalHeightValue !== undefined) {
        height = this.totalHeightValue;
      } else {
        height = rows.totalHeight();
      }
    }
    if (ri >= 0 || ci >= 0) {
      const merge = merges.getFirstIncludes(ri, ci);
      if (merge) {
        ri = merge.sri;
        ci = merge.sci;
        ({
          left, top, width, height, mergeRealHeight,
        } = this.cellRect(ri, ci));
        // 修复bug 合并单元格要添加表头的宽高
        left += cols.indexWidth;
        top += rows.height;
      }
    }
    return {
      ri, ci, left, top, width, height, mergeRealHeight,
    };
  }

  isSignleSelected() {
    const {
      sri, sci, eri, eci,
    } = this.selector.range;
    const cell = this.getCell(sri, sci);
    if (cell && cell.merge) {
      const [rn, cn] = cell.merge;
      if (sri + rn === eri && sci + cn === eci) return true;
    }
    return !this.selector.multiple();
  }

  canUnmerge() {
    const {
      sri, sci, eri, eci,
    } = this.selector.range;
    const cell = this.getCell(sri, sci);
    if (cell && cell.merge) {
      const [rn, cn] = cell.merge;
      if (sri + rn === eri && sci + cn === eci) return true;
    }
    return false;
  }

  merge() {
    const { selector, rows } = this;
    if (this.isSignleSelected()) return;
    const [rn, cn] = selector.size();
    // console.log('merge:', rn, cn);
    if (rn > 1 || cn > 1) {
      const { sri, sci } = selector.range;
      this.changeData('set-cell-Attr-merge', () => {
        const cell = rows.getCellOrNew(sri, sci);
        cell.merge = [rn - 1, cn - 1];
        this.merges.add(selector.range);
        // delete merge cells
        this.rows.deleteCells(selector.range);
        // console.log('cell:', cell, this.d);
        this.rows.setCell(sri, sci, cell);
      });
    }
  }

  unmerge() {
    const { selector } = this;
    if (!this.isSignleSelected()) return;
    const { sri, sci } = selector.range;
    this.changeData('set-cell-Attr-unmerge', () => {
      this.rows.deleteCell(sri, sci, 'merge');
      this.merges.deleteWithin(selector.range);
    });
  }

  canAutofilter() {
    return !this.autoFilter.active();
  }

  autofilter() {
    const { autoFilter, selector } = this;
    this.changeData('autoFilter', () => {
      if (autoFilter.active()) {
        autoFilter.clear();
        this.exceptRowSet = new Set();
        this.sortedRowMap = new Map();
        this.unsortedRowMap = new Map();
      } else {
        autoFilter.ref = selector.range.toString();
      }
    });
  }

  setAutoFilter(ri, ci, order, operator, value, isSelectAll, searchKey) {
    const { autoFilter } = this;
    autoFilter.addFilter(ri, ci, operator, value, isSelectAll, order, searchKey);
    // requestData定义在sheet.js里面
    if (typeof this.requestData === 'function') {
      this.requestData('resetAutoFilter', ri);
    }
    // autoFilter.setSort(ri, ci, order);
    // this.resetAutoFilter();
  }

  resetAutoFilter() {
    const { autoFilter, rows } = this;
    if (!autoFilter.active()) return;
    const { sort } = autoFilter;
    const { rset, fset } = autoFilter.filteredRows((r, c) => rows.getCell(r, c));
    const fary = Array.from(fset);
    const oldAry = Array.from(fset);
    if (sort) {
      fary.sort((a, b) => {
        if (sort.order === 'asc') return a - b;
        if (sort.order === 'desc') return b - a;
        return 0;
      });
    }
    this.exceptRowSet = rset;
    this.sortedRowMap = new Map();
    this.unsortedRowMap = new Map();
    fary.forEach((it, index) => {
      this.sortedRowMap.set(oldAry[index], it);
      this.unsortedRowMap.set(it, oldAry[index]);
    });
  }

  // linjian 这里增加 isRedo 是否是前进  range是前进要删除的范围
  deleteCell(what = 'all', isRedo, range) {
    const { selector } = this;
    const deleteFn = (deleteData, range1) => {
      const selectorRange = range1 || selector.range;
      this.rows.deleteCells(selectorRange, what, deleteData, this);
      if (what === 'all' || what === 'format') {
        this.merges.deleteWithin(selector.range);
      }
    };
    // linjian 这里先只记录删除文字的操作栈
    if (what === 'text') {
      let selectorRange = selector.range;
      if (isRedo) {
        selectorRange = range;
      }
      const { sri, eri } = selectorRange;
      const deleteData = {};
      if (eri - sri + 1 > this.historyNumCount) {
        this.hook.tipMessage('alert', '删除数据量过大，此操作不记录到历史记录');
        deleteFn(deleteData);
        // linjian 因为没有记录操作栈去，所以这里要回调afterTriggerHistoryAction，才会触发保存操作
        if (typeof this.hook.afterTriggerHistoryAction === 'function') {
          this.hook.afterTriggerHistoryAction('history-add', {
            actionType: 'delete-text', range: selector.range, data: deleteData,
          });
        }
      } else {
        deleteFn(deleteData, range);
        if (!isRedo) {
          this.changeData({
            actionType: 'delete-text', range: selector.range, data: deleteData,
          }, () => {});
        }
      }
    } else {
      this.changeData(`deleteCell-${what}`, deleteFn);
    }
  }

  undoInsert(type, dData, sri, eri) {
    const insertFn = () => {
      // let { sri, sci } = this.selector.range;
      const {
        rows, merges,
      } = this;
      const si = sri;
      const n = eri - sri + 1;
      if (type === 'row') {
        rows.insert(sri, n, null);
        Object.assign(rows._, dData);
        this.totalHeightValue += this.rows.sumHeight(sri, eri + 1);
        // this.totalHeightValue += rows.height;
      } else if (type === 'column') {
        // if (start !== undefined) {
        //   sci = start;
        // }
        // rows.insertColumn(sci, n);
        // si = sci;
        // cols.len += 1;
        // // linjian 动态增加列宽
        // this.totalWidthValue += cols.width;
      }
      merges.shift(type, si, n, (ri, ci, rn, cn) => {
        const cell = rows.getCell(ri, ci);
        cell.merge[0] += rn;
        cell.merge[1] += cn;
      });
    };
    insertFn();
  }

  // type: row | column linjian 这里添加起始行和模板行
  // linjian 添加isRedo参数，判断isRedo是否为true，如果为true，则说明是ctrl+Y,插入
  // isUndo 如果为true，这说明是ctrl + z操作完成
  insert(type, n = 1, start, templateRow, isRedo) {
    let templateObj;
    if (typeof templateRow === 'number') {
      templateObj = this.rows._[templateRow];
    } else {
      templateObj = templateRow;
    }
    let needAddMerges; // 插入行，如果有合并单元格要继承下来
    const insertFn = () => {
      let { sri, sci } = this.selector.range;
      const {
        rows, merges, cols, settings,
      } = this;
      let si = sri;
      if (type === 'row') {
        if (start !== undefined) {
          sri = start;
          si = start;
        }
        if (settings.streamCharts && templateRow !== undefined) {
          // 更新流表行号
          this.settings.streamCharts = helper.updateStreamCharts({
            streamCharts: settings.streamCharts,
            actionType: 'insert-row',
            start: templateRow,
            n,
          });
        }
        needAddMerges = rows.insert(sri, n, templateObj);
        this.totalHeightValue += this.rows.sumHeight(sri, sri + n);
        // this.totalHeightValue += rows.height;
      } else if (type === 'column') {
        if (start !== undefined) {
          sci = start;
        }
        rows.insertColumn(sci, n);
        si = sci;
        cols.len += 1;
        // linjian 动态增加列宽
        this.totalWidthValue += cols.width;
      }
      merges.shift(type, si, n, (ri, ci, rn, cn) => {
        const cell = rows.getCell(ri, ci);
        cell.merge[0] += rn;
        cell.merge[1] += cn;
      });
      needAddMerges.forEach((item) => {
        this.merges.add(new CellRange(item.sri, item.sci, item.eri, item.eci));
      });
    };
    if (!isRedo) {
      this.changeData({
        actionType: `insert-${type}`,
        start,
        n,
        templateRow,
        templateObj,
      }, insertFn);
    } else {
      insertFn();
    }
  }

  // type: row | column linjian 增加isUndo 用来判断是不是ctrl + z回退操作 dependences:删除行时，公式依赖
  delete(type, isUndo, undoRange, dependencesArr) {
    const {
      rows, merges, selector, cols, settings,
    } = this;
    let range;
    if (!isUndo) {
      // eslint-disable-next-line prefer-destructuring
      range = selector.range;
    } else {
      range = undoRange;
    }
    const {
      sri, sci, eri, eci,
    } = range;
    const [rsize, csize] = range.size();
    let actionType = `delete-${type}`;
    const dData = {}; // 存储删除的数据
    const tempRowObj = {};
    const ndata = rows._;
    if (!isUndo) {
      if (rsize > this.historyNumCount) {
        this.hook.tipMessage('alert', '删除数据量过大，此操作不记录到历史记录');
        actionType = 'no-record';
        if (typeof this.hook.afterTriggerHistoryAction === 'function') {
          const tempData = {
            actionType: 'delete-row',
            rsize,
            csize,
            dData: {},
          };
          this.hook.afterTriggerHistoryAction('history-add', { ...tempData, ...range });
        }
      } else {
        // 获取要删除的格子
        for (let index = sri; index < eri + 1; index++) {
          if (ndata[index]) {
            dData[index] = ndata[index];
            const templateRow = helper.getStreamLastRowTempRow(this.settings.streamCharts, index);
            if (templateRow !== undefined) {
              tempRowObj['1'] = templateRow;
            }
          }
        }
      }
    }
    const deleteFn = () => {
      let si = sri;
      let size = rsize;
      if (type === 'row') {
        if (settings.streamCharts) {
          setTimeout(() => {
            this.settings.streamCharts = helper.updateStreamCharts({
              streamCharts: settings.streamCharts,
              actionType: 'delete-row',
              start: sri,
              n: rsize,
            });
          }, 0);
        }
        rows.delete(sri, eri);
      } else if (type === 'column') {
        rows.deleteColumn(sci, eci);
        si = range.sci;
        size = csize;
        cols.len -= 1;
      }
      // console.log('type:', type, ', si:', si, ', size:', size);
      merges.shift(type, si, -size, (ri, ci, rn, cn) => {
        // console.log('ri:', ri, ', ci:', ci, ', rn:', rn, ', cn:', cn);
        const cell = rows.getCell(ri, ci);
        cell.merge[0] += rn;
        cell.merge[1] += cn;
        if (cell.merge[0] === 0 && cell.merge[1] === 0) {
          delete cell.merge;
        }
      });
    };
    if (isUndo) {
      deleteFn();
    } else {
      this.changeData({
        actionType,
        sri,
        sci,
        eri,
        eci,
        rsize,
        csize,
        dData,
        tempRowObj,
        dependencesArr,
      }, deleteFn);
    }
  }

  scrollx(x, cb) {
    const { scroll, freeze, cols } = this;
    const [, fci] = freeze;
    const [
      ci, left, width,
    ] = helper.rangeReduceIf(fci, cols.len, 0, 0, x, i => cols.getWidth(i));
    // console.log('fci:', fci, ', ci:', ci);
    let x1 = left;
    if (x > 0) x1 += width;
    if (scroll.x !== x1) {
      scroll.ci = x > 0 ? ci : 0;
      scroll.x = x1;
      cb();
    }
  }

  scrolly(y, cb) {
    const {
      scroll, freeze, rows, cachePosition,
    } = this;
    const [fri] = freeze;
    // linjian 为了支持一亿行数据滚动目前做了这个处理，支持让webworker计算缓存值，有缓存值，直接从缓存取值。
    let inits = 0; const initv = 0; let min = fri;
    let max = rows.len;
    if (cachePosition.length > 0) {
      if (y > cachePosition[0].val) {
        cachePosition.some((value, index) => {
          let currentVal = value.val;
          let nextVal = -1;
          if (cachePosition[index + 1]) {
            if (fri !== 0) {
              nextVal = cachePosition[index + 1].friValue;
            } else {
              nextVal = cachePosition[index + 1].val;
            }
          }
          if (fri !== 0) {
            currentVal = value.friValue;
          }
          if (currentVal <= y) {
            if (nextVal !== -1) {
              if (nextVal > y) {
                inits = currentVal;
                min = value.rowIndex;
                max = cachePosition[index + 1].rowIndex;
                return true;
              }
            } else {
              inits = currentVal;
              min = value.rowIndex;
              return true;
            }
          }
          return false;
        });
      }
    }
    const [
      ri, top, height,
    ] = helper.rangeReduceIf(min, max, inits, initv, y, i => rows.getHeight(i));
    let y1 = top;
    if (y > 0) y1 += height;
    // console.log('ri:', ri, ' ,y:', y1);
    if (scroll.y !== y1) {
      scroll.ri = y > 0 ? ri : 0;
      scroll.y = y1;
      cb(ri);
    }
  }

  // linjian这里增加传入startPostion起始位置，用于数据量大的时候 不需要从头开始算当前格子的left top
  cellRect(ri, ci, startPostion = this.startPostion, viewRange, renderPosition) {
    const { rows, cols } = this;
    // linjian 性能优化 使用cellCustomerPosition 替代cellPosition
    // eslint-disable-next-line prefer-const
    let { left, top } = this.cellCustomerPosition(ri, ci, startPostion);
    // const { left, top } = this.cellPosition(ri, ci);
    // const { sri = ri } = startPostion || {};
    const cell = rows.getCell(ri, ci);
    let width = cols.getWidth(ci);
    let height = rows.getHeight(ri);
    let mergeRealHeight = height; // 获取合并单元格的实际高度
    const { x, y } = this.scroll;
    let eriRange = -1;
    let eciRange = -1;
    let sriRange = -1;
    const [freezeX, freezeY] = this.freeze;
    if (viewRange) {
      eriRange = viewRange.eri;
      eciRange = viewRange.eci;
      sriRange = viewRange.sri;
    }
    if (cell !== null) {
      if (cell.merge) {
        const [rn, cn] = cell.merge;
        // console.log('cell.merge:', cell.merge);
        if (rn > 0) {
          let offsetHeight = 0;
          for (let i = 1; i <= rn; i += 1) {
            const riHeight = rows.getHeight(ri + i);
            height += riHeight;
            if (freezeX > 0 && viewRange && ri + i > eriRange) {
              offsetHeight += riHeight;
            }
          }
          mergeRealHeight = height;
          if (freezeX > 0 && viewRange) {
            if (offsetHeight > y) {
              height -= y;
            } else {
              height -= offsetHeight;
            }
          }
          if (sriRange > ri && renderPosition !== 'left-down') {
            for (let i = 0; i < sriRange - ri; i++) {
              height -= rows.getHeight(ri + i);
            }
          }
          // let offsetHeight = 0;
          // if (sri > ri && rn + ri >= sri) {
          //   const offsetRow = sri - ri;
          //   for (let i = sri; i <= sri + rn - offsetRow; i += 1) {
          //     height += rows.getHeight(i);
          //   }
          //   mergeRealHeight = height;
          //   for (let i = ri; i < sri; i += 1) {
          //     mergeRealHeight += rows.getHeight(i);
          //   }
          // } else {
          //   for (let i = 1; i <= rn; i += 1) {
          //     const riHeight = rows.getHeight(ri + i);
          //     height += riHeight;
          //     if (freezeY > 0 && viewRange && ri + i > eriRange) {
          //       offsetHeight += riHeight;
          //     }
          //   }
          //   if (freezeY > 0 && viewRange) {
          //     if (offsetHeight > y) {
          //       height -= y;
          //     } else {
          //       height -= offsetHeight;
          //     }
          //   }
          //   mergeRealHeight = height;
          // }
        }
        if (cn > 0) {
          // 处理冻结窗格状态下，跨列单元格不能超过当前viewrange的范围
          let offsetWidth = 0;
          for (let i = 1; i <= cn; i += 1) {
            const ciWidth = cols.getWidth(ci + i);
            width += ciWidth;
            if (freezeY > 0 && viewRange && ci + i > eciRange) {
              offsetWidth += ciWidth;
            }
          }
          if (freezeY > 0 && viewRange) {
            if (offsetWidth > x) {
              width -= x;
            } else {
              width -= offsetWidth;
            }
          }
        }
      }
    }
    // console.log('data:', this.d);
    return {
      left, top, width, height, cell, mergeRealHeight,
    };
  }

  /**
   * @description: 用来实现cellPostion性能优化版本，功能完全同cellPosition
   * @param {type}
   * @return:
   * @Author: linjian
   * @Date: 2019-05-13 18:59:21
   */
  cellCustomerPosition(ri, ci, startPostion) {
    const { cols, rows } = this;
    let left = 0;
    let top = 0;
    // linjian 为了支持一亿行数据滚动目前做了这个处理，支持让webworker计算缓存值，有缓存值，直接从缓存取值。
    // let inits = 0, min = 0;
    left = cols.sumWidth(0, ci);
    // 这里主要优化top获取
    // if(startPostion && startPostion.ri === ri) {
    //     top = startPostion.top;
    // } else if (cachePosition.length > 0) {
    //     if (ri > cachePosition[0].rowIndex) {
    //       cachePosition.some((value, index)=>{
    //         let rowIndex = value.rowIndex;
    //         if (cachePosition[index + 1]) {
    //           if (rowIndex <= ri && ri < cachePosition[index + 1].rowIndex) {
    //             inits = value.val;
    //             min = rowIndex;
    //             return true;
    //           }
    //         } else if (rowIndex <= ri) {
    //           inits = value.val;
    //           min = rowIndex;
    //           return true;
    //         }
    //       });
    //     }
    //   top = inits + rows.sumHeight(min, ri);
    // } else {
    //   top = rows.sumHeight(0, ri);
    // }
    if (startPostion && startPostion.sri !== undefined) {
      top = rows.sumHeight(startPostion.sri, ri);
    } else {
      top = rows.sumHeight(0, ri);
    }
    // if (startPostion){
    //   startPostion.top = top;
    //   startPostion.left = left;
    //   startPostion.ri = ri;
    //   startPostion.ci = ci;
    // }
    return {
      left, top,
    };
  }

  cellPosition(ri, ci, startPostion = {}, viewRangeOffset = 0) {
    const { cols, rows } = this;
    const left = cols.sumWidth(0, ci);
    let top = 0;
    if (startPostion && startPostion.sri !== undefined && (Math.abs(ri - startPostion.sri) <= viewRangeOffset + 1)) {
      top = rows.sumHeight(startPostion.sri, ri) + startPostion.top || 0;
    } else {
      top = rows.sumHeight(0, ri);
    }
    return {
      left, top,
    };
  }

  getCell(ri, ci) {
    return this.rows.getCell(ri, ci);
  }

  /**
   * @description: 返回行数据值
   * @param {type}
   * @return:
   * @Author: linjian
   * @Date: 2019-08-08 17:55:39
   */
  getRowCellTextOrDefault(ri) {
    const { cols } = this;
    const { len } = cols;
    const result = [];
    for (let index = 0; index < len; index++) {
      result.push(this.getCellTextOrDefault(ri, index, true));
    }
    return result;
  }

  getCellTextOrDefault(ri, ci, isGetValue) {
    const cell = this.getCell(ri, ci);
    if (cell) {
      if (cell.cellProps) {
        const { cellType } = cell.cellProps;
        if (cellType === 'image' || cellType === 'file') {
          if (isGetValue) {
            const { typeOptions } = cell;
            if (typeOptions) {
              if (cellType === 'image') {
                return typeOptions.images || [];
              }
              if (cellType === 'file') {
                return typeOptions.files || [];
              }
            }
          }
          return '';
        }
      }
      if (cell.text !== undefined && cell.text !== '') {
        if (isGetValue && helper.isFormulaExpression(cell.text)) {
          return cell.formularValue;
        }
        return cell.text;
      }
      // 默认值
      if (cell.cellProps && cell.cellProps.defaultValue !== undefined) {
        return cell.cellProps.defaultValue;
      }
    }
    return '';
  }

  // linjian 添加公式取值
  getCellFormulaTextOrDefault(ri, ci) {
    const cell = this.getCell(ri, ci);
    return (cell && cell.formularValue) ? cell.formularValue : '';
  }

  getCellStyle(ri, ci) {
    const cell = this.getCell(ri, ci);
    if (cell && cell.style !== undefined) {
      // return this.styles[cell.style];
      return cell.style;
    }
    return null;
  }

  /**
   * @description: 用于获取单元格属性值
   * @param {ri, ci} 单元格横纵坐标
   * @return: 返回单元格属性
   * @Author: linjian
   * @Date: 2019-05-08 15:49:55
   */
  getCellProps(ri, ci) {
    const cell = this.getCell(ri, ci);
    if (cell) {
      if (!cell.cellProps) {
        cell.cellProps = {};
      }
      return cell.cellProps;
    }
    return null;
  }

  getCellPropsAtRow(ri) {
    const rowProps = [];
    for (let i = 0; i < this.cols.len; i++) {
      rowProps.push(this.getCellProps(ri, i));
    }
    return rowProps;
  }

  getCellPropsOrNew(ri, ci) {
    const cell = this.rows.getCellOrNew(ri, ci);
    if (!cell.cellProps) {
      cell.cellProps = {
        cellType: 'text',
      };
    }
    return cell.cellProps;
  }

  getCellStyleOrDefault(ri, ci) {
    const { rows } = this;
    const cell = rows.getCell(ri, ci);
    // const cellStyle = (cell && cell.style !== undefined) ? styles[cell.style] : {};
    // linjian  这种方式不适合二维样式  采取直接取的方式
    const cellStyle = (cell && cell.style) || {};
    return helper.merge(this.defaultStyle(), cellStyle);
  }

  getSelectedCellStyle() {
    const { ri, ci } = this.selector;
    return this.getCellStyleOrDefault(ri, ci);
  }

  // state: input | finished linjian: 增加changeSource，标记是通过textare产生编辑还是像下拉那种直接点击输入
  setCellText(ri, ci, text, state, changeSource) {
    const { rows, history, validations } = this;
    const initText = this.getCellTextOrDefault(ri, ci);
    const cellProps = this.getCellPropsOrNew(ri, ci);
    const cell = this.getCell(ri, ci);

    if (state === 'finished') {
      const { cellType, scale } = cellProps;
      if (cellType === 'numeric') {
        if (!helper.isFormulaExpression(text)) {
          if (isNaN(text)) {
            this.hook.tipMessage('toast', '请输入数值！');
            return;
          }
          text = parseFloat(text);
        }
        if (scale) {
          const { type } = cellProps;
          switch (type) {
            case 'percentage':
              text = parseFloat(text.toFixed(scale + 2));
              break;
            case 'permillage':
              text = parseFloat(text.toFixed(scale + 3));
              break;
            default:
              text = parseFloat(text.toFixed(scale));
              break;
          }
        }
      }
      if (cellType === 'dropdown') {
        const { allowCustomValue } = cell.typeOptions || {};
        if (allowCustomValue) {
          cell.dropdownText = '';
          cell.customerText = '';
        }
      }
      const changes = [ri, ci, cell.initText, text];
      // linjian 如果是下拉项/时间/日期时间/日期，则直接取原来的值，如果是用户输入的，因为每输入字符都会写入cell.text，所以从cell.initText里面取
      const initSpecialType = ['dropdown', 'date', 'datetime', 'time', 'checkbox'];
      if (initSpecialType.includes(cellProps.cellType) && changeSource !== 'editorChange') {
        changes[2] = cell.text;
      } else if (cell.initText === undefined) {
        changes[2] = text;
      }
      delete cell.initText;
      cell.isEdit = true;
      // linjian 添加至改变操作栈 只有当值不相等的时候 ，才去记录历史
      // history.add(this.getData());
      if (changes[2] !== changes[3]) {
        history.add(null, {
          actionType: 'modify-data', ri, ci, data: { text: changes[2], currentData: changes[3] },
        });
      }
      rows.setCellText(ri, ci, text);
      // 处理同一列，如果是合并单元格，并且处理流表中，流表存在forceMergeCell字段 则要一起修改被合并的值
      // linjian 值改变后 触发公式依赖 如果改变的单元格是某个公式所依赖的  则更新公式
      this.hook.afterSetDataAtCell.call(this, changes, state);
    } else {
      if (cell.initText === undefined) {
        cell.initText = initText;
      }
      rows.setCellText(ri, ci, text);
      this.change(this.getData());
    }
    // validator
    validations.validate(ri, ci, text);
  }

  setCellImageSource(ri, ci, images) {
    console.log('setCellImageSource');
    const { rows, history } = this;
    const cell = rows.getCellOrNew(ri, ci);
    if (!cell.typeOptions) {
      cell.typeOptions = {};
    }
    const lastImages = cell.typeOptions.images || [];
    history.add(null, {
      actionType: 'modify-data', ri, ci, data: { text: lastImages, currentData: images },
    });
    cell.typeOptions.images = images;
  }

  setCellFileSource(ri, ci, files) {
    console.log('setCellFileSource');
    const { rows, history } = this;
    const cell = rows.getCellOrNew(ri, ci);
    if (!cell.typeOptions) {
      cell.typeOptions = {};
    }
    const lastFiles = cell.typeOptions.files || [];
    history.add(null, {
      actionType: 'modify-data', ri, ci, data: { text: lastFiles, currentData: files },
    });
    cell.typeOptions.files = files;
  }

  setCellFormulaValue(ri, ci, text) {
    const { rows } = this;
    if (this.isCalCulatorFormula) {
      const cell = rows.getCellOrNew(ri, ci);
      if (cell && cell.formularValue === text) {
        return false;
      }
      const params = JSON.stringify({
        actionType: 'modify-data',
        ri,
        ci,
        data: { currentData: text },
      });
      this.hook.afterTriggerHistoryAction('history-add', params);
    }
    rows.setCellFormulaValue(ri, ci, text);
    return true;
  }

  freezeIsActive() {
    const [ri, ci] = this.freeze;
    return ri > 0 || ci > 0;
  }

  setFreeze(ri, ci) {
    this.changeData('setFreeze', () => {
      this.freeze = [ri, ci];
    });
  }

  freezeTotalWidth() {
    return this.cols.sumWidth(0, this.freeze[1]);
  }

  freezeTotalHeight() {
    return this.rows.sumHeight(0, this.freeze[0]);
  }

  setRowHeight(ri, height) {
    this.changeData('setRowHeight', () => {
      this.rows.setHeight(ri, height);
    });
  }

  setColWidth(ci, width) {
    this.changeData('setColWidth', () => {
      this.cols.setWidth(ci, width);
    });
  }

  viewHeight() {
    const { view, showToolbar } = this.settings;
    let h = view.height();
    if (showToolbar) {
      h -= toolbarHeight;
    }
    return h;
  }

  viewWidth() {
    return this.settings.view.width();
  }

  freezeViewRange() {
    const [ri, ci] = this.freeze;
    return new CellRange(0, 0, ri - 1, ci - 1, this.freezeTotalWidth(), this.freezeTotalHeight());
  }

  exceptRowTotalHeight(sri, eri) {
    const { exceptRowSet, rows } = this;
    const exceptRows = Array.from(exceptRowSet);
    let exceptRowTH = 0;
    exceptRows.forEach((ri) => {
      if (ri < sri || ri > eri) {
        const height = rows.getHeight(ri);
        exceptRowTH += height;
      }
    });
    return exceptRowTH;
  }

  viewRange() {
    const {
      scroll, rows, cols, freeze, exceptRowSet,
    } = this;
    let { ri, ci } = scroll;
    if (ri <= 0) [ri] = freeze;
    if (ci <= 0) [, ci] = freeze;

    let [x, y] = [0, 0];
    let [eri, eci] = [rows.len, cols.len];
    for (let i = ri; i < rows.len; i += 1) {
      if (!exceptRowSet.has(i)) {
        y += rows.getHeight(i);
        eri = i;
      }
      if (y > this.viewHeight()) break;
    }
    for (let j = ci; j < cols.len; j += 1) {
      x += cols.getWidth(j);
      eci = j;
      if (x > this.viewWidth()) break;
    }
    // console.log(ri, ci, eri, eci, x, y);
    return new CellRange(ri, ci, eri, eci, x, y);
  }

  eachMergesInView(viewRange, cb) {
    this.merges.filterIntersects(viewRange)
      .forEach(it => cb(it));
  }

  isCellInRange(range, ri, ci) {
    return helper.isCellInRange(range, ri, ci);
  }

  getLastCellMerge(ri, ci) {
    return helper.getLastCellMerge(this.merges, ri, ci);
  }

  isCellInMerge(ri, ci) {
    const isInMerge = helper.isCellInMerge(this.merges, ri, ci);
    return isInMerge;
  }

  rowEach(min, max, cb) {
    let y = 0;
    const { rows } = this;
    const frset = this.exceptRowSet;
    const frary = [...frset];
    let offset = 0;
    for (let i = 0; i < frary.length; i += 1) {
      if (frary[i] < min) {
        offset += 1;
      }
    }
    // console.log('min:', min, ', max:', max, ', scroll:', scroll);
    for (let i = min + offset; i <= max + offset; i += 1) {
      if (frset.has(i)) {
        offset += 1;
      } else {
        const rowHeight = rows.getHeight(i);
        cb(i, y, rowHeight);
        y += rowHeight;
        if (y > this.viewHeight()) break;
      }
    }
  }

  colEach(min, max, cb) {
    let x = 0;
    const { cols } = this;
    for (let i = min; i <= max; i += 1) {
      const colWidth = cols.getWidth(i);
      if (colWidth === 0) {
        // eslint-disable-next-line no-continue
        continue;
      }
      cb(i, x, colWidth);
      x += colWidth;
      if (x > this.viewWidth()) break;
    }
  }

  defaultStyle() {
    return this.settings.style;
  }

  addStyle(nstyle) {
    // const { styles } = this;
    // // console.log('old.styles:', styles, nstyle);
    // for (let i = 0; i < styles.length; i += 1) {
    //   const style = styles[i];
    //   if (helper.equals(style, nstyle)) return i;
    // }
    // styles.push(nstyle);
    // return styles.length - 1;
    return nstyle;
  }

  changeData(actionParams, cb) {
    this.history.add(this.getData(), actionParams);
    cb();
    this.change(this.getData());
  }

  setData(d) {
    Object.keys(d).forEach((property) => {
      if (property === 'merges' || property === 'rows'
        || property === 'cols' || property === 'validations') {
        this[property].setData(d[property]);
      } else if (property === 'freeze') {
        const [x, y] = expr2xy(d[property]);
        this.freeze = [y, x];
      } else if (d[property] !== undefined) {
        this[property] = d[property];
      }
    });
    return this;
  }

  getData() {
    const {
      name, freeze, styles, merges, rows, cols, validations, autoFilter,
    } = this;
    return {
      name,
      freeze: xy2expr(freeze[1], freeze[0]),
      styles,
      merges: merges.getData(),
      rows: rows.getData(),
      cols: cols.getData(),
      validations: validations.getData(),
      autofilter: autoFilter.getData(),
    };
  }
}
